import React from 'react'
import { useTable, useFilters, useGlobalFilter, useSortBy, usePagination } from 'react-table'
import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TextField from '../../../../../SharedComponents/TextField/CustomTextField'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import matchSorter from 'match-sorter'
import classes from './scss/CustomTable.module.scss'

const DefaultColumnFilter = ({ column: { filterValue, setFilter } }) => {
  return (
    <div id='textField' className={classes.textField}>
      <TextField
        value={filterValue || ''}
        onChange={e => {
          setFilter(e.target.value || undefined) // Set undefined to remove the filter entirely
        }}
      />
    </div>
  )
}

const fuzzyTextFilterFn = (rows, id, filterValue) => {
  return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
}

// Let the table remove the filter if the string is empty
fuzzyTextFilterFn.autoRemove = val => !val

// Our table component

const ReactTableComponents = props => {
  React.useEffect(() => {}, [props.data])
  const filterTypes = React.useMemo(
    () => ({
      // Add a new fuzzyTextFilterFn filter type.
      fuzzyText: fuzzyTextFilterFn,
      // Or, override the default text filter to use
      // 'startWith'
      text: (rows, id, filterValue) => {
        return rows.filter(row => {
          const rowValue = row.values[id]
          return rowValue !== undefined
            ? String(rowValue)
              .toLowerCase()
              .startsWith(String(filterValue).toLowerCase())
            : true
        })
      }
    }),
    []
  )

  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Filter: DefaultColumnFilter
    }),
    []
  )

  const { columns, data, countName, type } = props

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,

    state: { pageIndex, pageSize }
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      filterTypes,
      initialState: {
        pageIndex: 0,
        pageSize: 10
      },
      disableSortRemove: true

      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  )

  return (
    <React.Fragment>
      <table
        {...getTableProps()}
        className={
          type === 'standardRates'
            ? classes.rootStandardRates
            : type === 'hospitalStandardRates'
              ? classes.hospitalStandardRates
              : classes.rootTable
        }
      >
        <div id='TableHeadRoot' className={classes.TableHeadRoot}>
          {headerGroups.map((headerGroup, index) => (
            <tr key={index} {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
              {headerGroup.headers.map((column, index) => (
                <th key={index} scope='row' align='left' className={classes.tableCell}>
                  {column.canFilter ? (
                    <div id='withTextfield' className={classes.withTextfield}>
                      {column.render('Header')}
                      <span
                        id='showIcon'
                        className={classes.showIcon}
                        {...column.getHeaderProps(column.getSortByToggleProps())}
                      >
                        {column.isSortedDesc ? (
                          <div id='sortIcon' className={classes.sortIcon}>
                            <FontAwesomeIcon icon='sort' />
                          </div>
                        ) : (
                          <div id='sortIcon' className={classes.sortIcon}>
                            <FontAwesomeIcon icon='sort' />
                          </div>
                        )}
                      </span>
                      <div> {column.render('Filter')} </div>
                    </div>
                  ) : (
                    <div id='noTextField' className={classes.noTextField}>
                      {column.render('Header')}
                    </div>
                  )}
                </th>
              ))}
            </tr>
          ))}
        </div>
        <div id='tableBody' className={classes.tableBody}>
          <div {...getTableBodyProps()}>
            {page.map((row, i) => {
              prepareRow(row)
              return (
                <tr key={i} {...row.getRowProps()} className={classes.tableRow}>
                  {row.cells.map((cell, index) => {
                    return (
                      <td
                        key={index}
                        component='th'
                        align='left'
                        scope='row'
                        className={classes.tableCell}
                        {...cell.getCellProps()}
                      >
                        {cell.render('Cell')}
                      </td>
                    )
                  })}
                </tr>
              )
            })}
          </div>
        </div>
      </table>

      <div className={classes.paginationDiv}>
        <div id='pageCount' className={classes.pageCount}>
          {pageIndex + 1} - {page.length} Out of {data.length} {countName}
        </div>
        <div className={classes.Pagination}>
          <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
            {'<<'}
          </button>{' '}
          <button onClick={() => previousPage()} disabled={!canPreviousPage}>
            {'<'}
          </button>{' '}
          <button onClick={() => nextPage()} disabled={!canNextPage}>
            {'>'}
          </button>{' '}
          <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
            {'>>'}
          </button>
          <span style={{ marginLeft: '1em' }}>
            Page{' '}
            <strong>
              {pageIndex + 1} of {pageOptions.length}
            </strong>{' '}
          </span>
          <select
            value={pageSize}
            onChange={e => {
              setPageSize(Number(e.target.value))
            }}
            style={{ marginLeft: '1.5em' }}
          >
            {[10, 15, 20].map(pageSize => (
              <option key={pageSize} value={pageSize}>
                Show {pageSize}
              </option>
            ))}
          </select>
        </div>
      </div>
    </React.Fragment>
  )
}

const ReactTable = React.memo(ReactTableComponents, (prevProps, nextProps) => {
  try {
    if (
      prevProps.page === nextProps.page &&
      prevProps.data === nextProps.data &&
      prevProps.columns === nextProps.columns &&
      prevProps.headerGroups === nextProps.headerGroups
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})

export default ReactTable
