import moment from 'moment'
import { ValidateDateFormat } from '../../../../../Components/Validators/Validators'
import { YearDateFormat, DateErrorHandler } from '../../../../../Components/Utils/utils'
import getNestedObjects from '../../../../../Components/getNestedObjects/getNestedObjects'

const Destructure = (profile, newForms) => {
  const assignments = profile.facility_assignment
    ? profile.facility_assignment.assignments !== null
      ? profile.facility_assignment.assignments
      : []
    : []

  // const result = FacilityValue(assignments)

  newForms['id'] = getNestedObjects(profile, ['id']) //  profile.id ? profile.id : ''
  newForms['first_name'] = getNestedObjects(profile, ['name', 'first']) /// profile.name ? profile.name.first : ''
  newForms['last_name'] = getNestedObjects(profile, ['name', 'last']) // profile.name ? profile.name.last : ''
  newForms['middle_name'] = getNestedObjects(profile, ['name', 'middle']) // profile.name ? profile.name.middle : ''
  // let specialization = profile.specialization ? profile.specialization : ''

  newForms['birthdate'] = profile.birthdate ? YearDateFormat(profile.birthdate) : ''
  newForms['civil_status'] = profile.civil_status ? profile.civil_status : ''
  newForms['assignments'] = assignments
  newForms['exclusiveTo'] = profile
    ? profile.facility_assignment
      ? profile.facility_assignment
        ? profile.facility_assignment.exclusive_to
        : ''
      : ''
    : ''
  newForms['payee'] = getNestedObjects(profile, ['treasury', 'payee']) // profile ? (profile.treasury ? (profile.treasury.payee ? profile.treasury.payee : '') : '') : ''
  newForms['onHoldReason'] = getNestedObjects(profile, ['reason']) // profile ? (profile.reason ? profile.reason : '') : ''
  newForms['gender'] = getNestedObjects(profile, ['gender']) // profile.gender ? profile.gender : ''
  newForms['main'] = getNestedObjects(profile, ['specialization', 'main']) // specialization.main
  newForms['major'] = getNestedObjects(profile, ['specialization', 'major']) // specialization.major
  newForms['sub'] = getNestedObjects(profile, ['specialization', 'sub']) // specialization.sub
  newForms['other'] = getNestedObjects(profile, ['specialization', 'other']) // specialization.other
  newForms['status'] = getNestedObjects(profile, ['status']) // profile.status ? profile.status : ''
  newForms['prcNumber'] = getNestedObjects(profile, ['license', 'prc', 'number']) // profile.license ? profile.license.prc.number : ''
  newForms['prcExp'] = profile.license ? YearDateFormat(profile.license.prc.expiration_date) : ''
  newForms['philNumber'] = getNestedObjects(profile, ['philhealth', 'accreditation_number']) // profile.philhealth ? profile.philhealth.accreditation_number : ''
  newForms['philExp'] = profile.philhealth
    ? ValidateDateFormat(moment(profile.philhealth.expiration_date).format('MM/DD/YYYY'))
      ? YearDateFormat(profile.philhealth.expiration_date)
      : ''
    : ''
  newForms['pmaNumber'] = getNestedObjects(profile, ['license', 'pma']) // profile.license ? profile.license.pma : ''
  newForms['s2Number'] = getNestedObjects(profile, ['license', 's2']) //  profile.license ? profile.license.s2 : ''
  newForms['ptrNumber'] = getNestedObjects(profile, ['license', 'ptr']) // profile.license ? profile.license.ptr : ''
  newForms['tiNumber'] = getNestedObjects(profile, ['treasury', 'tin']) // profile.treasury ? profile.treasury.tin : ''
  newForms['vatTaxRate'] = getNestedObjects(profile, ['treasury', 'vat', 'tax_rate']) // profile.treasury ? (profile.treasury.vat ? profile.treasury.vat.tax_rate : '') : ''
  newForms['tinStatus'] = getNestedObjects(profile, ['treasury', 'tin_status']) //  profile.treasury ? profile.treasury.tin_status : ''
  newForms['processType'] = getNestedObjects(profile, ['process_type']) // profile.process_type ? profile.process_type : ''
  newForms['paymentTerms'] = getNestedObjects(profile, ['treasury', 'payment_terms']) // profile.treasury

  newForms['professionalFee'] = getNestedObjects(profile, ['professional_fees', 'rate']) //  profile.professional_fees ? profile.professional_fees.rate : ''
  newForms['withTax'] = getNestedObjects(profile, ['treasury', 'withHoldingTax']) //  profile.treasury ? (profile.treasury.withHoldingTax ? profile.treasury.withHoldingTax : '') : ''
  newForms['clinicSched'] = getNestedObjects(profile, ['facility_info', '0', 'clinic_schedule']) //  profile.facility_info ? profile.facility_info[0].clinic_schedule : ''
  newForms['ahmopi'] = getNestedObjects(profile, ['ahmopi']) // profile.ahmopi && profile.ahmopi ? profile.ahmopi : ''
  newForms['facilityName'] = getNestedObjects(profile, ['facility_info', '0', 'name']) // profile.facility_info ? profile.facility_info[0].name : ''
  newForms['facilityAddress'] = getNestedObjects(profile, ['facility_info', '0', 'full_address']) // profile.facility_info

  newForms['dateOfAffiliation'] = profile.affiliation_status
    ? ValidateDateFormat(moment(profile.affiliation_status.date_submitted).format('MM/DD/YYYY'))
      ? YearDateFormat(profile.affiliation_status.date_submitted)
      : ''
    : ''
  newForms['dateOfDisaffiliation'] = profile.disaffiliation
    ? ValidateDateFormat(moment(profile.disaffiliation.date).format('MM/DD/YYYY'))
      ? YearDateFormat(profile.disaffiliation.date)
      : ''
    : ''

  return newForms
}

const SavingData = form => {
  let obj = {}

  // obj.last_name = form['last_name']
  // obj.first_name = form['first_name']
  // obj.middle_name = form['middle_name']
  obj.id = form['id']
  obj.status = form['status']
  //   obj.dateSubmitted=
  //  obj.affiliationStatus =
  obj.dateSubmitted = DateErrorHandler(form['dateOfAffiliation'])
  obj.dateOfDisaffiliation = DateErrorHandler(form['dateOfDisaffiliation'])
  obj.assignments = form['assignments']
  obj.exclusiveTo = form['exclusiveTo']
  //   obj.mno =
  obj.firstName = form['first_name']
  // form['first_name']
  obj.middleName = form['middle_name']
  obj.lastName = form['last_name']
  //   obj.suffix =
  obj.birthdate = DateErrorHandler(form['birthdate'])
  obj.civilStatus = form['civil_status']
  obj.gender = form['gender']
  //   obj.emailAddress =
  //   obj.contactNumber =
  obj.mainSpecialization = form['main']
  obj.majorSpecialization = form['major']
  obj.subSpecialization = form['sub']
  obj.otherSpecialization = form['other']
  obj.pmaNumber = form['pmaNumber']
  obj.ptrNumber = form['ptrNumber']
  obj.stNumber = form['s2Number']
  obj.prcNumber = form['prcNumber']
  obj.prcExpiration = form['prcExp']
  obj.philhealthNumber = form['philNumber']
  obj.philhealthExpiration = DateErrorHandler(form['philExp'])
  obj.tinNumber = form['tiNumber']
  obj.ahmopi = form['ahmopi']
  obj.tinStatus = form['tinStatus']
  obj.vatTaxRate = form['vatTaxRate']
  obj.withTax = form['withTax']
  obj.facilityName = form['facilityName']
  obj.reason = form['onHoldReason']
  //   obj.facilityMpId =
  obj.address = form['facilityAddress']
  obj.payee = form['payee']
  //   obj.roomNumber =
  //   obj.contactNumbers =
  obj.clinicSchedule = form['clinicSched']
  obj.paymentTerms = form['paymentTerms']
  obj.serviceRate = form['professionalFee']
  obj.processType = form['processType']

  return obj
}

export { Destructure, SavingData }
