import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
import datePickerValidation, { datepickerInvalidDate } from 'Pages/Protected/Partner/Components/DatePicker/DatePickerUtils'
import { validateTaxRateVal } from 'Pages/Protected/Partner/Components/Utils/utils'


const notRequiredFields = [
  'address',
  'roomNumber',
  'contactNumbers',
  'clinicSchedule',
  'mainCoordinatorName',
  'mainContactNum',
  'asstCoordinatorName',
  'asstContactNum',
  'applicantRemarks',
  'ptrNumber',
  's2Number',
  // 'philhealthNumber',
  'philhealthExpiration',
  'otherSpecialization',
  'suffix',
  'subSpecialization',
  'pmaNumber',
  'asstCoordinators',
  'affiliationInfos',
  'affiliatedGroup',
  'status',
  'affiliationStatus',
  'birthdate',
  'nonVatable',
  'ID'

]
const schema = Joi.object({
  medicalPartnerId: Joi.string().label('Medical Partner ID').required(),
  firstName: Joi.string()
    .label('First Name')
    .required(), // R
  birthDate: Joi.string()
    .label('Birth date')
    .required(), // R
  emailAddress: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(), // R
  mainSpecialization: Joi.string()
    .label('Main Specialization')
    .required(), // R
  prcNumber: Joi.string()
    .label('PRC Number')
    .required(), // R
  tinNumber: Joi.string()
    .label('Tin Number')
    .min(12)
    .required(), // R
  middleName: Joi.string()
    .label('Middle Name')
    .required(), // R
  lastName: Joi.string()
    .label('Last Name')
    .required(), // R
  civilStatus: Joi.string()
    .label('Civil Status')
    .required(), // R
  gender: Joi.string()
    .label('Gender')
    .required(), // R
  contactNumber: Joi.string()
    .label('Contact Number')
    .required(), // R
  majorSpecialization: Joi.string()
    .label('Major Specialization')
    .required(), // R
  prcExpiration: Joi.string()
    .label('PRC Expiration')
    .required(), // R
  payee: Joi.string()
    .label('Check Payee')
    .required(), // R
  tinStatus: Joi.string()
    .label('Tin Status')
    .required(), // R
  // nonVatable: Joi.string()
  //   .label('Non VATable')
  //   .required(),
  vatTaxRate: Joi.number()
    .label('Tax Rate')
    .required(),
  intellicareRemarks: Joi.string()
    .label('Intellicare Remarks')
    .required(),
  facilityName: Joi.string().label('Facility Name').required(),
  facilityMpId: Joi.string().label('Medical Partner ID').required(),
  philhealthNumber: Joi.string()
    .label('PhilHealth Accreditation No')
    .max(12)
    .min(12)
    .required()
})

export default function DoctorFormValidation (form, isAddedType, errors, dateVal = {}) {
  let newErrors = {...errors}
  console.log('date value', dateVal)
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      // newErrors[i.path[0]] = 'Required Fields'
      newErrors[i.path[0]] = i.message
      // if (form['tinStatus'] === '12%' || form['tinStatus'] === '') {
      //   delete errors['nonVatable']
      // }
      newErrors = {...datePickerValidation(dateVal, 'prcExpiration', newErrors, true)} // identify if input is required or invalid format
      newErrors = {...datePickerValidation(dateVal, 'birthDate', newErrors, true)}
      newErrors = {...validateTaxRateVal(form, newErrors)}

      if (form['philhealthNumber'] === '') {
        delete newErrors['philhealthNumber']
      } else {
        if (form['philhealthNumber'].length === 12) {
          delete newErrors['philhealthNumber']
        }
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete newErrors[i.path[0]]
      }

      if (!isAddedType) {
        delete newErrors['medicalPartnerId']
      }
      if (Object.keys(dateVal).length > 0) {
        newErrors = {...datePickerValidation(dateVal, 'philhealthExpiration', newErrors, false)}
      }
    }
  }
  console.log('newErrors', newErrors)

  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
