import React from 'react'
import DoctorAffiliationContext from './context/DoctorAffiliationContext'
import Page1 from './DoctorAffiliation_p1'
import Page2 from './DoctorAffiliation_p2'

const DoctorAffiliation = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  const [form, setForm] = React.useState({})

  const [affiliationType, setAffiliationType] = React.useState({})

  return (
    <React.Fragment>
      <DoctorAffiliationContext.Provider value={{ paramId: match.params.id, setAffiliationType, affiliationType, isApproved, setIsApproved, form, setForm }}>
        {!isNextPage && <Page1 setPage={setIsNextPage} history={history} />}
        {isNextPage && <Page2 setPage={setIsNextPage} history={history} />}
      </DoctorAffiliationContext.Provider>
    </React.Fragment>
  )
}
export default DoctorAffiliation
