import React from 'react'

const InputRefs = form => {
  const formerName = React.useRef({})
  const street = React.useRef({})
  const zipCode = React.useRef({})
  const clinicSchedule = React.useRef({})
  // const years = React.useRef({})
  // const months = React.useRef({})
  const philhealthNumber = React.useRef({})
  const checkPayee = React.useRef({})
  const creditLimit = React.useRef({})
  const paymentTerms = React.useRef({})
  const vatTaxRate = React.useRef({})
  const facilityDiscount = React.useRef({})
  const reason = React.useRef({})
  const exclusiveTo = React.useRef({})
  const specialInstructions = React.useRef({})

  formerName.current.value = form['formerName']
  street.current.value = form['street']
  zipCode.current.value = form['zipCode']
  clinicSchedule.current.value = form['clinicSchedule']
  // years.current.value = form['years']
  // months.current.value = form['months']
  philhealthNumber.current.value = form['philhealthNumber']
  checkPayee.current.value = form['checkPayee']
  creditLimit.current.value = form['creditLimit']
  paymentTerms.current.value = form['paymentTerms']
  vatTaxRate.current.value = form['vatTaxRate']
  facilityDiscount.current.value = form['facilityDiscount']
  reason.current.value = form['reason']
  exclusiveTo.current.value = form['exclusiveTo']
  specialInstructions.current.value = form['specialInstructions']

  return {
    formerName,
    street,
    zipCode,
    clinicSchedule,
    specialInstructions,
    // years,
    // months,
    reason,
    exclusiveTo,
    philhealthNumber,
    checkPayee,
    creditLimit,
    paymentTerms,
    vatTaxRate,
    facilityDiscount
  }
}

export default InputRefs
