import React from 'react'
import Grid from '@material-ui/core/Grid'
import moment from 'moment'
import { useDispatch, useSelector } from 'react-redux'
// Import Partner Components
import PartnerCard from '../../../../Components/PartnerCard/PartnerCardV2'
import OICViewList from '../../../../Components/OIC/Dashboard/OICViewList'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import OICProcessorServices from 'Services/Api/Partner/OICProcessorServices'
import { resetState } from '../../../Store/Actions/actions'
// Import scss module
import classes from './Dashboard.module.scss'

const Dashboard = props => {
  const [isLoaded, setIsLoaded] = React.useState(false)
  const dispatch = useDispatch()
  const [viewList, setViewList] = React.useState({
    isOpen: false,
    stage: ''
  })
  const [forOICCard, setForOICCard] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })
  const [forReOICCard, setForReOICCard] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })
  const [forSubmittedOICCard, setForSubmittedOICCard] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome([])
    setIsLoaded(true)
    dispatch(resetState())
  }, [])

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForSubmittedOICCard({ ...forSubmittedOICCard, isLoading: true })
      const { data } = await OICProcessorServices.getPartnerTileOICData({
        filter: forSubmittedOICCard.filter,
        stage_name: 'FOR_SUBMITTED_OIC'
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.afn_number ? obj.afn_number : obj.id,
          type: obj.type,
          name: obj.name
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForSubmittedOICCard({ ...forSubmittedOICCard, dataList: sortedData, count: fixData.length, isLoading: false })
    }
    fetchTileData()
  }, [forSubmittedOICCard.filter])

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForReOICCard({ ...forReOICCard, isLoading: true })
      const { data } = await OICProcessorServices.getPartnerTileOICData({
        filter: forReOICCard.filter,
        stage_name: 'FOR_OIC_REEVALUATION'
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          dateSubmitted: moment(obj.stage.date_submitted).format('MM/DD/YYYY'),
          type: obj.type,
          name: obj.name,
          location: `${obj.address.city}, ${obj.address.region}`
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForReOICCard({ ...forReOICCard, dataList: sortedData, count: fixData.length, isLoading: false })
    }
    fetchTileData()
  }, [forReOICCard.filter])

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForOICCard({ ...forOICCard, isLoading: true })
      const { data } = await OICProcessorServices.getPartnerTileOICData({
        filter: forOICCard.filter,
        stage_name: 'FOR_OIC'
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          dateSubmitted: moment(obj.stage.date_submitted).format('MM/DD/YYYY'),
          type: obj.type.replace('_', ' '),
          name: obj.name,
          location: `${obj.address.city}, ${obj.address.region}`
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForOICCard({ ...forOICCard, dataList: sortedData, count: fixData.length, isLoading: false })
    }
    fetchTileData()
  }, [forOICCard.filter])

  const getSelectedForOICRow = async param => {
    LoadingService.loading(true)
    const { data } = await OICProcessorServices.getFacilityFormInfo(param)
    LoadingService.loading(false)
    switch (data.data.type) {
      case 'HOSPITAL':
        return props.history.push(`/oicprocessor/hospital/${data.data.id}`)
      case 'CLINIC':
        return props.history.push(`/oicprocessor/medical/${data.data.id}`)
      case 'DENTAL_CLINIC':
        return props.history.push(`/oicprocessor/dental/${data.data.id}`)
    }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          {viewList.isOpen && (
            <OICViewList
              isOpen={viewList.isOpen}
              handleClose={status => setViewList({ ...viewList, isOpen: status })}
              stage={viewList.stage}
              getSelectedRow={obj => getSelectedForOICRow(obj.id)}
            />
          )}
          <div className={classes.root}>
            <h1 className={classes.pageHeader}>Dashboard</h1>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={8}>
                <PartnerCard
                  isLoading={forOICCard.isLoading}
                  headerCount={forOICCard.count}
                  headerTitle={'For Ocular Inspection'}
                  cardTitle='Your Task Tray'
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forOICCard.filter}
                  getSelectedFilter={filter => setForOICCard({ ...forOICCard, filter })}
                  headerList={['Affiliation No.', 'Date Submitted', 'Partner Type', 'Partner Name', 'Location']}
                  dataList={forOICCard.dataList}
                  getSelectedRow={obj => getSelectedForOICRow(obj.id)}
                  actionText={'Accomplish Form'}
                  openViewList={() => setViewList({ isOpen: true, stage: 'FOR_OIC' })}
                />
              </Grid>
              <Grid item xs={4}>
                <PartnerCard
                  headerCount={'0'}
                  headerCountStyle={classes.headerCountStyle}
                  cardTitle='Your Ticket List'
                  cardTitleStyle={classes.cardTitleStyle}
                  headerTitle={'Open Tickets'}
                  openViewList={() => false}
                  selectListFilter={[{ key: 'assignedToAll', value: 'Assigned To All' }]}
                  defaultFilter={'assignedToAll'}
                  getSelectedFilter={() => false}
                />
              </Grid>
            </Grid>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={6}>
                <PartnerCard
                  isLoading={forReOICCard.isLoading}
                  headerCount={forReOICCard.count}
                  headerTitle={'Facility to Re-evaluate'}
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forReOICCard.filter}
                  getSelectedFilter={filter => setForReOICCard({ ...forReOICCard, filter })}
                  headerList={['Affiliation No.', 'Date Submitted', 'Partner Type', 'Partner Name', 'Location']}
                  dataList={forReOICCard.dataList}
                  getSelectedRow={obj => getSelectedForOICRow(obj.id)}
                  actionText={'Evaluate'}
                  openViewList={() => setViewList({ isOpen: true, stage: 'FOR_OIC_REEVALUATION' })}
                />
              </Grid>
              <Grid item xs={6}>
                <PartnerCard
                  isLoading={forSubmittedOICCard.isLoading}
                  headerCount={forSubmittedOICCard.count}
                  headerTitle={'Submitted Ocular Inspection Checklist'}
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forSubmittedOICCard.filter}
                  getSelectedFilter={filter => setForSubmittedOICCard({ ...forSubmittedOICCard, filter })}
                  headerList={['Affiliation No.', 'Partner Type', 'Partner Name']}
                  dataList={forSubmittedOICCard.dataList}
                  getSelectedRow={obj => getSelectedForOICRow(obj.id)}
                  actionText={'View'}
                  openViewList={() => setViewList({ isOpen: true, stage: 'FOR_SUBMITTED_OIC' })}
                />
              </Grid>
            </Grid>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default Dashboard
