import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
const schema = Joi.object({
  cName: Joi.string()
    .label('Name')
    .required(), // required
  cDesignation: Joi.string()
    .label('Designation')
    .required(), // required
  cDepartment: Joi.string()
    .label('Department')
    .required(), // required
  cEmail: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(), // required
  cNumber: Joi.string()
    .label('Contact No')
    .required() // required
})

export default function ContactValidation (form) {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
    }
  }
  console.log('contact errors', errors)

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
