import React, { useState, useEffect, useContext } from 'react'

// Components
import TextField from '../../../../../../../../SharedComponents/TextField/CustomTextField'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import TwoTextArea from '../../../../../Components/TwoTextArea/TwoTextArea'

// material ui
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import Paper from '@material-ui/core/Paper'

// style scss
import classes from './AffiliationForm.module.scss'
import dentalAffiliationContext from '../../context/DentalClinicAffiliationOIContext'
import moment from 'moment'
import AffiliationTableDisplay from '../../../Components/affilationTableDisplay.js/AffiliationTableDisplay'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
import fetchDentistData from 'Pages/Protected/Partner/AffiliationFormEncoder/Pages/DentalClinicAffiliation/Components/Utilities/fetchDentistAndMapping'

const affiliationTableHeader = [
  { label: 'Full Name', name: 'fullName' },
  { label: 'Main Specialization', name: 'mainSpecification' },
  { label: 'Prc License No', name: 'prcLinceseNumber' },
  { label: 'Expiry Date', name: 'prcExpiryDate' },
  { label: 'Contact Number', name: 'contactNo' }
]
const existingAffiliationHmoHeader = [
  { label: 'Dental Organizations and Specialty Societies', name: 'affiliatedGroup' }
]

const AffiliationForm = () => {
  const [formData, setFormData] = useState({})
  const [radioButtonsValue, setRadioButtonsValue] = useState({})
  const [listOfDentalAssocs, setDentalAssocs] = useState([])
  const [listOfAffiliatedGroup, setAffiliatedGroup] = useState([])
  const { paramId } = useContext(dentalAffiliationContext)
  useEffect(() => {
    LoadingService.loading(true)
    fetchAffiliationData(paramId)
  }, [])

  const fetchAffiliationData = (paramId) => {
    try {
      setTimeout(async () => {
        const obj = {}
        const { data } = await AffiliationApproverServices.getFacilityPartnerById(paramId)
        let { affiliation_infos: affiliationInfos, dental_associates: dentalAssociates, treasury, dentist_id } = data['data']
        const { dentistProfile } = await fetchDentistData(dentist_id)
        console.log('dentist id rock', dentistProfile)
        setDentalAssocs(dentalAssociates)
        setAffiliatedGroup(affiliationInfos)
        obj.tinStatus = treasury['tin_status']
        setRadioButtonsValue(obj)
        setFormData({ ...data['data'], ...dentistProfile })
        LoadingService.loading(false)
      }, 1000)
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  return (
    <React.Fragment id='fragment'>
      <div id='root' className={classes.root}>
        <div id='topText' className={classes.topText}>
          DENTAL CLINIC INFORMATION
        </div>
        <div id='clinicGeneralInformation' className={classes.clinicGeneralInformation}>
          <div id='clinicTitle' className={classes.clinicTitle}>
            Clinic General Information
          </div>
          <div id='firstInputFields' className={classes.InputFields}>
            <div id='dentalClinicName' className={classes.dentalClinicName}>
              <label>Dental Clinic Name</label>
              <div>
                <TextField value={formData['name']} readonly />
              </div>
            </div>
            <div id='contactNumber' className={classes.SameSizeFields}>
              <label>Contact Number</label>
              <div>
                <TextField value={formData['contact_person_no']} />
              </div>
            </div>
          </div>
          <div id='SecondInputFields' className={classes.InputFields}>
            <div id='NameOfContact' className={classes.NameOfContact}>
              <label>Name of Contact Person</label>
              <div>
                <TextField value={formData['contact_person']} />
              </div>
            </div>
            <div id='EmailAdd' className={classes.EmailAdd}>
              <label>Email Address</label>
              <div>
                <TextField value={formData['email']} />
              </div>
            </div>
            <div id='CheckkPayee' className={classes.SameSizeFields}>
              <label>Check Payee</label>
              <div>
                <TextField value={formData['treasury'] && formData['treasury']['payee']} />
              </div>
            </div>
          </div>
          <div id='ThirdInputFields' className={classes.InputFields}>
            <div id='clinicAddress' className={classes.DentalAdd}>
              <label>
                Dental Clinic Address <br />
                Unit No./Floor/Bldg No
              </label>
              <div>
                <TextField value={formData['address'] && formData['address']['unit_flr_bldg']} />
              </div>
            </div>
            <div id='bldgName' className={classes.bldgName}>
              <label>
                <br />
                Building Name
              </label>
              <div>
                <TextField value={formData['address'] && formData['address']['building_name']} />
              </div>
            </div>
            <div id='StreetName' className={classes.SameSizeFields}>
              <label>
                <br />
                Street Name
              </label>
              <div>
                <TextField value={formData['address'] && formData['address']['street']} />
              </div>
            </div>
          </div>
          <div id='FourthInputFields' className={classes.InputFields}>
            <div id='subdivision' className={classes.subdivision}>
              <label>Subdivision</label>
              <div>
                <TextField value={formData['address'] && formData['address']['subdivision']} />
              </div>
            </div>
            <div id='region' className={classes.region}>
              <label>Region</label>
              <div>
                <TextField value={formData['address'] && formData['address']['region']} />
              </div>
            </div>
            <div id='District/Province' className={classes.DistrictProvince}>
              <label>District/Province</label>
              <div>
                <TextField value={formData['address'] && formData['address']['district']} />
              </div>
            </div>
          </div>
          <div id='FifthInputFields' className={classes.InputFields}>
            <div id='barangay' className={classes.barangay}>
              <label>Barangay</label>
              <div>
                <TextField value={formData['address'] && formData['address']['barangay']} />
              </div>
            </div>
            <div id='cityMunicipality' className={classes.cityMunicipality}>
              <label>City/Municipality</label>
              <div>
                <TextField value={formData['address'] && formData['address']['city']} />
              </div>
            </div>
            <div id='zipCode' className={classes.zipCode}>
              <label>Zip Code</label>
              <div>
                <TextField value={formData['address'] && formData['address']['zip_code']} />
              </div>
            </div>
          </div>
          <div id='SixInputFields' className={classes.InputFields}>
            <div id='clinicSchedule' className={classes.clinicSchedule}>
              <label>Clinic Schedule</label>
              <div>
                <TextField value={formData['clinic_schedule']} readonly />
              </div>
            </div>
            <div id='roomNumber' className={classes.zipCode}>
              <label>Room Number</label>
              <div>
                <TextField value={formData['room_number']} readonly />
              </div>
            </div>
          </div>
          <div id='SevenInputFields' className={classes.InputFields}>
            <div id='natureOfPractice' className={classes.natureOfPractice}>
              <label>Nature of Practice</label>
              <div>
                <TextField value={formData['nature_of_practice']} readonly />
              </div>
            </div>
          </div>
        </div>

        <div id='dentistInformation' className={classes.dentistInformation}>
          <div id='dentistText' className={classes.dentistText}>
            Dentist General Information
          </div>
          <div id='dentistFirstInputFields' className={classes.dentistInputFields}>
            <div id='FirstName' className={classes.FirstName}>
              <label>First Name</label>
              <div>
                <TextField value={formData['firstName']} readonly />
              </div>
            </div>
            <div id='middleName' className={classes.middleName}>
              <label>Middle Name</label>
              <div>
                <TextField value={formData['middleName']} readonly />
              </div>
            </div>
            <div id='lastName' className={classes.lastName}>
              <label>Last Name</label>
              <div>
                <TextField value={formData['lastName']} readonly />
              </div>
            </div>
          </div>
          <div id='dentistSecondInputFields' className={classes.dentistInputFields}>
            <div id='suffix' className={classes.suffix}>
              <label>Suffix</label>
              <div>
                <TextField value={formData['suffix']} readonly />
              </div>
            </div>
            <div id='birthDate' className={classes.birthDate}>
              <label>Birthdate</label>
              <div>
                <TextField value={moment(formData['birthDate']).format('MM/DD/YYYY')} />
              </div>
            </div>
            <div id='civilStatus' className={classes.civilStatus}>
              <label>Civil Status</label>
              <div>
                <TextField value={formData['civilStatus']} readonly />
              </div>
            </div>
            <div id='gender' className={classes.gender}>
              <label>Gender</label>
              <div>
                <TextField value={formData['gender']} readonly />
              </div>
            </div>
          </div>
          <div id='dentistThirdInputFields' className={classes.dentistInputFields}>
            <div id='emailAddress' className={classes.emailAddress}>
              <label>Email Address</label>
              <div>
                <TextField value={formData['emailAddress']} readonly />
              </div>
            </div>
            <div id='contact' className={classes.contact}>
              <label>Contact Number (phone/mobile)</label>
              <div>
                <TextField value={formData['contactNumber']} readonly />
              </div>
            </div>
          </div>
          <div id='dentistFourthInputFields' className={classes.dentistInputFields}>
            <div id='main' className={classes.main}>
              <label>Main Specialization</label>
              <div>
                <TextField value={formData['mainSpecialization']} readonly />
              </div>
            </div>
            <div id='major' className={classes.major}>
              <label>Major Specialization</label>
              <div>
                <TextField value={formData['majorSpecialization']} readonly />
              </div>
            </div>
          </div>
          <div id='dentistFifthInputFields' className={classes.dentistInputFields}>
            <div id='sub' className={classes.sub}>
              <label>Sub Specialization</label>
              <div>
                <TextField value={formData['subSpecialization']} readonly />
              </div>
            </div>
            <div id='other' className={classes.other}>
              <label>Other Specialization</label>
              <div>
                <TextField value={formData['otherSpecialization']} readonly />
              </div>
            </div>
          </div>
          <div id='dentistSixthInputFields' className={classes.dentistInputFields}>
            <div id='pda' className={classes.pda}>
              <label>PDA Number</label>
              <div>
                <TextField value={formData['pda']} readonly />
              </div>
            </div>
          </div>
          <div id='dentistSevenInputFields' className={classes.dentistInputFields}>
            <div id='prc' className={classes.prc}>
              <label>PRC License No.</label>
              <div>
                <TextField value={formData['prcNumber']} readonly />
              </div>
            </div>
            <div id='prcExpiry' className={classes.prcExpiry}>
              <label>PRC Expiry Date</label>
              <div>
                <TextField value={moment(formData['prcExpiration']).format('MM/DD/YYYY')} />
              </div>
            </div>
          </div>
          <div id='dentistEightInputFields' className={classes.dentistInputFields}>
            <div id='phil' className={classes.phil}>
              <label>PhilHealth Accreditation No.</label>
              <div>
                <TextField value={formData['philhealthNumber']} readonly />
              </div>
            </div>
            <div id='philExpiry' className={classes.philExpiry}>
              <label>PhilHealth Expiry Date</label>
              <div>
                <TextField value={moment(formData['philhealthExpiration']).format('MM/DD/YYYY')} />
              </div>
            </div>
          </div>
          <div id='dentisNineInputFields' className={classes.dentistInputFields}>
            <div id='tin' className={classes.tin}>
              <label>Tin Number</label>
              <div>
                <TextField value={formData['tinNumber']} />
              </div>
            </div>
          </div>
        </div>

        <div id='radioButtons' className={classes.radioButtons}>
          <div id='Tin-label' className={classes.Tinlabel}>
            <div id='Tin-radio-button'>
              <div id='Vatable'>
                <FormControl component='fieldset'>
                  <FormLabel className={classes.label} component='legend'>
                    TIN Status
                  </FormLabel>
                  <RadioGroup aria-label='gender'>
                    <FormControlLabel value='Vatable' checked={formData['tinStatus'] === '12%'} disabled={formData['tinStatus'] === '12%'} control={<Radio color='primary' disabled />} label='VATable' />
                  </RadioGroup>
                </FormControl>
              </div>
              <div id='NonVatable'>
                <FormControl className={classes.NonVat} component='fieldset'>
                  <RadioGroup aria-label='gender'>
                    <FormControlLabel value='Non-VATable' checked={formData['tinStatus'] === 'Non-VaTable'} disabled={formData['tinStatus'] !== 'Non-VaTable'} control={<Radio color='primary' />} label='Non-VATable' />
                  </RadioGroup>
                  {formData['tinStatus'] === 'Non-VaTable' && <div id='dropDown-label' className={classes['DropDownComponent']}>
                    <TextField value={formData['nonVatable']} />
                  </div>}
                </FormControl>
              </div>
            </div>
            <div id='taxRate'>
              <label>Tax Rate</label>
              <div id='percentInput' className={classes.percentInput}>
                <CustomTextFieldWithIcons value={formData['vatTaxRate']} styles={classes.customTag} tags={'%'} readonly />
              </div>
            </div>
          </div>
        </div>
        <div id='dentalAssociateInformation' className={classes.dentalAssociateInformation}>
          <div id='dentalAssociateGenInfo' className={classes.dentalAssociateGenInfo}>
            Dental Associate General Information
          </div>
        </div>
        <div id='tableFields' className={classes.tableFields}>
          <AffiliationTableDisplay header={affiliationTableHeader} data={formData['dental_associates']} />
        </div>
        <div id='affiliationTableFields' className={classes.affiliationTableFields}>
          <text id='AffiliaionText' className={classes.AffiliaionText}>
            Affiliations
          </text>
          <div id='tableDropDownInput' className={classes.tableDropDownInput}>
            <label id='textDesciption' className={classes.textDesciption}>
              Dental Organizations and Specialty Societies
            </label>
            <AffiliationTableDisplay header={existingAffiliationHmoHeader} data={listOfAffiliatedGroup} />
          </div>
        </div>
        <div id='remarksField' className={classes.remarksField}>
          <TwoTextArea val1={formData['remarks'] && formData['remarks']['external_remarks']} val2={formData['remarks'] && formData['remarks']['internal_remarks']} label1={'External Remarks'} label2={'Internal Remarks'} />
        </div>
      </div>
    </React.Fragment>
  )
}

export default AffiliationForm
