import React from 'react'
import MedicalClinicAffilitiationContext from './context/MedicalClinicAffilitiationContext'
import Page1 from './MedicalClinicAffiliation_p1'
import Page2 from './MedicalClinicAffiliation_p2'
export default function MedicalClinicAffilition ({ match, history }) {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setApproved] = React.useState(false)
  const [form, setForm] = React.useState(false)
  return (
    <React.Fragment>
      <MedicalClinicAffilitiationContext.Provider value={{paramId: match.params.id, isApproved, setApproved, form, setForm}}>
        {!isNextPage && <Page1 setPage={setIsNextPage} history={history} /> }
        {isNextPage && <Page2 setPage={setIsNextPage} history={history} /> }
      </MedicalClinicAffilitiationContext.Provider>
    </React.Fragment>
  )
}
