import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
const schema = Joi.object({
  desc: Joi.string()
    .label('Group Description')
    .required(), // required
  facility_type: Joi.string()
    .label('Facility Type')
    .required(), // required
  facility_names: Joi.array().label('Facility Groupings').min(1).required()
})

export default function AddFacilityGroup (form) {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
      if(i.path[0] === 'facility_names' && i.type === 'array.min') {
        errors[i.path[0]] = 'Facilities Included in the Group must contain at least 1 item'
      }
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
