import React from 'react'
import moment from 'moment'
import classes from './EditHospitalInformation.module.scss'
import HospitalProfileForm from '../Utilities/HospitalFormValidation'
import HospitalProfile from '../../context'
import TextField from '@material-ui/core/TextField'
import EditIcon from '@material-ui/icons/Edit'

import {
  PartnerInformationFooterButton,
  DialogBoxIfUpdate,
  DialogBoxIfCancel
} from '../../../../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'

import getNestedObjects from '../../../../../../Components/getNestedObjects/getNestedObjects'

import FacilityAssignment from '../../../../../../Components/FacilityAssignment'
import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'
import EditInputs from '../Utilities/EditInputs/EditInputs'
import Destructure from '../Utilities/Destructure'
import { dateFormat, DateErrorHandler, handleAddressChaining } from '../../../../../../Components/Utils/utils'
import ProfileService from '../../../../../../../../../Services/Api/Partner/ProfileService'
import { useParams } from 'react-router-dom'
import CustomAlertSnackbar from '../../../../../../Components/CustomSnackBar/CustomAlertSnackbar'

const EditHospitalInformation = props => {
  const {
    profile,
    setIsToUpdate,
    setProfile,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderName,
    setStatus,
    status,
    discountType
  } = React.useContext(HospitalProfile)
  const { facilityId } = useParams()

  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [provinces, setProvinces] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])

  const noOfBeds = React.useRef({})
  const facilityAddressStreet = React.useRef({})
  const facilityAddressZipcode = React.useRef({})
  const philheathNo = React.useRef({})
  const checkPayee = React.useRef({})
  const creditLimit = React.useRef({})
  const paymentTerms = React.useRef({})
  const formerName = React.useRef({})
  const taxRate = React.useRef({})
  const facilityDiscount = React.useRef({})
  const specialInstructions = React.useRef({})
  const exclusiveTo = React.useRef({})
  const reason = React.useRef({})

  const [form, setForm] = React.useState({
    facilityAssignment: '',
    medicalPartnerId: '',
    formerName: '',
    name: '',
    hospitalName: '',
    assignments: [],
    reason: '',
    status: '',
    exclusiveTo: '',
    tvs: '',
    street: '',
    region: '',
    district: '',
    city: '',
    barangay: '',
    zipCode: '',
    classification: '',
    noOfBeds: null,
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '',
    processType: '',
    creditLimit: '',
    paymentTerms: '',
    tinStatus: '',
    vatTaxRate: '',
    facilityDiscount: '',
    dateOfAffiliation: '',
    dateOfDisaffiliation: '',
    specialInstructions: ''
  })

  React.useEffect(() => {
    if (profile) {
      noOfBeds.current.value = getNestedObjects(profile, ['bed_capacity'])
      formerName.current.value = getNestedObjects(profile, ['former_name'])
      facilityAddressStreet.current.value = getNestedObjects(profile, ['address', 'street'])
      facilityAddressZipcode.current.value = getNestedObjects(profile, ['address', 'zip_code'])
      philheathNo.current.value = getNestedObjects(profile, ['philhealth', 'accreditation_number'])
      checkPayee.current.value = getNestedObjects(profile, ['treasury', 'payee'])
      paymentTerms.current.value = getNestedObjects(profile, ['treasury', 'payment_terms'])
      facilityDiscount.current.value = getNestedObjects(profile, ['treasury', 'discount'])
      specialInstructions.current.value = getNestedObjects(profile, ['treasury', 'special_instruction'])
      creditLimit.current.value = getNestedObjects(profile, ['treasury', 'credit_limit'])
      taxRate.current.value = getNestedObjects(profile, ['treasury', 'vat', 'tax_rate'])
      exclusiveTo.current.value = getNestedObjects(profile, ['facility_assignment', 'exclusive_to'])
      reason.current.value = getNestedObjects(profile, ['reason'])

      Destructure(form, profile, dateFormat, setStatus, setForm, setBarangays, setCities, setProvinces, setRegions)
    }
  }, [profile, status])

  const [openModal, setOpenModal] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [errors, setErrors] = React.useState({})

  const handleSubmitClose = () => {
    setOpenModal(false)
  }

  const save = async () => {
    setIsLoading(true)

    try {
      const obj = {
        unitFlrBldg: profile ? (profile['address'] ? profile['address']['unit_flr_bldg'] : '') : '',
        bldgName: profile ? (profile['address'] ? profile['address']['building_name'] : '') : '',
        subdivision: profile ? (profile['address'] ? profile['address']['subdivision'] : '') : '',
        dohNumber: profile ? (profile['license'] ? profile['license']['doh']['license_type'] : '') : '',
        dohLicenseType: profile ? (profile['license'] ? profile['license']['doh']['number'] : '') : '',
        dohLicenseExp: profile ? (profile['license'] ? profile['license']['doh']['license_exp'] : '') : '',
        years: profile ? (profile['operation'] ? profile['operation']['years'] : '') : '',
        months: profile ? (profile['operation'] ? profile['operation']['months'] : '') : '',
        stageName: profile ? (profile['stage'] ? profile['stage']['stage_name'] : '') : '',
        id: profile ? profile['id'] : '',
        contacts: profile ? profile['contacts'] : '',
        internalRemarks: profile ? (profile['remarks'] ? profile['remarks']['internal_remarks'] : '') : '',
        externalRemarks: profile ? (profile['remarks'] ? profile['remarks']['external_remarks'] : '') : '',
        type: profile ? profile['type'] : '',
        name: form.name

      }

      form['dateOfAffiliation'] = DateErrorHandler(form['dateOfAffiliation'])
      form['dateOfDisaffiliation'] = DateErrorHandler(form['dateOfDisaffiliation'])
      form['philhealthExpiration'] = DateErrorHandler(form['philhealthExpiration'])

      const newForm = { ...form, ...obj }
      const {
        data: { data: newData }
      } = await ProfileService.updateHospitalProfile(facilityId, newForm)
      setIsLoading(false)
      handleSubmitClose(false)
      setIsSave(true)
      setStatus(newData ? newData.status : '')
      setProfile(newData)
      setHeaderName(form.name)
      setTimeout(() => {
        setIsToUpdate(false)
        setIsEdit(false)
      }, 1000)
    } catch (error) {
      console.log('error', error)
      setIsLoading(false)
    }
  }

  const validate = () => {
    const { errors, isValid } = HospitalProfileForm(form)
    if (isValid) {
      setOpenModal(true)
      setErrors({})
    }

    setErrors(errors)
  }
  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const handleChange = ({ currentTarget: input }) => {
    const hospitalForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[input.name]
    setErrors(newErrors)
    hospitalForm[input.name] = input.value
    setForm(hospitalForm)
  }

  const handleDropdown = ({ target: select }) => {
    const hospitalForm = { ...form }
    hospitalForm[select.name] = select.value
    errors[select.name] = false
    setErrors(errors)
    setForm(hospitalForm)
    handleAddressChaining(select.name, select.value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }

  const handleDatePicker = e => {
    const { name, value } = e.target
    const hospitalForm = { ...form }

    const dateValue = moment(value).format('YYYY-MM-DD')

    hospitalForm[name] = dateValue
    setForm(hospitalForm)
  }

  React.useEffect(() => {
    HandleChangeName()
  }, [form.name, isEdit])

  const HandleChangeName = () => {
    if (isEdit) {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(false)
              }}
            />
          </div>
          <TextField
            id='inputName'
            className={classes.TextField}
            autoComplete='off'
            name='name'
            value={form.name}
            onChange={handleChange}
          />
        </div>
      )
    } else {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(true)
              }}
            />
          </div>
          {form.name}
        </div>
      )
    }
  }

  const discountTypeData = React.useMemo(() => discountType, [discountType])

  const HospitalInformation = EditInputs(
    form,
    errors,
    setForm,
    setErrors,
    handleChange,
    handleDropdown,
    handleDatePicker,
    noOfBeds,
    formerName,
    facilityAddressStreet,
    facilityAddressZipcode,
    checkPayee,
    creditLimit,
    paymentTerms,
    taxRate,
    facilityDiscount,
    specialInstructions,
    discountTypeData, regions, provinces, cities, barangays, isFetching
  )

  const HospitalInformationData = React.useMemo(() => HospitalInformation, [HospitalInformation])

  return (
    <React.Fragment>
      <form onSubmit={handleSubmit}>
        <div id='root' className={classes.root}>
          <FacilityAssignment
            form={form}
            setForm={setForm}
            errors={errors}
            setErrors={setErrors}
            reasonRef={reason}
            exclusiveRef={exclusiveTo}
          />
          <div id='wrapper' className={classes.wrapper}>
            <PartnerViewInformation
              data={HospitalInformationData}
              hideStar
              listStyles={classes.listStyles}
              firstColumStyle={classes.firstRow}
              secondColumnStyle={classes.secondRow}
            />
          </div>
        </div>

        {/* END OF ROOT DIV */}
        <PartnerInformationFooterButton isLoading={isLoading} setIsCancel={setIsCancel} handleSubmit={handleSubmit} />
      </form>
      <div>
        <DialogBoxIfUpdate
          handleSubmitClose={handleSubmitClose}
          HandleSave={save}
          isLoading={isLoading}
          openModal={openModal}
        />
      </div>
      <div>
        <DialogBoxIfCancel isCancel={isCancel} setIsCancel={setIsCancel} setIsToUpdate={setIsToUpdate} />
        {isSave && <CustomAlertSnackbar isSave={isSave} setIsSave={setIsSave} />}
      </div>
    </React.Fragment>
  )
}

export default React.memo(
  EditHospitalInformation,

  (prevProps, nextProps) => {
    try {
      if (
        prevProps.HospitalInformationData === nextProps.HospitalInformationData &&
        prevProps.errors === nextProps.errors &&
        prevProps.form === nextProps.form &&
        prevProps.reason === nextProps.exclusiveTo &&
        prevProps.discountTypeData === nextProps.discountTypeData
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)
