import React from 'react'
import classes from './EditPartnerInformation.module.scss'
import CustomAlertSnackbar from '../../../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import {
  PartnerInformationFooterButton,
  DialogBoxIfUpdate,
  DialogBoxIfCancel
} from '../../../../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'

import FacilityAssignmentForDoctors from '../../../../../../Components/FacilityAssignmentForDoctors/FacilityAssignmentForDoctors'

import dentistProfile from '../../context'
import InitialState from '../Utilities/InitialState'
import MedicalDentistFormValidation from '../Utilities/MedicalDentistFormValidation'
import ProfileService from '../../../../../../../../../Services/Api/Partner/ProfileService'
import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'
// import isEmpty from 'lodash/isEmpty'
import dentistInputsData from '../Utilities/EditInputs'
import { useParams } from 'react-router-dom'
import InputRefs from '../Utilities/inputRefs'
import { SetData, SaveData } from '../Utilities/SetandSave'
import EditIcon from '@material-ui/icons/Edit'
import TextField from '@material-ui/core/TextField'

const EditDentistInformation = () => {
  const { dentistId } = useParams()
  const [form, setForm] = React.useState(InitialState)
  const [errors, setErrors] = React.useState({})
  const [openModal, setOpenModal] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [change, setChange] = React.useState(false)
  const {
    profile,
    setIsToUpdate,
    setProfile,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    setDentistName,
    headerData,
    setCurrentStatus
  } = React.useContext(dentistProfile)

  React.useEffect(() => {
    if (profile) {
      const newForms = { ...form }
      SetData(newForms, setForm, profile)
    }
  }, [])

  const HandleChange = e => {
    const { name, value } = e.target
    const doctorForm = { ...form }
    setChange(true)
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    doctorForm[name] = value
    setForm(doctorForm)
  }

  const HandleSave = async () => {
    setIsLoading(true)
    try {
      const newForm = { ...form }
      const result = SaveData(newForm)
      const {
        data: { data: newData }
      } = await ProfileService.updateDoctorProfile(dentistId, result)
      const name = `${newData.name && newData.name.last}, ${newData.name && newData.name.first} ${newData.name &&
        newData.name.middle} ${newData.name ? (newData.name.suffix ? newData.name.suffix : '') : ''}`

      let fullName = `${newData.name && newData.name.first} ${newData.name && newData.name.middle} ${newData.name &&
        newData.name.last} ${newData.name ? (newData.name.suffix ? newData.name.suffix : '') : ''}`

      const newHeaderData = { ...headerData }
      newHeaderData['name'] = name
      newHeaderData['fullName'] = fullName
      setCurrentStatus(newData ? (newData.status ? newData.status : '') : '')
      setIsLoading(false)
      handleSubmitClose(false)
      setIsSave(true)
      setProfile(newData)
      setHeaderData(newHeaderData)
      setTimeout(() => {
        setIsToUpdate(false)
        setIsEdit(false)
      }, 1000)
    } catch (error) {
      setIsLoading(false)
    }
  }

  const validate = () => {
    const { errors, isValid } = MedicalDentistFormValidation(form)
    if (isValid) {
      setOpenModal(true)
      setErrors({})
    } else {
      setErrors(errors)
    }
  }

  React.useEffect(() => {
    HandleChangeName(profile)
  }, [form.first_name, form.last_name, form.middle_name])

  React.useEffect(() => {
    HandleChangeName()
  }, [isEdit])

  const HandleChangeName = () => {
    if (isEdit) {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(false)
              }}
            />
          </div>

          <TextField
            id='inputName'
            className={classes.TextField}
            autoComplete='off'
            placeholder='Last Name'
            name={'last_name'}
            value={form.last_name}
            onChange={HandleChange}
          />
          <TextField
            id='inputName'
            className={classes.TextField}
            autoComplete='off'
            name={'first_name'}
            placeholder='First Name'
            value={form.first_name}
            onChange={HandleChange}
          />
          <TextField
            id='inputName'
            className={classes.TextField}
            autoComplete='off'
            placeholder='Middle Name'
            name={'middle_name'}
            value={form.middle_name}
            onChange={HandleChange}
          />
        </div>
      )
    } else {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(true)
              }}
            />
          </div>
          {`${form['last_name']} ${form['first_name']} ${form['middle_name']}`}
        </div>
      )
    }
  }

  const handleSubmitClose = () => {
    setOpenModal(false)
  }

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const refValue = InputRefs(form)
  const Information = dentistInputsData(refValue, HandleChange, form, errors, setForm, setErrors)

  return (
    <React.Fragment>
      <form>
        <div id='root' className={classes.root}>
          <FacilityAssignmentForDoctors
            form={form}
            setForm={setForm}
            refValue={refValue}
            errors={errors}
            setErrors={setErrors}
          />

          <PartnerViewInformation
            data={Information}
            hideStar
            listStyles={classes.listStyles}
            firstColumStyle={classes.firstRow}
            secondColumnStyle={classes.secondRow}
          />
        </div>

        <PartnerInformationFooterButton isLoading={isLoading} setIsCancel={setIsCancel} handleSubmit={handleSubmit} />
      </form>
      <div>
        <DialogBoxIfUpdate
          handleSubmitClose={handleSubmitClose}
          HandleSave={HandleSave}
          isLoading={isLoading}
          openModal={openModal}
        />
      </div>
      <div>
        <DialogBoxIfCancel isCancel={isCancel} setIsCancel={setIsCancel} setIsToUpdate={setIsToUpdate} />
        {isSave && <CustomAlertSnackbar isSave={isSave} setIsSave={setIsSave} />}
      </div>
    </React.Fragment>
  )
}

export default React.memo(EditDentistInformation, (prevProps, nextProps) => {
  try {
    if (
      prevProps.Information === nextProps.Information &&
      prevProps.errors === nextProps.errors &&
      prevProps.form === nextProps.form &&
      prevProps.refValue === nextProps.refValue
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
