import React from 'react'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import useMediaQuery from '@material-ui/core/useMediaQuery'
import { useTheme } from '@material-ui/core/styles'

const YesOrNoDialogBox = props => {
  const { PrevPage } = props

  const [open, setOpen] = React.useState(false)
  const theme = useTheme()
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'))

  const handleClickOpen = () => {
    setOpen(true)
  }

  const handleClose = () => {
    setOpen(false)
  }

  return (
    <div>
      <Button
        variant='contained'
        color='secondary'
        style={{
          paddingTop: '0.75em',
          paddingBottom: '0.75em',
          backgroundColor: '#fd5451',
          paddingLeft: '1.5em',
          paddingRight: '1.5em'
        }}
        onClick={handleClickOpen}
      >
        Cancel
      </Button>
      <Dialog fullScreen={fullScreen} open={open} onClose={handleClose} aria-labelledby='responsive-dialog-title'>
        <DialogContent>
          <div
            style={{
              padding: '2em',
              fontSize: '0.8vw',
              textAlign: 'center'
            }}
          >
            All changes will not be saved,<br />are you sure you want to cancel?
          </div>
        </DialogContent>
        <DialogActions
          style={{
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center'
          }}
        >
          <Button
            style={{
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              paddingRight: '3em',
              paddingLeft: '3em',
              fontSize: '0.6vw',
              height: '3vh',
              textAlign: 'center',
              backgroundColor: '#fd5451',
              color: 'white',
              borderRadius: '5px',
              marginBottom: '2em'
            }}
            onClick={handleClose}
            variant='contained'
          >
            No
          </Button>
          <Button
            style={{
              paddingRight: '3em',
              paddingLeft: '3em',
              fontSize: '0.6vw',
              height: '3vh',
              textAlign: 'center',
              backgroundColor: '#1ab1c4',
              color: 'white',
              borderRadius: '5px',
              marginBottom: '2em'
            }}
            onClick={PrevPage}
            variant='contained'
          >
            Yes
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

export default YesOrNoDialogBox
