import React from 'react'
import DoctorProfileContext from './context'
import { ValidateDateFormat } from '../../../../Components/Validators/Validators'
import { dateFormat, MonthdateFormat, getTinStatus, getNestedObjects } from '../../../../Components/Utils/utils'
import PartnerViewInformation from '../../../../Components/PartnerViewInformation/PartnerViewInformation'
import moment from 'moment'
import classes from './PartnerInformation.module.scss'

const PartnerInformation = props => {
  const { setDoctorName, setCurrentStatus } = props

  const { profile } = React.useContext(DoctorProfileContext)

  let fullName = getNestedObjects(profile, ['full_name'])
  let status = getNestedObjects(profile, ['status'])

  React.useEffect(() => {
    setDoctorName(fullName.toUpperCase())
    setCurrentStatus(status.toUpperCase())
  }, [fullName, status])

  if (profile) {
    let birthdate = profile ? (profile.birthdate ? dateFormat(profile.birthdate) : '') : ''
    let license = profile.license ? profile.license : ''
    let prcExpiry = license.prc
      ? ValidateDateFormat(moment(license.prc.expiration_date).format('L'))
        ? MonthdateFormat(license.prc.expiration_date)
        : ''
      : ''

    let philhealthExpiration = profile.philhealth
      ? ValidateDateFormat(moment(profile.philhealth.expiration_date).format('MM/DD/YYYY'))
        ? MonthdateFormat(profile.philhealth.expiration_date)
        : ''
      : ''

    let dateOfAffiliation = profile.affiliation_status
      ? ValidateDateFormat(moment(profile.affiliation_status.date_submitted).format('MM/DD/YYYY'))
        ? MonthdateFormat(profile.affiliation_status.date_submitted)
        : ''
      : ''
    let disaffiliation = profile.disaffiliation
      ? ValidateDateFormat(moment(profile.disaffiliation.date).format('L'))
        ? MonthdateFormat(profile.disaffiliation.date)
        : ''
      : ''

    let tinStatus = getNestedObjects(profile, ['treasury', 'tin_status'])
    let assignments = profile
      ? profile.facility_assignment
        ? profile.facility_assignment
          ? profile.facility_assignment.assignments
            ? profile.facility_assignment.assignments
            : []
          : []
        : []
      : []

    const Information = [
      { id: 301, title: 'Medical Partner ID', value: getNestedObjects(profile, ['id']) },
      {
        id: 302,
        title: 'Birthdate',
        value: birthdate,
        title2: 'civil status',
        value2: getNestedObjects(profile, ['civil_status'])
      },
      { id: 303, title: 'gender', value: getNestedObjects(profile, ['gender']) },
      {
        id: 304,
        title: 'main specialization',
        value: getNestedObjects(profile, ['specialization', 'main']),
        title2: 'major specialization',
        value2: getNestedObjects(profile, ['specialization', 'major'])
      },
      {
        id: 305,
        title: 'sub specialization',
        value: getNestedObjects(profile, ['specialization', 'sub']),
        title2: 'other specialization',
        value2: getNestedObjects(profile, ['specialization', 'other'])
      },
      {
        id: 306,
        title: 'prc license no.',
        value: getNestedObjects(profile, ['license', 'prc', 'number']),
        title2: 'prc license exp.',
        value2: prcExpiry
      },
      {
        id: 307,
        title: 'PhilHealth Accreditation no.',
        value: getNestedObjects(profile, ['philhealth', 'accreditation_number']),
        title2: 'philhealth exp.',
        value2: philhealthExpiration
      },
      { id: 308, title: 'pma number', value: getNestedObjects(profile, ['license', 'pma']), title2: 's2 number', value2: getNestedObjects(profile, ['license', 's2']) },
      { id: 309, title: 'ptr number', value: getNestedObjects(profile, ['license', 'ptr']), title2: 'tin', value2: getNestedObjects(profile, ['treasury', 'tin']) },
      { id: 310, title: 'tin status', value: getTinStatus(tinStatus), title2: 'process type', value2: getNestedObjects(profile, ['processType']) },
      {
        id: 311,
        title: 'Tax Rate',
        value: getNestedObjects(profile, ['treasury', 'vat', 'tax_rate']),
        title2: 'professional fee',
        value2: getNestedObjects(profile, ['professional_fees', 'rate'])
      },
      { id: 312, title: 'payment terms', value: getNestedObjects(profile, ['treasury', 'payment_terms']), title2: 'ahmopi', value2: getNestedObjects(profile, ['ahmopi']) },
      { id: 31212, title: 'withholding tax', value: getNestedObjects(profile, ['treasury', 'withHoldingTax']) },
      { id: 313, title: 'medical facility', value: getNestedObjects(profile, ['facility_info', '0', 'name']), fullRow: true },
      {
        id: 314,
        title: 'facility address',
        value: getNestedObjects(profile, ['facility_info', '0', 'full_address']),
        fullRow: true
      },
      {
        id: 315,
        title: 'clinic schedule',
        value: getNestedObjects(profile, ['facility_info', '0', 'clinic_schedule']),
        fullRow: true
      },
      {
        id: 316,
        title: 'date of affiliation',
        value: dateOfAffiliation,
        title2: 'date of disaffiliation',
        value2: disaffiliation
      }
    ]

    return (
      <PartnerViewInformation
        data={Information}
        facilityLabel={assignments}
        type={'Doctor'}
        listStyles={classes.listStyles}
        firstColumStyle={classes.firstRow}
        secondColumnStyle={classes.secondRow}
        mergeColumnStyle={classes.fullRow}
      />
    )
  } else {
    return false
  }
}

export default PartnerInformation
