import React from 'react'
import Grid from '@material-ui/core/Grid'

// Import Partner Components
import PartnerCard from '../../../Components/PartnerCard/PartnerCardV2'
import EncoderViewList from './Components/EncoderViewList'

// import Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Import scss module
import classes from './Dashboard.module.scss'

// Constant
import cntsStage from 'Pages/Protected/Partner/Constants/stage'
import cntsType from 'Pages/Protected/Partner/Constants/partnerTypes'

// Services
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import moment from 'moment'
// import History from 'Pages/Protected/Partner/Components/History/History'
import { getDoctorSpecialization } from 'Pages/Protected/Partner/Components/Utils/utils'
import { setDoctorSpecialization } from 'Pages/Protected/Store/actions/Partner/autoSuggestAction'
import { useDispatch } from 'react-redux'
import { setAffiliationType, setDentistAffiliation } from 'Pages/Protected/Store/actions/affiliationType'
import { setDentalClinicAffilForm, setDentistProfile } from '../../../../Store/actions/Partner/dentalClinicAffilForm'
import { setDoctorAffiliation } from '../../../../Store/actions/Partner/doctorAffiliationForm'
import { setMedClinicFormData } from 'Pages/Protected/Store/actions/Partner/medicalClinicAffiliationForm'
import { setAffiliationFormData } from 'Pages/Protected/Store/actions/Partner/hospitalAffiliationForm'
import defaultValues from '../MedicalPartnerEnrollmentPage/defaultValues'
import cnts from 'Pages/Protected/Partner/Constants/partnerTypes'


const Dashboard = props => {
  const dispatch = useDispatch()
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [viewList, setViewList] = React.useState({
    isOpen: false,
    stage: ''
  })
  const [forAffilCompletionCard, setForAffilCompletionCard] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })
  const [forSubmittedAffilCard, setForSubmittedAffilCard] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })

  const [form, setForm] = React.useState({ // use to reset affiliation form

  })

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome([])
    setIsLoaded(true)
    dispatch(setAffiliationType({dateReceived: new Date().toISOString(),
      type: cnts.PARTNER.TYPE.HOSPITAL,
      affiliationType: 'new_affiliation',
      mno: ''}))
    dispatch(setDentistAffiliation({}))
    dispatch(setDentalClinicAffilForm(defaultValues.dentalClinic))
    dispatch(setDentistProfile(defaultValues.dentist))
    dispatch(setDoctorAffiliation(defaultValues.doctor))
    dispatch(setMedClinicFormData(defaultValues.getMedicalClinic))
    dispatch(setAffiliationFormData(defaultValues.hospitalForms))
    fetchDoctorSpecialization()
  }, [])

  const fetchDoctorSpecialization = async () => {
    let data = await getDoctorSpecialization()
    dispatch(setDoctorSpecialization(data))
  }

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForAffilCompletionCard({ ...forAffilCompletionCard, isLoading: true })
      const { data } = await AffiliationEncoderService.getAffiliationsForCompletion({
        filter: forAffilCompletionCard.filter,
        stage_name: cntsStage.PARTNER.STAGE.ENCODING
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          dateSubmitted: moment(obj.stage.date_submitted).format('MM/DD/YYYY'),
          type: obj.type,
          name: obj.type === 'DOCTOR' ? obj.full_name : obj.name,
          location:
            obj.type === 'DOCTOR'
              ? 'N/A'
              : `${obj.address ? obj.address.city : ''}, ${obj.address ? obj.address.region : ''}`
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForAffilCompletionCard({
        ...forAffilCompletionCard,
        dataList: sortedData,
        count: fixData.length,
        isLoading: false
      })
    }
    fetchTileData()
  }, [forAffilCompletionCard.filter])

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForSubmittedAffilCard({ ...forSubmittedAffilCard, isLoading: true })
      const { data } = await AffiliationEncoderService.getSubmittedAffiliations({
        filter: forSubmittedAffilCard.filter
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          type: obj.type,
          name: obj.type === 'DOCTOR' ? obj.full_name : obj.name
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForSubmittedAffilCard({
        ...forSubmittedAffilCard,
        dataList: sortedData,
        count: fixData.length,
        isLoading: false
      })
    }
    fetchTileData()
  }, [forSubmittedAffilCard.filter])

  const getSelectedForAppRow = (type, id) => {
    if (type === cntsType.PARTNER.TYPE.HOSPITAL) {
      props.history.push('/hospital-affiliation-form/' + id)
    } else if (type === cntsType.PARTNER.TYPE.CLINIC) {
      props.history.push('/medical-clinic-affiliation-form/' + id)
    } else if (type === cntsType.PARTNER.TYPE.DENTAL_CLINIC) {
      props.history.push('/dental-clinic-affiliation-form/' + id)
    } else if (type === cntsType.PARTNER.TYPE.DOCTOR) {
      props.history.push('/doctor-affiliation-form/' + id)
    } else if (type === cntsType.PARTNER.TYPE.OTHER_SERVICES) {
      props.history.push('/special-other-services-form/' + id)
    }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          {viewList.isOpen && (
            <EncoderViewList
              isOpen={viewList.isOpen}
              handleClose={status => setViewList({ ...viewList, isOpen: status })}
              stage={viewList.stage}
              getSelectedRow={obj => getSelectedForAppRow(obj.type, obj.id)}
            />
          )}
          <div className={classes.root}>
            <h1 className={classes.pageHeader}>Dashboard</h1>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={8}>
                <PartnerCard
                  isLoading={forAffilCompletionCard.isLoading}
                  headerCount={forAffilCompletionCard.count}
                  headerTitle={'Affiliation for Completion'}
                  addPartnerEnrollment={'+Add New Medical Partner'}
                  pathName={'/medical-partner-enrollment'}
                  cardTitle='Your Task Tray'
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forAffilCompletionCard.filter}
                  getSelectedFilter={filter => setForAffilCompletionCard({ ...forAffilCompletionCard, filter })}
                  headerList={['Affiliation No.', 'Date Submitted', 'Partner Type', 'Partner Name', 'Location']}
                  dataList={forAffilCompletionCard.dataList}
                  getSelectedRow={obj => getSelectedForAppRow(obj.type, obj.id)}
                  actionText={'Encode'}
                  openViewList={() => setViewList({ isOpen: true, stage: cntsStage.PARTNER.STAGE.ENCODING })}
                />
              </Grid>
              <Grid item xs={4}>
                <PartnerCard
                  headerCount={'0'}
                  headerCountStyle={classes.headerCountStyle}
                  cardTitle='Your Ticket List'
                  cardTitleStyle={classes.cardTitleStyle}
                  headerTitle={'Open Tickets'}
                  openViewList={() => false}
                  selectListFilter={[{ key: 'assignedToAll', value: 'Assigned To All' }]}
                  defaultFilter={'assignedToAll'}
                  getSelectedFilter={() => false}
                />
              </Grid>
            </Grid>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={6}>
                <PartnerCard
                  headerCount={'0'}
                  headerTitle={'Affiliation for Review (Submitted Thru Portal)'}
                  cardTitle=''
                  openViewList={() => false}
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'thisWeek', value: 'This Week' },
                    { key: 'thisMonth', value: 'This Month' }
                  ]}
                  defaultFilter={'today'}
                  getSelectedFilter={() => false}
                />
              </Grid>
              <Grid item xs={6}>
                <PartnerCard
                  isLoading={forSubmittedAffilCard.isLoading}
                  headerCount={forSubmittedAffilCard.count}
                  headerTitle={'Submitted Affiliation Form'}
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forSubmittedAffilCard.filter}
                  getSelectedFilter={filter => setForSubmittedAffilCard({ ...forSubmittedAffilCard, filter })}
                  headerList={['Affiliation No.', 'Partner Type', 'Partner Name']}
                  dataList={forSubmittedAffilCard.dataList}
                  getSelectedRow={obj => getSelectedForAppRow(obj.type, obj.id)}
                  actionText={'View'}
                  openViewList={() => setViewList({ isOpen: true, stage: '' })}
                />
              </Grid>
            </Grid>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default Dashboard
