let emailRegexp = RegExp(
  /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
)
let numberRegexp = RegExp(/^\d+$/)
let specialChar = RegExp(/[!@#$%^&*(),.?":{}|<>]/)
let lettersOnly = RegExp(/^[a-z]+$/i)
let contactNum = RegExp(/^0(9|4)\d{9}$/)
let dateFormatRegexp = RegExp(/^\d{2}\/\d{2}\/\d{4}$/)

const ValidateEmail = email => {
  if (emailRegexp.test(email)) {
    return true
  }
  return false
}

const ValidateOnlyNumbers = num => {
  if (numberRegexp.test(num)) {
    return true
  }

  return false
}

const ValidateSpecialChar = char => {
  if (specialChar.test(char)) {
    return true
  }
  return false
}

const ValidateLetterOnly = letters => {
  if (lettersOnly.test(letters)) {
    return true
  }
  return false
}

const ValidateContactNumber = num => {
  if (contactNum.test(num)) {
    return true
  }

  return false
}

const ValidateDateFormat = dateData => {
  if (dateFormatRegexp.test(dateData)) {
    return true
  }
  return false
}

// const ValidateDate = (date)

export {
  ValidateEmail,
  ValidateOnlyNumbers,
  ValidateSpecialChar,
  ValidateLetterOnly,
  ValidateContactNumber,
  ValidateDateFormat
}
