import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { contractRenewalCol } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import DropDown from '../../Components/CustomDrowpdown'

const ContractRenewal = () => {
  const [contractRenewal, setContractRenewal] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  const [categoryData, setCategoryData] = React.useState('')
  React.useEffect(() => {
    fetchData(categoryData)
  }, [])

  const fetchData = async (type) => {
    const { data } = await ReportServices.getContractRenewalData(type)
    const dataArr = data.data
    dataArr.forEach((value, index) => {
      dataArr[index].dataCol = {
        status: value.status,
        id: value.id,
        medical_partner_name: value.medical_partner_name,
        full_address: value.full_address,
        expiration_date: value.expiration_date
      }
    })
    setContractRenewal(dataArr)
  }
  const categories = [
    { id: 1, text: 'Hospital', value: 'Hospital' },
    { id: 2, text: 'Medical Clinic', value: 'Medical Clinic' },
    { id: 3, text: 'Medical Doctor', value: 'Medical Doctor' },
    { id: 4, text: 'Dental Clinic', value: 'Dental Clinic' },
    { id: 5, text: 'Medical Dentist', value: 'Medical Dentist' }
  ]
  const handleDropdown = async ({ target: select }) => {
    setCategoryData(select.value)
    fetchData(select.value)
  }
  return (
    <div>
      <div className={classes.rightBody}>
        <div style={{ width: '200px' }}>
          <DropDown onChange={handleDropdown} items={categories} value={categoryData} label='Medical Partner Category' />
        </div>
        <div id='root' className={classes.root} style={{ marginTop: '20px' }}>
          <ReactTable columns={contractRenewalCol} data={contractRenewal} setDownloadData={setDownloadData} />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={contractRenewalCol} filename={`${moment().format('YYYYMMDD')}-contract-renewal.csv`} />
      </div>
    </div>

  )
}

export default ContractRenewal
