
import instance from './../instance'
import { ExcludeErrorConstants } from 'Services/ErrorHandler/exclude'
const axios = instance('/p/l')

// const axios = instance('/api/')
export default class AddressChainingService {
  static getAddress ({category, field, value}) {
    return axios({
      url: `/partner/lookup`,
      method: 'GET',
      params: { category, field, value },
      headers: { [ExcludeErrorConstants['all']]: 1 }
    })
  }
}
