// 'use strict'
import moment from 'moment'

import {
  ValidateEmail,
  ValidateOnlyNumbers,
  ValidateSpecialChar,
  ValidateContactNumber
} from '../../../../../Components/Validators/Validators'

const checkIfHasErrors = obj => {
  for (var key in obj) {
    if (obj[key] !== null && obj[key] !== '') return false
  }
  return true
}

