// // ======= Dentist ==============
// firstName: '', // required
// middleName: '', // required
// lastName: '', // required
// birthDate: '', // required
// civilStatus: '', // required
// gender: '', // required
// emailAddress: '', // required
// contactNumber: '', // required
// mainSpecialization: '', // required
// majorSpecialization: '', // required

// prcNumber: '', // required
// prcExpiration: '', // required

// tinNumber: '', // required
// tinStatus: '', // required

// // =========================

// // dentalAsssociates
// ascName: '',
// ascNumber: '',
// ascMainSpec: '',
// ascPrcLicenseNo: '',
// ascPrcLicenseNoExp: '',
// //

// internalRemarks: '' // Required

const Validate = async data => {
  let formResult
  let errors = {}

  // console.log('data.birthDate', moment(data.birthDate).format('L') !== 'Invalid date')

  let required = 'This is Required Field'
  let numbersOnly = 'Numbers Only'
  let noSpecialChar = 'Contains Special Characters'
  let invalidEmail = 'Invalid Email'

  errors.name = data.name.length > 0 ? (ValidateSpecialChar(data.name) ? noSpecialChar : null) : required

  errors.mobile = data.mobile.length > 0 ? (ValidateOnlyNumbers(data.mobile) ? null : numbersOnly) : required

  errors.mainCoordinator =
    data.mainCoordinator.length > 0 ? (ValidateSpecialChar(data.mainCoordinator) ? noSpecialChar : null) : required

  errors.email = data.email.length > 0 ? (ValidateEmail(data.email) ? null : invalidEmail) : required

  errors.checkPayee =
    data.checkPayee.length > 0 ? (ValidateSpecialChar(data.checkPayee) ? noSpecialChar : null) : required

  errors.street = data.street.length > 0 ? null : required

  errors.street = data.street.length > 0 ? null : required

  errors.subdivision = data.subdivision.length > 0 ? null : required

  errors.region = data.region !== '' ? null : required

  errors.district = data.district !== '' ? null : required

  errors.barangay = data.barangay !== '' ? null : required

  errors.city = data.city !== '' ? null : required

  errors.natureOfPractice =
    data.natureOfPractice.length > 0
      ? ValidateSpecialChar(data.natureOfPractice)
        ? 'Contains Special Characters'
        : null
      : required

  errors.zipCode = data.zipCode.length > 0 ? (ValidateOnlyNumbers(data.zipCode) ? null : numbersOnly) : required

  errors.clinicSchedule = data.clinicSchedule.length > 0 ? null : required

  errors.firstName = data.firstName.length > 0 ? (ValidateSpecialChar(data.firstName) ? noSpecialChar : null) : required

  errors.middleName =
    data.middleName.length > 0 ? (ValidateSpecialChar(data.middleName) ? noSpecialChar : null) : required

  errors.lastName = data.lastName.length > 0 ? (ValidateSpecialChar(data.middleName) ? noSpecialChar : null) : required

  errors.birthDate = moment(data.birthDate).format('L') !== 'Invalid date' ? null : required

  errors.civilStatus = data.civilStatus !== '' ? null : required

  errors.gender = data.gender !== '' ? null : required

  errors.emailAddress =
    data.emailAddress.length > 0 ? (ValidateEmail(data.emailAddress) ? null : invalidEmail) : required

  errors.contactNumber =
    data.contactNumber.length > 0 ? (ValidateOnlyNumbers(data.contactNumber) ? null : numbersOnly) : required

  errors.mainSpecialization = data.mainSpecialization !== '' ? null : required

  errors.majorSpecialization = data.majorSpecialization !== '' ? null : required

  errors.prcNumber = data.prcNumber.length > 0 ? (ValidateOnlyNumbers(data.prcNumber) ? null : numbersOnly) : required

  errors.prcExpiration = moment(data.prcExpiration).format('L') !== 'Invalid date' ? null : required

  errors.tinNumber =
    data.tinNumber.length > 0
      ? ValidateOnlyNumbers(data.tinNumber)
        ? data.tinNumber.length === 12
          ? 'Must Be 12 digits'
          : null
        : null
      : required

  errors.tinStatus = data.tinStatus.length > 0 ? null : required

  errors.internalRemarks = data.internalRemarks.length > 0 ? null : required

  if (
    data.ascName !== undefined ||
    data.ascNumber !== undefined ||
    data.ascMainSpec !== undefined ||
    data.ascPrcLicenseNo !== undefined
  ) {
    console.log('eh dito ba napasok ')
    if (
      data.ascName.length > 0 ||
      data.ascNumber.length > 0 ||
      data.ascMainSpec !== '' ||
      data.ascPrcLicenseNo.length > 0
    ) {
      console.log('napasok ba dito ?')
      errors.ascName = data.ascName.length > 0 ? (ValidateSpecialChar(data.ascName) ? noSpecialChar : null) : required

      errors.ascNumber =
        data.ascNumber.length > 0 ? (ValidateOnlyNumbers(data.ascNumber) ? null : numbersOnly) : required

      errors.ascMainSpec = data.ascMainSpec !== '' ? null : required

      errors.ascPrcLicenseNo = data.ascPrcLicenseNo.length > 0 ? null : required

      errors.ascPrcLicenseNoExp = moment(data.ascPrcLicenseNoExp).format('L') !== 'Invalid date' ? null : required
    } else {
      errors.ascName = null

      errors.ascNumber = null

      errors.ascMainSpec = null

      errors.ascPrcLicenseNo = null

      errors.ascPrcLicenseNoExp = null
    }
  }

  formResult = !!checkIfHasErrors(errors)

  return Promise.resolve({ errors, formResult })
}

export default Validate
