import React from 'react'
import classes from './AutoSuggest.module.scss'
import { RenderInputFields } from '../../../Components/InputsForProfiles/InputsForProfile'

const AutosuggestComponent = props => {
  const { data, setValue, value, onBlur, onFocus, setSelectedVal = null } = props

  const [suggestions, setSuggestions] = React.useState([])

  const HandleChange = e => {
    const { value } = e.target
    let suggestionsValue = []
    try {
      if (value.length > 0) {
        const regexCompare = new RegExp(`^${value}`, 'i')
        suggestionsValue = data.sort().filter(items => regexCompare.test(items.code))
      }
      setSuggestions(suggestionsValue)
      setValue(value)
    } catch (e) {
      console.log('error', e)
    }
  }

  const selectedSuggestion = (value = {}) => {
    let { code, description, category } = value
    setValue(code)
    if (typeof setSelectedVal === 'function') {
      setSelectedVal({
        cpt: code,
        procedure: description,
        procedureDescription: category,
        status: 'Active' // status[0].toUpperCase() + status.slice(1) --> removed fetching of status from UM and add initial value
      })
    }
    setSuggestions([])
  }

  const RenderSuggestion = () => {
    if (suggestions.length === 0) {
      return null
    }

    return (
      <div className={classes.renderSuggestion}>
        <ul id='rootList' className={classes.list}>
          {suggestions.map(items => (
            <li key={items._id} onClick={() => selectedSuggestion(items)}>
              {items.code}
            </li>
          ))}
        </ul>
      </div>
    )
  }

  return (
    <div className={classes.root}>
      <RenderInputFields value={value} onChange={HandleChange} onBlur={onBlur} type={'noSpecialChar'} />
      {RenderSuggestion()}
    </div>
  )
}

export default AutosuggestComponent
