import React, { useState, useEffect } from 'react'
import { Input } from '@material-ui/core'
import classes from './InlineEditField.module.scss'

const InlineEditField = ({ contact, contacts, setContacts }) => {
  console.log('inline edit', contacts)
  useEffect(() => {
    setForm(contact)
  }, [])

  const [form, setForm] = useState({})

  const handleChange = ({ target: input }) => {
    const newForm = { ...form }
    const editedContact = [...contacts]
    const newContacts = editedContact.map(contact => (contact.cNumber === newForm.cNumber ? newForm : contact))
    newForm[input.name] = input.value
    setForm(newForm)
    setContacts(newContacts)
  }

  const { cName, cDesignation, cEmail, cNumber } = form

  return (
    <div id='contactDetail' className={classes.contactDetail}>
      <form>
        <Input value={cDesignation} disableUnderline name='cDesignation' onChange={handleChange} />
        <br />
        <Input disableUnderline value={cName} name='cName' onChange={handleChange} />
        <br />
        <Input disableUnderline value={cNumber} name='cNumber' onChange={handleChange} />
        <br />
        <Input disableUnderline value={cEmail} name='cEmail' onChange={handleChange} />
      </form>
    </div>
  )
}

export default InlineEditField
