import OtherServices from 'Services/Api/Partner/OtherServices'
import AddressChaining from '../../../../../../../../Constant/chaining-address'

let obj = {
  name: '',
  unitFlrBldg: '',
  subdivision: '',
  barangay: '',
  bldgName: '',
  region: '',
  city: '',
  street: '',
  district: '',
  zipCode: '',
  tinNumber: null,
  contactPerson: '',
  contactNumber: '',
  dateReceived: new Date().toISOString()
}
export async function FetchData (id) {
  try {
    const {
      data: { data }
    } = await OtherServices.getOtherServiceById(id)
    console.log('res data sa service', data)
    const { contact, stage, affiliation_status, status, type, name, tin_number, address, date_received } = data

    obj.bldgName = address['building_name'] ? address['building_name'] : ''
    obj.street = address['street'] ? address['street'] : ''
    obj.barangay = address['barangay'] ? address['barangay'] : ''
    obj.unitFlrBldg = address['unit_flr_bldg'] ? address['unit_flr_bldg'] : ''
    obj.region = address['region'] ? address['region'] : ''
    obj.city = address['city'] ? address['city'] : ''
    obj.district = address['district_prov'] ? address['district_prov'] : ''
    obj.subdivision = address['subdivision'] ? address['subdivision'] : ''
    obj.zipCode = address['zip_code'] ? address['zip_code'] : ''
    obj.contactNumber = contact['contact_number'] ? contact['contact_number'] : ''
    obj.contactPerson = contact['contact_person'] ? contact['contact_person'] : ''
    obj.tinNumber = tin_number ? tin_number : ''
    obj.dateReceived = date_received ? new Date(date_received).toISOString() : new Date().toISOString()
    obj.name = name
    // AddressChaining.getProvince(address['region'])
    // AddressChaining.getCityMunicipality(address['district'])
    // AddressChaining.getBarangays(address['city'])
    return obj
  } catch (errors) {
    console.log('errors sa service', errors)
  }
}
