import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './AddContactModal.module.scss'
import ContactDetailsValidation from '../ContactDetailsValidation/ContactDetailsValidation'

const AddContactModal = ({ contactsValue, setContacts, setOriginalContact, paramId, Endpoint, type, isToEdit }) => {
  const [open, setOpen] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [isFormValid, setIsFormValid] = React.useState(false)
  const [errors, setErrors] = React.useState({})
  const [form, setForm] = React.useState({
    cDesignation: '',
    cName: '',
    cNumber: '',
    cEmail: '',
    cDepartment: ''
  })

  // React.useEffect(() => {
  //   if (isToEdit) {
  //     setOpen(false)
  //   }
  // }, [isToEdit])

  React.useEffect(() => {
    return () => {
      setForm({
        cDesignation: '',
        cName: '',
        cNumber: '',
        cEmail: '',
        cDepartment: ''
      })
      setErrors({})
    }
  }, [])

  const validate = () => {
    const { errors, isValid } = ContactDetailsValidation(form, type)
    if (isValid) {
      setContacts([...contactsValue, form])
      setIsFormValid(isValid)
    } else {
      setErrors(errors)
    }
  }

  if (isFormValid) {
    setErrors({})
    try {
      let contacts = [...contactsValue]

      let obj = {}
      obj.id = paramId
      obj.contacts = contacts
      setIsLoading(true)
      setTimeout(async () => {
        const response = await Endpoint(obj)
        const datas = response
          ? response.data
            ? response.data.data
              ? response.data.data.contacts
                ? response.data.data.contacts
                : []
              : []
            : []
          : []

        setContacts(datas)
        setOriginalContact(datas)
        setIsLoading(false)
        handleClose(false)
      }, 1000)
      setIsFormValid(false)
    } catch (error) {
      console.log('add contact error', error)
      setIsLoading(false)
      setIsFormValid(false)
    }
  }

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const handleChange = ({ currentTarget: input }) => {
    const contactForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[input.name]
    contactForm[input.name] = input.value
    setErrors(newErrors)
    setForm(contactForm)
  }

  const handleClose = () => {
    setForm({
      cDesignation: '',
      cName: '',
      cNumber: '',
      cEmail: '',
      cDepartment: ''
    })
    setOpen(false)
    setErrors({})
  }
  return (
    <div id='container'>
      <label
        className={classes.addContact}
        color='primary'
        onClick={() => {
          isToEdit ? setOpen(false) : setOpen(true)
        }}
      >
        +Add Contact
      </label>
      <Dialog fullWidth maxWidth={'md'} open={open} aria-labelledby='max-width-dialog-title'>
        <DialogTitle id='max-width-dialog-title'>Add Contact</DialogTitle>
        <DialogContent>
          <form className={classes.root} noValidate autoComplete='off'>
            <div id='firstInput' className={type ? classes.firstInput1 : classes.firstInput}>
              <div id='fullnameField'>
                <TextField name='cName' onChange={handleChange} label='Full Name' error={errors['cName']} />
              </div>
              <div id='designationField'>
                <TextField
                  onChange={handleChange}
                  label='Designation'
                  name='cDesignation'
                  error={errors['cDesignation']}
                />
              </div>
              <div id='departmentField'>
                <TextField
                  id='department'
                  name='cDepartment'
                  onChange={handleChange}
                  error={errors['cDepartment']}
                  label='Department'
                />
              </div>
            </div>
            <div id='secondInput' className={type ? classes.secondInput1 : classes.secondInput}>
              <div id='emailAddField'>
                <TextField name='cEmail' onChange={handleChange} error={errors['cEmail']} label='Email Address' />
              </div>
              <div id='contactNumberfield'>
                <TextField name='cNumber' onChange={handleChange} error={errors['cNumber']} label='Contact Number' />
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={() => {
              handleClose(false)
            }}
            variant='contained'
            className={classes.Cancel}
          >
            CANCEL
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            {isLoading ? 'Adding...' : 'ADD'}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

export default AddContactModal
