import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { Provider } from 'react-redux'

import DoctorProfileParamsContext from './Pages/Context/DoctorProfileParamsContext'

import ProfileList from '../../Components/ProfileList/ProfileList'
import DoctorList from '../../../Partner/ProfileMaintainer/DoctorProfile/Pages/DoctorList/DoctorList'
import Partner from './Pages/PartnerInformation/index'
import ContactDetailsViewer from './Pages/ViewerContract/ContractDetailsViewer'
import ListOfAffiliatedFacility from '../Components/ListOfAffiliatedFacility/ListOfAffiliatedFacility'
import ContactDetails from '../../Components/ContactDetails/ContactDetails'
import OtherDocuments from './Pages/OtherDocuments/OtherDocuments'
import placeholderImage from '../../Assets/Icons/placeHolderPicture.png'
import Store from '../../../Store'

const DoctorProfileIndex = () => {
  const [doctorName, setDoctorName] = React.useState('')
  const [facilityInfos, setFacilityInfos] = React.useState('')
  const [currentStatus, setCurrentStatus] = React.useState('')
  const [isOpenAffiliatedDoctorsModal, setIsOpenAffiliatedDoctorsModal] = React.useState(false)
  const [isOpenAffiliatedFacilityModal, setIsOpenAffiliatedFacilityModal] = React.useState(false)
  const [paramId, setParamId] = React.useState('')
  const [afnNumber, setAfnNumber] = React.useState('')
  const [headerData, setHeaderData] = React.useState({
    status: '',
    name: '',
    fullName: ''
  })
  const [changeName, setChangeName] = React.useState(null)
  const [isEdit, setIsEdit] = React.useState(false)
  const [isToUpdate, setIsToUpdate] = React.useState(false)

  const breadcrumbs = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    HospitalPartnerProfile: {
      text: 'DOCTOR PARTNER PROFILE',
      to: '/hospitalAffiliationForm'
    },
    Doctor: {
      text: headerData.fullName,
      to: `/DoctorProfile/PartnerInformation/${paramId}`
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'HospitalPartnerProfile', 'Doctor'])
  }, [headerData.name, paramId, doctorName, currentStatus])

  const row = [
    {
      to: `/DoctorProfile/PartnerInformation/${paramId}`,
      name: ' Partner Information',
      path: '/DoctorProfile/PartnerInformation/:doctorId',
      click: () => setIsToUpdate(false),
      importPage: (
        <DoctorProfileParamsContext.Provider
          value={{
            setParamId: setParamId,
            paramId,
            isToUpdate,
            setIsToUpdate,
            setChangeName,
            setHeaderData,
            headerData,
            isEdit,
            setIsEdit
          }}
        >
          <Partner
            setDoctorName={setDoctorName}
            setCurrentStatus={setCurrentStatus}
            setAfnNumber={setAfnNumber}
            setFacilityInfos={setFacilityInfos}
            setParamId={setParamId}
          />
        </DoctorProfileParamsContext.Provider>
      )
    },
    {
      to: `/DoctorProfile/ContractDetails/${paramId}`,
      name: 'Contract Details',
      path: '/DoctorProfile/ContractDetails/:paramId',
      importPage: <ContactDetailsViewer paramId={paramId} facilityInfos={facilityInfos} />
    },
    {
      to: `/DoctorProfile/ListOfAffiliatedFacility/${paramId}`,
      name: 'List Of Affiliated Facility',
      path: '/DoctorProfile/ListOfAffiliatedFacility/:doctorId',
      open: setIsOpenAffiliatedFacilityModal
    },
    {
      to: `/DoctorProfile/ContactDetails/${paramId}`,
      name: 'Contact Details',
      path: '/DoctorProfile/ContactDetails/:doctorId',
      importPage: <ContactDetails paramId={paramId} type={'DOCTOR'} />
    },
    {
      to: `/DoctorProfile/OtherDocuments/${paramId}`,
      name: 'Other Documents',
      path: '/DoctorProfile/OtherDocuments/:paramId',
      importPage: <OtherDocuments paramId={afnNumber} />
    }
  ]

  return (
    <Provider store={Store}>
      <ProfileList
        ListItems={row}
        HeaderTitle={isToUpdate ? changeName : headerData.name}
        leftSideImage={placeholderImage}
        status={currentStatus}
      />
      {isOpenAffiliatedDoctorsModal && (
        <DoctorList open={isOpenAffiliatedDoctorsModal} setOpen={setIsOpenAffiliatedDoctorsModal} />
      )}
      {isOpenAffiliatedFacilityModal && (
        <ListOfAffiliatedFacility
          open={isOpenAffiliatedFacilityModal}
          setOpen={setIsOpenAffiliatedFacilityModal}
          paramId={paramId}
          endPoint={'/affiliatedFacility'}
        />
      )}
    </Provider>
  )
}

export default DoctorProfileIndex
