function CustomAutoComplete (props) {
  const {
    label = '',
    disabled,
    required,
    value,
    readOnly,
    customStyle,
    constainerStyle = {},
    error,
    inputValueLengthSize,
    onChange,
    suggestions = [],
    labelName = 'id'
  } = props

  const stlyedRock = useStyles()

  const {
    getRootProps,
    getInputProps,
    getListboxProps,
    getOptionProps,
    groupedOptions,
    inputValue

  } = useAutocomplete({
    id: 'use-autocomplete-demo',
    componentName: 'autoSuggest',
    disableOpenOnFocus: disabled,
    // onChange: (e, value) => disabled ? null : onChange(value ? value[labelName] : '', value),
    onChange: (e, value) => disabled ? null : onChange(value),
    options: suggestions,
    value,
    disabled: disabled,
    getOptionLabel: (option) => option[labelName] ? option[labelName] : value,
    getOptionSelected: (option, value) => option[labelName] === value
  })

  const InputSize = () => {
    if (inputValueLengthSize) {
      return inputValue.length > inputValueLengthSize
    } else {
      return inputValue
    }
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper style={{backgroundColor: disabled ? '#e2e7ec' : ''}} className={`${classes['text-field']} ${error && classes['has-error']}`} {...getRootProps()}>
              <InputBase
                style={{color: disabled ? 'white' : '#000000de'}}
                className={classes.input}
                disabled={disabled}
                {...getInputProps()}
              />
              {customStyle
                ? groupedOptions.length > 0 && InputSize() ? (

                  <ul className={groupedOptions.length === 6 || groupedOptions.length === 5 ? stlyedRock.listbox3 : groupedOptions.length === 4 || groupedOptions.length === 3 ? stlyedRock.listbox4 : groupedOptions.length === 2 || groupedOptions.length === 1 ? stlyedRock.listbox5 : groupedOptions.length === 7 || groupedOptions.length === 8 || groupedOptions.length === 9 ? stlyedRock.listbox6 : stlyedRock.listbox2} {...getListboxProps()}>
                    {groupedOptions.map((option, index) => (

                      <li {...getOptionProps({ option, index })} >{option[labelName] }</li>
                    ))}
                  </ul>
                ) : null
                : groupedOptions.length > 0 && InputSize() ? (

                  <ul className={stlyedRock.listbox} {...getListboxProps()}>
                    {groupedOptions.map((option, index) => (

                      <li {...getOptionProps({ option, index })} >{option[labelName] }</li>
                    ))}
                  </ul>
                ) : null
              }

            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(CustomAutoComplete)
