import React from 'react'
import classes from './PartnerInformation.module.scss'
import { dateFormat, MonthdateFormat, getTinStatus, getNestedObjects } from '../../../../../../Components/Utils/utils'
import DentistProfileContext from '../../context'
import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'

const MedicalDentistInformation = () => {
  const { profile } = React.useContext(DentistProfileContext)

  let assignments = profile
    ? profile.facility_assignment
      ? profile.facility_assignment.assignments
        ? profile.facility_assignment.assignments
        : []
      : []
    : []

  const medicalId = getNestedObjects(profile, ['id'])
  const birthdate = profile ? dateFormat(profile.birthdate) : ''
  const civilStatus = getNestedObjects(profile, ['civil_status'])
  const gender = getNestedObjects(profile, ['gender'])
  const main = getNestedObjects(profile, ['specialization', 'main'])
  const major = getNestedObjects(profile, ['specialization', 'major'])
  const other = getNestedObjects(profile, ['specialization', 'other'])
  const sub = getNestedObjects(profile, ['specialization', 'sub'])
  const prcNumber = getNestedObjects(profile, ['license', 'prc', 'number'])
  const prcExpiration = profile
    ? profile.license
      ? profile.license.prc
        ? profile.license.prc.expiration_date
          ? MonthdateFormat(profile.license.prc.expiration_date)
          : ''
        : ''
      : ''
    : ''
  const philhealthNumber = getNestedObjects(profile, ['philhealth', 'accreditation_number'])
  const philhealthExp = profile ? (profile.philhealth ? profile.philhealth.expiration_date ? MonthdateFormat(profile.philhealth.expiration_date) : '' : '') : ''
  const pda = getNestedObjects(profile, ['license', 'pda'])
  const professionalFee = getNestedObjects(profile, ['professional_fees', 'rate'])
  const tinNumber = getNestedObjects(profile, ['treasury', 'tin'])
  const tinStatus = getNestedObjects(profile, ['treasury', 'tin_status'])
  const taxRate = getNestedObjects(profile, ['treasury', 'vat', 'tax_rate'])
  const paymentTerms = getNestedObjects(profile, ['treasury', 'payment_terms'])
  const facilityAddress = getNestedObjects(profile, ['facility_info', '0', 'full_address'])
  const disaffiliation = profile ? (profile.disaffiliation ? MonthdateFormat(profile.disaffiliation.date) : '') : ''
  const dateOfAffiliation = profile
    ? profile.affiliation_status
      ? profile.affiliation_status.date_submitted ? MonthdateFormat(profile.affiliation_status.date_submitted)
        : ''
      : ''
    : ''
  const facilityName = getNestedObjects(profile, ['facility_info', '0', 'name'])
  const clinicSched = getNestedObjects(profile, ['facility_info', '0', 'clinic_schedule'])
  const withTax = getNestedObjects(profile, ['treasury', 'withHoldingTax'])

  const Information = [
    { id: 301, title: 'Medical Partner ID', value: medicalId },
    {
      id: 302,
      title: 'Birthdate',
      value: birthdate,
      title2: 'civil status',
      value2: civilStatus
    },
    { id: 303, title: 'gender', value: gender },
    {
      id: 304,
      title: 'main specialization',
      value: main,
      title2: 'major specialization',
      value2: major
    },
    {
      id: 305,
      title: 'sub specialization',
      value: sub,
      title2: 'other specialization',
      value2: other
    },
    {
      id: 306,
      title: 'prc license no.',
      value: prcNumber,
      title2: 'prc license exp.',
      value2: prcExpiration
    },
    {
      id: 307,
      title: 'philhealth accreditation no.',
      value: philhealthNumber,
      title2: 'philhealth exp.',
      value2: philhealthExp
    },
    { id: 308, title: 'pda number', value: pda, title2: 'professional fee', value2: professionalFee },
    { id: 309, title: 'tin', value: tinNumber },
    { id: 310, title: 'tin status', value: getTinStatus(tinStatus) },
    { id: 3101, title: 'tax rate', value: taxRate },
    { id: 3102, title: 'process type', value: getNestedObjects(profile, ['process_type']) },
    {
      id: 311,
      title: 'payment terms',
      value: paymentTerms
    },
    { id: 312, title: 'withholding tax', value: withTax },
    { id: 313, title: 'dental facility', value: facilityName },
    {
      id: 314,
      title: 'facility address',
      value: facilityAddress
    },
    {
      id: 315,
      title: 'clinic schedule',
      value: clinicSched
    },
    {
      id: 316,
      title: 'date of affiliation',
      value: dateOfAffiliation,
      title2: 'date of disaffiliation',
      value2: disaffiliation
    }
  ]

  return (
    <PartnerViewInformation
      data={Information}
      facilityLabel={assignments}
      type={'Doctor'}
      listStyles={classes.listStyles}
      firstColumStyle={classes.firstRow}
      secondColumnStyle={classes.secondRow}
    />
  )
}

export default MedicalDentistInformation
