import Select from '@material-ui/core/Select'

import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import MenuItem from '@material-ui/core/MenuItem'

import classes from './DropDown.module.scss'
import { useSelector } from 'react-redux'

/**
 * Props
 * id required
 * label
 * required
 * value
 * items array {value, text}
 * onChange
 *
 */
const DoctorSpecializationDropdown = props => {
  const {
    id,
    label = '',
    required,
    readOnly,
    onChange,
    disableComponents = false,
    error,
    minWidth,
    ...other
  } = props
  const { value = '' } = props
const { doctorSpecialization } = useSelector(state => state.autoSuggestData)

  const [_error, setError] = React.useState()
  const [isEmp, setisEmp] = React.useState(false)
  const [items , setItems] = React.useState([])
  React.useEffect(() => {
    if (typeof value === 'string') {
      if(value === '') {
        setisEmp(true)
      } else {
        setisEmp(false)
      }
      if (!value.trim()) setError(error)
    }
  }, [error, value])

  React.useEffect(() => {
    setItems(doctorSpecialization)
  }, [])


  return (
    <div className={classes.root}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper} style={minWidth}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper
              className={`${classes['select-container']} ${_error && !value && classes['has-error']} ${
                disableComponents && classes.disabled
              }`}
            >
              <Select
                id={id}
                onChange={onChange}
                readOnly={readOnly}
                displayEmpty={isEmp}
                value={value}
                renderValue={(value) => isEmp ? '' : value}
                classes={{ root: classes.select }}
                input={<InputBase className={classes.input} />}
                disabled={disableComponents}
                {...other}
              >
                <MenuItem value='' />
                { items.map((item, index) => (
                  <MenuItem key={item._id} id={item._id} value={item.description}>
                    {item.description}
                  </MenuItem>
                ))}
              </Select>
            </Paper>

            {_error && !value && <span className={classes.error}>{_error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(DoctorSpecializationDropdown)
