import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'

import classes from './Confirmation.module.scss'

const FooterDialogConfirmation = (props) => {
  const { dialogText, handleProceed, isSubmitted, disable, setDisable } = props
  const [open, setOpen] = React.useState(false)
  React.useEffect(() => setOpen(false), [isSubmitted])
  const handleProceedDialog = () => {
    setOpen(false)
    if (setDisable) {
      setDisable(true)
    }
    handleProceed()
  }
  return (
    <React.Fragment>
      <Button
        disabled={disable}
        style={{ backgroundColor: disable ? '#dddfe3' : '#3ab77d', color: 'white', paddingLeft: '1.5em', paddingRight: '1.5em' }}
        className={disable ? classes.disabledButton : classes.submitButton}
        variant='contained'
        onClick={() => {
          setOpen(true)
        }}
      >
        SUBMIT
      </Button>
      <Dialog fullWidth maxWidth={'sm'} open={open} aria-labelledby='max-width-dialog-title'>
        <DialogContent>
          <div className={classes.root} noValidate autoComplete='off'>
            Are you sure you want to proceed with the submission of <br />
            {dialogText} form <br />
            for approval?
          </div>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <div id='buttonDiv'>
            <Button
              onClick={() => {
                setOpen(false)
              }}
              variant='contained'
              className={classes.Cancel}
            >
              CANCEL
            </Button>
            <Button variant='contained' onClick={handleProceedDialog} className={classes.Proceed}>
              Proceed
            </Button>
          </div>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  )
}

export default FooterDialogConfirmation
