import instance from '../instance'
const facilities = instance('/p/f')
const otherServices = instance('/p/os/')
const doctor = instance('/p/d/')

export default class FacilitiesServices {
  static async getFacilitiesByType (type) {
    return facilities({
      url: `/partners/facilities/type/${type}`,
      method: 'GET'
    })
  }

  static async getServices (type) {
    return otherServices({
      url: `/partners/other-services/${type}`,
      method: 'GET'
    })
  }

  static async getAllDoctorsByType (type) {
    return doctor({
      url: `/partners/doctors/type/${type}`,
      method: 'GET'
    })
  }

  static async getAllAhmopiDoctors (Ahmopi) {
    return doctor({
      url: `/partners/doctors/amhopi/${Ahmopi}`,
      method: 'GET'
    })
  }

  static async getContractRenewalData (type) {
    return facilities({
      url: `/partners/facilities/contract/renewal/reports/data/${type}`,
      method: 'GET'
    })
  }

  static async getTotalMedicalPartnersData (location) {
    return facilities({
      url: `/partners/facilities/total/medical/partner/data/${location}`,
      method: 'GET'
    })
  }
}
