import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'

// Pages of Profile Maintainer
import Dashboard from './Dashboard/Dashboard'
import HospitalProfile from './HospitalProfile/Routes'
import MedicalClinicProfile from './MedicalClinicProfile/Routes'
import DoctorProfile from './DoctorProfile/Routes'
import MedicalDentistProfile from './MedicalDentistProfile/Routes'
import DentalClinicProfile from './DentalClinicProfile/Routes'
import SpecialOtherServicesProfile from './SpecialOtherServicesProfile2/Routes'

// Store
import Store from '../../Store'

const ProfileMaintainerRoutes = props => {
  React.useEffect(() => {
    props.history.push('/profile-maintainer')
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/profile-maintainer' exact component={Dashboard} />
        <Route path='/HospitalProfile' component={HospitalProfile} />
        <Route path='/DentalClinicProfile' component={DentalClinicProfile} />
        <Route path='/DoctorProfile' component={DoctorProfile} />
        <Route path='/MedicalClinicProfile' component={MedicalClinicProfile} />
        <Route path='/MedicalDentist' component={MedicalDentistProfile} />
        <Route path='/SpecialOtherServicesList' component={SpecialOtherServicesProfile} />
      </Switch>
    </Provider>
  )
}

export default ProfileMaintainerRoutes
