import React from 'react'
import { Provider, useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import store from '../../../../../Store'
import HeaderWithBreadCrumbsNoStepper from '../../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import OICProcessorServices from 'Services/Api/Partner/OICProcessorServices'

// Import Sub Pages
import GeneralInformation from '../../../Components/GeneralInformation/GeneralInformation'
import Services from '../Subpages/Services/Services'
import GeneralAppearance from '../Subpages/GeneralAppearance/GeneralAppearance'
import TypeOcularInspection from '../../../Components/TypeOcularInspection/TypeOcularInspection'
import Footer from '../../../Components/Footer/Footer'
import CustomAlertSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import {
  updateGeneralInformation,
  updateInspectorType,
  validateField,
  savePage,
  nextPage,
  submitPageForApproval,
  returnPage,
  openSaveDialog,
  updateState,
  setReadOnly,
  openSubmitDialog
} from '../../../Store/Actions/actions'

// Import State Constants
import SubmitDialog from '../../../Components/SubmitDialog/SubmitDialog'
import CancelDialog from '../../../Components/CancelDialog/CancelDialog'

import classes from './Dental.module.scss'

// This will be the container for dental form
const DentalPage = props => {
  const { role = 'OICProcessor', history = {}, group = '' } = props
  const { id } = useParams()
  const isComponentAttached = React.useRef(true)
  const formStage = React.useRef({})
  const version = React.useRef(0)
  const dispatch = useDispatch()
  const dentalState = useSelector(state => {
    return state.dental
  })
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  React.useEffect(() => {
    setIsLoaded(true)
    BreadcrumbsService.setSelectedItemsWithoutHome(['MEDICAL_NETWORK_OFFICER', 'DENTAL_OCULAR_INSPECTION_CHECKLIST'])
    return () => {
      isComponentAttached.current = false
    }
  }, [])

  React.useEffect(() => {
    LoadingService.loading(true)
    const fetchData = async () => {
      const { data } = await OICProcessorServices.getAffiliationFormInfo(id)
      if (isComponentAttached && data.data) {
        let generalInformation = {
          name: data.data.name,
          nature: data.data.nature_of_practice,
          mno: data.data.mno,
          address: data.data.full_address
        }
        if (data.data.partner_oic) {
          version.current = data.data.partner_oic.version
          generalInformation = {
            ...generalInformation,
            dateOfInspection: moment(data.data.partner_oic.generalInformation.dateOfInspection)
          }
          dispatch(
            updateState({
              ...data.data.partner_oic,
              generalInformation: { ...data.data.partner_oic.generalInformation, ...generalInformation }
            })
          )
        } else {
          generalInformation = {
            ...generalInformation,
            dateOfInspection: moment(dentalState.generalInformation.dateOfInspection)
          }
          dispatch(
            updateState({
              generalInformation: { ...dentalState.generalInformation, ...generalInformation }
            })
          )
        }
        formStage.current = { ...data.data.stage }
        if (
          ['FOR_OIC_APPROVAL', 'PROFILE_GENERATED', 'FOR_AFFILIATION_APPROVAL'].includes(data.data.stage.stage_name)
        ) {
          dispatch(setReadOnly(true))
        }
      }
      LoadingService.loading(false)
    }
    fetchData()
  }, [])

  React.useEffect(() => {
    if (dentalState.save) {
      LoadingService.loading(true)
      updateData(formStage.current)
      LoadingService.loading(false)
    }
  }, [dentalState.save])

  const updateData = async stage => {
    LoadingService.loading(true)
    let finalState = { ...dentalState, stage }
    delete finalState.readOnly
    delete finalState.openSaveDialog
    delete finalState.openSubmitDialog
    delete finalState.page
    delete finalState.save
    delete finalState.errors
    const { data } = await OICProcessorServices.saveFacilityOICForm({
      id,
      stage: stage,
      partner_oic: finalState,
      version: version.current
    })
    if (data.data.writeConflict) {
      dispatch(savePage(false))
      LoadingService.loading(false)
      return SnackbarService.set({
        open: true,
        message: 'Form used by others. Return to dashboard',
        severity: 'error'
      })
    }
    if (isComponentAttached.current) version.current = data.data.partner_oic.version
    if (data.data.stage.stage_name === 'FOR_OIC_APPROVAL') {
      dispatch(setReadOnly(true))
      dispatch(openSaveDialog(false))
    } else {
      dispatch(openSaveDialog(true))
    }
    dispatch(savePage(false))
    dispatch(openSubmitDialog(false))
    LoadingService.loading(false)
  }

  const onClickReturnPage = () => {
    switch (dentalState.page) {
      case 'services':
        return setIsCancel(true)
      case 'generalAppearance':
        return dispatch(returnPage('services'))
    }
  }

  const onClickNextPage = () => {
    dispatch(validateField())
    dispatch(nextPage('generalAppearance'))
  }

  const onClickSavePage = () => {
    dispatch(validateField())
    dispatch(savePage(true))
  }

  const handleOnSubmit = () => {
    updateData({ stage_name: 'FOR_OIC_APPROVAL', date_submitted: moment().format() })
  }

  const renderCurrentPage = () => {
    switch (dentalState.page) {
      case 'services':
        return <Services id='dentalServices' readOnly={dentalState.readOnly} />
      case 'generalAppearance':
        return <GeneralAppearance id='dentalGeneralAppearance' readOnly={dentalState.readOnly} />
    }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          <CustomAlertSnackBar isSave={dentalState.openSaveDialog} setIsSave={val => dispatch(openSaveDialog(false))} />
          <SubmitDialog
            isSubmit={dentalState.openSubmitDialog}
            setIsSubmit={val => dispatch(openSubmitDialog(val))}
            handleOnSubmit={handleOnSubmit}
            message='Are you sure  you want to proceed with the submission of Dental Clinic Ocular Inspection Checklist for approval?'
          />
          <CancelDialog
            isCancel={isCancel}
            setIsCancel={setIsCancel}
            handleOnCancel={() => history.push('/oicprocessor')}
          />
          <div id='dentalRoot' className={classes.root}>
            <Provider store={store}>
              <HeaderWithBreadCrumbsNoStepper id='dentalHeader' HeaderTitle='Dental Clinic Checklist' />
            </Provider>
            <div id='dentalContent' className={classes.content}>
              {dentalState.page !== 'generalAppearance' && (
                <React.Fragment>
                  <GeneralInformation
                    id='dentalGeneralInformation'
                    readOnly={dentalState.readOnly}
                    nameField={{ label: 'Name of Facility', content: `${dentalState.generalInformation.name}` }}
                    levelField={{ label: 'Nature of Practice', content: `${dentalState.generalInformation.nature}` }}
                    mnoField={{ label: 'MNO Assigned', content: `${dentalState.generalInformation.mno}` }}
                    addressField={{ label: 'Facility Address', content: `${dentalState.generalInformation.address}` }}
                    inspectorField={{
                      label: 'Name of Inspector *',
                      value: `${dentalState.generalInformation.nameOfInspector}`
                    }}
                    dateField={{
                      label: 'Date of Inspection *',
                      value: `${dentalState.generalInformation.dateOfInspection}`
                    }}
                    errors={{
                      nameOfInspector: dentalState.errors.nameOfInspector,
                      dateOfInspection: dentalState.errors.dateOfInspection
                    }}
                    dispatch={dispatch}
                    onChange={updateGeneralInformation}
                  />
                  <TypeOcularInspection
                    id='dentalTypeOcularInspection'
                    readOnly={dentalState.readOnly}
                    inspectorType={dentalState.inspectorType.type}
                    inspectorName={dentalState.inspectorType.name}
                    dispatch={dispatch}
                    onChange={updateInspectorType}
                    errors={{
                      name: dentalState.errors.name
                    }}
                  />
                </React.Fragment>
              )}
              {renderCurrentPage()}
            </div>
            <Footer
              id='dentalFooter'
              role={role}
              group={group}
              readOnly={dentalState.readOnly}
              currentPage={dentalState.page}
              onClickReturnPage={onClickReturnPage}
              onClickNextPage={onClickNextPage}
              onClickSavePage={onClickSavePage}
              onClickSubmitPage={() => dispatch(openSubmitDialog(true))}
            />
            <div id='dentalFooterPage' className={classes.footerPage}>
              <h3 className={classes.footerText}>
                {dentalState.page === 'services' && 'Dental Clinic Ocular Inspection Checklist 1 out of 2'}
                {dentalState.page === 'generalAppearance' && 'Dental Clinic Ocular Inspection Checklist 2 out of 2'}
              </h3>
            </div>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default DentalPage
