export function mapFetchData(res) {
  const affilType = {
    stageName: '',
    status: '',
    affiliationStatus: '',
    affiliationType: '',
    dateReceived: '',
    mno: '',
    type: '',
    id: ''
  }
  const form = {
    unitFlrBldg: '', // required
    bldgName: '', // required
    street: '', // required
    subdivision: '', // required
    region: '', // required
    district: '', // required
    barangay: '', // required
    city: '', // required
    zipCode: '',
    email: '',
    mainCoordinator: '',
    dentalAssociates: [],
    affiliationInfos: []
  }
  if (res) {
    console.log('dental clinic res', res)
    const {
      contact,
      email,
      contact_person,
      contact_person_no,
      philhealth,
      disaffiliation,
      stage,
      status,
      id,
      type,
      name,
      mno,
      address,
      remarks,
      affiliation_status,
      clinic_schedule,
      room_number,
      nature_of_practice,
      services,
      treasury,
      dental_associates,
      affiliation_infos,
      dentist_id
    } = res
    // affilType.affiliationStatus = res
    console.log('respossse bra', res)
    form.clinicName = name
    form.roomNumber = room_number
    form.clinicSchedule = clinic_schedule
    form.natureOfPractice = nature_of_practice

    form.payee = treasury['payee']
    // address
    form.district = address['district']
    form.region = address['region']
    form.street = address['street']
    form.unitFlrBldg = address['unit_flr_bldg']
    form.bldgName = address['building_name']
    form.city = address['city']
    form.barangay = address['barangay']
    form.subdivision = address['subdivision']
    form.zipCode = address['zip_code']
    form.affiliationInfos = affiliation_infos ? [...affiliation_infos] : []
    form.dentalAssociates = dental_associates ? [...dental_associates] : []
    form.dentistId = dentist_id
    // contact

    form.email = email || ''
    form.contactPerson = contact_person || ''
    form.contactPersonNo = contact_person_no || ''
    form.internalRemarks = remarks['internal_remarks']
    form.externalRemarks = remarks['external_remarks']

    // affiliation type
    affilType.status = status
    affilType.mno = mno
    affilType.type = type
    affilType.id = id
    affilType.affiliationStatus = affiliation_status['name']
    affilType.stageName = stage.stage_name
    affilType.status = status
    affilType.dateSubmitted = stage.date_submitted

  }

  return {
    affilType,
    form,
    dentistId: res.dentist_id ? res.dentist_id : null
  }
}
