
import Axios from 'axios'

import instance from './../instance'
const axiosFc = instance('/p/f')
const axiosCom = instance('/p/c')
const axiosSpecialization = instance('/p/l')

const URL = process.env.REACT_APP_SERVER_API
const endpoint = '/p/f/partners/facilities'

export default class AffiliationEncoderService {

  static createFacilities (data) {
    return axiosFc({
      url: '/partners/facilities',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updateFacilities (id, data) {
    return axiosFc({
      method: 'PUT',
      url: `/partners/facilities/${id}`,
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static getDentalClinicById (id) {
    return Axios.get(`${URL}${endpoint}/id/${id}`)
  }

  static getSingleDentalClinic (id) {
    return axiosFc({
      url: `/partners/facilities/id/${id}`,
      method: 'GET'
    })
  }

  static getDentalClinicList () {
    return axiosFc({
      url: '/partners/facilities/medical/profiles?stage=PROFILE_GENERATED&type=DENTAL_CLINIC',
      method: 'GET'
    })
  }

  static getMedicalClinicById (id) {
    return axiosFc({
      url: `partners/facilities/id/${id}`,
      method: 'GET'
    })
  }

  static getFacilityForDoctor (params) {
    console.log('parampam', params)
    return axiosFc({
      url: `/partners/facilities/auto-suggest/facility-information`,
      method: 'GET',
      params
    })
  }

  static getFacilityName (params) {
    return axiosFc({
      url: '/partners/facilities/auto-suggest/facility-name',
      method: 'GET',
      params
    })
  }
  static getFacilityIds (params) {
    return axiosFc({
      url: '/partners/facilities/auto-suggest/ids',
      method: 'GET',
      params
    })
  }

  static getAllFacilityName () {
    return axiosFc({
      url: '/partners/facilities/all/auto-suggest/name',
      method: 'GET'
    })
  }

  static getFacilityByType (params) {
    return axiosFc({
      url: '/partners/facilities/auto-suggest/searchterm',
      method: 'GET',
      params
    })
  }
  static getAllFacilityIds () {
    return axiosFc({
      url: '/partners/facilities/all/auto-suggest/ids',
      method: 'GET'
    })
  }

  static getAffiliationsForCompletion (params) {
    return axiosCom({
      url: '/partners/common/approver/dashboard',
      method: 'GET',
      params
    })
  }

  static getAffiliationsForCompletionList (params) {
    return axiosCom({
      url: '/partners/common/approver/dashboard/view-list',
      method: 'GET',
      params
    })
  }

  static getSubmittedAffiliations (params) {
    return axiosCom({
      url: '/partners/common/submitted/affiliations',
      method: 'GET',
      params
    })
  }

  static getSubmittedAffiliationsList () {
    return axiosCom({
      url: '/partners/common/submitted/affiliations/view-list',
      method: 'GET'
    })
  }

  static getUploadFile (id) {
    return axiosCom({
      url: `/partners/common/files/${id}`,
      method: 'GET'
    })
  }

  static uploadFile (data) {
    return axiosCom({
      url: '/partners/common/file-upload',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static deleteUploadFile (param) {
    return axiosCom({
      url: `/partners/common/file-upload/delete/${param}`,
      method: 'DELETE',
      headers: { skip_generate_form_data: 1 }
    })
  }

  static getDoctorSpecialization () {
    return axiosSpecialization({
      url: 'partner/lookup/specialization',
      method: 'GET',
    })
  }
}
