import debounce from 'lodash/debounce'
import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import classes from './JoiValidatiionCustomTextField.module.scss'

import ListComponent from '../InfiniteScroll/listComponent'

function CustomAutoComplete (props) {
  const {
    label = '',
    disabled = false,
    required,
    value,
    readOnly,
    constainerStyle = {},
    error,
    onChange,
    suggestions = [],
    onLoadFunc,
    searchTerm = '',
    setSearchTerm,
    onSelect,
    name,
    show = 2,
    ...rest
  } = props
  const inputRef = React.useRef({})
  React.useEffect(() => {
    inputRef.current.value = value
  }, [value])
  function handleChange (value, name) {
    onChange(value, name)
  }

  function handleSelect (value) {
    onSelect(value, name)
    inputRef.current.value = value[props.labelName]
    setSearchTerm('')
    onChange('')
  }
  const debounceChange = debounce((value, name) => handleChange(value, name), 200)

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper style={{backgroundColor: disabled ? '#e2e7ec' : ''}} className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                {...rest}
                style={{color: disabled ? 'white' : '#000000de'}}
                className={classes.input}
                disabled={disabled}
                inputRef={el => (inputRef.current = el)}
                onChange={({ target: { value, name } }) => debounceChange(value, name)}
                autocomplete='false'
              />
              {(searchTerm.length >= show) && (
                <ListComponent items={suggestions} handleSelect={handleSelect} {...rest} />
              )}
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(CustomAutoComplete)
