import React from 'react'
import CheckIcon from '@material-ui/icons/Check'
import CancelIcon from '@material-ui/icons/Cancel'
import classes from './CustomTable.module.scss'
import TextField from '@material-ui/core/TextField'

let errorsData = {
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  username: ''
}

const Form = props => {
  const { x } = props
  const [values, setValues] = React.useState(x)
  const [errors, setErrors] = React.useState(errorsData)

  const change = e => {
    const { name, value } = e.target
    setValues({ ...values, [name]: value })
  }

  const validate = () => {
    let isError = false
    const NewErrors = {
      firstName: '',
      lastName: '',
      username: '',
      email: '',
      password: ''
    }

    setErrors(NewErrors)

    return isError
  }

  const onSubmit = e => {
    e.preventDefault()
    const err = validate()
    if (!err) {
      props.handleSave(props.i, values)
    }
  }

  const { header, i } = props

  return [
    header.map((y, k) => (
      <TextField
        id='outlined-basic'
        name={y.prop}
        onChange={change}
        className={classes.textEdit}
        value={values[y.prop]}
        errorText={errors[y.prop]}
        variant='outlined'
      />
    )),

    <div className={classes.checkIcon}>
      <CheckIcon onClick={onSubmit} style={{ cursor: 'pointer', width: '15px', height: '15px' }} />
      <CancelIcon onClick={props.stopEditing} style={{ cursor: 'pointer', width: '15px', height: '15px' }} />
    </div>
  ]
}

export default Form
