import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import RoomTypeMaintenance from './Pages/RoomTypeMaintenance'

// Store
import Store from '../../../Store'

const RoomTypeMaintenanceRoutes = () => {
  const [isOpenRoomTypeMaintenance, setIsOpenRoomTypeMaintenance] = React.useState(false)

  React.useEffect(() => {
    setIsOpenRoomTypeMaintenance(true)
    return () => {
      setIsOpenRoomTypeMaintenance(false)
    }
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/RoomTypeMaintenance' exact render={(props) => <RoomTypeMaintenance {...props} open={isOpenRoomTypeMaintenance} setOpen={setIsOpenRoomTypeMaintenance} />} />
        {/* {isOpenRoomTypeMaintenance && <RoomTypeMaintenance open={isOpenRoomTypeMaintenance} setOpen={setIsOpenRoomTypeMaintenance} endPoint={'/roomType'} />} */}
      </Switch>
    </Provider>
  )
}

export default RoomTypeMaintenanceRoutes
