import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { otherServices } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'

const SpecialService = () => {
  const [service, setServices] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  React.useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    const { data } = await await ReportServices.getServices('OTHER_SERVICES')
    const dataArr = data.data
    dataArr.forEach((value, index) => {
      dataArr[index].dataCol = {
        status: value.status,
        medical_id: value.id,
        service_name: value.name,
        street: value.address ? value.address.street : null,
        barangay: value.address ? value.address.barangay : null,
        city: value.address ? value.address.city : null,
        district: value.address ? value.address.district : null,
        district: value.address ? value.address.district_prov : null,
        region: value.address ? value.address.region : null,
        contact: value.contact ? value.contact.contact_number : null
      }
    })
    setServices(dataArr)
  }
  return (
    <div>
      <div className={classes.rightBody}>
        <div id='root' className={classes.root}>
          <ReactTable columns={otherServices} data={service} setDownloadData={setDownloadData} />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={otherServices} filename={`${moment().format('YYYYMMDD')}-other-services.csv`} />
      </div>
    </div>
  )
}

export default SpecialService
