import React from 'react'
import ReactTable from '../../Helper/Pagination'
import Button from '@material-ui/core/Button'
import EditableTable from '../../Helper/EditablePagination'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import CustomDialogSave from '../components/CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import AddMultiplierModal from '../components/AddMultiplierModal/AddMultiplierModal'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbsNoStepper from '../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import classes from './FacilityGroupingMaintenance.module.scss'
import { facilityGroupingCol } from '../../../../../../Constant/tableColumnData'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import DisplayFacilitiesAdded from '../components/AddMultiplierModal/DisplayFaciliitesAdded'
import DialogActions from '@material-ui/core/DialogActions'
import Dialog from '@material-ui/core/Dialog'
import { setFacilityGroupingEditable } from 'Pages/Protected/Store/actions/Partner/facilityGroupingEditable'
import { useDispatch, useSelector } from 'react-redux'
import FacilityGroupingService from '../../../../../../Services/Api/FacilityGrouping/FacilityGrouping'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL COMPLIANCE ASSISTANT MANAGER',
    to: '/'
  },
  Maintenance: {
    text: 'MAINTENANCE TABLE',
    to: '/'
  },
  Table: {
    text: 'FACILITY GROUPING MAINTENANCE',
    to: '/FacilityGroupingMaintenance'
  }
}

const FacilityGroupingMaintenance = ({open, setOpen, endPoint}) => {
  const [tableData, setTableData] = React.useState([])
  const [forUpdateData, setForUpdateData] = React.useState([])
  const [facilityType, setFacilityType] = React.useState('')
  const [tableColumn, setTableCol] = React.useState([])
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const [editFamilyNamesDialog, setEditFamilyNamesDialog] = React.useState(false)
  const [form, setForm] = React.useState({
    facility_name: ''
  })

  const [tmpFacilityName, setTmpFacilityName] = React.useState({
    '_id': '',
    'name': ''
  })

  const [facilityNames, setFacilityNames] = React.useState([])
  const [editFacility, setEditFacility] = React.useState([])
  const [editFacilityNamesIndex, setEditFacilityNamesIndex] = React.useState([])
  const [facilityNamesByType, setFacilityNamesByType] = React.useState([])
  const [searchTerm, setSearchTerm] = React.useState('')
  const [isFetching, setIsFetching] = React.useState(false)
  const { facilityGroupingEditable } = useSelector(state => state.facilityGroupingEditable)
  const [ originalData, setOriginalData] = React.useState([])
  let dispatch = useDispatch()

  let headerTitle = 'Facility Grouping Maintenance'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Maintenance', 'Table'])

  React.useEffect(() => {
    fetchData()
    return () => {
      setOpen(false)
    }
  }, [])

  const fetchData = async () => {
    const { data } = await FacilityGroupingService.get()
    setTableData(data.data)
    setOriginalData(data.data)
    setTableCol(facilityGroupingCol)
  }

  const typeofFacility = (type) => {
    switch (type) {
      case 'Medical Clinic':
        setFacilityType('CLINIC')
        break
      case 'Dental Clinic':
        setFacilityType('DENTAL_CLINIC')
        break
      default:
        setFacilityType('HOSPITAL')
        break
    }
  }

  const editFacilityNames = (row, index) => {
    setEditFamilyNamesDialog(true)
    setFacilityNames(row.values.facilityNames)
    setEditFacility(row.values)
    setEditFacilityNamesIndex(index)
    typeofFacility(row.original ? row.original['facility_type'] : '')
  }

  const updateData = async () => {
    for (let data of forUpdateData) {
      await FacilityGroupingService.update(data)
    }
    setIsToUpdate(false)
    setIsSave(false)
    setSuccess(true)
    setTimeout(async () => {
      fetchData()
    }, 1000)
  }

  const addFacility = () => {
    if (tmpFacilityName.name !== '') {
      const newArray = facilityNames.slice() // Create a copy
      newArray.push(tmpFacilityName) // Push the object
      setFacilityNames(newArray)
      form.facility_name = ''
      setTmpFacilityName({
        _id: '',
        name: ''
      })
    }
  }

  const dataheader = [
    { name: 'Facilities Included in the Group', prop: 'name' }
  ]

  const handleRemoveFacility = index => {
    const newArray = [...facilityNames]
    const res = newArray.filter((facilityNames, i) => i !== index)
    setFacilityNames(res)
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    tableData[editFacilityNamesIndex].facilityNames = facilityNames
    editFacility.facilityNames = facilityNames
    setOriginalData(originalData)
    dispatch(setFacilityGroupingEditable(tableData))
    // setFacilityGroupingEditable([])
    setEditFamilyNamesDialog(false)
  }

  const [names, setNames] = React.useState([])
  const [facilityIds, setFacilityIds] = React.useState([])

  const handleFacilityNameChange = async (value, name) => {
    setSearchTerm(value)
    setIsFetching(true)
    if (typeof value === 'string' && value.length >= 1) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityByType({ name: value, type: facilityType })
        setFacilityNamesByType(data)
        setIsFetching(false)
      } catch (error) {
        setIsFetching(false)
        console.log('error occured', error)
      }
    }
  }

  const HandleSelectFacility = ({_id, name}) => {
    setTmpFacilityName({_id, name })
  }

  const handleCancel = () => {
    console.log('original data', originalData)
    console.log('edited data', originalData)
    dispatch(setFacilityGroupingEditable(originalData))
    fetchData()
    setIsCancel(true)
  }

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbsNoStepper
        id='HeaderWithBreadCrumbsNoStpper'
        HeaderTitle={headerTitle}
      />
      <div id='container' className={classes.container}>
        <div id='updateButton'>
          <Button className={classes.buttonAdd} variant='contained'>
            <AddMultiplierModal dataLength={tableData.length} fetchData={fetchData} data={tableData} />
          </Button>
          <Button className={classes.buttonUpdate} onClick={() => setIsToUpdate(true)} variant='contained'>
            Update
          </Button>
        </div>
        { !isToUpdate && <ReactTable columns={tableColumn} data={tableData} label={'Facility Groupings'} />}
        { isToUpdate && <EditableTable columns={tableColumn} data={tableData} label={'Facility Groupings'} editFacilityNames={editFacilityNames} setForUpdateData={setForUpdateData} />}

      </div>
      { isToUpdate &&
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div>
            <Button
              onClick={handleCancel}
              variant='contained'
              color='secondary'
              className={classes.cancelButton}
            >
                Cancel
            </Button>
          </div>
          <div />
          <div>
            <div>
              <Button variant='contained' type='submit' className={classes.SaveBtn} onClick={() => setIsSave(true)}>
                  Save
              </Button>
            </div>
          </div>
        </span>
      </div>
      }
      <div>
        <CustomDialog id='cancelCustomDialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
          <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='cancelCustomDialogMessage'>All changes will not be saved. Are you sure you want to cancel?</h3>
            <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={() => {
                  setIsCancel(false)
                  setIsToUpdate(false)
                }}
                id='cancelCustomDialogBtnYes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
              >
                Yes
              </Button>
              <Button
                onClick={() => setIsCancel(false)}
                id='cancelCustomDialogBtnNo'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                No
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <div>
        <CustomDialogSave
          onClose={() => {
            setOpen(false)
          }}
          id='saveCustomDialog'
          isSave={isSave}
          setIsSave={() => setIsSave(false)}>
          <div id='saveCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='saveCustomDialogMessage'>Are you sure you want to update?</h3>
            <div id='saveCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={() => setIsSave(false)}
                id='saveCustomDialogBtnNo'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={updateData}
                id='saveCustomDialogBtnYes'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialogSave>
        <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
      </div>
      <div>
        <Dialog fullWidth maxWidth={'xs'} open={editFamilyNamesDialog} aria-labelledby='dialogTitle'>
          <DialogTitle id='dialogTitle' style={{ color: '#1e2071', marginTop: '2%', minHeight: '400px' }}>UPDATE FACILITY GROUP</DialogTitle>
          <DialogContent>
            <form className={classes.root} noValidate autoComplete='off' style={{ marginTop: '65px', paddingLeft: '30px' }}>
              <div style={{ 'display': 'inline-flex', paddingRight: '47px' }}>
                <div id='facilityName' className={classes.facilityField}>
                  {/* <CustomAutoComplete suggestions={names} labelName='name' name='facility_name' value={form.facility_name} onChange={HandleChangeFacility} /> */}
                  <AutoSuggest
                    suggestions={facilityNamesByType}
                    error={''}
                    value={tmpFacilityName['name'] ? tmpFacilityName['name'] : ''}
                    labelName='name'
                    name='facilityName'
                    searchTerm={searchTerm}
                    onChange={handleFacilityNameChange}
                    onSelect={HandleSelectFacility}
                    isFetching={isFetching}
                    setSearchTerm={setSearchTerm}
                    show={1} // length of search string to show auto suggestions
                  />
                </div>
                <a style={{ margin: '12px 0 0 10px', cursor: 'pointer', color: '#3ab77d' }} onClick={addFacility}>+Add Facility</a>
              </div>
              <div style={{ paddingRight: '47px' }}>
                <DisplayFacilitiesAdded
                  dataheader={dataheader}
                  dataValue={facilityNames.length > 0 ? facilityNames : []}
                  handleRemove={handleRemoveFacility}
                />
              </div>
            </form>
          </DialogContent>
          <DialogActions className={classes.DialogActions} style={{paddingTop: '73px', paddingLeft: '47px'}}>
            <Button
              onClick={() => {
                setEditFamilyNamesDialog(false)
              }}
              variant='contained'
              className={classes.Cancel}
            >
              Cancel
            </Button>
            <Button variant='contained' disabled={facilityNames.length <= 0} onClick={handleSubmit} className={classes.Add}>
              Save
            </Button>
          </DialogActions>
        </Dialog>
      </div>
    </div>
  )
}

export default FacilityGroupingMaintenance
