import React from 'react'

// shared Components

import DatePicker from 'SharedComponents/DatePicker/DatePicker'

// marterial ui
import TextField from '../../../../../Components/JoiValidationInputField'

// style scss
import classes from './SOSForm.module.scss'
import isEmpty from 'lodash/isEmpty'

function SOSForm ({ formData }) {
  const [specialOthersForm, setSpecialOthersFrom] = React.useState({
    dateRecieved: new Date().toISOString(),
    nameOfService: '',
    address: '',
    subdivision: '',
    barangay: '',
    buildingName: '',
    region: '',
    cityMunicipality: '',
    street: '',
    district: '',
    zipCode: '',
    tinNumber: null,
    contactPerson: '',
    contactNumber: ''
  })

  React.useEffect(() => {
    if (!isEmpty(formData)) {
      let newForm = { ...specialOthersForm }
      for (let key in formData) {
        newForm[key] = formData[key]
      }
      setSpecialOthersFrom(newForm)
    }
  }, [formData])

  return (
    <div className={classes.otherServiceForm}>
      <div className={classes.firstPartInputFields}>
        <div>
          <label>Date Received</label>
          <DatePicker name='dateRecieved' value={specialOthersForm['dateRecieved']} disabled />
        </div>
        <div>
          <label>Name of Services</label>
          <TextField name='nameOfService' value={specialOthersForm['name']} disabled />
        </div>
      </div>

      <div className={classes.SecondPartInputFields}>
        <div>
          <div>
            <label>
              Address <br />
              Unit No./Floor/Bldg.No.
            </label>
            <TextField name='address' value={specialOthersForm['address'] && specialOthersForm['address']['building_name']} disabled />
          </div>
          <div>
            <label>Subdivision</label>
            <TextField name='subdivision' value={specialOthersForm['address'] && specialOthersForm['address']['subdivision']} disabled />
          </div>
          <div>
            <label>Barangay</label>
            <TextField name='subdivision' value={specialOthersForm['address'] && specialOthersForm['address']['barangay']} disabled />
          </div>
        </div>
        <div>
          <div>
            <label>
              <div /> <br /> Building Name
            </label>
            <TextField name='buildingName' value={specialOthersForm['address'] && specialOthersForm['address']['building_name']} disabled />
          </div>
          <div>
            <label>Region</label>
            <TextField name='region' value={specialOthersForm['address'] && specialOthersForm['address']['region']} disabled />
          </div>
          <div>
            <label>City/Municipality</label>
            <TextField name='cityMunicipality' value={specialOthersForm['address'] && specialOthersForm['address']['city']} disabled />
          </div>
        </div>
        <div>
          <div>
            <label>
              <div /> <br /> Street Name
            </label>
            <TextField name='street' value={specialOthersForm['address'] && specialOthersForm['address']['street']} disabled />
          </div>
          <div>
            <label>District / Province</label>
            <TextField name='district' value={specialOthersForm['address'] && specialOthersForm['address']['district_prov']} disabled />
          </div>
          <div>
            <label>Zip Code</label>
            <TextField name='zipCode' value={specialOthersForm['address'] && specialOthersForm['address']['zip_code']} disabled />
          </div>
        </div>
      </div>
      <div className={classes.thirdPartInputFields}>
        <div>
          <label>TIN Number</label>
          <TextField name='tinNumber' value={specialOthersForm['tin_number']} disabled />
        </div>
        <div>
          <label>Contact Person</label>
          <TextField name='contactPerson' value={specialOthersForm['contact'] && specialOthersForm['contact']['contact_person']} disabled />
        </div>
        <div>
          <label>Contact Number</label>
          <TextField name='contactNumber' value={specialOthersForm['contact'] && specialOthersForm['contact']['contact_number']} disabled />
        </div>
      </div>
    </div>
  )
}

export default SOSForm
