import React from 'react'
import classes from '../scss/EditableTable.module.scss'

let specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>]/)
// let forEmail = RegExp(/[!#$%^&*(),?":;'`~{}|<>]([[]]*)/)
// let numAndPercentage = RegExp(/[!@#$^&*(),?":;'`~_{}|<>]/)
let numbersOnly = RegExp(/^[\d\b]+$/)
let lettersOnly = RegExp(/^[a-z]+$/)

const DisableCharacters = (e, type) => {
  switch (type) {
    case 'number':
      if (
        e.key === 'e' ||
        e.key === 'E' ||
        e.keyCode === 189 ||
        e.keyCode === 111 ||
        e.keyCode === 106 ||
        e.keyCode === 107 ||
        e.keyCode === 109
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'integerOnly':
      if (
        e.key === 'e' ||
        e.key === 'E' ||
        e.keyCode === 190 ||
        e.keyCode === 189 ||
        e.keyCode === 111 ||
        e.keyCode === 106 ||
        e.keyCode === 107 ||
        e.keyCode === 109 ||
        e.keyCode === 187 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220 ||
        e.keyCode === 222 ||
        specialChar.test(e.key) ||
        lettersOnly.test(e.key)
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'noSpecialChar':
      if (
        specialChar.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 191 ||
        e.keyCode === 189 ||
        e.keyCode === 219 ||
        e.keyCode === 221 ||
        e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'lettersOnly':
      if (
        specialChar.test(e.key) ||
        numbersOnly.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 189 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    default:
      return {}
  }
}

const InputFieldComponents = props => {
  const { value, onChange, onBlur, name } = props

  const typeResult = () => {
    switch (name) {
      case 'standard':
        return 'integerOnly'

      case 'opd':
        return 'integerOnly'

      default:
        return ''
    }
  }

  return (
    <input
      value={value}
      onChange={onChange}
      onBlur={onBlur}
      type={typeResult()}
      onKeyDown={e => DisableCharacters(e, typeResult())}
      className={classes.inputField}
    />
  )
}

const InputField = React.memo(InputFieldComponents, (prevProps, nextProps) => {
  try {
    if (prevProps.value === nextProps.value && prevProps.name === nextProps.name && prevProps.type === nextProps.type) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})

export default InputField
