import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import classes from './UploadFIleTable.module.scss'

import Previews from '../../../../AffiliationFormEncoder/Pages/PreviewImage/PreviewImage'
import { useDispatch } from 'react-redux'
import Axios from 'axios'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'

const URL = `${process.env.REACT_APP_SERVER_API}`

const TextWithSubscript = ({ mainText, subtext }) => {
  return (
    <div id='locationMapText' className={classes.WithSubScript} style={{maxWidth: '480px'}}>
      <text>{mainText}</text>
      <sub id='subscript-location' className={classes.subscript}>
        {subtext}
      </sub>
    </div>
  )
}

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const UploadFIleTable = props => {
  const { buttonText } = props

  let dispatch = useDispatch()
  React.useEffect(() => {
    fetchData()
  }, [])
  const forView = false
  const [mainFiles, setMainFiles] = useState([])
  const fetchData = async () => {
    try {
      const { data } = await Axios.get(URL + `/p/f/partners/facilities/id/${props.paramId}`)
      const paramAffilId = data.data.afn_number
      dispatch(setAffiliationType({id: paramAffilId}))
      const filesData = await Axios.get(`${URL}/p/c/partners/common/files/${paramAffilId}`)
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles((previousState) => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file => Object.assign(file, {
            preview: `${URL}/uploads/${file.path}`,
            fileType: fileData.file_type
          }))
        }))
      }
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData(<Previews desc={'Hospital Profile'} fileType={'hospital-profile'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'Updated List of Services Offered with Rates and Room Rates'} fileType={'service-offered'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'List of Doctors to be Accredited'} fileType={'accredited-doctors'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'Medical Directory'} fileType={'medical-directory'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={<TextWithSubscript
      mainText={'Location Map'}
      subtext={
        '(Preferably Screenshot from Google Maps)'
      }
    />} fileType={'location-map'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />
    ),
    createData(<Previews desc={<TextWithSubscript
      mainText={'Actual Image of Areas in the Facility'}
      subtext={
        '*All facility department (Ancilliary Dept, Lab, Radiology, ER, Rehab, Others), All room types, Front Area/Front Image of the facility.'
      }
    />} fileType={'areas-in-facility'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),

    createData(
      <Previews desc={'Check Payable Letter'} fileType={'check-payable-letter'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />
    ),
    createData(
      <Previews desc={<TextWithSubscript mainText={'APE Packages'} subtext={'*Basic 5, ECG, PapSmear and Drug Test'} />} fileType={'APE-packages'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />
    ),
    createData(<Previews desc={'ECU Packages'} fileType={'ECU-pages'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'Business Permit'} fileType={'business-permit'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'BIR COR 2303'} fileType={'bir-cor-2303'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'DOH PERMIT '} fileType={'doh-permit'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />),
    createData(<Previews desc={'SEC/DTI Registration'} fileType={'sec-dti-registration'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'PhilHealth Accreditation'} fileType={'philhealh-accreditation'} mainFiles={mainFiles} setMainFiles={setMainFiles} forView={forView} buttonText={buttonText} />)
  ]
  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <Table className={classes.table} size='small' aria-label='a dense table'>
        <TableBody id='table-body'>
          {rows.map((row, index) => (
            <TableRow id='TableRow' className={classes['table-row']} key={index}>
              <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                {row.description}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default UploadFIleTable
