import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import classes from './tableData.module.scss'

const row = (contact, i, header) => {
  return (
    <StyledTableRow key={`tr-${contact[i]}`} className={classes.inlineNormal}>
      {header.map((y, k) => (
        <StyledTableCell className={classes.inlineTableCell} key={`trc-${k}`} align='left' component='th' scope='row'>
          {contact[y.name]}
        </StyledTableCell>
      ))}
    </StyledTableRow>
  )
}

export default ({ data = [], header = [] }) => (
  <TableContainer className={classes.tableContainer}>
    <Table className={classes.tableRoot} aria-label='customized table'>
      <TableHead className={classes.tableHeader}>
        <TableRow className={classes.tableRow}>
          {header.map((header, i) => (
            <TableCell className={classes.tableCell} key={`thc-${header.name}`} align='left'>
              {header.label}
            </TableCell>
          ))}
          <TableCell align='left' />
        </TableRow>
      </TableHead>
      <TableBody className={classes.tableBody}>
        {data ? data.map((contact, i) => row(contact, i, header)) : null}
      </TableBody>
    </Table>
  </TableContainer>
)

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.lightgrey,
    color: theme.palette.common.lighgrey
  },
  body: {
    fontSize: 12
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default
    },
    '&:nth-of-type(even)': {
      backgroundColor: '#f5f7fb'
    }
  }
}))(TableRow)
