import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import FacilityGroupingMaintenance from './Pages/FacilityGroupingMaintenance'

// Store
// import Store from '../../Store'
import Store from '../../../Store'

const FacilityGroupingMaintenanceRoutes = () => {
  const [isOpenFacilityGroupingMaintenance, setIsOpenFacilityGroupingMaintenance] = React.useState(false)

  React.useEffect(() => {
    setIsOpenFacilityGroupingMaintenance(true)
    return () => {
      setIsOpenFacilityGroupingMaintenance(false)
    }
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/FacilityGroupingMaintenance' exact render={(props) => <FacilityGroupingMaintenance {...props} open={isOpenFacilityGroupingMaintenance} setOpen={setIsOpenFacilityGroupingMaintenance} />} />
      </Switch>
    </Provider>
  )
}

export default FacilityGroupingMaintenanceRoutes
