import React from 'react'

const DisplaySelectedProcedures = props => {
  const { data, setOpen, displayProcedures } = props

  const selectedProcedures = (data instanceof Array) ? Array.prototype.map.call(data, function (item) { return item.procedure }).join(',') : null
  return (
    <div>
      <span style={{cursor: 'pointer', userSelect: 'none'}} onDoubleClick={e => {
        displayProcedures(data)
        setOpen(true)
      }}>{selectedProcedures}</span>
    </div>
  )
}

export default React.memo(DisplaySelectedProcedures)
