import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
// import TextField from '../../../../../SharedComponents/TextField/CustomTextField'

import classes from './DialogBoxConfirmation.module.scss'
import { useSelector } from 'react-redux'
import Axios from 'axios'
const URL = process.env.REACT_APP_SERVER_API
let color

const DialogBoxConfirmation = props => {
  const { hospitalForms } = useSelector(state => state.AffiliationHospitalForm)
  const { affiliationType } = useSelector(state => state.affiliationType)
  const { dialogText, setDisable, disable, setIsApproved } = props
  const [open, setOpen] = React.useState(false)

  React.useEffect(() => {
    if (affiliationType.affiliationStatus === 'Submitted') {
      setDisable(true)
      setIsApproved(true)
    }
  }, [])

  const handleProceed = async () => {
    try {
      affiliationType.stageName = 'AFFILIATION_APPROVAL_FOR_OIC' // found this bug it causes duplicate to be passed it just have to override
      affiliationType.affiliationStatus = 'Submitted'
      affiliationType.dateSubmitted = new Date().toISOString()
      console.log('dialog affiliation type', affiliationType)
      console.log('hospitalForms', hospitalForms)
      let res = {}
      if (typeof affiliationType.id === 'undefined') {
        res = await Axios.post(`${URL}/p/f/partners/facilities`, { ...affiliationType, ...hospitalForms })
        affiliationType.id = res.data.data.id
      } else {
        res = await Axios.put(`${URL}/p/f/partners/facilities/${affiliationType.id}`, {
          ...affiliationType,
          ...hospitalForms
        })
      }
      setDisable(true)
      setIsApproved(true)
      setOpen(false)
    } catch (error) {
      console.log('error in save', error)
    }
  }

  if (disable) {
    color = 'lightgray'
  } else {
    color = '#3ab77d'
  }

  return (
    <React.Fragment>
      <Button
        style={{ backgroundColor: color, color: 'white', paddingLeft: '1.5em', paddingRight: '1.5em', textTransform: 'capitalize' }}
        className={classes.submitButton}
        variant='contained'
        onClick={() => {
          setOpen(true)
        }}
        disabled={disable}
      >
        Submit
      </Button>
      <Dialog fullWidth maxWidth={'sm'} open={open} aria-labelledby='max-width-dialog-title'>
        <DialogContent>
          <div className={classes.root} noValidate autoComplete='off'>
            Are you sure you want to proceed with the submission of <br />
            {dialogText} form <br />
            for approval?
          </div>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <div id='button-div'>
            <Button
              onClick={() => {
                setOpen(false)
              }}
              variant='contained'
              className={classes.Cancel}
            >
              Cancel
            </Button>
            <Button variant='contained' onClick={handleProceed} className={classes.Proceed}>
              Proceed
            </Button>
          </div>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  )
}

export default DialogBoxConfirmation
