import React from 'react'

import Button from '@material-ui/core/Button'

// Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import AffiliationForm from './Components/AffiliationForm/AffiliationForm'
import History from 'Pages/Protected/Partner/Components/History/History'
// Constant
import cntsPartnerType from 'Pages/Protected/Partner/Constants/partnerTypes'
// Styles
import classes from './HospitalAffiliation.module.scss'

import HospitalAffiliationContext from './context/HospitalAffiliationContext'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Hospital: {
    text: 'HOSPITAL AFFILIATION',
    to: '/'
  }
}

const HospitalAffiliation = props => {
  let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Hospital Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  const { paramId, isApproved } = React.useContext(HospitalAffiliationContext)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Hospital'])

  const disapproved = async () => {
    try {
      const res = await AffiliationApproverServices.disapproved(paramId, cntsPartnerType.PARTNER.TYPE.HOSPITAL)
      props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  return (
    <React.Fragment>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={1}
        />
        <div id='AffiliationForm-div'>
          <AffiliationForm id='AffiliationForm' />
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                variant='contained'
                color='secondary'
                className={classes.disapproveButton}
                // onClick={() => History.push('/')}
                onClick={disapproved}
                disabled={isApproved} style={{backgroundColor: isApproved ? '#dddfe3' : ''}}
              >
                Disapproved
              </Button>
            </div>
            <div />
            <div>
              <div />
              <div>
                <Button
                  variant='contained'
                  className={classes.nextButton}
                  onClick={() => props.setPage(true)}
                >
                  Next
                </Button>
              </div>
            </div>
          </span>
          <div
            style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}
          >
            Facility Affiliation Form 1 out of 2
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default HospitalAffiliation
