import React from 'react'

import ReactTable from '../../../Helpers/PaginationForPages'
import EditableTable from '../../../Helpers/EditPaginationForPages'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './StandardRates.module.scss'

import { GeneralController } from '../../../../../../../Controllers/GeneralController'
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'

import CustomDialog from '../../../../Components/CustomDialog/CustomDialog'
import CustomDialogSave from '../../../../Components/CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import CustomDeleteSnackbar from '../../../../Components/CustomSnackBar/CustomAlertDelSnackBar'
import CustomConfirmationDialog from '../../../../Components/CustomConfirmationDialog/CustomConfirmationDialog'
import ProfileServices from '../../../../../../../Services/Api/Partner/ProfileService'
import Columns from './Utilities/Columns'
import UpdateColumns from './Utilities/UpdateColumns'
import { Link } from 'react-router-dom'
import RoomTypeService from '../../../../../../../Services/Api/RoomType/RoomType'

const StandardRates = props => {
  const { paramId, open, setOpen, pathName } = props

  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [delSuccess, setDelSuccess] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const [isDelete, setDelete] = React.useState(false)
  const [rows, setRows] = React.useState([])
  const [rowsOriginal, setRowsOriginal] = React.useState([])
  const [indexTodelete, setIndexToDelete] = React.useState(null)
  const [passIndex, setPassIndex] = React.useState(-1)
  // const [toDelete, setToDelete] = React.useState(false)
  const [openDialog, setOpenDialog] = React.useState(false)
  const [forUpdateData, setForUpdateData] = React.useState([])
  const [cptCodesIndexDbList, setCptCodesIndexDbList] = React.useState([])
  const [roomDesc, setRoomDesc] = React.useState([])

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
      cptCodesList()
    }

    return () => (mounted = false)
  }, [paramId])

  const fetchData = async () => {
    try {
      const { data: newData } = await ProfileServices.getStandardRates(paramId)

      const {
        data: { data: roomDesc }
      } = await RoomTypeService.get()

      setRoomDesc(roomDesc.map(items => (items ? (items.desc ? items.desc : '') : '')))

      const standardRate = newData
        ? newData.data
          ? newData.data.standard_rate
            ? newData.data.standard_rate
            : []
          : []
        : []

      setRows(standardRate)
      setForUpdateData(standardRate)
      setRowsOriginal(standardRate)
    } catch (e) {
      console.log('error', e)
    }
  }

  const cptCodesList = () => {
    IcdCptStorageServices.readStorageEntries(IcdCptStorageServices.OBJECT_STORE_NAME_CPT, function (entries) {
      // Check if there are no CPT entries
      if (!entries || entries.length === 0) {
        // Trigger fetching and saving of ICD and CPT entries
        GeneralController.fetchAndSaveIcdAndCptEntries()
        return
      }

      setCptCodesIndexDbList(entries)
    })
  }

  const HandleDelete = () => {
    setPassIndex(indexTodelete)
    setDelete(false)
    setTimeout(() => {
      setDelSuccess(true)
    }, 100)
  }

  const columns = Columns()
  const updatecolumns = UpdateColumns(setIndexToDelete, setDelete)

  const HandleDialog = () => {
    console.log('handle diaolog called')
    if (!validateDuplicateCpt(getCptCodes())) {
      setSuccess(false)
      setOpenDialog(true)
    } else {
      alert('Duplicate CPT code and duplicate room type')
    }
  }

  const getCptCodes = () => {
    // let arr = []
    // for (let i of forUpdateData) {
    //   arr.push(...[i.cpt, i.roomType])
    // }
    return forUpdateData.map(d => d.cpt + d.roomType)
  }

  const validateDuplicateCpt = arr => {
    return new Set(arr).size !== arr.length
  }
  const HandleSave = async () => {
    let standardRate = [...forUpdateData]

    let type = 'HOSPITAL'
    let id = paramId
    try {
      const { data: newData } = await ProfileServices.updateStandardRates(paramId, { id, type, standardRate })
      const standardRateResult = newData
        ? newData.data
          ? newData.data.standard_rate
            ? newData.data.standard_rate
            : []
          : []
        : []

      setOpenDialog(false)
      setRows(standardRateResult)
      setRowsOriginal(standardRateResult)
      setForUpdateData(standardRateResult)
      setOpenDialog(false)
      setSuccess(true)
      setTimeout(() => {
        setIsSave(false)
        setIsToUpdate(false)
      }, 800)
    } catch (e) {
      console.log('error', e)
    }
  }

  const HandleCancel = () => {
    setOpen(true)
    setIsCancel(false)
    setIsToUpdate(false)
    setRows(rowsOriginal)
  }

  const data = React.useMemo(() => rows, [rows])
  const cptCodeData = React.useMemo(() => cptCodesIndexDbList, [cptCodesIndexDbList])
  const roomDescData = React.useMemo(() => roomDesc, [roomDesc])

  return (
    <React.Fragment>
      <Modal open={open} aria-labelledby='simple-modal-title' aria-describedby='simple-modal-description'>
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div id='Update-button'>
              <Button className={classes.button} onClick={() => setIsToUpdate(true)} variant='contained'>
                Update
              </Button>
            </div>
            <div id='tables' className={classes.tables}>
              {!isToUpdate && (
                <ReactTable columns={columns} data={data} type={'hospitalStandardRates'} countName={'Procedures'} />
              )}
              {isToUpdate && (
                <EditableTable
                  columns={updatecolumns}
                  data={data}
                  setRows={setRows}
                  indexTodelete={passIndex}
                  setPassIndex={setPassIndex}
                  setForUpdateData={setForUpdateData}
                  cptCodeData={cptCodeData}
                  roomDescData={roomDescData}
                  type={'hospitalStandardRates'}
                  countName={'Procedures'}
                  hide
                  style={{ fontSize: '0.7rem', width: '90%' }}
                />
              )}
            </div>

            <div id='buttonDiv' className={classes.buttonDiv}>
              {!isToUpdate && (
                <div id='Close-button'>
                  <Button
                    className={classes.button}
                    variant='contained'
                    component={Link}
                    to={pathName}
                    onClick={() => setOpen(false)}
                  >
                    Close
                  </Button>
                </div>
              )}
              {isToUpdate && (
                <div id='cancel-save-button'>
                  <Button
                    id='cancel-btn'
                    className={classes.btnCancel}
                    variant='contained'
                    onClick={() => setIsCancel(true)}
                  >
                    Cancel
                  </Button>
                  <Button id='save-btn' className={classes.btnSave} variant='contained' onClick={HandleDialog}>
                    Save
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      </Modal>
      <div>
        {isDelete && (
          <CustomConfirmationDialog
            open={isDelete}
            onApprove={HandleDelete}
            close={() => setDelete(false)}
            message='Are you sure you want to delete Procedure?'
          />
        )}
      </div>
      <div>
        <CustomDialog id='sos_custom_dialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
          <div id='sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='sos_custom_dialog_message'>All changes will not be saved. Are you sure you want to cancel?</h3>
            <div id='sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => setIsCancel(false)}
                id='sos_custom_dialog_btn_no'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={HandleCancel}
                id='sos_custom_dialog_btn_yes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <div>
        <CustomDialogSave
          onClose={() => {
            setOpen(false)
          }}
          id='a_sos_custom_dialog'
          isSave={openDialog}
          // setIsSave={() => setIsSave(false)}
        >
          <div id='a_sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='a_sos_custom_dialog_message'>Are you sure you want to update?</h3>
            <div id='a_sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => setOpenDialog(false)}
                id='a_sos_custom_dialog_btn_no'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={HandleSave}
                id='a_sos_custom_dialog_btn_yes'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialogSave>
      </div>
      <CustomAlerSnackBar setDelay={800} isSave={success} setIsSave={() => setSuccess(false)} />
      {delSuccess && <CustomDeleteSnackbar isSave={delSuccess} setIsSave={() => setDelSuccess(false)} />}
    </React.Fragment>
  )
}

export default StandardRates
