import React from 'react'

import classes from './ConditionComponent.module.scss'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import Button from '@material-ui/core/Button'
import { MultiTextComponent } from '../RowInputComponents/RowInputComponents'

const ConditionComponent = props => {
  const { isOpen, setIsOpen, handleOnSubmit = () => { }, message = '', value = '', onChange = () => { } } = props
  return (
    <CustomDialog id='ga_custom_dialog' isCancel={isOpen} setIsCancel={() => setIsOpen(false)}>
      <div id='ga_custom_dialog_container' className={classes.dialogContainer}>
        <React.Fragment>
          <h3 style={{ textAlign: 'left' }}>Condition/Remarks</h3>
          <MultiTextComponent
            id='conditionalFormApprover'
            rowsMax={'15'}
            value={value}
            onChange={e => onChange(e.target.value)}
            placeHolder='Type the condition/remarks for the facility to be approved.'
          />
          <h3 id='ga_custom_dialog_message' style={{ textAlign: 'center' }}>
            {`Are you sure  you want to proceed with the submission of ${message} Affiliation form for re-evaluation?`}
          </h3>
          <div id='ga_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              style={{ textTransform: 'none' }}
              onClick={() => setIsOpen(false)}
              id='ga_custom_dialog_btn_cancel'
              className={classes.btnRed}
              variant='contained'
            >
              Cancel
            </Button>
            <Button
              style={{ textTransform: 'none', background: '#1AB1C4', color: 'white' }}
              id='ga_custom_dialog_btn_save'
              variant='contained'
              onClick={handleOnSubmit}
            >
              Submit
            </Button>
          </div>
        </React.Fragment>
      </div>
    </CustomDialog>
  )
}

export default ConditionComponent
