import React from 'react'
import DialogContent from '@material-ui/core/DialogContent'
import classes from '../scss/TableDisplay.module.scss'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import { CSVLink } from 'react-csv'

const PreviewDownload = (props) => {
  const { openPreview, setOpenPreview, downloadData, headerColumn, filename } = props
  const [csvDownload, setCsvDownload]                                         = React.useState([])

  React.useEffect(() => {
    csvFormat()
  }, [downloadData])

  const csvFormat = () => {
    if (downloadData) {
      let tmpData = []
      tmpData.push(headerColumn.map(column => {
        return column.Header
      }))
      const dataData = downloadData.map(row => {
        return Object.values(row.values).map(value => { return value })
      })
      tmpData.push(...dataData)
      setCsvDownload(tmpData)
    }
  }

  const tableTdBold        = (data) => {
    return <td><b>{data}</b></td>
  }
  const tableTd        = (data) => {
    return <td>{data}</td>
  }
  const tableRowHeader = (columns) => {
    return <tr>
      {
        columns.map(column => {
          return tableTdBold(column.Header)
        })
      }
    </tr>
  }
  const tableRowData   = (columns) => {
    return Object.values(columns.values).map(value => {
      return tableTd(value)
    })
  }
  const tableBody      = (tableData) => {
    return tableData.map(row => {
      return <tr>{tableRowData(row)}</tr>
    })
  }
  return (
    <Dialog fullWidth maxWidth={'xl'} open={openPreview} onClose={() => { setOpenPreview(false) }} aria-labelledby='dialogTitle'>
      <DialogContent>
        <table className={classes.previewTable}>
          <thead>
            {tableRowHeader(headerColumn)}
          </thead>
          <tbody>
            {tableBody(downloadData)}
          </tbody>
        </table>
      </DialogContent>
      <DialogActions className={classes.DialogActions}>
        <Button
          onClick={() => {
            setOpenPreview(false)
          }}
          variant='contained'
          className={classes.Cancel}
        >
          Close
        </Button>
        <CSVLink data={csvDownload} filename={filename} onClick={() => setOpenPreview(false)} style={{textDecoration: 'none'}}>
          <Button variant='contained' className={classes.Add}>
            Download
          </Button>
        </CSVLink>
      </DialogActions>
    </Dialog>
  )
}

export default PreviewDownload
