import React from 'react'
import ReactTable from '../../Helpers/PaginationForPages'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './ListOfAffiliatedDoctors.module.scss'
import EditableTable from './ReactTable/EditTable/EditableTable'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import CustomDialogSave from '../../SpecialOtherServicesProfile/components/CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import ProfileService from '../../../../../../Services/Api/Partner/ProfileService'
import { profileListOfAffiliatedDoctorColumns } from '../../../../../../Constant/tableColumnData.json'

const ListOfAffiliatedDoctors = ({ open, setOpen, endPoint, paramId }) => {
  const [tableData, setTableData] = React.useState([])
  const [originalData, setOriginalData] = React.useState([])

  const [tableColumn, setTableCol] = React.useState([])
  const [listOfFacility, setListOfFacility] = React.useState({})
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [openDialog, setOpenDialog] = React.useState(false)
  const [success, setSuccess] = React.useState(false)

  React.useEffect(() => {
    fetchData()
    return () => {
      setOpen(false)
    }
  }, [])

  const fetchData = async () => {
    const tableColumn = []
    let listOfAffiliatedDoctors = []
    const { data: appData } = await ProfileService.getAffiliatedDoctors(paramId, 'HOSPITAL')
    const { data: newData } = appData

    const listOffAffiliatedDoctorsData = newData || []

    for (let key in listOffAffiliatedDoctorsData) {
      listOfAffiliatedDoctors.push(listOffAffiliatedDoctorsData[key])
    }

    for (let i of listOfAffiliatedDoctors) {
      const obj = {}
      obj.status = i.status
      obj.id = i.id

      console.log('i.id', i.id)

      const { data: appData } = await ProfileService.getAffiliatedFacility(i.id, 'DOCTOR')
      const { data: newData } = appData

      const newDataValue = newData || []

      setListOfFacility(newDataValue)
      obj.full_name = i.full_name
      obj.specialization = i.specialization
      obj.schedule = i.schedule
      obj.full_address = i.full_address ? i.full_address : ''
      obj.room_number = i.room_number ? i.room_number : ''
      obj.contact_numbers = i.contact_numbers ? i.contact_numbers : ''
      obj.remarks = i.remarks
      obj.expiration_date = i.expiration_date ? i.expiration_date : ''
      obj.date_submitted = i.date_submitted ? i.date_submitted : ''
      tableColumn.push(obj)
    }

    setTableData(tableColumn)
    setOriginalData(tableColumn)
    setTableCol(profileListOfAffiliatedDoctorColumns)
  }

  const HandleSave = async () => {
    let obj = {}
    obj.id = paramId
    obj.affiliatedDoctors = [...tableData]

    const newTableData = { ...tableData[0] }

    let affliatedObj = {}
    affliatedObj.id = newTableData.id
    affliatedObj.affiliatedFacility = [
      {
        facility_id: listOfFacility.facility_id,
        clinic_schedule: newTableData.schedule,
        expiration_date: listOfFacility.expiration_date ? listOfFacility.expiration_date : '',
        date_submitted: listOfFacility.date_submitted ? listOfFacility.date_submitted : '',
        location: listOfFacility.location ? listOfFacility.location : '',
        name: listOfFacility.name ? listOfFacility.name : '',
        remarks: newTableData.remarks ? newTableData.remarks : '',
        status: newTableData.status
      }
    ]

    try {
      const { data: newData } = await ProfileService.updateListOfAffiliatedDoctors(obj)
      const datas = newData ? (newData.data ? newData.data : []) : []

      setOpenDialog(false)
      setSuccess(true)
      setTimeout(() => {
        setIsToUpdate(false)
        setTableData(datas)
        setOriginalData(datas)
      }, 800)
    } catch (e) {
      console.log('error', e)
    }
  }

  const HandleOpenDialog = () => {
    setOpenDialog(true)
    setSuccess(false)
  }

  const HandleCancel = () => {
    setOpen(true)
    setTableData(originalData)
    setIsToUpdate(false)
    setIsCancel(false)
  }

  return (
    <React.Fragment>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false)
        }}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div id='Update-button'>
              <Button className={classes.button} onClick={() => setIsToUpdate(true)} variant='contained'>
                Update
              </Button>
            </div>
            <div id='tables' className={classes.tables}>
              {!isToUpdate && <ReactTable columns={tableColumn} data={tableData} countName={'Affiliated Doctors'} />}
              {isToUpdate && (
                <EditableTable
                  columns={tableColumn}
                  data={tableData}
                  setTableData={setTableData}
                  countName={'Affiliated Doctors'}
                />
              )}
            </div>
            <div id='buttonDiv' className={classes.buttonDiv}>
              {!isToUpdate && (
                <div id='Close-button'>
                  <Button
                    className={classes.button}
                    variant='contained'
                    onClick={() => {
                      setOpen(false)
                    }}
                  >
                    Close
                  </Button>
                </div>
              )}
              {isToUpdate && (
                <div id='cancel-save-button'>
                  <Button
                    id='cancel-btn'
                    className={classes.btnCancel}
                    variant='contained'
                    onClick={() => setIsCancel(true)}
                  >
                    Cancel
                  </Button>
                  <Button id='save-btn' className={classes.btnSave} variant='contained' onClick={HandleOpenDialog}>
                    Save
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      </Modal>
      <div>
        <CustomDialog id='sos_custom_dialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
          <div id='sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='sos_custom_dialog_message'>All changes will not be saved. Are you sure you want to cancel?</h3>
            <div id='sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => {
                  //      setOpen(false)
                  setIsCancel(false)
                }}
                id='sos_custom_dialog_btn_no'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
              >
                No
              </Button>

              <Button
                onClick={HandleCancel}
                id='sos_custom_dialog_btn_yes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <div>
        <CustomDialogSave
          onClose={() => {
            setOpen(false)
          }}
          id='a_sos_custom_dialog'
          isSave={openDialog}
          setIsSave={() => setIsSave(false)}
        >
          <div id='a_sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='a_sos_custom_dialog_message'>Are you sure you want to update?</h3>
            <div id='a_sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => setOpenDialog(false)}
                id='a_sos_custom_dialog_btn_no'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={HandleSave}
                id='a_sos_custom_dialog_btn_yes'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialogSave>
      </div>
      <CustomAlerSnackBar isSave={success} setDelay={800} setIsSave={() => setSuccess(false)} />
    </React.Fragment>
  )
}

export default ListOfAffiliatedDoctors
