import React from 'react'
import HospitalPageOne from './HospitalAffiliation_p1'
import HospitalPageTwo from './HospitalAffiliation_p2'
import HospitalAffiliationContext from './context/HospitalAffiliationContext'

const HospitalAffiliation = ({match, history}) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  const [form, setForm] = React.useState({})
  return (
    <React.Fragment>
      <HospitalAffiliationContext.Provider value={{paramId: match.params.id, isApproved, setIsApproved, form, setForm}}>
        {!isNextPage && <HospitalPageOne setPage={setIsNextPage} history={history} /> }
        {isNextPage && <HospitalPageTwo setPage={setIsNextPage} history={history} /> }
      </HospitalAffiliationContext.Provider>
    </React.Fragment>
  )
}
export default HospitalAffiliation
