import React from 'react'
import Table from './tableData/tableData'
import orderBy from 'lodash/orderBy'
import classes from './Affiliation.module.scss'

const AffiliationTable = props => {
  const { dataheader, dataValue, handleRemove, startEditing, stopEditing, handleSave, editIdx, isSubmitted, hasError = false } = props

  const [data, setData] = React.useState(dataValue)
  const [isSubmiteStatus, setIsSubmitted] = React.useState(false)
  React.useEffect(() => {
    setData(data)
  }, [data])

  React.useEffect(() => {
    if (isSubmitted) {
      setIsSubmitted(isSubmitted)
    }
  }, [isSubmitted])

  return (
    <div style={{border: hasError ? 'solid red 1px' : ''}} className={classes.tableContainer}>
      <Table
        handleRemove={handleRemove}
        startEditing={startEditing}
        editIdx={editIdx}
        stopEditing={stopEditing}
        handleSave={handleSave}
        data={orderBy(dataValue)}
        header={dataheader}
        isSubmitted={isSubmiteStatus}
      />
    </div>
  )
}

export default React.memo(AffiliationTable)
