import matchSorter from 'match-sorter'
import classes from './scss/CustomTable.module.scss'
import React from 'react'
import { useFilters, useGlobalFilter, usePagination, useSortBy, useTable } from 'react-table'
import Table from '@material-ui/core/Table'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import TableBody from '@material-ui/core/TableBody'
import TableHead from '@material-ui/core/TableHead'
import TextField from '../../../../../SharedComponents/TextField/CustomTextField'
import HistoryBrowser from '../../Components/History/History'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'
import Pagination from '@material-ui/lab/Pagination'

const DefaultColumnFilter = ({ column: { filterValue, setFilter } }) => {
  return (
    <div id='textField' className={classes.textField}>
      <TextField
        value={filterValue || ''}
        onChange={e => {
          setFilter(e.target.value || undefined) // Set undefined to remove the filter entirely
        }}
      />
    </div>
  )
}

const fuzzyTextFilterFn = (rows, id, filterValue) => {
  return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
}

// Let the table remove the filter if the string is empty
fuzzyTextFilterFn.autoRemove = val => !val

// Our table component

const ReactTable = props => {
  // const [open, setOpen] = React.useState(false)
  React.useEffect(() => {}, [props.data])
  const filterTypes = React.useMemo(
    () => ({
      // Add a new fuzzyTextFilterFn filter type.
      fuzzyText: fuzzyTextFilterFn,
      // Or, override the default text filter to use
      // "startWith"
      text: (rows, id, filterValue) => {
        return rows.filter(row => {
          const rowValue = row.values[id]
          return rowValue !== undefined
            ? String(rowValue)
                .toLowerCase()
                .startsWith(String(filterValue).toLowerCase())
            : true
        })
      }
    }),
    []
  )

  const [currentPage,setCurrentPage] = React.useState(0)

  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Filter: DefaultColumnFilter
    }),
    []
  )

  const { columns, data } = props
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    rows,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize }
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      filterTypes,
      disableSortRemove: true,
      initialState: { pageIndex: 0, pageSize: 10 }
      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  )

  // console.log('data', data)

  // const LimitRows = rows.slice(0, 10)

  const doubleClickRow = (hospitalID, to) => {
    HistoryBrowser.push(`/HospitalProfile/PartnerInformation/${hospitalID}#/`)
  }

  const HandleClick = e => {
    props.CloseModal(false)
  }


  const HandlePagination = (e,value) => {
    const page = value ? Number(value) - 1 : 0
    setCurrentPage(page + 1)
    gotoPage(page)
  }





  const TableRowDisplay = ({i, row, redirect}) => {
    if (redirect != null) {
      return <TableRow
        onDoubleClick={HandleClick}
        key={i}
        {...row.getRowProps()}
        component={Link}
        to={redirect}
        className={classes.tableRow}
      >
        {row.cells.map(cell => {
          return (
            <TableCell
              component='th'
              scope='row'
              align='left'
              className={classes.tableCell}
              {...cell.getCellProps()}
            >
              {(cell.value instanceof Array) ? Array.prototype.map.call(cell.value, function (item) { return item.name }).join(',') : cell.render('Cell')}
            </TableCell>
          )
        })}
      </TableRow>
    } else {
      return <TableRow
        key={i}
        {...row.getRowProps()}
        className={classes.tableRow}
      >
        {row.cells.map(cell => {
          return (
            <TableCell
              component='th'
              scope='row'
              align='left'
              className={classes.tableCell}
              {...cell.getCellProps()}
            >
              {(cell.value instanceof Array) ? Array.prototype.map.call(cell.value, function (item) { return item.name }).join(',') : cell.render('Cell')}
            </TableCell>
          )
        })}
      </TableRow>
    }
  }

  return (
    // <Router>
    <>
      <div id='table'>
        <Table {...getTableProps()} className={classes.rootTable}>
          <TableHead className={classes.tableHead}>
            {headerGroups.map(headerGroup => (
              <TableRow {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
                {headerGroup.headers.map(column => (
                  <React.Fragment>
                    <TableCell
                      component='th'
                      scope='row'
                      align='left'
                      className={classes.tableCell}
                    >
                      {column.canFilter ? (
                        <div id='withTextfield' className={classes.withTextfield}>
                          {column.render('Header')}
                          <span id='showIcon' className={classes.showIcon} {...column.getHeaderProps(column.getSortByToggleProps())}>
                            {column.isSortedDesc ? (
                              <div id='sortIcon' className={classes.sortIcon}>
                                <FontAwesomeIcon icon='sort' />
                              </div>
                            ) : (
                              <div id='sortIcon' className={classes.sortIcon}>
                                <FontAwesomeIcon icon='sort' />
                              </div>
                            )}
                          </span>
                          <div> {column.render('Filter')} </div>
                        </div>
                      ) : (
                        <div id='noTextField' className={classes.noTextField}>
                          {column.render('Header')}
                          {/* <span>{column.isSorted ? (column.isSortedDesc ? ' ' : ' ') : ''}</span> */}
                        </div>
                      )}
                    </TableCell>
                  </React.Fragment>
                ))}
              </TableRow>
            ))}
          </TableHead>
          <TableBody {...getTableBodyProps()}>
            {page.map((row, i) => {
              prepareRow(row)
              let redirect = null
              if (props.link) {
                redirect = `${props.link}/${row['values']['medicalPartnerId']}`
              }
              return (
                <TableRowDisplay redirect={redirect} i={i} row={row} />
              )
            })}
          </TableBody>
        </Table>
      </div>
      <div className={classes.Pagination} style={{display:'flex',flexDirection:'row',alignItems:'center',marginBottom:'5em'}}>

      <div style={{flex:'2',marginLeft:'2em'}}>
        <span>
          <strong style={{marginRight:'0.5em'}}>  
            {page.length} - {pageSize}
          </strong>
          of {`${rows.length} ${props.label ? props.label : ''}`}
        </span>
      </div>
  <div style={{flex:'2',display:'flex',flexDirection:'row',alignItems:'center',justifyContent:'flex-end'}}> 
      <select
          value={pageSize}
          onChange={e => {
            setPageSize(Number(e.target.value))
          }}
          
        >
          {[10, 15, 20].map(pageSize => (
            <option key={pageSize} value={pageSize}>
              Show {pageSize}
            </option>
          ))}
        </select>
      <Pagination
                    count={pageOptions.length}
                   page={currentPage}
                   onChange={HandlePagination}
                    showFirstButton
                    showLastButton

/>
</div>
 
    

      </div>
  
    </>
  )
}

export default ReactTable
