import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'
import { dateFormat } from 'Pages/Protected/Partner/Components/Utils/utils'
export default async function getMedDoctor (id, update = false) {
  let {
    data: { data }
  } = await PartnerDoctorService.getMedicalPartner(id)
  if (!update) {
    return mapData(data)
  } else {
    return mapUpdateData(data)
  }
}

function mapData (data, formRef) {
  let {
    civil_status,
    gender,
    specialization,
    name,
    email_address,
    license,
    contact_number,
    birthdate,
    philhealth,
    treasury,
    id
  } = data
  let obj = {}
  obj.emailAddress = email_address ? email_address : ''
  obj.contactNumber = contact_number ? contact_number : ''
  obj.firstName = name.first ? name.first : ''
  obj.lastName = name.last ? name.last : ''
  obj.middleName = name.middle ? name.middle : ''
  obj.civilStatus = civil_status ? civil_status : ''
  obj.gender = gender ? gender : ''
  obj.suffix = name.suffix ? name.suffix : ''
  obj.birthDate = birthdate ? dateFormat(birthdate) : ''
  obj.pmaNumber = license.pma ? license.pma : ''
  obj.prcNumber = license.prc ? license.prc['number'] : ''
  obj.prcExpiration = license.prc ? dateFormat(license.prc['expiration_date']) : ''
  obj.ptrNumber = license.ptr ? license.ptr : ''
  obj.s2Number = license.s2 ? license.s2 : ''
  obj.mainSpecialization = specialization.main ? specialization.main : ''
  obj.subSpecialization = specialization.sub ? specialization.sub : ''
  obj.majorSpecialization = specialization.major ? specialization.major : ''
  obj.otherSpecialization = specialization.other ? specialization.other : ''
  obj.philhealthNumber = philhealth.accreditation_number ? philhealth.accreditation_number : ''
  obj.philhealthExpiration = philhealth.expiration_date ? philhealth.expiration_date : ''
  obj.tinStatus = treasury.tin_status ? treasury.tin_status : ''
  obj.nonVatable = treasury.non_vatable ? treasury.non_vatable : ''
  obj.tinNumber = treasury.tin ? treasury.tin : ''
  obj.payee = treasury.payee ? treasury.payee : ''
  obj.vatTaxRate = treasury.vat ? treasury.vat['tax_rate'] : ''
  return { obj, id }
}

function mapUpdateData (data, formRef) {
  let {
    civil_status,
    gender,
    specialization,
    name,
    email_address,
    license,
    contact_number,
    birthdate,
    philhealth,
    facility_info,
    coordinators,
    remarks,
    affiliation_infos,
    affiliation_status,
    affiliation_type,
    treasury,
    medical_partner_id,
    id,
    stage,
    mno,
    type,
    status
  } = data
  let obj = {}
  console.log('birth date fetch', birthdate)
  obj.emailAddress = email_address ? email_address : ''
  obj.contactNumber = contact_number ? contact_number : ''
  obj.firstName = name.first ? name.first : ''
  obj.lastName = name.last ? name.last : ''
  obj.middleName = name.middle ? name.middle : ''
  obj.civilStatus = civil_status ? civil_status : ''
  obj.gender = gender ? gender : ''
  obj.suffix = name.suffix ? name.suffix : ''
  obj.birthDate = birthdate ? birthdate : ''
  obj.pmaNumber = license.pma ? license.pma : ''
  obj.prcNumber = license.prc ? license.prc['number'] : ''
  obj.prcExpiration = license.prc ? dateFormat(license.prc['expiration_date']) : ''

  obj.ptrNumber = license.ptr ? license.ptr : ''
  obj.s2Number = license.s2 ? license.s2 : ''
  obj.mainSpecialization = specialization.main ? specialization.main : ''
  obj.subSpecialization = specialization.sub ? specialization.sub : ''
  obj.majorSpecialization = specialization.major ? specialization.major : ''
  obj.otherSpecialization = specialization.other ? specialization.other : ''
  obj.philhealthNumber = philhealth.accreditation_number ? philhealth.accreditation_number : ''
  obj.philhealthExpiration = philhealth.expiration_date ? philhealth.expiration_date : ''
  obj.facilityName = facility_info[0].name ? facility_info[0].name : ''
  obj.facilityMpId = facility_info[0].facility_id ? facility_info[0].facility_id : ''
  obj.address = facility_info[0]['full_address'] ? facility_info[0]['full_address'] : ''
  obj.roomNumber = facility_info[0].room_number ? facility_info[0].room_number : ''
  obj.contactNumbers = facility_info[0].contact_numbers ? facility_info[0].contact_numbers : ''
  obj.clinicSchedule = facility_info[0].clinic_schedule ? facility_info[0].clinic_schedule : ''
  obj.mainCoordinatorName = coordinators.main_coordinator_name ? coordinators.main_coordinator_name : ''
  obj.mainContactNum = coordinators.main_contact_num ? coordinators.main_contact_num : ''
  obj.asstCoordinators = coordinators.asst_coordinators ? coordinators.asst_coordinators : []
  obj.affiliationInfos = affiliation_infos ? affiliation_infos : []
  obj.applicantRemarks = remarks.applicant ? remarks.applicant : ''
  obj.intellicareRemarks = remarks.intellicare ? remarks.intellicare : ''
  obj.tinStatus = treasury.tin_status ? treasury.tin_status : ''
  obj.nonVatable = treasury.non_vatable
    ? treasury.non_vatable
    : treasury.tin_status_value
    ? treasury.tin_status_value
    : ''
  obj.tinNumber = treasury.tin ? treasury.tin : ''
  obj.payee = treasury.payee ? treasury.payee : ''
  obj.vatTaxRate = treasury.vat ? treasury.vat['tax_rate'] : ''
  obj.medicalPartnerId = medical_partner_id
  let affil = {}

  affil.status = status
  affil.mno = mno
  affil.type = type
  affil.id = id
  affil.affiliationType = affiliation_type
  affil.affiliationStatus = affiliation_status['name']
  affil.stageName = stage.stage_name
  affil.dateSubmitted = stage.date_submitted

  return { obj, affil }
}
