import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import classes from './UploadFIleTable.module.scss'
import Previews from '../../../../AffiliationFormEncoder/Pages/PreviewImage/PreviewImage'
import Axios from 'axios'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import { useDispatch } from 'react-redux'

const URL = `${process.env.REACT_APP_SERVER_API}`

const LocationMapWithSubscript = () => {
  return (
    <div id='locationMapText' className={classes.WithSubScript}>
      Location Map{' '}
      <sub id='subscript-location' className={classes.subscript}>
        *preferably screenshot from google maps
      </sub>
    </div>
  )
}

const AcutalImageAreas = () => {
  return (
    <div id='ActualImageArea' className={classes.WithSubScript}>
      Actual Image of Areas in the Facility<br />
      <small id='subscript-ActualImageArea' className={classes.subscript}>
        *All facility department (Ancilliary Dept, Lab, Radiology, ER, Rehab, Others), All room types,<br />
        Front Area/Front Image of the facility.
      </small>
    </div>
  )
}

const ApePackages = () => {
  return (
    <div id='ApePackages' className={classes.WithSubScript}>
      APE Packages
      <sub id='subscript-ApePackages' className={classes.subscript}>
        *Basic 5, ECG, PapSmear and Drugtest
      </sub>
    </div>
  )
}

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}
const UploadFIleTable = props => {
  const { buttonText } = props
  let dispatch = useDispatch()

  const forView = false
  React.useEffect(() => {
    fetchData()
  }, [])
  const [mainFiles, setMainFiles] = useState([])

  const fetchData = async () => {
    try {
      const { data } = await Axios.get(URL + `/p/f/partners/facilities/id/${props.paramId}`)
      const paramAffilId = data.data.afn_number
      dispatch(setAffiliationType({id: paramAffilId}))
      const filesData = await Axios.get(`${URL}/p/c/partners/common/files/${paramAffilId}`)
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles((previousState) => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file => Object.assign(file, {
            preview: `${URL}/uploads/${file.path}`,
            fileType: fileData.file_type
          }))
        }))
      }
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData(<Previews desc={'Medical Clinic Profile'} fileType={'medical-clinic-profile'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'Updated List of Services Offered with Rates'} fileType={'service-offered-with-rates'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'List of Doctors to be Accredited'} fileType={'list-of-doctors-accredited'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'Medical Directory'} fileType={'medical-directory'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={<LocationMapWithSubscript />} fileType={'location-map'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'Check Payable Letter'} fileType={'check-payable-letter'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={<AcutalImageAreas />} fileType={'image-address'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={<ApePackages />} fileType={'ape-page'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'ECU Packages'} fileType={'ecu-package'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'Business Permit'} fileType={'business-permit'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'BIR COR 2303'} fileType={'bir-cor-2303'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'DOH PERMIT'} fileType={'doh-permit'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'SEC/DTI Registration'} fileType={'sec-dti-registration'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />),
    createData(<Previews desc={'PhilHealth Accreditation'} fileType={'philhealth-accreditation'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={buttonText} forView={forView} />)
  ]

  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <Table className={classes.table} size='small' aria-label='a dense table'>
        <TableBody id='table-body'>
          {rows.map((row, index) => (
            <TableRow id='TableRow' className={classes['table-row']} key={index}>
              <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                {row.description}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default UploadFIleTable
