import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { medicalClinic } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'

const MedicalClinic = () => {
  const [dentalClinics, setDentalClinics] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  React.useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    const { data } = await await ReportServices.getFacilitiesByType('CLINIC')
    const dataArr = data.data
    setDentalClinics(dataArr)
  }
  return (
    <div>
      <div className={classes.rightBody}>
        <div id='root' className={classes.root}>
          <ReactTable columns={medicalClinic} data={dentalClinics} setDownloadData={setDownloadData} />
        </div>

      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={medicalClinic} filename={`${moment().format('YYYYMMDD')}-medical-clinic.csv`} />
      </div>
    </div>
  )
}

export default MedicalClinic
