import classes from './FacilityAssignmentForDoctors.module.scss'
import React from 'react'
import Star from '../../Assets/Icons/star.png'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import Checkbox from '@material-ui/core/Checkbox'
import FormGroup from '@material-ui/core/FormGroup'
import { RenderInputFields } from '../../Components/InputsForProfiles/InputsForProfile'

const FacilityAssignmentForDoctorsComponent = props => {
  const { form, setForm, refValue, errors, setErrors } = props

  let bool = true

  const { facilityAssignment } = form

  const handleChange = ({ target: input }) => {
    const newVal = { ...form }
    const { value, name, checked } = input

    if (name === 'assignments') {
      checkedAndUncheckAssignment(value, checked)
    } else {
      newVal[name] = value
      setForm(newVal)
    }
    delete errors[name]
    setErrors(errors)
  }

  const checkedAndUncheckAssignment = (value, checked) => {
    const newVal = { ...form }
    if (checked && !newVal['assignments'].includes(value)) {
      newVal['assignments'] = [...newVal['assignments'], value]
      setForm(newVal)
    }
    if (!checked && form['assignments'].includes(value)) {
      newVal['assignments'] = newVal.assignments.filter(assign => assign !== value)
      setForm(newVal)
    }
  }

  const handleAssignmentChange = ({ target: { value, name } }) => {
    let newVal = { ...form }
    if (value === 'None') {
      newVal['assignments'] = []
      newVal['facilityAssignment'] = value
      setForm(newVal)
    } else {
      newVal['facilityAssignment'] = value
      setForm(newVal)
    }
    delete errors[name]
    setErrors(errors)
  }

  const handleDisabledCheckbox = React.useMemo(() => {
    if (facilityAssignment === 'Assigned') {
      return false
    } else {
      return true
    }
  }, [facilityAssignment])

  React.useEffect(() => {
    if (form['assignments'].length > 0) {
      let newForm = { ...form }
      newForm['facilityAssignment'] = 'Assigned'
      setForm(newForm)
    } else {
      let newForm = { ...form }
      newForm['facilityAssignment'] = 'None'
      setForm(newForm)
    }
  }, [form['assignments']])

  return (
    <React.Fragment>
      <div className={classes.headerGroupContainer}>
        <div className={classes.doctorAssignmentContainer}>
          <div className={classes.title}>
            <img src={Star} alt='star' />
            <label>DOCTOR ASSIGNMENT</label>
          </div>
          <FormControl component='fieldset' className={classes.radioGroupContainer}>
            <RadioGroup
              row={bool}
              aria-label='position'
              name='facilityAssignment'
              onChange={handleAssignmentChange}
              value={form['facilityAssignment']}
              className={classes.radioGroup}
            >
              <div>
                <FormControlLabel
                  className={classes.none}
                  value='None'
                  control={<Radio color='primary' />}
                  label='None'
                />
              </div>
              <div className={classes.assignedGroup}>
                <FormControlLabel
                  className={classes.assigned}
                  value='Assigned'
                  control={<Radio color='primary' />}
                  label='Assigned'
                />
                <FormGroup
                  aria-label='position'
                  name='assignments'
                  onChange={handleChange}
                  className={classes.checkBoxGroup}
                >
                  <FormControlLabel
                    value='panelDoctor'
                    control={<Checkbox color='primary' name='assignments' />}
                    label='Panel Doctor'
                    labelPlacement='end'
                    disabled={handleDisabledCheckbox}
                    checked={form['assignments'].includes('panelDoctor')}
                  />
                  <FormControlLabel
                    value='fullertonOne'
                    control={<Checkbox color='primary' name='assignments' />}
                    label='Fullerton One'
                    labelPlacement='end'
                    disabled={handleDisabledCheckbox}
                    checked={form['assignments'].includes('fullertonOne')}
                  />
                  <div id='specialAccommodation' className={classes.specialAccommodation}>
                    <FormControlLabel
                      value='specialAccommodation'
                      control={<Checkbox color='primary' name='assignments' />}
                      label='Special Accommodation'
                      labelPlacement='end'
                      disabled={handleDisabledCheckbox}
                      checked={form['assignments'].includes('specialAccommodation')}
                    />
                    <div
                      id='exclusiveTo'
                      className={
                        form['facilityAssignment'] === 'Assigned' &&
                        form['assignments'].includes('specialAccommodation')
                          ? classes.exclusiveTo
                          : classes.inputHide
                      }
                    >
                      <label>Exclusive to:</label>
                      <div id='inputfield' className={classes.inputField}>
                        <RenderInputFields
                          name={'exclusiveTo'}
                          inputRef={refValue.exclusiveTo}
                          error={errors['exclusiveTo']}
                          onChange={handleChange}
                        />
                      </div>
                    </div>
                  </div>
                </FormGroup>
              </div>
            </RadioGroup>
          </FormControl>
        </div>
        <div className={classes.medicalPartnerStatusContainer}>
          <div className={classes.title}>
            <label>Medical Partner Status</label>
          </div>
          <FormControl component='fieldset' className={classes.radioGroupContainer}>
            <RadioGroup
              // aria-label='position'
              name='status'
              value={form['status']}
              onChange={handleChange}
              className={classes.radioGroup}
            >
              <FormControlLabel value='Active' control={<Radio color='primary' />} label='Active' />
              <div id='onHold' className={classes.onHold}>
                <FormControlLabel value='On-Hold' control={<Radio color='primary' />} label='On-Hold' />
                <div
                  id='onHoldInput'
                  className={form['status'] !== 'On-Hold' ? classes.onHoldHide : classes.onHoldInput}
                >
                  <label>Reason for On-Hold:</label>
                  <div id='inputfield' className={classes.inputField}>
                    <RenderInputFields
                      name={'onHoldReason'}
                      inputRef={refValue.reason}
                      error={errors['onHoldReason']}
                      onChange={handleChange}
                    />
                  </div>
                </div>
              </div>
              <FormControlLabel value='Inactive' control={<Radio color='primary' />} label='Inactive' />
            </RadioGroup>
          </FormControl>
        </div>
      </div>
    </React.Fragment>
  )
}

const FacilityAssignmentForDoctors = React.memo(FacilityAssignmentForDoctorsComponent)

export default FacilityAssignmentForDoctors
