import React from 'react'

import { useSelector } from 'react-redux'

import Button from '@material-ui/core/Button'
import EditIcon from '@material-ui/icons/Edit'
import DeleteIcon from '@material-ui/icons/Delete'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import AddContactModal from '../AddContactModal/AddContactModal'
import EditContactModal from '../AddContactModal/EditContactModal'
import CustomDialogSave from '../CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../CustomSnackBar/CustomAlertSnackbar'

import InlineEditField from '../InlineEditField/InlineEditField'

import classes from './ContactDetails.module.scss'
import CustomConfirmationDialog from '../CustomConfirmationDialog/CustomConfirmationDialog'

import ProfileService from '../../../../../Services/Api/Partner/ProfileService'
import { RenderInputFields } from '../InputsForProfiles/InputsForProfile'

const RenderButton = ({ setIsToEdit, isToEdit }) => {
  return (
    <div id='buttonContainer'>
      <Button variant='contained' onClick={() => setIsToEdit(true)} className={classes.buttonContainer}>
        <div id='insideText' className={classes.insideText}>
          <label>Edit Contact </label>
        </div>
      </Button>
    </div>
  )
}

const ContactDetails = ({ open, setOpen, paramId, type }) => {
  const affiliatedContacts = useSelector(state => state.affiliatedContacts.affiliatedContacts)

  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [success, setSuccess] = React.useState(false)

  const [isToEdit, setIsToEdit] = React.useState(false)
  const [isDelete, setDelete] = React.useState(false)
  const [editOpen, setEditOpen] = React.useState(false)
  const [id, setId] = React.useState(null)
  const [contactsValue, setContacts] = React.useState([])
  const [contactToEdit, setContactToEdit] = React.useState({})
  const [originaLContact, setOriginalContact] = React.useState([])
  const [contact, setContact] = React.useState('')
  const [callFlowNumber, setCallFlowNumber] = React.useState('')
  const [checkedValue, setCheckedValue] = React.useState('')
  const [originaCheckedValue, setOriginalCheckValue] = React.useState('')

  React.useEffect(() => {
    let dataValue = []
    const data = affiliatedContacts ? (affiliatedContacts.contacts ? affiliatedContacts.contacts : []) : []
    const callFlow = affiliatedContacts ? (affiliatedContacts.callFlow ? affiliatedContacts.callFlow : '') : ''

    dataValue = data.map((items, index) => {
      if (items.cDesignation === '' && items.cNumber === '' && items.cName === '' && items.cEmail === '') {
        return {}
      }
      return items
    })

    const newDataValue = dataValue.filter(value => Object.keys(value).length !== 0)

    const callFlowNumber = callFlow ? (callFlow.number ? callFlow.number : '') : ''
    const callFlowChecked = callFlow ? (callFlow.checked ? callFlow.checked : '') : ''

    setCheckedValue(callFlowChecked)
    setOriginalCheckValue(callFlowChecked)
    setContact(callFlowNumber)
    setContacts(newDataValue)
    setOriginalContact(newDataValue)
    setCallFlowNumber(callFlowNumber)

    return () => {
      setContacts(newDataValue)
      setOriginalContact(newDataValue)
      setContact(callFlow)
      setCallFlowNumber(callFlow)
    }
  }, [])

  const Endpoint = data => {
    switch (type) {
      case 'DOCTOR':
        return ProfileService.updateDoctorContacts(data)

      case 'DENTIST':
        return ProfileService.updateDoctorContacts(data)

      default:
        return ProfileService.updateFacilitiesContacts(data)
    }
  }

  const handleDelete = index => {
    setDelete(true)
    setId(index)
  }
  const cancelUpdate = () => {
    closeAllEditField()
    setIsToEdit(false)
    setIsCancel(false)
    setContact(callFlowNumber)
    setCheckedValue(originaCheckedValue)
  }

  const closeAllEditField = () => {
    const newContacts = [...originaLContact]
    for (let i of newContacts) {
      delete i.isOpen
    }
    setContacts(newContacts)
  }

  const handleEdit = number => {
    const newContacts = [...contactsValue]
    for (let [index, contact] of newContacts.entries()) {
      if (contact.cNumber === number) {
        contact.index = index
        setContactToEdit(contact)
        setEditOpen(true)
      }
    }
    // setContacts(newContacts)
  }

  const onDelete = async () => {
    const newContacts = [...contactsValue]
    const updatedContacts = newContacts.filter(contact => newContacts.indexOf(contact) !== id)
    setContacts(updatedContacts)
    setDelete(false)
  }

  const HandleCallFlow = React.useCallback(
    e => {
      const { value } = e.target
      setContact(value)
    },
    [contact]
  )

  const HandleCallFlowRadio = React.useCallback(
    e => {
      const { value } = e.target
      setCheckedValue(value)
    },
    [checkedValue]
  )

  const HandleSave = async () => {
    setIsSave(true)
    setSuccess(false)
  }

  const handleEditSave = async () => {
    let obj = {}
    let contacts = [...contactsValue]
    obj.id = paramId
    obj.contacts = contacts
    obj.callFlow = {
      number: contact || '',
      checked: checkedValue || ''
    }
    try {
      const response = await Endpoint(obj)
      const datas = response.data ? (response.data ? (response.data.data ? response.data.data.contacts : []) : []) : []

      const callflowRes = response.data
        ? response.data
          ? response.data.data
            ? response.data.data.callFlow
            : []
          : []
        : []

      setContacts(datas)
      setOriginalContact(datas)
      const callFlowNumber = callflowRes ? (callflowRes.number ? callflowRes.number : '') : ''
      const checked = callflowRes ? (callflowRes.checked ? callflowRes.checked : '') : ''

      setCallFlowNumber(callFlowNumber) // responsible for the display after saving.
      setContact(callFlowNumber)
      setCheckedValue(checked)
      setOriginalCheckValue(checked)
      setSuccess(true)
      setIsSave(false)

      setTimeout(() => {
        setIsToEdit(false)
      }, 800)
    } catch (e) {
      throw e
    }
    setIsToEdit(false)
  }

  return (
    <React.Fragment>
      <div id='editButton' className={classes.editButton}>
        <RenderButton setIsToEdit={setIsToEdit} isToEdit={isToEdit} />
      </div>
      <div id='root' className={classes.root}>
        <div id='div-1' className={classes['div-1']}>
          <div id='calllFlow' className={classes.calllFlow}>
            <text>Call Flow</text>
            <EditIcon className={classes.editIcon} />
          </div>
          {!isToEdit && (
            <div id='numberText' className={classes.numberText}>
              {callFlowNumber}
            </div>
          )}
          {isToEdit && (
            <div id='numberText' className={classes.numberText}>
              <RenderInputFields type={'number'} value={contact} autoFocus onChange={HandleCallFlow} />
            </div>
          )}

          {isToEdit && (
            <div id='radioButtonsField' className={classes.radioButtonsField}>
              <FormControl component='fieldset'>
                <RadioGroup value={checkedValue} onChange={HandleCallFlowRadio}>
                  <FormControlLabel value='Yes' control={<Radio color='primary' />} label='Yes' />
                  <FormControlLabel value='No' control={<Radio color='primary' />} label='No' />
                </RadioGroup>
              </FormControl>
            </div>
          )}
        </div>
        <div id='div-2' className={classes['div-2']}>
          <div id='contactDetailsBody' className={classes.contactDetailsBody}>
            <div id='contactDetailsWrapper' className={classes.contactDetailsWrapper}>
              {contactsValue.map((contact, index) => (
                <div id='contactDetailsBox' key={index} className={classes.contactDetailsBox}>
                  <div id='details'>
                    {!contact.isOpen && (
                      <React.Fragment>
                        <div id='designation' className={classes.designation}>
                          {contact.cDesignation && contact.cDesignation}
                        </div>
                        <div id='name' className={classes.name}>
                          {contact.cName && contact.cName}
                        </div>
                        <div id='contacNumber' className={classes.contacNumber}>
                          {contact.cNumber && contact.cNumber}
                        </div>
                        <div id='email' className={classes.email}>
                          {contact.cEmail && contact.cEmail}
                        </div>
                      </React.Fragment>
                    )}
                    {contact.isOpen && (
                      <InlineEditField contact={contact} contacts={contactsValue} setContacts={setContacts} />
                    )}
                  </div>
                  {isToEdit && (
                    <div id='icons'>
                      <EditIcon
                        id='update'
                        style={{ color: contact.isOpen ? '#26c281' : '#151c1b' }}
                        onClick={() => handleEdit(contact.cNumber)}
                        className={classes.icons}
                      />
                      <DeleteIcon id='del' onClick={() => handleDelete(index)} className={classes.icons} />
                    </div>
                  )}
                </div>
              ))}
            </div>
          </div>
        </div>
        <div id='div-3' className={classes['div-3']}>
          <AddContactModal
            setContacts={setContacts}
            contactsValue={contactsValue}
            setOriginalContact={setOriginalContact}
            paramId={paramId}
            isToEdit={isToEdit}
            Endpoint={Endpoint}
            type={type}
          />
          {editOpen && (
            <EditContactModal
              setOpen={setEditOpen}
              open={editOpen}
              contact={contactToEdit}
              editedContacts={contactsValue}
              setEditetContact={setContacts}
              type={type}
            />
          )}
        </div>
      </div>
      {isToEdit && (
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                onClick={() => setIsCancel(true)}
                variant='contained'
                color='secondary'
                className={classes.cancelButton}
              >
                Cancel
              </Button>
            </div>
            <div />
            <div>
              <div>
                <Button variant='contained' type='submit' className={classes.SaveBtn} onClick={HandleSave}>
                  Save
                </Button>
              </div>
            </div>
          </span>
        </div>
      )}
      <div>
        {/* dialogs */}
        {isCancel && (
          <CustomConfirmationDialog
            open={isCancel}
            close={() => setIsCancel(false)}
            onApprove={cancelUpdate}
            message='All changes will not be saved. Are you sure you want to cancel?'
          />
        )}
        {isDelete && (
          <CustomConfirmationDialog
            open={isDelete}
            close={() => setDelete(false)}
            onApprove={onDelete}
            message='Are you sure you want to Delete?'
          />
        )}
      </div>
      <div>
        <CustomDialogSave
          onClose={() => {
            setOpen(false)
          }}
          id='a_sos_custom_dialog'
          isSave={isSave}
          setIsSave={() => setIsSave(false)}
        >
          <div id='a_sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='a_sos_custom_dialog_message'>Are you sure you want to update?</h3>
            <div id='a_sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => setIsSave(false)}
                id='a_sos_custom_dialog_btn_no'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={handleEditSave}
                id='a_sos_custom_dialog_btn_yes'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialogSave>
      </div>
      <CustomAlerSnackBar setDelay={800} isSave={success} setIsSave={() => setSuccess(false)} />
    </React.Fragment>
  )
}

export default ContactDetails
