import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import NumberFormat from 'react-number-format'
let formatNum = {}

function NumberFormatCustom (props) {
  const { inputRef, onChange, ...other } = props

  return (
    <NumberFormat
      {...other}
      getInputRef={inputRef}
      format={formatNum[props.name]}
      suffix='-'
      mask='_'
      onValueChange={(values) => {
        onChange({
          target: {
            name: props.name,
            value: values.value
          }
        })
      }}
    />
  )
}

function InputBaseWithNumberFormat (props) {
  const {
    disabled,
    required,
    inputRef,
    value,
    readOnly,
    error,
    onChange,
    type,
    format,
    name,
    ...other
  } = props

  formatNum[name] = format
  let specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>]/)
  let forEmail = RegExp(/[!#$%^&*(),?":;'`~{}|<>]([[]]*)/)
  let numbersOnly = RegExp(/^[\d\b]+$/)
  let lettersOnly = RegExp(/^[a-z]+$/)
  // let dashAndNum = RegExp(/^[\d -]{14}/)

  const DisableCharacters = e => {
    switch (type) {
      case 'number':
        if (
          e.key === 'e' ||
          e.key === 'E' ||
          e.keyCode === 189 ||
          e.keyCode === 111 ||
          e.keyCode === 106 ||
          e.keyCode === 107 ||
          e.keyCode === 109
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'noSpecialChar':
        if (
          specialChar.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 191 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'dashAndNum':
        if (
          specialChar.test(e.key) ||
          lettersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'lettersOnly':
        if (
          specialChar.test(e.key) ||
          numbersOnly.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }
      case 'email':
        if (
          forEmail.test(e.key) ||
          e.keyCode === 187 ||
          e.keyCode === 189 ||
          e.keyCode === 219 ||
          e.keyCode === 191 ||
          e.keyCode === 221 ||
          e.keyCode === 220
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      default:
        return {}
    }
  }


  return (
    <InputBase
      onChange={onChange}
      disabled={disabled}
      type={type}
      inputRef={inputRef}
      autoComplete='off'
      name={name}
      onKeyDown={e => DisableCharacters(e)}
      value={value}
      inputComponent={NumberFormatCustom}
      {...other}
    />
  )
}

export default React.memo(InputBaseWithNumberFormat)
