import React from 'react'
import { makeStyles } from '@material-ui/core/styles'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'

const useStyles = makeStyles((theme) => ({
  // input: {
  //   width: '100%'
  // },
  listbox: {
    width: '100%',
    margin: 0,
    padding: 0,
    zIndex: 1,
    position: 'absolute',
    transform: 'translate(0, 180px)',
    left: 0,
    bottom: 0,
    listStyle: 'none',
    backgroundColor: theme.palette.background.paper,
    borderRadius: '2px',
    overflow: 'auto',
    height: 200,
    maxHeight: 200,
    minHeight: '44px !important',
    overflowX: 'auto',

    paddingLeft: 5,
    border: '1px solid rgba(0,0,0,.25)',
    '& li[data-focus="true"]': {
      backgroundColor: '#4a8df6',
      color: 'white',
      cursor: 'pointer'
    },
    '& li:active': {
      backgroundColor: '#2977f5',
      color: 'white'
    }
  },
  loadingWrapper: {
    width: '100%',
    margin: 0,
    padding: 0,
    borderRadius: '2px',
    height: 200,
    maxHeight: 200,
    position: 'absolute',
    transform: 'translate(0, 180px)',
    left: 0,
    bottom: 0
  },
  noResult : {
    fontSize: '1em',
    textAlign: 'center',
    color: 'grey'
  }

}))

function ListComponent ({items = [], handleSelect, isFetching, labelName}) {
  const classes = useStyles()

  return (
    <>
      { isFetching ? (<div className={classes.loadingWrapper}>
          <ProgressComponent />
        </div>)
        : <ul className={classes.listbox}>
        {items.map((option, index) => (
          <li key={option[labelName]} onClick={() => handleSelect(option)}>{option[labelName]}</li>
          ))}
        {!isFetching && items.length <= 0  && <p className={classes.noResult}>No result found.</p>}
        </ul>
      }
    </>
  )
}

export default React.memo(ListComponent)
