import React from 'react'
import Axios from 'axios'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../../../../Components/JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './AddMultiplierModal.module.scss'
import AddMultiplierValidation from '../validations/AddMultiplierValidation'
import FormHelperText from '@material-ui/core/FormHelperText'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import DropDown from '../../../../Components/CustomDrowpdown'
import { pfMultiplierCol } from '../../../../../../../Constant/tableColumnData'

import RoomTypeService from '../../../../../../../Services/Api/RoomType/RoomType'
import CustomAlerSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'

const URL = `${process.env.REACT_APP_SERVER_API}`

const AddRoomMultiplierModal = props => {
  const ctr = parseInt(props.dataLength) + 1;
  const [open, setOpen] = React.useState(false)
  const [errors, setErrors]       = React.useState({})
  const [roomTypes, setRoomTypes] = React.useState([])
  const [success, setSuccess] = React.useState(false)
  const [form, setForm] = React.useState({
    'amhopiSociety': '',
    'pfType': '',
    'roomType': '',
    'rate': '',
    'code': ''
  })

  const handleSubmit = (e) => {
    e.preventDefault()
    validate()
  }

  const validate = () => {
    const { errors, isValid } = AddMultiplierValidation(form)
    if (isValid) {
      setErrors({})
      save()
    } else {
      setErrors(errors)
    }
  }

  const save = () => {
    try {
      setSuccess(true)
      setTimeout(async () => {
        await Axios.post(`${URL}/p/f/partners/pf-multiplier/add`, form)
        props.fetchData()
        setForm({
          'amhopiSociety': '',
          'pfType': '',
          'roomType': '',
          'rate': '',
          'code': ''
        })
        setOpen(false)
      }, 1000)
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  const handleChange = ({ currentTarget: input }) => {
    const contactForm = { ...form }
    contactForm[input.name] = input.value
    setForm(contactForm)
  }

  const setCounterCode = (type) => {
    let counter = 1
    console.log(props.data)
    if (props.data) {
      const typeData = props.data.filter(function (data) {
        return data.amhopi_society == type
      }).length
      console.log(typeData);
      counter        = typeData + 1
    }
    let typeText = ''
    switch (type) {
      case 'PCS':
        typeText = 'PFPCS'
        break
      case 'POGS':
        typeText = 'PFPOG'
        break
      case 'PPS':
        typeText = 'PFPPS'
        break
      case 'PSA':
        typeText = 'PFPSA'
        break
      case 'Regular':
        typeText = 'PFREG'
        break
      case 'UDAA':
        typeText = 'PFUDA'
        break
      case 'UHDA':
        typeText = 'PFUHD'
        break
      case 'MACU':
        typeText = 'PFMAC'
        break
    }
    const pad = '0000'
    const counterText = (pad + counter).slice(-pad.length)
    return `${typeText}${counterText}`
  }

  const amhopiSociety = [
    { id: 1, text: 'PCS', value: 'PCS' },
    { id: 2, text: 'POGS', value: 'POGS' },
    { id: 3, text: 'PPS', value: 'PPS' },
    { id: 4, text: 'PSA', value: 'PSA' },
    { id: 5, text: 'Regular', value: 'Regular' },
    { id: 6, text: 'UDAA', value: 'UDAA' },
    { id: 7, text: 'UHDA', value: 'UHDA' },
    { id: 8, text: 'MACU', value: 'MACU' }
  ]

  const pfTypes = [
    { id: 1, text: 'Surgical', value: 'Surgical' },
    { id: 2, text: 'Out-Patient', value: 'Out-Patient' },
    { id: 2, text: 'Daily Visit', value: 'Daily Visit' }
  ]

  React.useEffect(() => {
    fetchRoomType()
  }, [])

  const fetchRoomType = async () => {
    const { data } = await RoomTypeService.get()
    if (data.data) {
      let roomTypesData = []
      for (let tmp of data.data) {
        roomTypesData.push({
          id: tmp.code,
          text: tmp.desc,
          value: tmp.desc
        })
      }
      setRoomTypes(roomTypesData)
    }
  }

  const handleDropdown = ({ target: select }) => {
    const updateForm = { ...form }
    updateForm[select.name] = select.value
    if (select.name == 'amhopiSociety') {
      updateForm['code'] = setCounterCode(select.value)
    }
    setForm(updateForm)
  }

  return (
    <React.Fragment id='container'>
      <text
        onClick={() => {
          setOpen(true)
        }}
      >
        Add Multiplier
      </text>
      <Dialog fullWidth maxWidth={'xs'} open={open} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%' }}>ADD MULTIPLIER</DialogTitle>
        <DialogContent>
          <form className={classes.root} onSubmit={handleSubmit} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <div id='amhopi'>
                <DropDown name='amhopiSociety' error={errors['amhopiSociety']} onChange={handleDropdown} items={amhopiSociety} value={form.amhopiSociety} label='AMHOPI Society*' />
              </div>
              <div id='pfType'>
                <DropDown onChange={handleDropdown} error={errors['pfType']} items={pfTypes} value={form.pfType} label='PF Type*' name='pfType' />
              </div>
              <div id='roomType'>
                <DropDown onChange={handleDropdown} error={errors['roomType']} items={roomTypes} value={form.roomType} label='Room Type*' name='roomType' />
              </div>
              <div id='rate'>
                <TextField onChange={handleChange} label='Rate*' name='rate' />
                <FormHelperText style={{ color: 'red' }}>{errors['rate']} </FormHelperText>
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={() => {
              setOpen(false)
            }}
            variant='contained'
            className={classes.Cancel}
          >
            Cancel
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            Create
          </Button>
        </DialogActions>
      </Dialog>
      <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
    </React.Fragment>
  )
}

export default AddRoomMultiplierModal
