import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import PFMultiplierMaintenance from './Pages/PFMultiplierMaintenance'

// Store
// import Store from '../../Store'
import Store from '../../../Store'

const PFMultiplierMaintenanceRoutes = () => {
  const [isOpenPFMultiplierMaintenance, setIsOpenPFMultiplierMaintenance] = React.useState(false)

  React.useEffect(() => {
    setIsOpenPFMultiplierMaintenance(true)
    return () => {
      setIsOpenPFMultiplierMaintenance(false)
    }
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/PFMultiplier' exact render={(props) => <PFMultiplierMaintenance {...props} open={isOpenPFMultiplierMaintenance} setOpen={setIsOpenPFMultiplierMaintenance} />} />
      </Switch>
    </Provider>
  )
}

export default PFMultiplierMaintenanceRoutes
