import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { ahmopiDoctorsCol } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import DropDown from '../../Components/CustomDrowpdown'

const AHMOPIDoctors = ({ open, setOpen, paramId }) => {
  const [ahmopiDoctors, setAhmopiDoctors] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [ahmopiData, setAhmopiData] = React.useState('all-ahmopi')
  const [openPreview, setOpenPreview] = React.useState(false)
  React.useEffect(() => {
    fetchData(ahmopiData)
  }, [])

  const fetchData = async (ahmopi) => {
    const { data } = await ReportServices.getAllAhmopiDoctors(ahmopi)
    const dataArr = data.data
    dataArr.forEach((value, index) => {
      dataArr[index].dataCol = {
        status: value.status,
        medical_id: value.medical_id,
        full_name: value.full_name,
        facility_name: value.facility_name,
        specialization: value.specialization,
        prc_number: value.prc_number,
        room: value.room,
        schedule: value.schedule,
        street: value.street,
        barangay: value.barangay,
        city: value.city,
        district: value.district,
        region: value.region,
        contact: value.contact
      }
    })
    setAhmopiDoctors(dataArr)
  }
  const handleDropdown = async ({ target: select }) => {
    if (select.value) {
      setAhmopiData(select.value)
      fetchData(select.value)
    } else {
      setAhmopiData('all-ahmopi')
      fetchData('all-ahmopi')
    }
  }

  const ahmopiItems = [
    { id: 1, text: 'POGS', value: 'POGS' },
    { id: 2, text: 'PCS', value: 'PCS' },
    { id: 3, text: 'PCP', value: 'PCP' },
    { id: 4, text: 'PPS', value: 'PPS' },
    { id: 5, text: 'PSA', value: 'PSA' },
    { id: 6, text: 'UDAA (for SLMCGC)', value: 'UDAA (for SLMCGC)' },
    { id: 7, text: 'UHDA (for CSMC))', value: 'UHDA (for CSMC)' },
    { id: 8, text: 'MACU (for OLLH)', value: 'MACU (for OLLH)' }
  ]

  return (
    <div>
      <div className={classes.rightBody}>
        <div style={{ width: '200px' }}>
          <DropDown onChange={handleDropdown} items={ahmopiItems} value={ahmopiData} label='AMHOPI Society*' />
        </div>
        <div id='root' className={classes.root} style={{ marginTop: '20px' }}>
          <ReactTable columns={ahmopiDoctorsCol} data={ahmopiDoctors} setDownloadData={setDownloadData} />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={ahmopiDoctorsCol} filename={`${moment().format('YYYYMMDD')}-ahmopi-doctors.csv`} />
      </div>
    </div>

  )
}

export default AHMOPIDoctors
