import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'

// Pages of Maintenance Table Maintainer
import Dashboard from './Dashboard/Dashboard'
import RoomTypeMaintenanceRoutes from './RoomTypeMaintenance/Routes'
import PFMultiplierMaintenanceRoutes from './PFMultiplierMaintenance/Routes'
import FacilityGroupingMaintenanceRoutes from './FacilityGroupingMaintenance/Routes'

// Store
import Store from '../../Store'

const MaintenanceTableMaintainerRoutes = props => {
  React.useEffect(() => {
    props.history.push('/maintenance-table-maintainer')
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/maintenance-table-maintainer' exact component={Dashboard} />
        <Route path='/FacilityGroupingMaintenance' component={FacilityGroupingMaintenanceRoutes} />
        <Route path='/PFMultiplier' component={PFMultiplierMaintenanceRoutes} />
        <Route path='/RoomTypeMaintenance' component={RoomTypeMaintenanceRoutes} />
      </Switch>
    </Provider>
  )
}

export default MaintenanceTableMaintainerRoutes
