/** DoctorInformation */

import React from 'react'
import Axios from 'axios'
// import { generate } from 'shortid'

// Components
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import AffiliationForm from '../Components/AffiliationForm/AffiliationForm'
import history from '../../../../Components/History/History'
import ErrorMsg from '../Components/Utilities/ErrorMsg'
import Validate from '../Components/Utilities/DoctorFormValidation'
import InitialState from '../Components/Utilities/InitalState'
// import SendData from '../../../Components/Api/sendData'

// Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import classes from './DoctorInformation.module.scss'
import { useSelector } from 'react-redux'

// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'

const URL = `${process.env.REACT_APP_SERVER_API}`
const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Doctor: {
    text: 'DOCTOR AFFILIATION',
    to: '/doctor-affiliation-form'
  }
}

const DoctorInformation = ({history}) => {
  const { affiliationType } = useSelector(state => state.affiliationType)
  let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Doctor Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Doctor'])

  const doctor = useSelector(state => state.doctor)

  let stage = {
    stageName: cnts.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL,
    dateSubmitted: new Date().toISOString()
  }

  const [states, setStates] = React.useState(InitialState)
  const [form, setForm] = React.useState(false)

  const OnSubmit = async () => {
    let data = await doctor.doctorForms
    try {
      setStates({ ...data, ...affiliationType, ...stage })
      setForm(true)
    } catch (e) {
      setForm(false)
    }
  }

  const checkForm = async () => {
    try {
      const { data: res } = await Axios.post(URL + '/p/d/partners/doctors', states)
      console.log('res', res)
      try {
        history.push('/doctor-affiliation-requirements')
      } catch (e) {
        console.log('errors', e)
      }
    } catch (error) {
      console.log('errors', error)
    }
  }

  if (form) {
    // werd: commented out coz it triggers the checkForm instantly
    checkForm()
  }

  return (
    <React.Fragment>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={1}
        />
        <div>
          <AffiliationForm onSubmit={OnSubmit} history={history} />
        </div>
      </div>
    </React.Fragment>
  )
}

export default DoctorInformation
