import React, { useState, useEffect, useContext } from 'react'

import Button from '@material-ui/core/Button'
import Axios from 'axios'
// Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbsNoStepper from '../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import SOSForm from './Components/SOSForm/SOSForm'
import CustomDialogApprove from '../../Pages/Components/CustomDialogApprove/CustomDialogApprove'
import History from 'Pages/Protected/Partner/Components/History/History'
// Constant
import cntsPartnerType from 'Pages/Protected/Partner/Constants/partnerTypes'
import cnts from 'Pages/Protected/Partner/Constants/stage'
// Styles
import classes from './SpecialOtherServices.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Others: {
    text: 'SPECIAL AND OTHER SERVICES FORM',
    to: '/'
  }
}

// SERVER URL
const URL = process.env.REACT_APP_SERVER_API

const SpecialOtherServices = props => {
  const [paramId, setParamId] = useState('')
  console.log('***** Called Special Other Services...')
  let headerTitle = 'Special / Other Services'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Others'])

  const [isApprove, setIsApprove] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)
  const [formData, setFormData] = React.useState({})

  React.useEffect(() => {
    console.log('*** called use effelct 1')
    const { match } = props
    LoadingService.loading(true)
    fetchData(match.params.id)
    setParamId(match.params.id)
  }, [])

  const fetchData = (id) => {
    console.log('*** 1')
    try {
      setTimeout(async () => {
        const data = await AffiliationApproverServices.getOSPartnerById(id)
        console.log(data.data.data)
        setFormData(data.data.data)
        LoadingService.loading(false)
      }, 1000)
    } catch (error) {
      console.log('fetcherr', error)
    }
  }

  const approveAffiliation = async () => {
    try {
      let data = {
        stageName: cnts.PARTNER.STAGE.PROFILE_GENERATED,
        dateSubmitted: new Date().toISOString(),
        newId: 'SOS' + Math.floor(Math.random() * 10000), // TODO: Generation should be in the backend
        type: cntsPartnerType.PARTNER.TYPE.OTHER_SERVICES
      }

      console.log(`***** DATA TYPE : ${data.type}`)

      const res = await AffiliationApproverServices.approveAffiliation(paramId, data)
      setIsApprove(false)
      setIsApproved(true)
      // props.history.push('/affiliation-approver')
    } catch (error) {
      console.log('error', error)
    }
  }

  const disapproved = async () => {
    try {
      const res = await AffiliationApproverServices.disapproved(paramId, cntsPartnerType.PARTNER.TYPE.OTHER_SERVICES)
      props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  return (
    <React.Fragment>
      <CustomDialogApprove id='aa_custom_dialog' isApprove={isApprove} setIsApprove={() => setIsApprove(false)}>
        <div id='aa_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='aa_custom_dialog_message'>Are you sure you want to approve the Special/Other Affiliation form?</h3>
          <div id='aa_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              onClick={() => setIsApprove(false)}
              id='aa_custom_dialog_btn_cancel'
              className={classes.btnCancel}
              variant='contained'
            >
              Cancel
            </Button>
            <Button id='aa_custom_dialog_btn_approve' className={classes.btnApprove} variant='contained' onClick={approveAffiliation} >
              Approve
            </Button>
          </div>
        </div>
      </CustomDialogApprove>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbsNoStepper id='HeaderWithBreadCrumbsNoStepper' HeaderTitle={headerTitle} />
        <div id='other-service-form-div'>
          <SOSForm id='other-service-form' formData={formData} />
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                variant='contained'
                color='secondary'
                className={classes.disapproveButton}
                // onClick={() => props.history.push(`/`)}
                disabled={isApproved} style={{backgroundColor: isApproved ? '#dddfe3' : ''}}
                onClick={disapproved}
              >
                Disapproved
              </Button>
            </div>
            <div />
            <div>
              <div>
                <Button variant='contained' disabled={isApproved} style={{backgroundColor: isApproved ? '#dddfe3' : ''}} className={classes.approveButton} onClick={() => setIsApprove(true)}>
                  Approve
                </Button>
              </div>
            </div>
          </span>
        </div>
      </div>
    </React.Fragment>
  )
}

export default SpecialOtherServices
