import React from 'react'

// Components

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import AffilationForm from '../components/AffiliationForm/AffiliationForm'
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
// import history from '../../../../Components/History/History'

import InitalState from '../components/Utilities/InitialState'
import { useSelector } from 'react-redux'

// import FooterWithButtons from '../../../Components/FooterWithButtons/FooterWithButtons'
// Services
import SendData from '../../../Components/Api/sendData'
// Utilities
// Styles
import classes from './HospitalAffiliationForm.module.scss'
// Validator../components/ValidateForm/validateForms

// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Hospital: {
    text: 'HOSPITAL AFFILIATION',
    to: '/hospital-affiliation-form'
  }
}

let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
let headerTitle = 'Hospital Affiliation Form'

const HospitalAffiliation = ({history}) => {
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [states, setStates] = React.useState(InitalState)
  const [contacts, setAddToContacts] = React.useState([])
  const [affiliationInfos, setaffiliationInfos] = React.useState([])
  const [form, setForm] = React.useState(false)

  const hospitalData = useSelector(state => state.AffiliationHospitalForm)

  let stage = {
    stageName: cnts.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC,
    dateSubmitted: new Date().toISOString()
  }
  const handleSubmit = async () => {
    console.log('STAGE SUBMITT', stage)
    let data = await hospitalData.hospitalForms
    try {
      setStates({ ...data, ...affiliationType, ...stage, contacts, affiliationInfos })
      setForm(true)
    } catch (e) {
      setForm(false)
    }
  }

  const hanldeAddTolistValue = React.useCallback(
    data => {
      setAddToContacts([...contacts, data])
    },
    [form]
  )

  const hanldeAddToGroupsValue = React.useCallback(
    data => {
      setaffiliationInfos([...affiliationInfos, data])
    },
    [form]
  )

  if (form) {
    try {
      if (SendData('/facilities', states)) {
      }
      try {
        history.push('/hospital-affiliation-requirements')
      } catch (e) {
        console.log('errors')
      }
    } catch (e) {
      throw e
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Hospital'])

    return form ? <AffilationForm /> : undefined
  }, [])

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbs
        id='HeaderWithBreadCrumbs'
        HeaderTitle={headerTitle}
        stepDetails={stepDetails}
        currentStep={1}
      />

      <div id='AffiliationForm-div'>
        {/* <HospitalAffiliationContext.Provider value={{ form: hospitalForm, setHospitalForm }}> */}
        <AffilationForm
          AddToContacts={hanldeAddTolistValue}
          AddToGroups={hanldeAddToGroupsValue}
          SubmitForms={handleSubmit}
          contacts={contacts}
          affiliationInfos={affiliationInfos}
          history={history}
        />
        {/* </HospitalAffiliationContext.Provider> */}
      </div>
    </div>
  )
}

export default HospitalAffiliation
