import React from 'react'
import classes from './EditInputs.module.scss'
import moment from 'moment'
import {
  RenderInputFields,
  RenderDropDown,
  RenderDropDownWithLabel,
  RenderInputWithlabel,
  RenderInputMask
} from '../../../../../../../Components/InputsForProfiles/InputsForProfile'
import {
  TinStatus,
  processType,
  // classification,
  MedicalClassification
} from '../../../../../../../Constants/dropDownItems'

import { getTinStatusValue, checkArray } from '../../../../../../../Components/Utils/utils'

import FacilityAutoSuggest from '../../../../../../Components/FacilityDiscountAutoSuggest'

import YearsOperation from '../../../../../../Components/YearsOfOperation/YearsOfOperation'

import { PropTypes } from 'prop-types'

const FacilityAddress = props => {
  const {
    streetError,
    regionError,
    districtProvError,
    cityMunError,
    barangayError,
    zipError,
    streetRef,
    zipRef,
    regionValue,
    districtValue,
    cityValue,
    barangayValue,
    handleChange,
    regionItems,
    provincesItems,
    citiesItems,
    barangayItems,
    isFetching
  } = props

  let regionArr = []
  let provinceArr = []
  let citiesArr = []
  let barangayArr = []

  let bool = true

  checkArray(regionItems).map(items => {
    regionArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(provincesItems).map(items => {
    provinceArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(citiesItems).map(items => {
    citiesArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(barangayItems).map(items => {
    barangayArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  return (
    <div className={classes.facilityAddressFields}>
      <div id='streetVillage' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={streetError}
          name={'street'}
          inputRef={streetRef}
          label={'Street Village'}
          onChange={handleChange}
        />
      </div>
      <div id='Region' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={regionError}
          name={'region'}
          items={regionArr}
          value={regionValue}
          label={'Region'}
          onChange={handleChange}
        />
      </div>
      <div id='district/Province' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={districtProvError}
          name={'district'}
          items={provinceArr}
          value={isFetching['district'] ? 'Loading...' : districtValue}
          label={'District / Province'}
          onChange={handleChange}
          disabled={provinceArr.length > 0 ? false : bool}
        />
      </div>
      <div id='city/Municipality' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={cityMunError}
          name={'city'}
          items={citiesArr}
          value={isFetching['city'] ? 'Loading...' : cityValue}
          label={'City / Municipality'}
          onChange={handleChange}
          disabled={citiesArr.length > 0 ? false : bool}
        />
      </div>
      <div id='barangay' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={barangayError}
          items={barangayArr}
          name={'barangay'}
          label={'Barangay'}
          value={isFetching['barangay'] ? 'Loading...' : barangayValue}
          onChange={handleChange}
          disabled={barangayArr.length > 0 ? false : bool}
        />
      </div>
      <div id='zipCode' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={zipError}
          name={'zipCode'}
          inputRef={zipRef}
          label={'Zip Code'}
          type={'noSpecialChar'}
          onChange={handleChange}
        />
      </div>
    </div>
  )
}

const EditInputs = (handleChange, handleDropdown, errors, form, resultRef, setForm, setErrors, discountTypeData, regions, provinces, cities, barangays, isFetching) => {
  const HospitalInformation = [
    {
      id: 302,
      title: 'Medical Partner Id',
      value: form['medicalPartnerId']
    },
    {
      id: 3022,
      title: 'Former Name',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          name={'formerName'}
          inputRef={resultRef.formerName}
          onChange={handleChange}
        />
      )
    },
    {
      id: 303,
      title: 'Facility Address',
      value: (
        <FacilityAddress
          streetError={errors['street']}
          regionError={errors['region']}
          districtProvError={errors['district']}
          cityMunError={errors['city']}
          barangayError={errors['barangay']}
          zipError={errors['zipCode']}
          streetRef={resultRef.street}
          zipRef={resultRef.zipCode}
          regionValue={form['region']}
          districtValue={form['district']}
          cityValue={form['city']}
          barangayValue={form['barangay']}
          handleChange={handleDropdown}
          regionItems={regions}
          provincesItems={provinces}
          citiesItems={cities}
          barangayItems={barangays}
          isFetching={isFetching}
        />
      )
    },
    {
      id: 3033,
      title: 'Years of Operation',
      value: (
        <YearsOperation errors={errors} form={form} resultRef={resultRef} setForm={setForm} setErrors={setErrors} />
      )
    },
    {
      id: 305,
      title: 'Classification',
      value: (
        <RenderDropDown
          items={MedicalClassification}
          onChange={handleDropdown}
          name='classification'
          value={form['classification']}
        />
      )
    },
    {
      id: 3066,
      title: 'Clinic Schedule',
      value: (
        <RenderInputFields
          onChange={handleChange}
          inputRef={resultRef.clinicSchedule}
          error={errors['clinicSchedule']}
          name={'clinicSchedule'}
          value={form['clinicSchedule']}
        />
      )
    },
    {
      id: 306,
      title: 'PhilHealth Accreditation no.',
      value: (
        <RenderInputMask
          name={'philhealthNumber'}
          error={errors['philhealthNumber']}
          type={'dashAndNum'}
          value={form['philhealthNumber']}
          format={'####-#######-#'}
          form={form}
          setForm={setForm}
          setErrors={setErrors}
          errors={errors}
        />
      ),
      title2: 'PHILHEALTH EXP.',
      value2: (
        <RenderInputFields
          type={'date'}
          name={'philhealthExpiration'}
          onChange={handleChange}
          value={moment(form['philhealthExpiration'], 'YYYY-MM-DD').format('YYYY-MM-DD')}
        />
      )
    },
    {
      id: 307,
      title: 'CHECK PAYEE',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          name={'checkPayee'}
          error={errors['checkPayee']}
          onChange={handleChange}
          inputRef={resultRef.checkPayee}
        />
      )
    },
    {
      id: 308,
      title: 'PROCESS TYPE',
      value: (
        <RenderDropDown
          items={processType}
          name={'processType'}
          value={form['processType']}
          error={errors['processType']}
          onChange={handleDropdown}
        />
      )
    },
    {
      id: 309,
      title: 'CREDIT LIMIT',
      value: (
        <RenderInputFields
          type={'number'}
          name={'creditLimit'}
          inputRef={resultRef.creditLimit}
          onChange={handleChange}
        />
      )
    },
    {
      id: 310,
      title: 'PAYMENT TERMS',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          name={'paymentTerms'}
          inputRef={resultRef.paymentTerms}
          onChange={handleChange}
        />
      )
    },
    {
      id: 311,
      title: 'TIN STATUS',
      value: (
        <RenderDropDown
          items={TinStatus}
          name={'tinStatus'}
          error={errors['tinStatus']}
          onChange={handleChange}
          value={getTinStatusValue(form['tinStatus'])}
        />
      ),
      title2: 'TAX RATE',
      value2: (
        <RenderInputFields
          type={'numPercent'}
          error={errors['vatTaxRate']}
          name={'vatTaxRate'}
          inputRef={resultRef.vatTaxRate}
          onChange={handleChange}
          tags={'%'}
        />
      )
    },
    {
      id: 312,
      title: 'FACILITY discount',
      value: (
        <FacilityAutoSuggest
          data={discountTypeData}
          error={errors['facilityDiscount']}
          form={form}
          formValue={form['facilityDiscount']}
          setForm={setForm}
          name={'facilityDiscount'}
        />
      )
    },
    {
      id: 313,
      title: 'Date of affiliation',
      value: (
        <RenderInputFields
          type={'date'}
          name={'dateSubmitted'}
          onChange={handleChange}
          value={moment(form['dateSubmitted'], 'YYYY-MM-DD').format('YYYY-MM-DD')}
        />
      )
    },
    {
      id: 314,
      title: 'Date of disaffiliation',
      value: (
        <RenderInputFields
          type={'date'}
          name={'dateOfDisaffiliation'}
          onChange={handleChange}
          value={moment(form['dateOfDisaffiliation'], 'YYYY-MM-DD').format('YYYY-MM-DD')}
        />
      )
    },
    {
      id: 315,
      title: 'Remarks',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          inputRef={resultRef.specialInstructions}
          name={'specialInstructions'}
          onChange={handleChange}
          error={errors['specialInstructions']}
        />
      )
    }
  ]

  return HospitalInformation
}

export default EditInputs

FacilityAddress.propTypes = {
  streetError: PropTypes.string,
  regionError: PropTypes.string,
  districtProvError: PropTypes.string,
  cityMunError: PropTypes.string,
  barangayError: PropTypes.string,
  zipError: PropTypes.any,
  regionValue: PropTypes.any,
  districtValue: PropTypes.any,
  cityValue: PropTypes.any,
  barangayValue: PropTypes.any,
  handleChange: PropTypes.func.isRequired
}
