import AddressChaining from '../../../../../../../../../Constant/chaining-address'
import ProfileService from '../../../../../../../../../Services/Api/Partner/ProfileService'
import { YearDateFormat, DateErrorHandler, fetchAddress, fetchRegion } from '../../../../../../Components/Utils/utils'
import getNestedObjects from '../../../../../../Components/getNestedObjects/getNestedObjects'

const set = (form, data, dateFormat, setForm, setBarangays, setCities, setProvinces, setRegions) => {
  const newForm = { ...form }
  const address = data ? (data.address ? data.address : {}) : {}
  // const operation = data.operation ? data.operation : ''
  const philhealth = data ? (data.philhealth ? data.philhealth : {}) : {}
  const dateOAffiliation = data.affiliation_status ? data.affiliation_status.date_submitted : ''
  const facilityAssignments = data
    ? data.facility_assignment
      ? data.facility_assignment.assignments
        ? data.facility_assignment.assignments
        : []
      : []
    : []

  /** Setting of Edit Form values
   */
  newForm.medicalPartnerId = getNestedObjects(data, ['id'])
  newForm.name = getNestedObjects(data, ['name'])
  newForm.clinicName = getNestedObjects(data, ['name'])
  newForm.formerName = getNestedObjects(data, ['former_name'])
  newForm.assignments = facilityAssignments
  newForm.reason = getNestedObjects(data, ['reason'])
  newForm.status = getNestedObjects(data, ['status'])
  newForm.exclusiveTo = getNestedObjects(data, ['facility_assignment', 'exclusive_to'])
  newForm.tvs = getNestedObjects(data, ['tvs'])
  newForm.years = getNestedObjects(data, ['operation', 'years'])
  newForm.months = getNestedObjects(data, ['operation', 'months'])
  newForm.street = getNestedObjects(data, ['address', 'street'])
  newForm.region = getNestedObjects(data, ['address', 'region'])
  newForm.district = getNestedObjects(data, ['address', 'district'])
  newForm.city = getNestedObjects(data, ['address', 'city'])
  newForm.barangay = getNestedObjects(data, ['address', 'barangay'])
  newForm.zipCode = getNestedObjects(data, ['address', 'zip_code'])
  newForm.classification = getNestedObjects(data, ['classification'])
  newForm.clinicSchedule = getNestedObjects(data, ['clinic_schedule'])
  newForm.philhealthNumber = getNestedObjects(data, ['philhealth', 'accreditation_number'])
  newForm.philhealthExpiration = YearDateFormat(philhealth.expiration_date)
  newForm.checkPayee = getNestedObjects(data, ['treasury', 'payee'])
  newForm.processType = getNestedObjects(data, ['process_type'])
  newForm.creditLimit = getNestedObjects(data, ['treasury', 'credit_limit'])
  newForm.paymentTerms = getNestedObjects(data, ['treasury', 'payment_terms'])
  newForm.tinStatus = getNestedObjects(data, ['treasury', 'tin_status'])
  newForm.vatTaxRate = getNestedObjects(data, ['treasury', 'vat', 'tax_rate'])
  newForm.facilityDiscount = getNestedObjects(data, ['treasury', 'discount'])
  newForm.dateSubmitted = dateOAffiliation ? YearDateFormat(dateOAffiliation) : ''
  newForm.dateOfDisaffiliation = data.disaffiliation
    ? data.disaffiliation.date
      ? YearDateFormat(data.disaffiliation.date)
      : ''
    : ''
  newForm.specialInstructions = getNestedObjects(data, ['treasury', 'special_instruction'])

  AddressChaining.getProvince(address.region)
  AddressChaining.getCityMunicipality(address.district)
  AddressChaining.getBarangays(address.city)
  fetchAddress(newForm, setBarangays, setCities, setProvinces)
  fetchRegion(setRegions)
  setForm(newForm)
}

const Save = async (form, profile) => {
  const newForm = { ...form }
  // newForm.name = newForm.formerName

  let obj = {}
  // const address = profile ? profile.address : ''
  const email = profile ? profile.email : ''
  // const dateSubmitted = profile ? (profile.affiliation_status ? profile.affiliation_status.date_submitted : '') : ''
  const dohNumber = profile ? (profile.license ? (profile.license.doh ? profile.license.doh.number : '') : '') : ''
  const dohType = profile ? (profile.license ? (profile.license.doh ? profile.license.doh.license_type : '') : '') : ''
  const dohExpiration = profile
    ? profile.license
      ? profile.license.doh
        ? profile.license.doh.license_exp
        : ''
      : ''
    : ''
  // const treasury = profile ? profile.treasury : ''
  /** Builder of request payload. */
  obj.id = profile.id
  obj.name = newForm['clinicName']
  obj.assignments = newForm['assignments']
  obj.exclusiveTo = newForm['exclusiveTo']
  obj.status = newForm['status']
  obj.tvs = newForm['tvs']
  obj.dateOfAffiliation = DateErrorHandler(newForm['dateSubmitted'])
  obj.dateOfDisaffiliation = DateErrorHandler(newForm['dateOfDisaffiliation'])
  obj.reason = newForm['reason']
  obj.specialInstructions = ''
  obj.years = newForm['years']
  obj.months = newForm['months']
  obj.dohNumber = dohNumber
  obj.philhealthNumber = newForm['philhealthNumber']
  obj.philhealthExpiration = DateErrorHandler(newForm['philhealthExpiration'])
  obj.creditLimit = newForm['creditLimit']
  obj.paymentTerms = newForm['paymentTerms']
  obj.tinStatus = newForm['tinStatus']
  obj.facilityDiscount = newForm['facilityDiscount']
  obj.payee = newForm['checkPayee']
  obj.classification = newForm['classification']
  obj.nonVat = ''
  obj.vatTaxRate = newForm['vatTaxRate']
  obj.taxRate = newForm['vatTaxRate']
  obj.processType = newForm['processType']
  obj.district = newForm['district']
  obj.street = newForm['street']
  obj.region = newForm['region']
  obj.barangay = newForm['barangay']
  obj.city = newForm['city']
  obj.zipCode = newForm['zipCode']
  obj.clinicSchedule = newForm['clinicSchedule']
  obj.formerName = newForm['formerName']
  obj.specialInstructions = newForm['specialInstructions']

  const {
    data: { data }
  } = await ProfileService.updateMedicalClinicProfile(profile.id, obj)

  return data
}

export { set, Save }
