import React from 'react'
import Button from '@material-ui/core/Button'
// import history from '../../../../../Components/History/History'
import CustomAlertSnackBar from '../CustomSnackbar/CustomAlertSnackbar'
import YesOrNoDialogBox from '../../../../../Components/YesOrNoDialogBox/YesOrNoDialogBox'
// import ApplicationRequirements from '../../../Partner/Hospital/Pages/ApplicationRequirements/ApplicationRequirements'
import classes from './FooterWithButtons.module.scss'

const FooterWithButtons = props => {
  const { footerStatus, FacilityformPage, disableButton, onClickSave, isSuccess = false, setIsSuccess, onClickProceed, isSubmitted, proceedSubmitForm } = props
  let BackgroundChange
  const PrevPage = e => {
    e.preventDefault()
    proceedSubmitForm()
    props.history.push('/medical-partner-enrollment')
  }

  if (disableButton) {
    BackgroundChange = classes.backgroundChange
  }

  return (
    <React.Fragment>
      <CustomAlertSnackBar isSave={isSuccess} setIsSave={setIsSuccess} />
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div>
            <YesOrNoDialogBox PrevPage={PrevPage} />
          </div>
          <div />
          <div id-='TwoButtons'>
            <div
              id='SaveButton'
              onClick={onClickSave}
            >
              <Button variant='contained' className={isSubmitted ? classes.disabledButton : classes.SaveButton}>
                <div style={{ fontSize: '12px' }}> Save</div>
                {/* <div style={{ fontSize: '5px' }}>Auto Saved 5 minutes ago</div> */}
              </Button>
            </div>
            <div id='ProceedButton'>
              <Button
                variant='contained'
                onClick={onClickProceed}
                disabled={disableButton}
                className={BackgroundChange || classes.ProceedButton}
              >
                Proceed
              </Button>
            </div>
          </div>
        </span>
        <div style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}>
          {footerStatus} Affiliation Form 1 out of 2
        </div>
      </div>
    </React.Fragment>
  )
}

export default FooterWithButtons
