import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import classes from './AfiiliationTableWithInput.module.scss'
import styled from 'styled-components'
import EditIcon from '@material-ui/icons/Edit'
import TrashIcon from '@material-ui/icons/Delete'
import InlineForm from './InlineText/InlineText'
import orderBy from 'lodash/orderBy'
import { useSelector } from 'react-redux'

const row = (x, i, header, handleRemove, startEditing, editIdx, handleSave, stopEditing, affiliationType) => {
  const currentlyEditing = editIdx === i
  return currentlyEditing ? (
    <StyledTableRow key={`inline-form-${i}`} className={classes.tableRowEdit}>
      <StyledTableCell className={classes.tableCellEdit} key={`trc-${i}`}>
        <InlineForm handleSave={handleSave} header={header} x={x} i={i} stopEditing={stopEditing} />
      </StyledTableCell>
    </StyledTableRow>
  ) : (
    <StyledTableRow key={`tr-${i}`} className={classes.inlineNormal}>
      {header.map((y, k) => (
        <StyledTableCell className={classes.inlineTableCell} key={`trc-${k}`} align='left' component='th' scope='row'>
          {x[y.prop]}
        </StyledTableCell>
      ))}
      {affiliationType['affiliationStatus'] !== 'Submitted' && <StyledTableCell className={classes.editAndDelete}>
        <EditIcon onClick={() => startEditing(i)} />
        <TrashIcon onClick={() => handleRemove(i)} />
      </StyledTableCell>}
    </StyledTableRow>
  )
}

const AfiiliationTableWithInput = props => {
  const { handleValue, dataheader, onClick, inputField } = props

  const { affiliationType } = useSelector(state => state.affiliationType)

  const AddToGroup = React.useMemo(() => handleValue, [handleValue])

  const [data, setData] = React.useState()
  const [editIdx, setEditIdx] = React.useState(-1)

  React.useEffect(() => {
    setData(AddToGroup)
  }, [AddToGroup])

  const handleRemove = React.useCallback(
    i => {
      setData(data => data.filter((row, j) => j !== i))
      props.HandleRemove(i)
    },
    [data]
  )

  const startEditing = i => {
    setEditIdx(i)
  }

  const stopEditing = () => {
    setEditIdx(-1)
  }

  const handleSave = (i, x) => {
    setData(data => data.map((row, j) => (j === i ? x : row)))
    props.HandleEdit(i, x)
    stopEditing()
  }

  React.useEffect(() => {
    if (typeof props.AddToGroupsArr === 'function') {
      props.AddToGroupsArr(data)
    }
  }, [data])

  let datass = React.useMemo(() => orderBy(data), [data])

  let header = dataheader

  return (
    <div className={classes.tableContainer}>
      <TableContainer>
        <Table className={classes.table} aria-label='customized table'>
          <TableHead>
            <TableRow>
              <StyledTableCell>
                <div className={classes.InputConatiner}>
                  <span className={classes.InputFieldHeader}>{inputField}</span>
                  <span className={classes.AddTolist}>
                    <HoverText onClick={onClick}>+Add to list</HoverText>
                  </span>
                </div>
              </StyledTableCell>
            </TableRow>
          </TableHead>
          <TableBody className={classes.TableBody}>
            {datass.map((x, i) => row(x, i, header, handleRemove, startEditing, editIdx, handleSave, stopEditing, affiliationType))}
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  )
}

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.lightgrey,
    color: theme.palette.common.lighgrey
  },
  body: {
    fontSize: 14
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default
    },
    '&:nth-of-type(even)': {
      backgroundColor: '#f5f7fb'
    }
  }
}))(TableRow)

const HoverText = styled.p`
  :hover {
    text-decoration: underlined;
    cursor: pointer;
    font-weight: bolder;
  }
`
export default React.memo(AfiiliationTableWithInput)
