import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { createMuiTheme } from '@material-ui/core/styles'
import { ThemeProvider } from '@material-ui/styles'
// Routes by role
// import SpecialOtherServicesRoutes from './SpecialAndOtherServices/Routes'
import PartnerOIC from './OIC/Routes'
import AffiliationFormEncoderRoutes from './AffiliationFormEncoder/Routes'
import AffiliationApproverRoutes from './AffiliationApprover/Routes'
import ProfileMaintainerRoutes from '../Partner/ProfileMaintainer/Routes'
import MaintenanceTableMaintainerRoutes from '../Partner/TableMaintainer/Routes'
import Reports from './Reports/Routes'
import ContractManager from './ContractManager/Routes'
import GlobalFonts from 'Fonts/Fonts'

const theme = createMuiTheme({
  typography: {
    fontFamily: ['NunitoSans'].join(',')
  }
})

const PartnerModule = () => {
  const [userRole, setUserRole] = React.useState('')
  const [isLoading, setIsLoading] = React.useState(true)

  React.useEffect(() => {
    if (localStorage.getItem('role')) {
      setUserRole(localStorage.getItem('role'))
    } else {
      setUserRole('AFFILIATION_FORM_ENCODER')
    }

    document.title = 'HIMS - Partner'
    // setUserRole('OCULAR_INSPECTION_CHECKLIST_PROCESSOR')
    // setUserRole('MEDICAL_PARTNER_PROFILE')

    setIsLoading(false)
  }, [])

  return (
    <React.Fragment>
      <GlobalFonts />
      {/* <Provider store={Store}>
        <PartnerSideBarComponents />
      </Provider> */}
      {!isLoading && (
        <ThemeProvider theme={theme}>
          <Switch>
            {/* {userRole === 'SPECIAL' && <Route path='/' component={SpecialOtherServicesRoutes} />} */}
            {userRole === 'AFFILIATION_FORM_ENCODER' && <Route path='/' component={AffiliationFormEncoderRoutes} />}
            {userRole === 'AFFILIATION_APPROVER' && <Route path='/' component={AffiliationApproverRoutes} />}
            {userRole === 'OCULAR_INSPECTION_CHECKLIST_PROCESSOR' && (
              <Route path='/' render={props => <PartnerOIC {...props} role='OICProcessor' />} />
            )}
            {userRole === 'OCULAR_INSPECTION_CHECKLIST_APPROVER' && (
              <Route path='/' render={props => <PartnerOIC {...props} role='OICApprover' />} />
            )}
            {userRole === 'MEDICAL_PARTNER_PROFILE_MAINTAINER' && (
              <Route path='/' component={ProfileMaintainerRoutes} />
            )}
            {userRole === 'MAINTENANCE_TABLE_MAINTAINER' && (
              <Route path='/' component={MaintenanceTableMaintainerRoutes} />
            )}
            {userRole === 'REPORTS' && <Route path='/' component={Reports} />}
            {userRole === 'CONTRACT_MANAGER' && <Route path='/' component={ContractManager} />}
            {userRole === 'MAINTENANCE_TABLE_MAINTAINER' && (
              <Route path='/' component={MaintenanceTableMaintainerRoutes} />
            )}
            {userRole === 'REPORTS_MANAGER' && <Route path='/' component={Reports} />}
          </Switch>
        </ThemeProvider>
      )}
    </React.Fragment>
  )
}

export default PartnerModule
