import AddressChainingService from 'Services/Api/Partner/AddressChainingService'

const address = require('./philippine-address.json')

export default class AddressChaining {
  provinces = []
  citiesMunicipalities = []
  barangays = []

  static cityArr = []
  static barangayArr = []
  static provinceArr = []
  static regions = [
    { id: 100, text: 'REGION I (ILOCOS REGION)', value: 'REGION I' },
    { id: 101, text: 'REGION II (CAGAYAN VALLEY)', value: 'REGION II' },
    { id: 102, text: 'REGION III (CENTRAL LUZON)', value: 'REGION III' },
    { id: 103, text: 'REGION IV-A (CALABARZON)', value: 'REGION IV-A' },
    { id: 104, text: 'REGION IV-B (MIMAROPA)', value: 'REGION IV-B' },
    { id: 105, text: 'REGION V (BICOL REGION)', value: 'REGION V' },
    { id: 106, text: 'REGION VI (WESTERN VISAYAS)', value: 'REGION VI' },
    { id: 107, text: 'REGION VII (CENTRAL VISAYAS)', value: 'REGION VII' },
    { id: 108, text: 'REGION VIII (EASTERN VISAYAS)', value: 'REGION VIII' },
    { id: 109, text: 'REGION IX (ZAMBOANGA PENINSULA)', value: 'REGION IX' },
    { id: 110, text: 'REGION X (NORTHERN MINDANAO)', value: 'REGION X' },
    { id: 111, text: 'REGION XI (DAVAO REGION)', value: 'REGION XI' },
    { id: 112, text: 'REGION XII (SOCCSKSARGEN)', value: 'REGION XII' },
    { id: 113, text: 'NATIONAL CAPITAL REGION (NCR)', value: 'NCR' },
    { id: 114, text: 'CORDILLERA ADMINISTRATIVE REGION (CAR)', value: 'CAR' },
    {
      id: 115,
      text: 'AUTONOMOUS REGION IN MUSLIM MINDANAO (ARMM)',
      value: 'BARMM',
    },
    { id: 116, text: 'REGION XIII (Caraga)', value: 'REGION XIII' },
  ]

  static getProvince (region) {
    this.provinces = address[region] ? address[region].province_list : {}
    let data = transformToArray(this.provinces)
    this.provinceArr = formatArray(data, 'prvnc')
    this.cityArr = []
    this.barangayArr = []
  }

  static getCityMunicipality (cityMunicipality) {
    if (cityMunicipality) {
      this.citiesMunicipalities = this.provinces[cityMunicipality]
        ? this.provinces[cityMunicipality].municipality_list
        : {}
      let data = transformToArray(
        this.provinces[cityMunicipality] ? this.provinces[cityMunicipality].municipality_list : {}
      )
      this.cityArr = formatArray(data, 'cty')
      this.barangayArr = []
    }
  }

  static getBarangays (cityMunicipality) {
    if (cityMunicipality) {
      this.barangays = this.citiesMunicipalities[cityMunicipality]
        ? this.citiesMunicipalities[cityMunicipality].barangay_list
        : {}
      this.barangayArr = formatArray(this.barangays, 'brgy')
    }
  }

  static async fetchAddress({category = 'region', field, value}) {
    try {
      const {data: { data: { body } }} = await AddressChainingService.getAddress({category, field, value})
      return body
    } catch (error) {
      console.log('error in region list', error)
    }
  }
}

function transformToArray (obj) {
  let data = []
  data = JSON.stringify(Object.keys(obj))
  return JSON.parse(data)
}

function formatArray (arr = [], prefix) {
  let variable = []
  let data = JSON.parse(JSON.stringify(arr))
  for (let i = 0; i < data.length; i++) {
    let obj = { id: prefix + i, text: arr[i], value: arr[i] }
    variable.push(obj)
  }
  return variable
}

