import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { Provider } from 'react-redux'

import ProfileList from '../../Components/ProfileList/ProfileList'
import Partner from './Pages/PartnerInformation'
import ContactDetailsViewer from './Pages/ViewerContract/ContractDetailsViewer'
import ListOfAffiliatedDoctors from '../Components/ListOfAffiliatedDoctors/ListOfAffiliatedDoctors'
import ContactDetails from '../../Components/ContactDetails/ContactDetails'
import OtherDocuments from './Pages/OtherDocuments/OtherDocuments'
import placeholderImage from '../../Assets/Icons/affinitydental.jpg'
import StandardRates from '../Components/StandardRates/StandardRates'
import Store from '../../../Store'
import DentalClinicProfileParamsContext from './Pages/context/DentalClinicProfileParamsContext'
import FacilityDiscounts from '../FacilityDiscount/FacilityDiscountPage'

export default function DentalClinicProfile () {
  const [isOpenAffiliatedDoctorsModal, setIsOpenAffiliatedDoctorsModal] = React.useState(false)
  const [DentalClinicFacilityName, setDentalClinicName] = React.useState('dental clinic facility')
  const [cityName, setCityName] = React.useState('Alabang')
  const [changeName, setChangeName] = React.useState(null)
  const [isEdit, setIsEdit] = React.useState(false)
  const [currentStatus, setCurrentStatus] = React.useState('')
  const [paramId, setParamId] = React.useState('')
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [open, setOpen] = React.useState(false)

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'DentalClinicPartnerProfile', 'Clinic'])
    setIsOpenAffiliatedDoctorsModal(false)
    return () => {
      setIsOpenAffiliatedDoctorsModal(false)
    }
  }, [cityName, DentalClinicFacilityName])
  const breadcrumbs = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    DentalClinicPartnerProfile: {
      text: 'DENTAL CLINIC PARTNER PROFILE',
      to: '/dentalClinicAffiliationForm'
    },
    Clinic: {
      text: `${DentalClinicFacilityName} - ${cityName}`,
      to: '/dentalClinicAffiliationForm'
    }
  }

  const row = [
    {
      to: `/DentalClinicProfile/PartnerInformation/${paramId}`,
      name: ' Partner Information',
      path: '/DentalClinicProfile/PartnerInformation/:id',
      click: () => setIsToUpdate(false),
      importPage: (
        <DentalClinicProfileParamsContext.Provider
          value={{
            setParamId,
            paramId,
            isToUpdate,
            setIsToUpdate,
            setDentalClinicName,
            setCityName,
            setCurrentStatus,
            setChangeName,
            isEdit,
            setIsEdit,
            setOpen: setIsOpenAffiliatedDoctorsModal
          }}
        >
          <Partner />
        </DentalClinicProfileParamsContext.Provider>
      )
    },
    {
      to: `/DentalClinicProfile/ContractDetails/${paramId}`,
      name: 'Contract Details',
      path: '/DentalClinicProfile/ContractDetails/:paramId',
      importPage: <ContactDetailsViewer paramId={paramId} />
    },
    {
      to: `/DentalClinicProfile/StandardRates/${paramId}`,
      name: 'Standard Rates',
      path: '/DentalClinicProfile/StandardRates/:id',
      importPage: (
        <StandardRates
          paramId={paramId}
          open={open}
          setOpen={setOpen}
          pathName={`/DentalClinicProfile/PartnerInformation/${paramId}`}
        />
      ),
      open: setOpen
    },
    {
      to: `/DentalClinicProfile/FacilityDiscounts/${paramId}`,
      name: 'Facility Discounts',
      path: '/DentalClinicProfile/FacilityDiscounts/:paramId',
      importPage: <FacilityDiscounts open={open} setOpen={setOpen} paramId={paramId} />,
      open: setOpen
    },
    {
      to: `/DentalClinicProfile/ListOfAffiliatedDoctors/${paramId}`,
      name: 'List Of Affiliated Doctors',
      path: '/DentalClinicProfile/ListOfAffiliatedDoctors/:facilityId',
      open: setIsOpenAffiliatedDoctorsModal
    },
    {
      to: `/DentalClinicProfile/ContactDetails/${paramId}`,
      name: 'Contact Details',
      path: '/DentalClinicProfile/ContactDetails/:id',
      importPage: <ContactDetails paramId={paramId} />
    },
    {
      to: `/DentalClinicProfile/OtherDocuments/${paramId}`,
      name: 'Other Documents',
      path: '/DentalClinicProfile/OtherDocuments/:paramId',
      importPage: <OtherDocuments paramId={paramId} />
    }
  ]

  return (
    <Provider store={Store}>
      <ProfileList
        ListItems={row}
        HeaderTitle={isToUpdate ? changeName : DentalClinicFacilityName}
        leftSideImage={placeholderImage}
        status={currentStatus.toUpperCase()}
      />
      {isOpenAffiliatedDoctorsModal && (
        <ListOfAffiliatedDoctors
          open={isOpenAffiliatedDoctorsModal}
          setOpen={setIsOpenAffiliatedDoctorsModal}
          paramId={paramId}
          endPoint={'/affiliatedDoctors'}
        />
      )}
    </Provider>
  )
}
