import React from 'react'
import classes from './EditInputs.module.scss'
import { YearDateFormat, getTinStatusValue, checkArray } from '../../../../../../../Components/Utils/utils'
import {
  RenderInputFields,
  RenderDropDown,
  RenderDropDownWithLabel,
  RenderInputWithlabel,
  RenderInputMask
} from '../../../../../../../Components/InputsForProfiles/InputsForProfile'
import { TinStatus, processType, classification } from '../../../../../../../Constants/dropDownItems'

import FacilityAutoSuggest from '../../../../../../Components/FacilityDiscountAutoSuggest'

import { PropTypes } from 'prop-types'

const FacilityAddress = props => {
  const {
    streetError,
    regionError,
    districtProvError,
    cityMunError,
    barangayError,
    zipError,
    streetRef,
    zipRef,
    regionValue,
    districtValue,
    cityValue,
    barangayValue,
    handleChange,
    regionItems,
    provincesItems,
    citiesItems,
    barangayItems,
    isFetching
  } = props

  let regionArr = []
  let provinceArr = []
  let citiesArr = []
  let barangayArr = []

  let bool = true

  checkArray(regionItems).map(items => {
    regionArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(provincesItems).map(items => {
    provinceArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(citiesItems).map(items => {
    citiesArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(barangayItems).map(items => {
    barangayArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  return (
    <div className={classes.facilityAddressFields}>
      <div id='streetVillage' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={streetError}
          name={'street'}
          inputRef={streetRef}
          label={'Street Village'}
          onChange={handleChange}
        />
      </div>
      <div id='Region' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={regionError}
          name={'region'}
          items={regionArr}
          value={regionValue}
          label={'Region'}
          onChange={handleChange}
        />
      </div>
      <div id='district/Province' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={districtProvError}
          name={'district'}
          items={provinceArr}
          value={isFetching['district'] ? 'Loading...' : districtValue}
          label={'District / Province'}
          onChange={handleChange}
          disabled={provinceArr.length > 0 ? false : bool}
        />
      </div>
      <div id='city/Municipality' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={cityMunError}
          name={'city'}
          items={citiesArr}
          value={isFetching['city'] ? 'Loading...' : cityValue}
          label={'City / Municipality'}
          onChange={handleChange}
          disabled={citiesArr.length > 0 ? false : bool}
        />
      </div>
      <div id='barangay' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={barangayError}
          items={barangayArr}
          name={'barangay'}
          label={'Barangay'}
          value={isFetching['barangay'] ? 'Loading...' : barangayValue}
          onChange={handleChange}
          disabled={barangayArr.length > 0 ? false : bool}
        />
      </div>
      <div id='zipCode' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={zipError}
          name={'zipCode'}
          inputRef={zipRef}
          label={'Zip Code'}
          type={'noSpecialChar'}
          onChange={handleChange}
        />
      </div>
    </div>
  )
}

const EditInputs = (
  form,
  errors,
  setForm,
  setErrors,
  handleChange,
  handleDropdown,
  handleDatePicker,
  noOfBeds,
  formerName,
  facilityAddressStreet,
  facilityAddressZipcode,
  checkPayee,
  creditLimit,
  paymentTerms,
  taxRate,
  facilityDiscount,
  specialInstructions,
  discountTypeData, regions, provinces, cities, barangays, isFetching
) => {
  const HospitalInformation = [
    {
      id: 302,
      title: 'Medical Partner Id',
      value: form['medicalPartnerId']
    },
    {
      id: 3022,
      title: 'Former Name',
      value: (
        <RenderInputFields type={'noSpecialChar'} name={'formerName'} inputRef={formerName} onChange={handleChange} />
      )
    },
    {
      id: 303,
      title: 'Facility Address',
      value: (
        <FacilityAddress
          streetError={errors['street']}
          regionError={errors['region']}
          districtProvError={errors['district']}
          cityMunError={errors['city']}
          barangayError={errors['barangay']}
          zipError={errors['zipCode']}
          streetRef={facilityAddressStreet}
          zipRef={facilityAddressZipcode}
          regionValue={form['region']}
          districtValue={form['district']}
          cityValue={form['city']}
          barangayValue={form['barangay']}
          handleChange={handleDropdown}
          regionItems={regions}
          provincesItems={provinces}
          citiesItems={cities}
          barangayItems={barangays}
          isFetching={isFetching}
        />
      )
    },
    {
      id: 304,
      title: 'Classification',
      value: (
        <RenderDropDown
          items={classification}
          onChange={handleDropdown}
          name='classification'
          value={form['classification']}
          error={errors['classification']}
        />
      )
    },
    {
      id: 305,
      title: 'No. of Beds',
      value: (
        <RenderInputFields
          type={'integerOnly'}
          error={errors['noOfBeds']}
          name={'noOfBeds'}
          inputRef={noOfBeds}
          onChange={handleChange}
        />
      )
    },

    {
      id: 306,
      title: 'PhilHealth Accreditation no.',
      value: (
        <RenderInputMask
          name={'philhealthNumber'}
          error={errors['philhealthNumber']}
          type={'integerOnly'}
          value={form['philhealthNumber']}
          format={'####-#######-#'}
          form={form}
          setForm={setForm}
          setErrors={setErrors}
          errors={errors}
        />
      ),
      title2: 'PHILHEALTH EXP.',
      value2: (
        <RenderInputFields
          type={'date'}
          name={'philhealthExpiration'}
          error={errors['philhealthExpiration']}
          onChange={handleDatePicker}
          value={YearDateFormat(form['philhealthExpiration'])}
        />
      )
    },
    {
      id: 307,
      title: 'CHECK PAYEE',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          name={'payee'}
          onChange={handleChange}
          error={errors['payee']}
          inputRef={checkPayee}
        />
      )
    },
    {
      id: 308,
      title: 'PROCESS TYPE',
      value: (
        <RenderDropDown
          items={processType}
          name={'processType'}
          value={form['processType']}
          onChange={handleDropdown}
          error={errors['processType']}
        />
      )
    },
    {
      id: 309,
      title: 'CREDIT LIMIT',
      value: (
        <RenderInputFields
          type={'integerOnly'}
          name={'creditLimit'}
          inputRef={creditLimit}
          error={errors['creditLimit']}
          onChange={handleChange}
        />
      )
    },
    {
      id: 310,
      title: 'PAYMENT TERMS',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          name={'paymentTerms'}
          inputRef={paymentTerms}
          onChange={handleChange}
          error={errors['paymentTerms']}
        />
      )
    },
    {
      id: 311,
      title: 'TIN STATUS',
      value: (
        <RenderDropDown
          items={TinStatus}
          name={'tinStatus'}
          error={errors['tinStatus']}
          onChange={handleChange}
          value={getTinStatusValue(form['tinStatus'])}
        />
      ),
      title2: 'TAX RATE',
      value2: (
        <RenderInputFields
          type={'number'}
          error={errors['vatTaxRate']}
          name={'vatTaxRate'}
          inputRef={taxRate}
          onChange={handleChange}
          tags={'%'}
        />
      )
    },
    {
      id: 312,
      title: 'FACILITY discount',
      value: (
        <FacilityAutoSuggest
          data={discountTypeData}
          error={errors['facilityDiscount']}
          form={form}
          formValue={form['facilityDiscount']}
          setForm={setForm}
          name={'facilityDiscount'}
        />
      )
    },
    {
      id: 313,
      title: 'Date of affiliation',
      value: (
        <RenderInputFields
          type={'date'}
          name={'dateOfAffiliation'}
          onChange={handleDatePicker}
          value={YearDateFormat(form['dateOfAffiliation'])}
          error={errors['dateOfAffiliation']}
        />
      )
    },
    {
      id: 314,
      title: 'Date of disaffiliation',
      value: (
        <RenderInputFields
          type={'date'}
          name={'dateOfDisaffiliation'}
          onChange={handleDatePicker}
          error={errors['dateOfDisaffiliation']}
          value={YearDateFormat(form['dateOfDisaffiliation'])}
        />
      )
    },
    {
      id: 315,
      title: 'Remarks',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          inputRef={specialInstructions}
          name={'specialInstructions'}
          onChange={handleChange}
          error={errors['specialInstructions']}
        />
      )
    }
  ]

  return HospitalInformation
}

export default EditInputs

FacilityAddress.propTypes = {
  streetError: PropTypes.any,
  regionError: PropTypes.any,
  districtProvError: PropTypes.any,
  cityMunError: PropTypes.any,
  barangayError: PropTypes.any,
  zipError: PropTypes.any,
  regionValue: PropTypes.any,
  districtValue: PropTypes.any,
  cityValue: PropTypes.any,
  barangayValue: PropTypes.any,
  handleChange: PropTypes.func.isRequired
}
