import React from 'react'
import classes from './InputsForProfile.module.scss'
import NativeSelect from '@material-ui/core/NativeSelect'
import InputBase from '@material-ui/core/InputBase'
import NumberFormat from 'react-number-format'

let specialChar = RegExp(/[!@#$%^&*(),.?":;'`~_{}|<>]/)
let forEmail = RegExp(/[!#$%^&*(),?":;'`~{}|<>]([[]]*)/)
let numAndPercentage = RegExp(/[!@#$^&*(),?":;'`~_{}|<>]/)
let numbersOnly = RegExp(/^[\d\b]+$/)
let lettersOnly = RegExp(/^[a-z]+$/)
// let zeroTo100 = RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)

const DisableCharacters = (e, type) => {
  switch (type) {
    case 'number':
      if (
        e.key === 'e' ||
        e.key === 'E' ||
        e.keyCode === 189 ||
        e.keyCode === 111 ||
        e.keyCode === 106 ||
        e.keyCode === 107 ||
        e.keyCode === 109
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'integerOnly':
      if (
        e.key === 'e' ||
        e.key === 'E' ||
        e.keyCode === 190 ||
        e.keyCode === 189 ||
        e.keyCode === 111 ||
        e.keyCode === 106 ||
        e.keyCode === 107 ||
        e.keyCode === 109 ||
        e.keyCode === 187 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220 ||
        e.keyCode === 222 ||
        specialChar.test(e.key) ||
        lettersOnly.test(e.key)
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'noSpecialChar':
      if (
        specialChar.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 191 ||
        e.keyCode === 189 ||
        e.keyCode === 219 ||
        e.keyCode === 221 ||
        e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'dashAndNum':
      if (
        specialChar.test(e.key) ||
        lettersOnly.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    case 'lettersOnly':
      if (
        specialChar.test(e.key) ||
        numbersOnly.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 189 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }
    case 'email':
      if (
        forEmail.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 189 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220
      ) {
        return e.preventDefault()
      } else {
        return {}
      }
    case 'numPercent':
      if (
        numAndPercentage.test(e.key) ||
        lettersOnly.test(e.key) ||
        e.keyCode === 187 ||
        e.keyCode === 219 ||
        e.keyCode === 191 ||
        e.keyCode === 221 ||
        e.keyCode === 220 ||
        e.keyCode === 189
      ) {
        return e.preventDefault()
      } else {
        return {}
      }

    default:
      return {}
  }
}

const RenderInputMaskField = props => {
  const { error, value, name, type, autoFocus, onBlur, form, setForm, setErrors, errors, format } = props

  const HandleChange = e => {
    const { name, value } = e.target
    const newValue = value.trim().replace(/-/g, '')
    const newForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    newForm[name] = newValue.trim()
    setForm(newForm)
  }

  return (
    <React.Fragment>
      <NumberFormat
        value={value}
        name={name}
        type={type}
        autoFocus={autoFocus}
        onChange={HandleChange}
        onBlur={onBlur}
        className={error ? classes.InputFieldsErrorMask : classes.InputFieldsMask}
        autoComplete='off'
        onKeyDown={e => DisableCharacters(e, type)}
        format={format}
        allowEmptyFormatting
      />
      <div className={classes.error}> {error && error}</div>
    </React.Fragment>
  )
}

const RenderInputWithTagsComponent = props => {
  const {
    error,
    value,
    name,
    type,
    autoFocus,
    onBlur,
    form,
    setForm,
    setErrors,
    errors,
    format,
    decimalSeparator,
    tags,
    thousandSeparator
  } = props

  const HandleChange = e => {
    // if (typeof onChange === 'function') onChange({ target: { value: e.floatValue } })
    const { name, value } = e.target
    const newValue = value.trim()
    const newForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    newForm[name] = newValue.trim()
    setForm(newForm)
  }

  return (
    <React.Fragment>
      <div id='root' className={classes.root}>
        <div id='numberFormat' className={classes.numberFormat}>
          <NumberFormat
            value={value}
            name={name}
            type={type}
            autoFocus={autoFocus}
            onChange={HandleChange}
            onBlur={onBlur}
            isNumericString
            displayType={'input'}
            thousandSeparator={thousandSeparator}
            decimalScale={2}
            fixedDecimalScale
            isAllowed={values => {
              const { floatValue } = values
              return floatValue <= 10000000000000
            }}
            allowNegative={false}
            decimalSeparator={decimalSeparator}
            className={error ? classes.InputFieldTagsError : classes.InputFieldTags}
            autoComplete='off'
            onKeyDown={e => DisableCharacters(e, type)}
            format={format}
            allowEmptyFormatting
          />
        </div>
        <div id='tags' className={classes.tags}>
          <label>{tags}</label>
        </div>
      </div>
      <div className={classes.error}> {error && error}</div>
    </React.Fragment>
  )
}

const RenderInputFieldsComponent = props => {
  const { error, onChange, value, name, inputRef, type, autoFocus, onBlur, onFocus, style, tags } = props

  if (name === 'vatTaxRate') {
    return (
      <React.Fragment>
        <div id='root' className={classes.root}>
          <div id='numberFormat' className={classes.numberFormat}>
            <InputBase
              value={value}
              onFocus={onFocus}
              name={name}
              style={style}
              type={type}
              autoFocus={autoFocus}
              inputRef={inputRef}
              onChange={onChange}
              onBlur={onBlur}
              className={error ? classes.InputFieldTagsError : classes.InputFieldTags}
              autoComplete='off'
              onKeyDown={e => DisableCharacters(e, type)}
            />
          </div>
          <div id='tags' className={classes.tags}>
            <label>{tags}</label>
          </div>
        </div>
        <div className={classes.error}> {error && error}</div>
      </React.Fragment>
    )
  } else {
    return (
      <React.Fragment>
        <InputBase
          value={value}
          onFocus={onFocus}
          name={name}
          style={style}
          type={type}
          autoFocus={autoFocus}
          inputRef={inputRef}
          onChange={onChange}
          onBlur={onBlur}
          className={error ? classes.InputFieldsError : classes.InputFields}
          autoComplete='off'
          onKeyDown={e => DisableCharacters(e, type)}
        />
        <div className={classes.error}> {error && error}</div>
      </React.Fragment>
    )
  }
}

const RenderDropDownField = props => {
  const { name, value, onChange, items, error } = props
  let itemData = items === undefined ? [] : items

  return (
    <React.Fragment>
      <NativeSelect
        name={name}
        value={value}
        onChange={onChange}
        className={error ? classes.selectInputError : classes.selectInput}
      >
        <option value='' />
        {itemData.map((item, index) => (
          <option key={index} value={item.value}>
            {item.text}
          </option>
        ))}
      </NativeSelect>
      <div className={classes.error}> {error && error}</div>
    </React.Fragment>
  )
}

const RenderDropDownWithLabelField = props => {
  const { name, value, onChange, items, error, label, disabled } = props
  let itemData = items === undefined ? [] : items

  return (
    <div id='dropDownwithLabel' className={classes.dropDownwithLabel}>
      <label>{label}</label>
      <NativeSelect
        name={name}
        value={value}
        onChange={onChange}
        disabled={disabled}
        className={error ? classes.selectInputError : classes.selectInput}
      >
        <option value='' />
        {itemData.map((item, index) => (
          <option key={index} id='options' value={item.value} className={classes.options}>
            {item.text}
          </option>
        ))}
      </NativeSelect>
      <div className={classes.error}> {error && error}</div>
    </div>
  )
}

const RenderInputWithlabelFields = props => {
  const { name, value, onChange, error, label, inputRef, type } = props

  const zipCodeLength = () => {
    if (name === 'zipCode') {
      return 4
    }
    return null
  }

  return (
    <div id='RenderInputWithlabelFields' className={classes.dropDownwithLabel}>
      <label>{label}</label>
      <InputBase
        value={value}
        name={name}
        type={type}
        inputRef={inputRef}
        onChange={onChange}
        inputProps={{maxLength: zipCodeLength()}}
        className={error ? classes.selectInputError : classes.selectInput}
        autoComplete='off'
        onKeyDown={e => DisableCharacters(e, type)}
      />
      <div className={classes.error}> {error && error}</div>
    </div>
  )
}

