const RenderInputMask = React.memo(RenderInputMaskField)

const RenderInputFields = React.memo(RenderInputFieldsComponent)

const RenderInputWithlabel = React.memo(RenderInputWithlabelFields)
const RenderNumberFormat = React.memo(RenderNumberFormatComponent)
const MedicalFacilityInput = React.memo(MedicalFacilityInputField)
const FacilityInput = React.memo(FacilityInputField)
const RenderDropDown = React.memo(RenderDropDownField)
const RenderDropDownWithLabel = React.memo(RenderDropDownWithLabelField)
const RenderInputWithTags = React.memo(RenderInputWithTagsComponent)

export {
  RenderInputFields,
  RenderDropDown,
  FacilityInput,
  MedicalFacilityInput,
  RenderDropDownWithLabel,
  RenderInputWithlabel,
  RenderInputMask,
  RenderInputWithTags,
  RenderNumberFormat
}
