import React from 'react'
import ReactTable from '../../Helpers/PaginationForPages'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './ListOfAffiliatedFacility.module.scss'
import EditableTable from './ReactTable/EditTable/EditableTable'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import CustomDialogSave from '../../SpecialOtherServicesProfile/components/CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import ProfileService from '../../../../../../Services/Api/Partner/ProfileService'
import { profileListOfAffiliatedFacilityColumns } from '../../../../../../Constant/tableColumnData.json'

const ListOfAffiliatedDoctors = ({ open, setOpen, endPoint, paramId }) => {
  const [tableData, setTableData] = React.useState([])
  const [originalData, setOriginalData] = React.useState([])
  const [listofAffiliatedDoctors, setListOfAffiliatedDoctors] = React.useState({})
  const [tableColumn, setTableCol] = React.useState([])
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [openDialog, setOpenDialog] = React.useState(false)
  const [success, setSuccess] = React.useState(false)

  React.useEffect(() => {
    fetchData()
    return () => {
      setOpen(false)
    }
  }, [])

  const fetchData = async () => {
    const tableColumn = []
    let listOfAffiliatedFacility = []
    const { data: appData } = await ProfileService.getAffiliatedFacility(paramId, 'DOCTOR')
    const { data: newData } = appData

    const listOffAffiliatedFacilityData = newData || []

    for (let key in listOffAffiliatedFacilityData) {
      listOfAffiliatedFacility.push(listOffAffiliatedFacilityData[key])
    }

    for (let i of listOfAffiliatedFacility) {
      const obj = {}
      // const { data: appData } = await ProfileService.getAffiliatedDoctors(i.facility_id, 'HOSPITAL')
      // const { data: newData } = appData

      // setListOfAffiliatedDoctors(newData[0])

      obj.main = i.main ? i.main : false // important in profile partner information
      obj.status = i.status ? i.status : ''
      obj.facility_id = i.facility_id ? i.facility_id : ''
      obj.name = i.name ? i.name : ''
      obj.location = i.location ? i.location : ''
      obj.full_address = i.full_address ? i.full_address : '' // important in profile partner information
      obj.clinic_schedule = i.clinic_schedule ? i.clinic_schedule : ''
      obj.room_number = i.room_number ? i.room_number : '' // used in encoding
      obj.contact_numbers = i.comtact_number ? i.contact_number : '' // used in encoding
      obj.remarks = i.remarks ? i.remarks : ''
      obj.expiration_date = i.expiration_date ? i.expiration_date : ''
      obj.date_submitted = i.date_submitted ? i.date_submitted : ''
      obj.disaffiliation_date = i.disaffiliation_date ? i.disaffiliation_date : '' // important in profile partner information

      tableColumn.push(obj)
    }

    setTableData(tableColumn)
    setOriginalData(tableColumn)
    setTableCol(profileListOfAffiliatedFacilityColumns)
  }

  const HandleSave = async () => {
    let obj = {}
    obj.id = paramId

    obj.affiliatedFacility = [...tableData]

    try {
      const { data: newData } = await ProfileService.updatedListOfAffiliatedFacilities(obj)
      const datas = newData ? (newData.data ? newData.data : []) : []

      setOpenDialog(false)
      setSuccess(true)

      setTimeout(() => {
        setTableData(datas)
        setOriginalData(datas)
        setIsToUpdate(false)
      }, 800)
    } catch (e) {
      console.log('error', e)
    }
  }

  const HandleOpenDialog = () => {
    setOpenDialog(true)
    setSuccess(false)
  }

  const HandleCancel = () => {
    setOpen(true)
    setTableData(originalData)
    setIsToUpdate(false)
    setIsCancel(false)
  }
  return (
    <React.Fragment>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false)
        }}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div id='Update-button'>
              <Button className={classes.button} onClick={() => setIsToUpdate(true)} variant='contained'>
                Update
              </Button>
            </div>
            <div id='tables' className={classes.tables}>
              {!isToUpdate && <ReactTable columns={tableColumn} data={tableData} countName={'Affiliated Facilities'} />}
              {isToUpdate && (
                <EditableTable
                  columns={tableColumn}
                  data={tableData}
                  setTableData={setTableData}
                  countName={'Affiliated Facilities'}
                />
              )}
            </div>
            <div id='buttonDiv' className={classes.buttonDiv}>
              {!isToUpdate && (
                <div id='Close-button'>
                  <Button
                    className={classes.button}
                    variant='contained'
                    onClick={() => {
                      setOpen(false)
                    }}
                  >
                    Close
                  </Button>
                </div>
              )}
              {isToUpdate && (
                <div id='cancel-save-button'>
                  <Button
                    id='cancel-btn'
                    className={classes.btnCancel}
                    variant='contained'
                    onClick={() => setIsCancel(true)}
                  >
                    Cancel
                  </Button>
                  <Button id='save-btn' className={classes.btnSave} variant='contained' onClick={HandleOpenDialog}>
                    Save
                  </Button>
                </div>
              )}
            </div>
          </div>
        </div>
      </Modal>
      <div>
        <CustomDialog id='sos_custom_dialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
          <div id='sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='sos_custom_dialog_message'>All changes will not be saved. Are you sure you want to cancel?</h3>
            <div id='sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => {
                  setIsCancel(false)
                }}
                id='sos_custom_dialog_btn_no'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={HandleCancel}
                id='sos_custom_dialog_btn_yes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <div>
        <CustomDialogSave
          onClose={() => {
            setOpen(false)
          }}
          id='a_sos_custom_dialog'
          isSave={openDialog}
          setIsSave={() => setIsSave(false)}
        >
          <div id='a_sos_custom_dialog_container' className={classes.dialogContainer}>
            <h3 id='a_sos_custom_dialog_message'>Are you sure you want to update?</h3>
            <div id='a_sos_custom_dialog_btn_group' className={classes.btnGroup}>
              <Button
                onClick={() => setOpenDialog(false)}
                id='a_sos_custom_dialog_btn_no'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={HandleSave}
                id='a_sos_custom_dialog_btn_yes'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialogSave>
      </div>
      <CustomAlerSnackBar isSave={success} setDelay={800} setIsSave={() => setSuccess(false)} />
    </React.Fragment>
  )
}

export default ListOfAffiliatedDoctors
