import React, { useState } from 'react'

import Button from '@material-ui/core/Button'
import History from 'Pages/Protected/Partner/Components/History/History'
// Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import CustomDialogApprove from '../Components/CustomDialogApprove/CustomDialogApprove'
import FacilityInformation from '../Components/FacilityInformation/FacilityInformation'
import UploadFileTable from '../DoctorAffiliation/Components/UploadFIleTable/UploadFIleTable'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
import DoctorAffiliationContext from './context/DoctorAffiliationContext'
// Constant
import cntsPartnerType from 'Pages/Protected/Partner/Constants/partnerTypes'
import cnts from 'Pages/Protected/Partner/Constants/stage'
// Styles
import classes from './DoctorAffiliation.module.scss'
import Axios from 'axios'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER AFFILIATION',
    to: '/'
  },
  Doctor: {
    text: 'DOCTOR AFFILIATION',
    to: '/'
  }
}

const DoctorAffiliation = props => {
  let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Doctor Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Doctor'])
  const { paramId, affiliationType, isApproved, setIsApproved } = React.useContext(DoctorAffiliationContext)
  const [isApprove, setIsApprove] = React.useState(false)

  const approveAffiliation = async () => {
    try {
      let data = {
        stageName: cnts.PARTNER.STAGE.PROFILE_GENERATED,
        dateSubmitted: new Date().toISOString(),
        type: cntsPartnerType.PARTNER.TYPE.DOCTOR,
        medPartnerId: affiliationType.medical_partner_id,
        affilType: affiliationType.affiliation_type,
        facilityInfo: affiliationType.facility_info
      }

      console.log(`***** DATA : ', ${data}`)

      const res = await AffiliationApproverServices.approveAffiliation(paramId, data)
      setIsApprove(false)
      // props.history.push('/affiliation-approver')
      setIsApproved(true)
    } catch (error) {
      console.log('error', error)
    }
  }

  const disapproved = async () => {
    try {
      const res = await AffiliationApproverServices.disapproved(paramId, cntsPartnerType.PARTNER.TYPE.DOCTOR)
      props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  return (
    <React.Fragment>
      <CustomDialogApprove id='aa_custom_dialog' isApprove={isApprove} setIsApprove={() => setIsApprove(false)}>
        <div id='aa_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='aa_custom_dialog_message'>Are you sure you want to approve the Affiliation form?</h3>
          <div id='aa_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              onClick={() => setIsApprove(false)}
              id='aa_custom_dialog_btn_cancel'
              className={classes.btnCancel}
              variant='contained'
            >
              Cancel
            </Button>
            <Button id='aa_custom_dialog_btn_approve' className={classes.btnApprove} variant='contained' onClick={approveAffiliation}>
              Approve
            </Button>
          </div>
        </div>
      </CustomDialogApprove>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={2}
        />
        <div id='contentWrapper' className={classes.contentWrapper}>
          <FacilityInformation
            titleText={'Doctor Profile'}
            uploadFileTable={<UploadFileTable buttonText={'Upload'} affiliationTypeId={paramId} forView={true}  />}
          />
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                variant='contained'
                color='secondary'
                className={classes.disapproveButton}
                onClick={disapproved}
                disabled={isApproved} style={{backgroundColor: isApproved ? '#dddfe3' : ''}}
              >
                Disapproved
              </Button>
            </div>
            <div />
            <div>
              <div>
                <Button
                  variant='contained'
                  className={classes.returnButton}
                  onClick={() => props.setPage(false)}
                >
                  Return
                </Button>
              </div>
              <div>
                <Button variant='contained' disabled={isApproved} style={{backgroundColor: isApproved ? '#dddfe3' : ''}} className={classes.approveButton} onClick={() => setIsApprove(true)}>
                  Approve
                </Button>
              </div>
            </div>
          </span>
          <div
            style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}
          >
            Doctor Affiliation Form 2 out of 2
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default DoctorAffiliation
