import React, { useState } from 'react'

import Button from '@material-ui/core/Button'

// Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import CustomDialogApprove from '../Components/CustomDialogApprove/CustomDialogApprove'
import History from 'Pages/Protected/Partner/Components/History/History'
import FacilityInformation from '../Components/FacilityInformation/FacilityInformation'
import UploadFileTable from '../../../AffiliationFormEncoder/Pages/DentalClinicAffiliation/Components/UploadFIleTable/UploadFIleTable'
import DentalClinicAffiliationOIContext from './context/DentalClinicAffiliationOIContext'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
// Constant
import cntsPartnerType from 'Pages/Protected/Partner/Constants/partnerTypes'
import { AffiliationStatusEnum } from 'Pages/Protected/Partner/Constants/AffiliationStatusEnum'
// Styles
import classes from './DentalClinicAffiliationOI.module.scss'
import Axios from 'axios'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  DentalClinic: {
    text: 'DENTAL CLINIC AFFILIATION',
    to: '/'
  }
}

const DentalClinicAffiliationOI = props => {
  let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Dental Clinic Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'DentalClinic'])
  const { paramId, isApproved, setIsApproved } = React.useContext(DentalClinicAffiliationOIContext)
  const [isApprove, setIsApprove] = React.useState(false)
  const [isProcessed, setProcessed] = React.useState(false)

  const save = async () => {
    try {
      let stage = {
        stage_name: 'FOR_OIC',
        date_submitted: new Date().toISOString()
      }
      const res = await AffiliationApproverServices.approveAffiliationForOIC(paramId, stage)
      setIsApprove(false)
      const affiliationStatus = res.data.data.affiliation_status.name
      // setProcessed : true => Disabled All Buttons.
      setProcessed(affiliationStatus === AffiliationStatusEnum.PROCESSING)
      setIsApproved(affiliationStatus === AffiliationStatusEnum.PROCESSING)
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  const disapproved = async () => {
    try {
      const res = await AffiliationApproverServices.disapproved(paramId, cntsPartnerType.PARTNER.TYPE.DENTAL_CLINIC)
      props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  return (
    <React.Fragment>
      <CustomDialogApprove id='aa_custom_dialog' isApprove={isApprove} setIsApprove={() => setIsApprove(false)}>
        <div id='aa_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='aa_custom_dialog_message'>
            Are you sure you want to approve the Dental Clinic Affiliation form for Ocular Inspection?
          </h3>
          <div id='aa_custom_dialog_btn_group' className={classes.btnGroup}>
            <Button
              onClick={() => setIsApprove(false)}
              id='aa_custom_dialog_btn_cancel'
              className={classes.btnCancel}
              variant='contained'
            >
              Cancel
            </Button>
            <Button id='aa_custom_dialog_btn_approve' onClick={save} className={classes.btnApprove} variant='contained'>
              Approve
            </Button>
          </div>
        </div>
      </CustomDialogApprove>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={2}
        />
        <div id='contentWrapper' className={classes.contentWrapper}>
          <FacilityInformation
            titleText={'Dental Clinic Profile'}
            uploadFileTable={<UploadFileTable buttonText={'Upload'} affiliationTypeId={paramId} forView={true} />}
          />
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                variant='contained'
                color='secondary'
                className={classes.disapproveButton}
                // onClick={() => props.history.push(`/`)}
                onClick={disapproved}
                disabled={isApproved}
                style={{backgroundColor: isApproved ? '#dddfe3' : ''}}
              >
                Disapproved
              </Button>
            </div>
            <div />
            <div>
              <div>
                <Button
                  variant='contained'
                  className={classes.returnButton}
                  onClick={() => props.setPage(false)}
                >
                  Return
                </Button>
              </div>
              <div>
                <Button variant='contained' disabled={isApproved} style={{backgroundColor: isApproved ? '#dddfe3' : ''}} className={classes.approveButton} onClick={() => setIsApprove(true)}>
                  Approve
                </Button>
              </div>
            </div>
          </span>
          <div
            style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}
          >
            Facility Affiliation Form 2 out of 2
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default DentalClinicAffiliationOI
