import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Grid from '@material-ui/core/Grid'
import Checkbox from '@material-ui/core/Checkbox'
import { withStyles } from '@material-ui/core/styles'
import { green } from '@material-ui/core/colors'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import { IntegerComponent } from '../../../../Components/RowInputComponents/RowInputComponents'
import { updateDentalService } from '../../../../Store/Actions/actions'

// import scss module
import classes from './Services.module.scss'

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: green[600]
    }
  },
  checked: {}
})(props => <Checkbox color='default' {...props} />)

const ServicesGroup = React.memo(
  props => {
    const { readOnly = false, services, onChange = () => {} } = props
    const onChangeValue = (name, newState) => {
      onChange({ [name]: newState })
    }

    const onChangeDentalXray = (name, newState) => {
      onChange({ dentalXray: { ...services.dentalXray, [name]: newState } })
    }

    return (
      <React.Fragment>
        <TableRow>
          <TableCell className={classes.tableCell} align='left' colSpan={6}>
            <div style={{ display: 'flex' }}>
              <FormControlLabel
                style={{ marginLeft: '0px' }}
                control={
                  readOnly ? (
                    services.noOfDentalChairs === 0 ? (
                      <span />
                    ) : (
                      <span>{services.noOfDentalChairs}</span>
                    )
                  ) : (
                    <IntegerComponent
                      paperStyle={{ width: '100px' }}
                      readOnly={readOnly}
                      value={services.noOfDentalChairs}
                      onChange={val => onChangeValue('noOfDentalChairs', val)}
                    />
                  )
                }
                label={<p style={{ fontSize: '14px', marginRight: '15px' }}>Number of Dental Chairs</p>}
                labelPlacement='start'
              />
              <FormControlLabel
                style={{
                  width: '150px',
                  display: 'flex',
                  justifyContent: 'space-between',
                  marginRight: '15px'
                }}
                control={
                  <GreenCheckbox
                    checked={services.autoclave}
                    onClick={readOnly ? () => {} : () => onChangeValue('autoclave', !services.autoclave)}
                  />
                }
                label={<p style={{ fontSize: '14px', marginLeft: '25px' }}>Autoclave</p>}
                labelPlacement='start'
              />
              <FormControlLabel
                style={{ width: '150px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.airconditioned}
                    onClick={readOnly ? () => {} : () => onChangeValue('airconditioned', !services.airconditioned)}
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Airconditioned</p>}
                labelPlacement='start'
              />
              <FormControlLabel
                style={{ width: '155px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.telephoneLine}
                    onClick={readOnly ? () => {} : () => onChangeValue('telephoneLine', !services.telephoneLine)}
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Telephone Line</p>}
                labelPlacement='start'
              />
            </div>
          </TableCell>
        </TableRow>
        <TableRow>
          <TableCell className={classes.tableCell} align='left' colSpan={6}>
            {'Dental X-ray'}
          </TableCell>
        </TableRow>
        <TableRow>
          <TableCell
            className={classes.tableCell}
            style={{ display: 'flex', flexDirection: 'column' }}
            align='left'
            colSpan={6}
          >
            <div style={{ display: 'flex' }}>
              <FormControlLabel
                style={{ width: '130px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.dentalXray.biteWing}
                    onClick={readOnly ? () => {} : () => onChangeDentalXray('biteWing', !services.dentalXray.biteWing)}
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Bite-wing</p>}
                labelPlacement='start'
              />
              <FormControlLabel
                style={{ width: '130px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.dentalXray.occlusal}
                    onClick={readOnly ? () => {} : () => onChangeDentalXray('occlusal', !services.dentalXray.occlusal)}
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Occlusal</p>}
                labelPlacement='start'
              />
              <FormControlLabel
                style={{ width: '150px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.dentalXray.cephalometric}
                    onClick={
                      readOnly
                        ? () => {}
                        : () => onChangeDentalXray('cephalometric', !services.dentalXray.cephalometric)
                    }
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Cephalometric</p>}
                labelPlacement='start'
              />
            </div>
            <div style={{ display: 'flex' }}>
              <FormControlLabel
                style={{ width: '130px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.dentalXray.periapical}
                    onClick={
                      readOnly ? () => {} : () => onChangeDentalXray('periapical', !services.dentalXray.periapical)
                    }
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Periapical</p>}
                labelPlacement='start'
              />
              <FormControlLabel
                style={{ width: '130px', display: 'flex', justifyContent: 'space-between', marginRight: '15px' }}
                control={
                  <GreenCheckbox
                    checked={services.dentalXray.panoramic}
                    onClick={
                      readOnly ? () => {} : () => onChangeDentalXray('panoramic', !services.dentalXray.panoramic)
                    }
                  />
                }
                label={<p style={{ fontSize: '14px' }}>Panoramic</p>}
                labelPlacement='start'
              />
            </div>
          </TableCell>
        </TableRow>
      </React.Fragment>
    )
  },
  (prevProps, nextProps) => {
    try {
      if (
        prevProps.readOnly === nextProps.readOnly &&
        prevProps.services.noOfDentalChairs === nextProps.services.noOfDentalChairs &&
        prevProps.services.autoclave === nextProps.services.autoclave &&
        prevProps.services.airconditioned === nextProps.services.airconditioned &&
        prevProps.services.telephoneLine === nextProps.services.telephoneLine &&
        prevProps.services.dentalXray.biteWing === nextProps.services.dentalXray.biteWing &&
        prevProps.services.dentalXray.occlusal === nextProps.services.dentalXray.occlusal &&
        prevProps.services.dentalXray.cephalometric === nextProps.services.dentalXray.cephalometric &&
        prevProps.services.dentalXray.periapical === nextProps.services.dentalXray.periapical &&
        prevProps.services.dentalXray.panoramic === nextProps.services.dentalXray.panoramic
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)

const Services = props => {
  const dispatch = useDispatch()
  const services = useSelector(state => state.dental.services)
  const { readOnly = false } = props

  return (
    <React.Fragment>
      <div className={classes.root}>
        <h2 id='roomAndBoardTableTitle' className={classes.title}>
          SERVICES OFFERED
        </h2>
        <div id='roomAndBoardContent' className={classes.content}>
          <Grid id='ct_grid' container spacing={1}>
            <Grid item xs={12}>
              <TableContainer>
                <Table aria-label='simple table' className={classes.table}>
                  <TableBody className={classes.tableBody}>
                    <ServicesGroup
                      readOnly={readOnly}
                      services={services}
                      onChange={val => dispatch(updateDentalService(val))}
                    />
                  </TableBody>
                </Table>
              </TableContainer>
            </Grid>
          </Grid>
        </div>
      </div>
    </React.Fragment>
  )
}

export default Services
