import React from 'react'
import { generate } from 'shortid'

// import IsFormValid from '../../../../Components/IsFormValid/IsFormValid'
import { NonVatableData } from '../../../../Components/Placeholders/Placeholders'

import DoctorFormValidation from '../Utilities/DoctorFormValidation'
import CalendarInlineWithInlineEdit from '../../../../../Components/DatePicker/DatePickerInlineEdit'
import { isDateValid } from '../../../../../Components/DatePicker/DatePickerUtils'
import debounce from 'lodash/debounce'
// Components

import TextField from '../../../../../Components/JoiValidationInputField/index'
import CustomTextFieldNumberFormat from '../../../../../Components/JoiValidationInputFieldNumberFormat'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'
import CustomizedTextArea from '../../../../../Components/CustomizeTextArea/CustomTextAreaWithRef'
import CheckBox from '../../../../../Components/CheckBox/CheckBox'
import AffiliationFormInput from '../../../../../Components/AfiiliationTableWithInput/AfiiliationTableWithInput'

// shared Components
import DropDownComponent from '../../../../../Components/CustomDrowpdown/index'
import CustomAutoComplete from 'Pages/Protected/Partner/Components/AutoSuggest'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'

import FooterWithButtons from '../FooterWithButtons/FooterWithButtons'

// marterial ui
import ClearIcon from '@material-ui/icons/Clear'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import FormHelperText from '@material-ui/core/FormHelperText'

// style scss
import classes from './AffiliationForm.module.scss'
import styles from '../../../../../Components/TwoTextArea/TwoTextArea.module.scss'

// Constants
import {
  suffixList,
  specializationList,
  genderList,
  civilStatusList
} from 'Pages/Protected/Partner/Constants/dropDownItems'

// Redux Store
import { useSelector, useDispatch } from 'react-redux'
// Actions
import { setDoctorAffiliation } from '../../../../../../Store/actions'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'

// Utilities
import inputFieldArr from '../Utilities/inputFieldRefArr'
import getMedDoctor from '../Utilities/doctorEncoderService'

// Router
import { useParams } from 'react-router-dom'

// Service
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'
import ProgressComponent from '../../../../Components/ProgressComponent/ProgressComponent'
import { dateFormat, formatTinNumber, formatPhilHealthNumber } from 'Pages/Protected/Partner/Components/Utils/utils'
import DoctorSpecialization from 'Pages/Protected/Partner/Components/DoctorSpecialization'

const AffiliationForm = props => {
  const dispatch = useDispatch()

  const { affiliationType } = useSelector(state => state.affiliationType)

  const { doctorForms } = useSelector(state => state.doctor)

  const { id } = useParams()

  const [isSuccess, setIsSuccess] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)

  const [isAddedType, setIsAddedType] = React.useState(false)

  const [isSubmitted, setIsSubmitted] = React.useState(false)

  const [facilityNameSearchTerm, setFacilityNameSearchTerm] = React.useState('')

  const [facilityNames, setFaciltyNames] = React.useState([])

  const [facilityIdSearchTerm, setFacilityIdSearchTerm] = React.useState('')

  const [facilityIds, setFacilityIds] = React.useState([])

  const [isFetching, setIsFetching] = React.useState(false)

  const [isFetchingIDs, setIsFetchingIDS] = React.useState(false)

  const [doctorIds, setDoctorIds] = React.useState([])

  const [form, setForm] = React.useState({ ...doctorForms })

  const [errors, setErrors] = React.useState({})

  const [dateValue, setDateValue] = React.useState({})

  const [asstCoordinators, setAsstCoordinators] = React.useState([])

  const [doctorId, setDoctorId] = React.useState('')

  const [searchTermID, setSearchTermID] = React.useState('')

  const [addToGroup, setAddToGroup] = React.useState([])

  let formRef = React.useRef({ ...form })
  let affiliatedGroupRef = React.useRef({})

  React.useEffect(() => {
    // reset non-vatable dropdwown
    if (form['tinStatus'] === '12%') {
      const newForm = { ...form }
      newForm['nonVatable'] = ''
      setForm(newForm)
      delete errors['nonVatable']
    }
  }, [form.tinStatus])

  React.useEffect(() => {
    if (affiliationType.affiliationStatus === 'Submitted') {
      setIsSubmitted(true)
    }
  }, [affiliationType.affiliationStatus])

  React.useEffect(() => {
    if (affiliationType['affiliationType'] === 'additional_affiliation') {
      setIsAddedType(true)
    }
    if (id) {
      fetchData(id)
      setDoctorId(id)
      console.log('is this been called sa id', id)
    } else {
      setAddToGroup(form.affiliationInfos)
      setAsstCoordinators([...doctorForms['asstCoordinators']])
      mapFieldsToRef({ ...form })
    }

    if (affiliationType.hasOwnProperty('id')) {
      setDoctorId(affiliationType['id'])
    }
  }, [])

  const fetchData = async id => {
    let nForm = { ...form }
    setIsLoading(true)
    try {
      const { obj, affil } = await getMedDoctor(id, true)
      nForm = obj
      // const newForm = { ...nForm, ...obj }
      if (affil['affiliationType'] === 'additional_affiliation') {
        setIsAddedType(true)
      }
      if (affil['stageName'] === 'FOR_AFFILIATION_APPROVAL' || affil['affiliationStatus'] === 'Submitted') {
        setIsSubmitted(true)
      } else {
        setIsSubmitted(false)
      }
      setForm(nForm)
      setAsstCoordinators([...obj['asstCoordinators']])
      setAddToGroup(obj.affiliationInfos)
      dispatch(setAffiliationType(affil))
      dispatch(setDoctorAffiliation(nForm))
      setIsLoading(false)

      try {
        mapFieldsToRef({ ...nForm })
      } catch (error) {
        console.log('errors sa map state to props', error)
      }
    } catch (error) {
      setIsLoading(false)
      console.log('fetchdata error', error)
    }
  }

  const mapFieldsToRef = form => {
    for (let key in form) {
      if (inputFieldArr.includes(key)) {
        if (form[key]) {
          formRef.current[key].value = form[key]
        }
      }
    }
  }

  const save = async () => {
    try {
      affiliationType.stageName = 'ENCODING'
      affiliationType.dateSubmitted = new Date().toISOString()
      affiliationType.status = 'Active'
      affiliationType.affiliationStatus = 'In-Progress'
      affiliationType.source = 'PNM'

      let res = {}
      if (!doctorId) {
        res = await PartnerDoctorService.createdPartnerDoctor({ ...affiliationType, ...form })
        affiliationType.id = res.data.data.id
        setDoctorId(res.data.data.id)
        dispatch(setAffiliationType(affiliationType))
      } else {
        res = await PartnerDoctorService.updatePartnerDoctor(doctorId, { ...affiliationType, ...form })
      }
      dispatch(setDoctorAffiliation(form))
      setIsSuccess(true)
    } catch (error) {}
  }

  const validate = () => {
    const { newErrors, isValid } = DoctorFormValidation({ ...form }, isAddedType, errors, dateValue)

    console.log('isValid', isValid)
    if (isValid) {
      save()
      setErrors({})
    } else {
      setErrors(newErrors)
    }
  }

  const HandleChange = (value, name) => {
    const newErrors = { ...errors }
    const nForm = { ...form }
    nForm[name] = value
    delete newErrors[name]
    setForm(nForm)
    setErrors(newErrors)
  }

  const HandleChangeTinStatus = ({ target: { name, value } }) => {
    const newErrors = { ...errors }
    const nForm = { ...form }
    nForm[name] = value
    delete newErrors[name]
    setForm(nForm)
    setErrors(newErrors)
  }
  const HandleChangeFacility = ({id, name}) => {
    if (name) {
      handleDoctorFacility(null, name)
    } else {
      handleDoctorFacility(id, null)
    }
  }

  const handleFacilityNameChange = async (value, name) => {
    setFacilityNameSearchTerm(value)
    setIsFetching(true)
    if (typeof value === 'string' && value.length >= 1) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityName({ name: value })
        setFaciltyNames(data)
        setIsFetching(false)
      } catch (error) {
        setIsFetching(false)
        console.log('error occured', error)
      }
    }
  }
  const handleFacilityIdChange = async (value, name) => {
    console.log('is this called id', value)
    setFacilityIdSearchTerm(value)
    setIsFetchingIDS(true)
    if (typeof value === 'string' && value.length >= 6) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityIds({ id: value })
        setFacilityIds(data)
        setIsFetchingIDS(false)
      } catch (error) {
        setIsFetchingIDS(false)
        console.log('error occured', error)
      }
    }
  }

  const handleDoctorFacility = async (id, name) => {
    const nForm = { ...form }
    const nErrors = { ...errors }
    try {
      const {
        data: { data }
      } = await AffiliationEncoderService.getFacilityForDoctor({ id, name })
      let newForm = { ...nForm, ...data }
      if (data) {
        formRef.current.address.value = data.address
        dispatch(setDoctorAffiliation(newForm))
        setForm(newForm)
        delete nErrors['facilityName']
        delete nErrors['facilityMpId']
        setErrors(nErrors)
      }
    } catch (error) {
      console.log('handle facility doctor get element by id', error)
    }
  }

  const HandleDropdown = ({ target: { value, name } }) => {
    const nForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[name]
    nForm[name] = value
    setForm(nForm)
    setErrors(newErrors)
  }

  const HandleDate = (name, date) => {
    let dateVal = { ...dateValue }
    const { isValid, dateFormatted } = isDateValid(date)
    if (isValid) {
      const nForm = { ...form }
      const newErrors = { ...errors }
      nForm[name] = dateFormatted
      delete dateVal[name]
      setDateValue(dateVal)
      setForm(nForm)
      delete newErrors[name]
      setErrors(newErrors)
    } else {
      dateVal[name] = date
      setDateValue(dateVal)
      if (name !== 'philhealthExpiration') {
        handleRequiredDatesErrorMessage(date, name)
      } else {
        let newErrors = { ...errors }
        if (date !== '') {
          newErrors[name] = 'Invalid date format'
        } else {
          delete newErrors[name]
          delete dateVal[name]
          setDateValue(dateVal)
        }
        setErrors(newErrors)
      }
    }
  }

  const handleRequiredDatesErrorMessage = (date, name) => {
    let newErrors = { ...errors }
    if (date.length >= 1) {
      newErrors[name] = 'Invalid date format'
      setErrors(newErrors)
    } else {
      newErrors[name] = `"${name === 'prcExpiration' ? 'PRC Expiration' : 'Birth date'} is not allowed to be empty`
      setErrors(newErrors)
    }
  }

  const HandleAddAssistant = (value, name, index, id) => {
    let newForm = { ...form }
    let asst = [...asstCoordinators]
    for (let i of asst) {
      if (i.id === id) {
        i[name] = value
      }
    }
    newForm['asstCoordinators'] = asst
    setAsstCoordinators(asst)
    setForm(newForm)
  }

  const handleGroup = e => {
    e.preventDefault()
    let obj = {}
    obj.affiliatedGroup = affiliatedGroupRef.current.value
    if (obj.affiliatedGroup !== 'undefined' && obj.affiliatedGroup) {
      setAddToGroup([...addToGroup, obj])
      affiliatedGroupRef.current.value = ''
    }
  }

  const disabledButton = React.useMemo(() => {
    try {
      const { newErrors, isValid } = DoctorFormValidation(form, isAddedType, errors, dateValue)
      console.log('new errors sa disabled butotn', newErrors)
      if (isValid) {
        return false
      } else {
        return true
      }
    } catch (error) {
      console.log('error in disabled button', error)
    }
  }, [form])

  const HandleRemove = React.useCallback(
    i => {
      setAddToGroup(addToGroup => addToGroup.filter((row, j) => j !== i))
    },
    [addToGroup]
  )

  const HandleEdit = React.useCallback(
    (i, x) => {
      setAddToGroup(addToGroup => addToGroup.map((row, j) => (j === i ? x : row)))
    },
    [addToGroup]
  )

  const handleGroupValue = React.useCallback(
    data => {
      let value = data
      console.log('data value', value)
      const nForm = { ...form }
      nForm['affiliationInfos'] = value
      setForm(nForm)
    },
    [addToGroup]
  )

  const handleClickSave = e => {
    validate()
  }

  const handleProceed = e => {
    if (!isSubmitted) validate()
    props.history.push('/doctor-affiliation-requirements')
    props.onSubmit()
    // handleSubmit(e)
  }

  const handleRemoveAssistCoor = id => {
    let assist = [...asstCoordinators]
    let newForm = { ...form }
    let newArr = assist.filter(contact => contact.id !== id)
    newForm['asstCoordinators'] = newArr
    setAsstCoordinators(newArr)
    setForm(newForm)
  }

  const handleAddAssistantCoor = () => {
    if (asstCoordinators.length <= 4) {
      setAsstCoordinators(currentAssistant => [
        ...currentAssistant,
        {
          id: generate(),
          asstCoordinatorName: '',
          asstContactNum: null
        }
      ])
    }
  }

  const handlePartnerId = async ({id}, name) => {
    const nForm = { ...form }
    try {
      let { obj, id: ID } = await getMedDoctor(id)
      nForm.medicalPartnerId = ID
      let res = { ...nForm, ...obj }
      setErrors({})
      setForm(res)
      mapFieldsToRef({ ...obj, asstCoordinators: nForm['asstCoordinators'] })
      dispatch(setDoctorAffiliation(res))
    } catch (error) {
      console.log('partner id error', error)
    }
  }

  const searchParnerIds = React.useCallback(
    async (id, name) => {
      setSearchTermID(id)
      if (typeof id === 'string' && id.length >= 6) {
        setIsFetching(true)
        try {
          const {
            data: { data }
          } = await PartnerDoctorService.getIDs({ id })
          setDoctorIds(data)
          setIsFetching(false)
        } catch (error) {
          console.log('error', error)
          setIsFetching(false)
        }
      } else {
        setDoctorIds([])
      }
    },
    [searchTermID]
  )

  const debounceChange = debounce((value, name) => HandleChange(value, name), 150)
  const debounceAddAssistant = debounce((value, name, index, id) => HandleAddAssistant(value, name, index, id), 100)
  if (isLoading && id) return <ProgressComponent />
  return (
    <React.Fragment>
      <form autocomplete='off'>
        <div id='root' className={classes.root}>
          <div id='topText' className={classes.topText}>
            DOCTOR INFORMATION
          </div>
          <div id='medicalPartnerID' className={classes.medicalPartnerID}>
            <div id='medicalPartnerIdContainer' className={classes.medicalPartnerIdContainer}>
              <label>Medical Partner ID</label>
              <AutoSuggest
                suggestions={doctorIds}
                error={errors['medicalPartnerId']}
                disabled={isSubmitted || affiliationType['affiliationType'] === 'new_affiliation'}
                value={form['medicalPartnerId'] ? form['medicalPartnerId'] : ''}
                labelName='id'
                name='partnerId'
                searchTerm={searchTermID}
                onChange={searchParnerIds}
                onSelect={handlePartnerId}
                isFetching={isFetching}
                setSearchTerm={setSearchTermID}
                show={6} // length of search string to show auto suggestions
              />
            </div>
          </div>
          <div id='GeneralInformation' className={classes.GeneralInformationField}>
            <div id='generalInfoText' className={classes.generalInfoText}>
              General Information
            </div>
            <div id='generalInfoFields' className={classes.generalInfoFields}>
              <div id='firstPartField' className={classes.firstPartField}>
                <div id='11' className={classes.InputContainer}>
                  <label>First Name *</label>
                  <TextField
                    disabled={isSubmitted || isAddedType}
                    name='firstName'
                    id='firstName'
                    inputRef={el => (formRef.current.firstName = el)}
                    error={errors['firstName']}
                    type={'noSpecialChar'}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='12' className={classes.InputContainerTwo}>
                  <div id='Suffix-div' className={classes.suffix}>
                    <label>Suffix</label>
                    <DropDownComponent
                      disabled={isSubmitted || isAddedType}
                      name='suffix'
                      items={suffixList}
                      id='Suffix'
                      error={errors['suffix']}
                      value={form['suffix']}
                      onChange={HandleDropdown}
                    />
                  </div>
                  <div className={classes.birthDate}>
                    <label>Birthdate *</label>
                    <CalendarInlineWithInlineEdit
                      error={errors['birthDate']}
                      errors={errors}
                      setErrors={setErrors}
                      editInputWidth={classes.editInputWidth}
                      id='birthDate'
                      name='birthDate'
                      placeholder='Select date'
                      value={dateFormat(form['birthDate'])}
                      onChange={HandleDate}
                      disabled={isSubmitted || isAddedType}
                    />
                  </div>
                </div>
                <div id='13' className={classes.InputContainer}>
                  <label>Email Address *</label>
                  <TextField
                    disabled={isSubmitted || isAddedType}
                    type={'email'}
                    id='emailAddress'
                    error={errors['emailAddress']}
                    inputRef={el => (formRef.current.emailAddress = el)}
                    name='emailAddress'
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='14' className={classes.InputContainerDropDown}>
                  <label>Main Specialization *</label>
                  <DoctorSpecialization
                    disabled={isSubmitted || isAddedType}
                    value={form['mainSpecialization']}
                    id='Main Specialization'
                    error={errors['mainSpecialization']}
                    name='mainSpecialization'
                    onChange={HandleDropdown}
                  />
                </div>
                <div id='15' className={classes.InputContainerDropDown}>
                  <label>Sub-Specialization </label>
                  <DoctorSpecialization
                    disabled={isSubmitted || isAddedType}
                    id='subSpecialization'
                    value={form['subSpecialization']}
                    name='subSpecialization'
                    onChange={HandleDropdown}
                  />
                </div>
                <div id='16' className={classes.InputContainer}>
                  <label>PMA Number</label>
                  <TextField
                    disabled={isSubmitted || isAddedType}
                    type={'noSpecialChar'}
                    id='pmaNumber'
                    name='pmaNumber'
                    inputRef={el => (formRef.current.pmaNumber = el)}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='17' className={classes.InputContainer}>
                  <label>PRC License No. *</label>
                  <TextField
                    disabled={isSubmitted || isAddedType}
                    type={'noSpecialChar'}
                    id='prcNumber'
                    name='prcNumber'
                    error={errors['prcNumber']}
                    inputRef={el => (formRef.current.prcNumber = el)}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='18' className={classes.InputContainer}>
                  <label>TIN Number *</label>
                  <CustomTextFieldNumberFormat
                    disabled={isSubmitted || isAddedType}
                    type={'dashAndNum'}
                    error={errors['tinNumber']}
                    placeholder={isAddedType ? formatTinNumber(form['tinNumber']) : 'ex.000-000-000-000'}
                    id='tin'
                    name='tinNumber'
                    defaultValue={form['tinNumber'] && form['tinNumber']}
                    // inputRef={(el) => (formRef.current.tinNumber = el)}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    format='###-###-###-###'
                  />
                </div>
              </div>
              <div id='secondPartField' className={classes.secondPartField}>
                <div id='21' className={classes.firstTextField}>
                  <div id='middle-name' className={classes.middleName}>
                    <label>Middle Name *</label>
                    <TextField
                      disabled={isSubmitted || isAddedType}
                      error={errors['middleName']}
                      id='middleName'
                      name='middleName'
                      type={'noSpecialChar'}
                      inputRef={el => (formRef.current.middleName = el)}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </div>
                  <div id='last-name' className={classes.lastName}>
                    <label>Last Name *</label>
                    <TextField
                      disabled={isSubmitted || isAddedType}
                      error={errors['lastName']}
                      inputRef={el => (formRef.current.lastName = el)}
                      id='lastName'
                      name='lastName'
                      type={'noSpecialChar'}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </div>
                </div>
                <div id='22' className={classes.secondTextField}>
                  <div id='civil-status' className={classes.civilStatus}>
                    <label>Civil Status *</label>
                    <DropDownComponent
                      disabled={isSubmitted || isAddedType}
                      items={civilStatusList}
                      value={form['civilStatus']}
                      error={errors['civilStatus']}
                      id='civilStatus'
                      name='civilStatus'
                      onChange={HandleDropdown}
                    />
                  </div>
                  <div id='gender' className={classes.gender}>
                    <label>Gender *</label>
                    <DropDownComponent
                      disabled={isSubmitted || isAddedType}
                      items={genderList}
                      error={errors['gender']}
                      id='gender'
                      name='gender'
                      value={form['gender']}
                      onChange={HandleDropdown}
                    />
                  </div>
                </div>
                <div id='23' className={classes.textField}>
                  <label>Contact Number (phone/mobile) *</label>
                  <TextField
                    disabled={isSubmitted || isAddedType}
                    type={'number'}
                    error={errors['contactNumber']}
                    id='contactNumber'
                    name='contactNumber'
                    inputRef={el => (formRef.current.contactNumber = el)}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='24' className={classes.DropDowntextField}>
                  <label>Major Specialization *</label>
                  <DoctorSpecialization
                    disabled={isSubmitted || isAddedType}
                    error={errors['majorSpecialization']}
                    value={form['majorSpecialization']}
                    id='major'
                    name='majorSpecialization'
                    onChange={HandleDropdown}
                  />
                </div>
                <div id='25' className={classes.DropDowntextField}>
                  <label>Other Specialization </label>
                  <DoctorSpecialization
                    disabled={isSubmitted || isAddedType}
                    value={form['otherSpecialization']}
                    id='other'
                    name='otherSpecialization'
                    onChange={HandleDropdown}
                  />
                </div>
                <div id='26' className={classes.ptrNumberAndS2Number}>
                  <div id='PTR-number-label'>
                    <label>PTR Number</label>
                    <TextField
                      disabled={isSubmitted || isAddedType}
                      type={'noSpecialChar'}
                      id='ptrNumber'
                      name='ptrNumber'
                      inputRef={el => (formRef.current.ptrNumber = el)}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </div>
                  <div id='S2-number-label'>
                    <label>S2 Number</label>
                    <TextField
                      disabled={isSubmitted || isAddedType}
                      type={'noSpecialChar'}
                      id='s2Number'
                      name='s2Number'
                      inputRef={el => (formRef.current.s2Number = el)}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </div>
                </div>
                <div id='27' className={classes.textFieldforPRCfield}>
                  <div id='PRC-Expiry-date'>
                    <label>PRC Expiry Date *</label>
                    <CalendarInlineWithInlineEdit
                      error={errors['prcExpiration']}
                      errors={errors}
                      setErrors={setErrors}
                      editInputWidth={classes.editInputWidth}
                      id='prcExpiration'
                      name='prcExpiration'
                      placeholder='Select date'
                      value={dateFormat(form['prcExpiration'])}
                      onChange={HandleDate}
                      disabled={isSubmitted || isAddedType}
                    />
                  </div>
                  <div id='PhilHealth-Acrreditation-No'>
                    <label>PhilHealth Accreditation No. </label>
                    <CustomTextFieldNumberFormat
                      disabled={isSubmitted || isAddedType}
                      type={'dashAndNum'}
                      error={errors['philhealthNumber']}
                      name='philhealthNumber'
                      id='philhealthNumber'
                      placeholder={isAddedType ? formatPhilHealthNumber(form['philhealthNumber']) : 'Ex.0000-0000000-0'}
                      defaultValue={form['philhealthNumber'] && form['philhealthNumber']}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                      format='####-#######-#'
                    />
                  </div>
                  <div id='Philhealth-EXPIRY-DATE'>
                    <label>Philhealth Expiry Date</label>
                    <CalendarInlineWithInlineEdit
                      error={errors['philhealthExpiration']}
                      errors={errors}
                      setErrors={setErrors}
                      editInputWidth={classes.editInputWidth}
                      id='philhealthExpiration'
                      name='philhealthExpiration'
                      placeholder='Select date'
                      value={dateFormat(form['philhealthExpiration'])}
                      onChange={HandleDate}
                      disabled={isSubmitted || isAddedType}
                    />
                  </div>
                </div>
                <div id='28' className={classes.textField}>
                  <label>Check Payee *</label>
                  <TextField
                    disabled={isSubmitted || isAddedType}
                    id='payee'
                    name='payee'
                    inputRef={el => (formRef.current.payee = el)}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    type={'noSpecialChar'}
                    error={errors['payee']}
                  />
                </div>
              </div>
            </div>
          </div>
          <div id='Tin-status' className={classes.RadioButtons}>
            <div id='TinStatus' className={classes.TinStatus}>
              <div id='tinStatusRadioButtons' className={classes.tinStatusRadioButtons}>
                <div id='tinStatusSelect' className={classes.tinStatusSelect}>
                  <FormControl component='fieldset'>
                    <FormLabel className={classes.label} component='legend'>
                      <label style={{ color: '#1e2071' }}> TIN Status *</label>
                    </FormLabel>
                    <RadioGroup
                      aria-label='gender'
                      name='tinStatus'
                      className={classes.radioGroup}
                      value={form['tinStatus']}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    >
                      <FormControlLabel
                        value='12%'
                        disabled={isSubmitted || isAddedType}
                        className={classes.Vatable}
                        control={<Radio color='primary' />}
                        label='VATable'
                      />
                      <FormControlLabel
                        disabled={isSubmitted || isAddedType}
                        className={classes.NonVatable}
                        value='Non-VaTable'
                        control={<Radio color='primary' />}
                        label='Non-VATable'
                      />
                    </RadioGroup>
                    {errors['tinStatus'] && (
                      <FormHelperText style={{ color: 'red' }}>{errors['tinStatus']}</FormHelperText>
                    )}
                  </FormControl>
                </div>
                <div id='taxRate' className={classes.taxRate}>
                  <label>Tax Rate *</label>
                  <div id='taxRateInput' className={classes.taxRateInput}>
                    <CustomTextFieldWithIcons
                      type={'number'}
                      error={errors['vatTaxRate']}
                      disabled={isSubmitted || isAddedType}
                      styles={classes.customTag}
                      inputRef={el => (formRef.current.vatTaxRate = el)}
                      tags={'%'}
                      name='vatTaxRate'
                      onChange={HandleChangeTinStatus}
                    />
                  </div>
                </div>
              </div>
              <div id='taxRateAndNonVatableDropDown' className={classes.taxRateAndNonVatableDropDown}>
                <div id='NonVatableDropDown' className={classes['DropDownContainer']}>
                  <DropDownComponent
                    items={NonVatableData}
                    id='DropDownComponent'
                    name='nonVatable'
                    value={form['nonVatable']}
                    error={errors['nonVatable']}
                    disableComponents={isSubmitted || isAddedType || form['tinStatus'] === '12%'}
                    onChange={HandleDropdown}
                  />
                </div>
              </div>
            </div>
          </div>
          <div id='facilityInformationField' className={classes.facilityInformationField}>
            <div id='facilityInformation'>
              Facility Information <label id='facilityTobeAffiliated'>(Facility to be Affiliated)</label>
            </div>
            <div id='textFieldArea' className={classes.textFieldArea}>
              <div id='firstLayerInputs' className={classes['firstLayerInputs']}>
                <div id='facilityInput' className={classes.facilityInput}>
                  <label>Facility Name *</label>
                  <AutoSuggest
                    error={errors['facilityName']}
                    suggestions={facilityNames}
                    disabled={isSubmitted}
                    labelName='name'
                    value={form['facilityName'] && form['facilityName']}
                    onChange={handleFacilityNameChange}
                    name='name'
                    searchTerm={facilityNameSearchTerm}
                    onSelect={HandleChangeFacility}
                    isFetching={isFetching}
                    setSearchTerm={setFacilityNameSearchTerm}
                  />
                </div>
                <div id='medicalPartnerId' className={classes.medicalPartnerId}>
                  <label id='medicalPartnerId-label'> Medical Partner ID (Facility) *</label>
                  <AutoSuggest
                    suggestions={facilityIds}
                    error={errors['facilityMpId']}
                    disabled={isSubmitted}
                    value={form['facilityMpId']}
                    labelName='id'
                    onChange={handleFacilityIdChange}
                    name='id'
                    searchTerm={facilityIdSearchTerm}
                    onSelect={HandleChangeFacility}
                    isFetching={isFetchingIDs}
                    setSearchTerm={setFacilityIdSearchTerm}
                    show={6}
                  />
                </div>
              </div>
              <div id='SecondLayerInputs' className={classes['SecondLayerInputs']}>
                <div id='addressInput' className={classes.addressInput}>
                  <CustomizedTextArea label={'Address'}>
                    <textarea
                      id='Address'
                      disabled={isSubmitted || true}
                      className={classes.AddressField}
                      name='address'
                      type={'noSpecialChar'}
                      ref={el => (formRef.current.address = el)}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </CustomizedTextArea>
                </div>
              </div>
              <div id='ThirdLayerInputs' className={classes['ThirdLayerInputs']}>
                <div id='room-number-div' className={classes.roomNumber}>
                  <label>Room Number </label>
                  <TextField
                    disabled={isSubmitted}
                    inputRef={el => (formRef.current.roomNumber = el)}
                    type={'noSpecialChar'}
                    id='roomNumber'
                    name='roomNumber'
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='contact-number-fields' className={classes.contactNumber}>
                  <label>Contact Number/s</label>
                  <TextField
                    disabled={isSubmitted}
                    inputRef={el => (formRef.current.contactNumbers = el)}
                    id='contactNumber'
                    name='contactNumbers'
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='Clinic-Schedule-div' className={classes['Clinic-Schedule-div']}>
                  <label>Clinic Schedule</label>
                  <TextField
                    disabled={isSubmitted}
                    inputRef={el => (formRef.current.clinicSchedule = el)}
                    id='clinicSchedule'
                    name='clinicSchedule'
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
              </div>
              <div id='FourthLayerInputs' className={classes['FourthLayerInputs']}>
                <div id='coordinators-input' className={classes['coordinators-input']}>
                  <label> Coordinators</label>
                  <div>
                    <label>Name of Main Coordinators</label>
                    <TextField
                      disabled={isSubmitted}
                      inputRef={el => (formRef.current.mainCoordinatorName = el)}
                      id='mainCoordinator'
                      name='mainCoordinatorName'
                      type={'noSpecialChar'}
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </div>
                </div>
                <div id='Contact-Number-division' className={classes['Contact-Number']}>
                  <label> '</label>
                  <div>
                    <label>Contact Number</label>
                    <TextField
                      disabled={isSubmitted}
                      inputRef={el => (formRef.current.mainContactNum = el)}
                      id='mainCoordinatorContactNumber'
                      name='mainContactNum'
                      onChange={({ target: { value, name } }) => debounceChange(value, name)}
                    />
                  </div>
                </div>
              </div>
              <div id='FifthLayerInputs' className={classes['FifthLayerInputs']}>
                <div id='namenameOfAssitantFields' className={classes.namenameOfAssitantFields}>
                  <div id='nameOfAssitantInputs'>
                    <label>Name of Assistant Coordinator/s</label>
                  </div>
                  <div id='Contact-Number-One-div' className={classes['Contact-Number-One-div']}>
                    <label>Contact Number </label>
                  </div>
                </div>
                <div id='contactFields' className={classes.AdditionalNameOfAssistantFields}>
                  {asstCoordinators.map((items, index) => {
                    return (
                      <div key={items.id} id='AddAssistantWrapper' className={classes.AddAssistantWrapper}>
                        <div id='AddNewAssistantCoordinatorField'>
                          <TextField
                            disabled={isSubmitted}
                            id={`${index}-AddNewAssistantCoordinator`}
                            type={'noSpecialChar'}
                            name='asstCoordinatorName'
                            defaultValue={items.asstCoordinatorName}
                            onChange={({ target: { value, name } }) =>
                              debounceAddAssistant(value, name, index, items.id)
                            }
                          />
                        </div>
                        <div>
                          <div id='Contact-Number-two-div' className={classes['Contact-Number-two-div']}>
                            <TextField
                              disabled={isSubmitted}
                              defaultValue={items.asstContactNum}
                              id={`${index}-AddNewAssistantCoorNumber`}
                              name='asstContactNum'
                              onChange={({ target: { value, name } }) =>
                                debounceAddAssistant(value, name, index, items.id)
                              }
                            />
                          </div>
                        </div>
                        {!isSubmitted && (
                          <span id='deleteIcon' className={items.id === 1 ? classes.hideDelete : classes.delete}>
                            <ClearIcon onClick={() => handleRemoveAssistCoor(items.id)} />
                          </span>
                        )}
                      </div>
                    )
                  })}
                </div>
              </div>
            </div>
            <div id='addButtonAssitant' className={classes.addButtonAssitant}>
              <label onClick={isSubmitted ? null : handleAddAssistantCoor}>+Add Assistant Coordinator</label>
            </div>
          </div>
          <div id='AffiliationsPart' className={classes.AffiliationPart}>
            <label id='AffiliaionText' className={classes.AffiliaionText}>
              Affiliations
            </label>
            <div id='checkBox' className={classes.checkBox}>
              <CheckBox label={'Affiliated to AHMOPI?'} name='ahmopiAffiliated' />
            </div>
            <div id='tableDropDownInput' className={classes.tableDropDownInput}>
              <label id='textDesciption' className={classes.textDesciption}>
                AHMOPI Affiliations, Health Organizations and Specialty Societies
              </label>
              <AffiliationFormInput
                handleValue={addToGroup}
                HandleRemove={HandleRemove}
                onClick={handleGroup}
                AddToGroupsArr={handleGroupValue}
                HandleEdit={HandleEdit}
                inputField={
                  <TextField
                    disabled={isSubmitted}
                    name='affiliatedGroup'
                    inputRef={affiliatedGroupRef}
                    placeholder='Type Affiliate group'
                    type={'noSpecialChar'}
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                }
                dataheader={[{ prop: 'affiliatedGroup' }]}
              />
            </div>
          </div>
          <div id='textArea' className={classes.textArea}>
            <div className={styles.RemarksField}>
              <span>
                <div id='applicantRemarks'>
                  <label>Applicant Remarks</label>
                  <textarea
                    disabled={isSubmitted}
                    ref={el => (formRef.current.applicantRemarks = el)}
                    name='applicantRemarks'
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                </div>
                <div id='intellicareRemarks'>
                  <label>Intellicare Remarks *</label>
                  <textarea
                    disabled={isSubmitted}
                    ref={el => (formRef.current.intellicareRemarks = el)}
                    className={errors['intellicareRemarks'] && styles.errorClass}
                    name='intellicareRemarks'
                    onChange={({ target: { value, name } }) => debounceChange(value, name)}
                  />
                  <label className={styles.error}>{errors['intellicareRemarks']}</label>
                </div>
              </span>
            </div>
          </div>
        </div>
        <FooterWithButtons
          footerStatus={'Doctor'}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          onClickSave={handleClickSave}
          disableButton={disabledButton}
          onClickProceed={handleProceed}
          isSubmitted={isSubmitted}
          history={props.history}
        />
      </form>
    </React.Fragment>
  )
}

export default AffiliationForm
