import React, { useState, useEffect } from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './CustomTextFieldWithIcons.module.scss'

const CustomTextFieldWithIcons = props => {
  const {
    label = '',
    tags = '',
    styles,
    disabled,
    inputRef,
    required,
    readOnly,
    // handleOnChange,
    constainerStyle = {},
    error,
    onChange,
    type,
    ...rest
  } = props
  const { value = '', name = '' } = props
  const lettersOnly = RegExp(/^[a-z]+$/)
  const [_error, setError] = useState()
  const [inputVal, setInputVal] = useState(null)
  useEffect(() => {
    setError(error)
    if (typeof value === 'string') {
      if (!value.trim()) setError(error)
    }
    if (typeof value === 'number') {
      if (value <= 0) setError(error)
    }
  }, [error, value])

  const handleOnChange = e => {
    setError()
    if (typeof onChange === 'function') onChange(e)
  }

  const disableE = e => {
    if (type === 'integerOnly') DisableCharacters(e)
    if (type === 'number') {
      if (e.key === 'e' || e.key === 'E') {
        return e.preventDefault()
      }
    } else {
      return {}
    }
  }


  const DisableCharacters = e => {
    switch (type) {
      case 'integerOnly':
        if (
          e.key === 'e' ||
          e.key === 'E' ||
          lettersOnly.test(e.key) ||
          // e.keyCode === 50 ||
          e.keyCode === 190 ||
          e.keyCode === 189 ||
          e.keyCode === 111 ||
          e.keyCode === 106 ||
          e.keyCode === 107 ||
          e.keyCode === 109
        ) {
          return e.preventDefault()
        } else {
          return {}
        }
      default:
        return {}
    }
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                name={name}
                onChange={handleOnChange}
                type={type}
                // value={value}
                disabled={disabled}
                readOnly={readOnly}
                inputRef={inputRef}
                autoComplete='off'
                onKeyDown={e => disableE(e)}
                {...rest}
              />
              <div id='button' className={classes.button}>
                <label id='tags' className={`${styles}`}>
                  {tags}
                </label>
              </div>
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default CustomTextFieldWithIcons
