import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './AddContactModal.module.scss'
import ContactDetailsValidation from '../ContactDetailsValidation/ContactDetailsValidation'
// import Axios from 'axios'
// import isEmpty from 'lodash/isEmpty'

// const APIurl = process.env.REACT_APP_SERVER_API

const EditContact = ({ setOpen, open, contact, editedContacts, setEditetContact, type }) => {
  // const [open, setOpen] = React.useState(false)

  console.log('validate type', type)
  const [isLoading, setIsLoading] = React.useState(false)
  const [isFormValid, setIsFormValid] = React.useState(false)
  const [errors, setErrors] = React.useState({})
  const [form, setForm] = React.useState({
    cDesignation: '',
    cName: '',
    cNumber: '',
    cEmail: '',
    cDepartment: ''
  })

  React.useEffect(() => {
    setForm({ ...contact })
  }, [])

  const validate = () => {
    const { errors, isValid } = ContactDetailsValidation(form, type)

    if (isValid) {
      let newContacts = editedContacts.map((contact, index) => (index === form.index ? form : contact))
      setEditetContact(newContacts)
      setOpen(false)
      setIsFormValid(isValid)
    } else {
      setErrors(errors)
    }
  }

  if (isFormValid) {
    setErrors({})
    try {
      // let contacts = [...contactsValue]
      // console.log('add contacts data', contacts)
      // let obj = {}
      // obj.id = paramId
      // obj.contacts = contacts
      // setIsLoading(true)
      // setTimeout(async () => {
      //   const response = await Axios.put(APIurl + '/p/f/partners/facilities/medical/profile/contacts', obj).then(
      //     res => {
      //       return res
      //     }
      //   )
      //   console.log('response', response)
      //   setIsLoading(false)
      //   handleClose(false)
      // }, 1000)
      setIsFormValid(false)
    } catch (error) {
      console.log('add contact error', error)
      setIsLoading(false)
      setIsFormValid(false)
    }
  }

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const handleChange = ({ currentTarget: input }) => {
    const contactForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[input.name]
    contactForm[input.name] = input.value
    setErrors(newErrors)
    setForm(contactForm)
  }

  const handleClose = () => {
    setForm({
      cDesignation: '',
      cName: '',
      cNumber: '',
      cEmail: '',
      cDepartment: ''
    })
    setOpen(false)
    setErrors({})
  }
  return (
    <div id='container'>
      <Dialog fullWidth maxWidth={'md'} open={open} aria-labelledby='max-width-dialog-title'>
        <DialogTitle id='max-width-dialog-title'>Edit Contact</DialogTitle>
        <DialogContent>
          <form className={classes.root} noValidate autoComplete='off'>
            <div id='firstInput' className={type ? classes.firstInput1 : classes.firstInput}>
              <div id='fullnameField'>
                <TextField
                  name='cName'
                  onChange={handleChange}
                  label='Full Name'
                  value={form['cName']}
                  error={errors['cName']}
                />
              </div>
              <div id='designationField'>
                <TextField
                  onChange={handleChange}
                  label='Designation'
                  name='cDesignation'
                  error={errors['cDesignation']}
                  value={form['cDesignation']}
                />
              </div>
              <div id='departmentField'>
                <TextField
                  id='department'
                  name='cDepartment'
                  onChange={handleChange}
                  error={errors['cDepartment']}
                  label='Department'
                  value={form['cDepartment']}
                />
              </div>
            </div>
            <div id='secondInput' className={type ? classes.secondInput1 : classes.secondInput}>
              <div id='emailAddField'>
                <TextField
                  name='cEmail'
                  onChange={handleChange}
                  error={errors['cEmail']}
                  label='Email Address'
                  value={form['cEmail']}
                />
              </div>
              <div id='contactNumberfield'>
                <TextField
                  name='cNumber'
                  onChange={handleChange}
                  error={errors['cNumber']}
                  label='Contact Number'
                  value={form['cNumber']}
                />
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions style={{ marginRight: 8 }} className={classes.DialogActions}>
          <Button
            onClick={() => {
              handleClose(false)
            }}
            variant='contained'
            className={classes.editCancel}
          >
            CANCEL
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            {isLoading ? 'Saving...' : 'Save Changes'}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  )
}

export default EditContact
