import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import classes from './UploadFIleTable.module.scss'
import React, { useState, ajaxCall } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { setFiles } from 'Pages/Protected/Store/actions/Partner/FilesAffiliation'

import Preview from '../../../PreviewImage/PreviewImage'
import Axios from 'axios'
import validateForm from './validations'
import UploadFileContext from '../../../../Components/context/fileUploadContext'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

const URL = `${process.env.REACT_APP_SERVER_API}`

const TextWithSubscript = ({ mainText, subtext, isRequired }) => {
  // const required = isRequired ? <span className={classes.red}> * </span> : null
  return (
    <div id='locationMapText' className={classes.WithSubScript}>
      <text>{mainText}</text>
      {/* {required} */}
      {subtext && (
        <sub id='subscript-location' className={classes.subscript}>
          {subtext}
        </sub>
      )}
    </div>
  )
}

const ActualImageAreas = () => {
  return (
    <div id='ActualImageArea' className={classes.WithSubScript}>
      Actual Image of Areas in the Facility<br />
      <small id='subscript-ActualImageArea' className={classes.subscript}>
        *All facility department (Ancilliary Dept, Lab, Radiology, ER, Rehab, Others), All room types, Front Area/Front Image of the facility.
      </small>
    </div>
  )
}

const createData = uploadButton => {
  return { uploadButton }
}

const UploadFIleTable = props => {
  const { buttonText, forView, disable, setDisable, setIsApproved } = props
  const [mainFiles, setMainFiles] = useState([])
  const [errors, setErrors] = React.useState({})
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [form, setForm] = useState({
    'hospital-profile': 0,
    'service-offered': 0,
    'location-map': 0,
    'check-payable-letter': 0,
    'business-permit': 0,
    'bir-cor-2303': 0,
    'doh-permit': 0,
    'sec-dti-registration': 0
  })

  const validate = () => {
    const { errors, isValid } = validateForm(form)
    if (isValid) {
      setErrors({})
      if (setDisable) {
        setDisable(false)
      }
    } else {
      if (setDisable) {
        setDisable(true)
        setErrors(errors)
      }
    }
  }

  React.useEffect(() => {
    if (affiliationType['affiliationStatus'] === 'Submitted' || affiliationType['stageName'] === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setDisable(true)
      setIsApproved(true)
    } else {
      validate()
    }
  }, [form, mainFiles, affiliationType.affiliationStatus])

  React.useEffect(() => {
    if (props.affiliationTypeId) {
      fetchData(props.affiliationTypeId)
    }

    if (affiliationType.id) {
      fetchData()
    }
  }, [])

  const fetchData = async affiliationTypeId => {
    try {
      let id = affiliationType.id
      if (affiliationTypeId) {
        id = affiliationTypeId
      }
      const filesData = await AffiliationEncoderService.getUploadFile(id)
      const updateForm = { ...form }
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles(previousState => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file =>
            Object.assign(file, {
              preview: `${URL}/uploads/${file.path}`,
              fileType: fileData.file_type
            })
          )
        }))

        if (form && form.hasOwnProperty(fileData.file_type) && tmpFiles.length > 0) {
          updateForm[fileData.file_type] = tmpFiles.length
        }
      }
      setForm(updateForm)
    } catch (e) {
      console.log('error', e)
    }
  }

  const rows = [
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'Hospital Profile'} isRequired />}
        fileType={'hospital-profile'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={' Updated List of Services Offered with Rates and Room Rates'} isRequired />}
        fileType={'service-offered'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={'List of Doctors to be Accredited'}
        fileType={'accredited-doctors'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={'Medical Directory'}
        fileType={'medical-directory'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={
          <TextWithSubscript
            mainText={'Location Map'}
            subtext={'*Preferably screenshot from google maps'}
            isRequired
          />
        }
        fileType={'location-map'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'Check Payable Letter'} isRequired />}
        fileType={'check-payable-letter'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<ActualImageAreas />}
        fileType={'areas-in-facility'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'APE Packages'} subtext={'*Basic 5, ECG, PapSmear and Drug Test'} />}
        fileType={'APE-packages'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={'ECU Packages'}
        fileType={'ECU-pages'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'Business Permit'} isRequired />}
        fileType={'business-permit'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'BIR COR 2303'} isRequired />}
        fileType={'bir-cor-2303'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'DOH PERMIT'} isRequired />}
        fileType={'doh-permit'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={<TextWithSubscript mainText={'SEC/DTI Registration'} isRequired />}
        fileType={'sec-dti-registration'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    ),
    createData(
      <Preview
        desc={'PhilHealth Accreditation'}
        fileType={'philhealth-accreditation'}
        mainFiles={mainFiles}
        setMainFiles={setMainFiles}
        buttonText={'Upload'}
        forView={forView}
        form={form}
        setForm={setForm}
        errors={errors}
      />
    )
  ]
  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <UploadFileContext.Provider value={{disabled: disable}}>
        <Table className={classes.table} size='small' aria-label='a dense table'>
          <TableBody id='table-body'>
            {rows.map((row, index) => (
              <TableRow id='TableRow' className={classes['table-row']} key={index}>
                <TableCell className={classes['description-row']} component='th' scope='row'>
                  {row.uploadButton}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </UploadFileContext.Provider>
    </TableContainer>
  )
}

export default UploadFIleTable
