import React from 'react'
import { Switch, Route } from 'react-router-dom'
import history from '../Components/History/History'
import { Provider } from 'react-redux'

// Pages of Affiliation Form Encoder
import Dashboard from './Pages/Dashboard/Dashboard'
import MedicalPartnerEnrollmentPage from './Pages/MedicalPartnerEnrollmentPage/MedicalPartnerEnrollmentPage'
import HospitalAffiliationForm from './Pages/HospitalAffiliation/HospitalAffiliationForm/HospitalAffiliationForm'
import HospitalApplicationRequirements from './Pages/HospitalAffiliation/ApplicationRequirements/ApplicationRequirements'
import MedicalInformation from './Pages/MedicalClinicAffiliation/MedicalInformation/MedicalInformation'
import MedicalApplicationRequirements from './Pages/MedicalClinicAffiliation/ApplicationRequirements/ApplicationRequirements'
import DentalClinicInformation from './Pages/DentalClinicAffiliation/DentalClinicInformation/DentalClinicInformation'
import DentalApplicationRequirements from './Pages/DentalClinicAffiliation/ApplicationRequirements/ApplicationRequirements'
import DoctorInformation from './Pages/DoctorAffiliation/DoctorInformation/DoctorInformation'
import DoctorApplicationRequirements from './Pages/DoctorAffiliation/ApplicationRequirements/ApplicationRequirements'
import SpecialOtherServicesForm from './Pages/SpecialOtherServices/SpecialOtherServicesForm/SpecialOtherServicesForm'

// Store
import Store from '../../Store'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'


const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/affiliation-form-encoder'
  },
  {
    label: 'Partners',
    icon: 'users',
    to: '/test'
  },
  {
    label: 'Tickets',
    icon: 'list',
    to: '/test2'
  },
  {
    label: 'Reports',
    icon: 'chart-bar',
    to: '/test3'
  },
  {
    label: 'User',
    icon: 'user',
    to: '/test4'
  },
  {
    label: 'Table Maintenance',
    icon: 'calendar-alt',
    to: '/test5'
  }
]
const AffiliationFormEncoderRoutes = props => {
  React.useEffect(() => {
    SideBarService.setItems(sideBarItems)
    props.history.push('/medical-partner-enrollment')
  }, [])

  return (
    <React.Fragment>
      <Provider store={Store}>
        <Switch>
          <Route path='/affiliation-form-encoder' exact render={props => <Dashboard {...props} />} />
          <Route path='/medical-partner-enrollment' exact component={MedicalPartnerEnrollmentPage} />
          <Route path='/hospital-affiliation-form' exact render={props => <HospitalAffiliationForm {...props} />} />
          <Route
            path='/hospital-affiliation-requirements'
            exact
            render={props => <HospitalApplicationRequirements {...props} />}
          />
          <Route path='/hospital-affiliation-form/:id' exact render={props => <HospitalAffiliationForm {...props} />} />
          <Route path='/medical-clinic-affiliation-form' exact render={props => <MedicalInformation {...props} />} />
          <Route
            path='/medical-clinic-affiliation-requirements'
            exact
            render={props => <MedicalApplicationRequirements {...props} />}
          />
          <Route
            path='/medical-clinic-affiliation-form/:id'
            exact
            render={props => <MedicalInformation {...props} />}
          />
          <Route
            path='/dental-clinic-affiliation-form'
            exact
            render={props => <DentalClinicInformation {...props} />}
          />
          <Route
            path='/dental-clinic-affiliation-requirements'
            exact
            render={props => <DentalApplicationRequirements {...props} />}
          />
          <Route
            path='/dental-clinic-affiliation-form/:id'
            exact
            render={props => <DentalClinicInformation {...props} />}
          />
          <Route path='/doctor-affiliation-form' exact render={props => <DoctorInformation {...props} />} />
          <Route
            path='/doctor-affiliation-requirements'
            exact
            render={props => <DoctorApplicationRequirements {...props} />}
          />
          <Route path='/doctor-affiliation-form/:id' exact render={props => <DoctorInformation {...props} />} />
          <Route path='/special-other-services-form' exact render={props => <SpecialOtherServicesForm {...props} />} />
          <Route path='/special-other-services-form/:id' exact render={props => <SpecialOtherServicesForm {...props} />} />
        </Switch>
      </Provider>
    </React.Fragment>
  )
}

export default AffiliationFormEncoderRoutes
