import isEmpty from 'lodash/isEmpty'

import Joi from '@hapi/joi'
const schema = Joi.object({
  status: Joi.string()
    .label('Status')
    .required(), // required
  discount_type: Joi.string()
    .label('Discount Type')
    .required(), // required
  claim_type: Joi.string()
    .label('Claim Type')
    .required(), // required
  discount_application: Joi.string()
    .label('Discount Application')
    .required(), // required
  included_procedure: Joi.array()
    .label('Included Procedure')
    .required(), // required
  payment_type: Joi.string()
    .label('Payment Type')
    .required(), // required
  terms_in_days: Joi.number()
    .label('Terms in days')
    .required(), // required
  rate: Joi.number()
    .label('Rate')
    .required(), // required
  effectivity_date: Joi.string()
    .label('Effectivity Date')
    .required(), // required
  end_date: Joi.string()
    .label('End Date')
    .required(), // required
  discount_to_be_deducted_from: Joi.string()
    .label('Discount to be deducted from')
    .required(), // required
  other_details_of_discount: Joi.string()
    .label('Other details of discount')
    .required(), // required
  addProcedure: Joi.string().messages({'any.required': `"Add Procedure" is required`}).required()
})

export default function AddDiscountValidation (form, targetValue) {
  let errors = {}

  const errorMessageForProcedure = (errors) => {
    if (targetValue === 'discount_application') {
      delete errors['addProcedure']
      if (form['discount_application'] === 'Per Procedure') {
        delete errors['addProcedure']
        if (form['included_procedure'].length === 0) {
          errors['addProcedure'] = 'Add Procedure is a required field'
        } else {
          delete errors['addProcedure']
        }
      }
    } else {
      if (form['discount_application'].length > 0) {
        delete errors['addProcedure']
        if (form['discount_application'] === 'Per Procedure') {
          delete errors['addProcedure']
          if (form['included_procedure'].length === 0) {
            errors['addProcedure'] = 'Add Procedure is a required field'
          } else {
            delete errors['addProcedure']
          }
        }
      }
    }
  }

  const validateIfEmployeeDiscount = errors => {
    if (form && form['discount_type'] === 'Early Payment Discount') {
      if (form && form['terms_in_days'].length > 0) {
        if (form && Number.isInteger(Number(form['terms_in_days']))) {
          delete errors['terms_in_days']
        } else {
          errors['terms_in_days'] = 'Terms in days Integer only'
        }
      }
    }
  }

  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      if (targetValue === 'discount_type') {
        if (form['discount_type'].length > 0) {
          errors[i.path[0]] = i.message
          delete errors['addProcedure']
          validateIfEmployeeDiscount(errors)
          errorMessageForProcedure(errors)
        }
      } else {
        if (form['discount_type'].length > 0) {
          errors[i.path[0]] = i.message
          delete errors['addProcedure']
          validateIfEmployeeDiscount(errors)
          errorMessageForProcedure(errors)
        }
      }
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
