'use strict'

import React from 'react'

// Components
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import UploadFileTable from '../Components/UploadFIleTable/UploadFIleTable'
import FacilityInformation from '../Components/FacilityInformation/FacilityInformation'
// shared components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Material UI

// styles
import classes from './ApplicationRequirements.module.scss'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Doctor: {
    text: 'DOCTOR AFFILIATION',
    to: '/doctor-affiliation-form'
  }
}

const ApplicationRequirements = ({ history }) => {
  let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Doctor Affiliation Form'
  const [disable, setDisable] = React.useState(true)
  const [isApproved, setIsApproved] = React.useState(false)
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Doctor'])

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbs
        id='HeaderWithBreadCrumbs'
        HeaderTitle={headerTitle}
        stepDetails={stepDetails}
        currentStep={2}
      />
      <div id='contentWrapper' className={classes.contentWrapper}>
        <FacilityInformation
          history={history}
          returnPage={'/doctor-affiliation-form'}
          dialogText={'Doctor Affiliation'}
          titleText={'Doctor Profile'}
          adjustDrawer={classes.adjustDrawer}
          setDisable={setDisable}
          disable={disable}
          setIsApproved={setIsApproved}
          uploadFileTable={<UploadFileTable buttonText={'Upload'} setDisable={setDisable} disable={isApproved} setIsApproved={setIsApproved} />}
        />
      </div>
    </div>
  )
}

export default ApplicationRequirements
