import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'

export default function CustomDeleteSnackbar (props) {
  const { isSave, setIsSave } = props

  React.useEffect(() => {
    const autoClose = setTimeout(() => {
      setIsSave(false) /** Todo : Temporaryly commented due to error */
    }, 500)
    return () => clearTimeout(autoClose)
  })

  return (
    <React.Fragment>
      <Dialog open={isSave}>
        <DialogContent>
          <p style={{ padding: '20px', color: '#151c1b' }}>Deleted Successfully</p>
        </DialogContent>
      </Dialog>
    </React.Fragment>
  )
}
