import React from 'react'
import Paper from '@material-ui/core/Paper'
import { useSelector, useDispatch } from 'react-redux'
import Button from '@material-ui/core/Button'
import classes from './FacilityInformation.module.scss'
import DialogBoxConfirmation from '../DialogBoxConfirmation/DialogBoxConfirmation'
import CustomAlerSnackBar from '../CustomSnackbar/CustomAlertSnackbar'

import Axios from 'axios'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
const URL = `${process.env.REACT_APP_SERVER_API}`

const FacilityInformation = props => {
  const dispatch = useDispatch()
  const { uploadFileTable, returnPage, titleText, dialogText, adjustDrawer, disable, setDisable, setIsApproved } = props
  const [isSave, setIsSave] = React.useState(false)
  const { hospitalForms } = useSelector(state => state.AffiliationHospitalForm)
  const { affiliationType } = useSelector(state => state.affiliationType)
  const { affiliationStatus, stageName } = affiliationType

  React.useEffect(() => {
    if (affiliationStatus === 'Submitted' || stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setDisable(true)
      setIsApproved(true)
    }
  }, [affiliationStatus, stageName])

  const PrevPage = e => {
    e.preventDefault()
    props.history.push(returnPage)
    // window.$isReturn = true
    // window.$stageValue = window.$stageName
  }

  const handleSave = async () => {
    try {
      affiliationType.affiliationStatus = 'In-Progress'
      affiliationType.dateSubmitted = new Date().toISOString()
      let res = {}
      if (typeof affiliationType.id === 'undefined') {
        res = await Axios.post(`${URL}/p/f/partners/facilities`, { ...affiliationType, ...hospitalForms })
        affiliationType.id = res.data.data.id
      } else {
        res = await Axios.put(`${URL}/p/f/partners/facilities/${affiliationType.id}`, {
          ...affiliationType,
          ...hospitalForms
        })
      }
      dispatch(setAffiliationType(affiliationType))
      setIsSave(true)
    } catch (error) {
      console.log('error in save', error)
    }
  }

  return (
    <React.Fragment>
      <CustomAlerSnackBar isSave={isSave} setIsSave={setIsSave} />
      <div id='bodyContainer' className={classes.bodyContainer}>
        <div id='text-title' className={classes['text-title']}>
          <text>APPLICATION REQUIREMENTS</text>
        </div>
        <div id='tableContainer' className={classes.tableContainer}>
          <div id='uploadFileTable' className={classes.uploadFileTable}>
            <label id='tableTopText' className={classes.tableTopText}>
              <text>*Please Provide the following documents in order for your application to be processed.</text>
            </label>
            <Paper id='tableBox' className={classes.tableBox}>
              <div id='documentDesriptionText' className={classes.documentDesriptionText}>
                Document Description
              </div>
              <Paper id='uploadingOfDocuments' className={classes.uploadingOfDocuments}>
                {uploadFileTable}
              </Paper>
            </Paper>
          </div>
          {/* <div id='viewFileDrawer' className={classes.viewFileDrawer}>
            <div id='drawerContainer' className={adjustDrawer || classes.drawerContainer}>
              <FileDrawer titleText={titleText} />
            </div>
          </div> */}
        </div>
        <div id='buttonContainer' className={classes.buttonContainer}>
          <div id='buttonDiv' className={classes.buttonDiv}>
            <div id='buttonBox' className={classes.buttonBox}>
              <div id='first-child'>
                <Button variant='contained' className={classes.ReturnButton} onClick={PrevPage} style={{ textTransform: 'capitalize' }}>
                  Return
                </Button>
              </div>
              <div id='second-child'>
                <Button
                  variant='contained'
                  style={{ textTransform: 'capitalize' }}
                  className={disable ? classes.backgroundChange : classes.SaveButton}
                  disabled={disable}
                  onClick={() => {
                    handleSave()
                  }}
                >
                  <div style={{ fontSize: '12px' }}> Save</div>
                  {/* <div style={{ fontSize: '5px' }}>Auto Saved 5 minutes ago</div> */}
                </Button>
              </div>
              <div id='third-child'>
                <DialogBoxConfirmation
                  dialogText={dialogText}
                  setIsSave={setIsSave}
                  setDisable={setDisable}
                  disable={disable}
                  setIsApproved={setIsApproved}
                />
              </div>
            </div>
          </div>
        </div>
        <div id='footer-division'>
          <label id='footer' className={classes.Footer}>
            FACILITY AFFILIATION FORM 2 out of 2
          </label>
        </div>
      </div>
    </React.Fragment>
  )
}

// const IsReturn = () => {
//   return true
// }

export default FacilityInformation
