import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import DentalClinicInformation from '../PartnerInformation/DentalClinicInformation/PartnerInformation/PartnerInformation'
import UpdateDentalClinicInformation from '../PartnerInformation/DentalClinicInformation/EditPartnerInformation/EditPartnerInformation'
import ClinicProfileContext from './context'
import { getDentalClinicProfile } from './services/DentalClinicService'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'
import { useParams } from 'react-router-dom'
import DentalClinicProfileParamsContext from '../context/DentalClinicProfileParamsContext'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import getNestedObjects from '../../../../Components/getNestedObjects/getNestedObjects'

const PartnerDentalClinicInformation = props => {
  const { id } = useParams()
  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setDentalClinicName,
    setCityName,
    setCurrentStatus,
    setChangeName,
    isEdit,
    setIsEdit,
    setOpen
  } = React.useContext(DentalClinicProfileParamsContext)
  // const [isToUpdate, setIsToUpdate] = React.useState(false)
  const dispatch = useDispatch()
  const [profile, setProfile] = React.useState({})
  const [additionalInfo, setAdditionalProfilInfo] = React.useState({})
  const [discountTypeData, setDiscountType] = React.useState([])

  React.useEffect(() => {
    fetchData()
    setParamId(id)

    return () => {
      fetchData()
      setParamId(id)
    }
  }, [id, isToUpdate])

  const fetchData = async () => {
    const { profile, additionalProfileInfo } = await getDentalClinicProfile(id)

    const { data: facilityData } = await ProfileService.getFacilityDiscount(id)

    const { data } = facilityData

    setDiscountType(data.map(items => (items ? (items.discount_type ? items.discount_type : []) : [])))

    if (profile) {
      setDentalClinicName(profile.partnerName)
      const callFlow = getNestedObjects(profile, ['callFlow'])
      const contacts = profile ? (profile.contacts ? profile.contacts : []) : []
      const contactObj = { callFlow, contacts }
      dispatch(setListOfAffiliatedContacts(contactObj))
      setCityName(getNestedObjects(profile, ['city']))
      setCurrentStatus(profile ? profile.status : '')
      setAdditionalProfilInfo(additionalProfileInfo)
      setProfile(profile)
    }
  }

  const discountType = React.useMemo(() => discountTypeData, [discountTypeData])

  if (profile) {
    return (
      <React.Fragment>
        <div id='editButton' className={classes.editButton}>
          <div id='buttonContainer'>
            <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
              <div id='insideText' className={classes.insideText}>
                <text>Edit Partner</text>
                <text>Information</text>
              </div>
            </Button>
          </div>
        </div>
        <ClinicProfileContext.Provider
          value={{
            profile,
            setIsToUpdate,
            setProfile,
            setOpen,
            setChangeName,
            isEdit,
            setIsEdit,
            setDentalClinicName,
            additionalInfo,
            discountType
          }}
        >
          {!isToUpdate && <DentalClinicInformation />}
          {isToUpdate && <UpdateDentalClinicInformation />}
        </ClinicProfileContext.Provider>
      </React.Fragment>
    )
  }
}

export default PartnerDentalClinicInformation
