import React from 'react'

import isEmpty from 'lodash/isEmpty'

import classes from './EditPartnerInformation.module.scss'

import DentalClinicProfileForm from '../../utilities/DentalClinicFormValidation'

import ClinicProfile from '../../context'

import FacilityAssignment from 'Pages/Protected/Partner/Components/FacilityAssignment'

import { handleAddressChaining, fetchAddress, fetchRegion } from '../../../../../../Components/Utils/utils'

import ProfileService from '../../../../../../../../../Services/Api/Partner/ProfileService'

import {
  PartnerInformationFooterButton,
  DialogBoxIfUpdate,
  DialogBoxIfCancel
} from '../../../../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'

import CustomAlertSnackbar from '../../../../../../Components/CustomSnackBar/CustomAlertSnackbar'

import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'

import EditInputs from '../../utilities/EditInputs/EditInputs'

import InputRefs from '../../utilities/InputRefs'

import SavingData from '../../utilities/SavingData'

import TextField from '@material-ui/core/TextField'

import EditIcon from '@material-ui/icons/Edit'

// import AddressChaining from '../../../../../../../../../Constant/chaining-address'

// import { handleAddressChaining, fetchAddress, fetchRegion } from '../../../../../Components/Utils/utils'

const EditDentalClinicInformation = () => {
  const [openModal, setOpenModal] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const {
    profile,
    setIsToUpdate,
    setProfile,
    setChangeName,
    isEdit,
    setIsEdit,
    setDentalClinicName,
    additionalInfo,
    discountType
  } = React.useContext(ClinicProfile)

  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [provinces, setProvinces] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])

  const [errors, setErrors] = React.useState({})

  const [form, setForm] = React.useState({
    id: '',
    formerName: '',
    street: '',
    partnerName: '',
    region: '',
    district: '',
    city: '',
    barangay: '',
    zipCode: '',
    classification: '',
    accreditationNumber: '',
    philhealthExp: new Date().toISOString(),
    payee: '',
    creditLimit: null,
    paymentTerms: '',
    tinStatus: '',
    vatTaxRate: '',
    facilityDiscount: '',
    specialInstructions: '',
    dateOfAffiliation: new Date().toISOString(),
    dateOfDisaffiliation: new Date().toISOString(),
    processType: '',
    clinicSchedule: '',
    tvs: 'yes',
    status: 'Active',
    assignments: [],
    facilityAssignment: 'None',
    reason: '',
    exclusiveTo: '',
    years: '',
    months: ''
  })

  React.useEffect(() => {
    let newForm = { ...form }
    if (!isEmpty(profile)) {
      for (let key in profile) {
        newForm[key] = profile[key]
      }

      fetchAddress(newForm, setBarangays, setCities, setProvinces)
      fetchRegion(setRegions)
      setForm(newForm, profile)
    }
  }, [])

  const resultRef = InputRefs(form)

  const handleSubmitClose = () => {
    setOpenModal(false)
  }

  React.useEffect(() => {
    HandleChangeName(profile)
  }, [isEdit])

  const save = async () => {
    setIsLoading(true)

    try {
      // let newForm = changeName(form, 'formerName', 'name')
      let result = SavingData(form)
      const { data } = await ProfileService.updateDentalClinicProfile(profile.id, { ...result, ...additionalInfo })
      setIsLoading(false)
      handleSubmitClose(false)
      setProfile(data)
      setIsSave(true)
      setTimeout(() => {
        setIsToUpdate(false)
        setIsEdit(false)
      }, 1000)
    } catch (error) {
      console.log('error', error)

      setIsLoading(false)
    }
  }

  const validate = () => {
    const { errors } = DentalClinicProfileForm(form)
    if (form['status'] !== '') {
      delete errors['status']
    }
    if (isEmpty(errors)) {
      setOpenModal(true)
      setErrors({})
    }
    setErrors(errors)
  }
  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const handleChange = ({ currentTarget: input }) => {
    const ClinicForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[input.name]
    setErrors(newErrors)
    ClinicForm[input.name] = input.value

    setForm(ClinicForm)
  }

  const handleDropdown = ({ target: select }) => {
    const ClinicForm = { ...form }
    ClinicForm[select.name] = select.value
    errors[select.name] = false
    setErrors(errors)
    setForm(ClinicForm)
    handleAddressChaining(select.name, select.value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }

  React.useEffect(() => {
    HandleChangeName()
  }, [form.partnerName])

  const HandleChangeName = () => {
    if (isEdit) {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(false)
              }}
            />
          </div>
          <TextField
            id='inputName'
            className={classes.TextField}
            autoComplete='off'
            name='partnerName'
            // inputRef={resultRef.partnerName}
            value={form.partnerName}
            onChange={handleChange}
          />
        </div>
      )
    } else {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(true)
              }}
            />
          </div>
          <div id='partnerName' className={classes.partnerName}>
            {form.partnerName}
          </div>
        </div>
      )
    }
  }
  const discountTypeData = React.useMemo(() => discountType, [discountType])

  const Information = EditInputs(
    handleChange,
    handleDropdown,
    errors,
    form,
    resultRef,
    setForm,
    setErrors,
    discountTypeData,
    regions, provinces, cities, barangays, isFetching
  )

  const memoizedInformation = React.useMemo(() => Information, [Information])

  return (
    <React.Fragment>
      <form onSubmit={handleSubmit}>
        <div id='root' className={classes.root}>
          <FacilityAssignment
            form={form}
            setForm={setForm}
            errors={errors}
            setErrors={setErrors}
            reasonRef={resultRef.reason}
            exclusiveRef={resultRef.exclusiveTo}
          />
          <div id='wrapper' className={classes.wrapper}>
            <PartnerViewInformation
              data={memoizedInformation}
              facilityLabel={'Preferred Medical Partner'}
              listStyles={classes.listStyles}
              firstColumStyle={classes.firstRow}
              secondColumnStyle={classes.secondRow}
              ifHastTitle2
              hideStar
              listStyles2={classes.listStyles2}
            />
          </div>
        </div>
        {/* END OF ROOT DIV */}
        <PartnerInformationFooterButton setIsCancel={setIsCancel} handleSubmit={handleSubmit} />
      </form>
      <div>
        <DialogBoxIfUpdate
          handleSubmitClose={handleSubmitClose}
          HandleSave={save}
          isLoading={isLoading}
          openModal={openModal}
        />
      </div>
      <div>
        <DialogBoxIfCancel isCancel={isCancel} setIsCancel={setIsCancel} setIsToUpdate={setIsToUpdate} />
        {isSave && <CustomAlertSnackbar isSave={isSave} setIsSave={setIsSave} />}
      </div>
    </React.Fragment>
  )
}

export default React.memo(EditDentalClinicInformation, (prevProps, nextProps) => {
  try {
    if (prevProps.Information === nextProps.Information) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
