import React from 'react'
import classes from './EditDoctorInformation.module.scss'
import CustomAlertSnackbar from '../../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import {
  PartnerInformationFooterButton,
  DialogBoxIfUpdate,
  DialogBoxIfCancel
} from '../../../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'
import FacilityAssignmentForDoctors from '../../../../../Components/FacilityAssignmentForDoctors/FacilityAssignmentForDoctors'
import DoctorProfile from '../context'
import InitialState from '../InputsforEdit/InitialState'
import DoctorValidation from './DoctorValidation'
import ProfileService from '../../../../../../../../Services/Api/Partner/ProfileService'
import PartnerViewInformation from '../../../../../Components/PartnerViewInformation/PartnerViewInformation'
// import isEmpty from 'lodash/isEmpty'
import DoctorProfileData from '../InputsforEdit/InputsforEdit'
import { useParams } from 'react-router-dom'
import InputRefs from '../InputsforEdit/InputRefs'
import { Destructure, SavingData } from './DeStructure'
import EditIcon from '@material-ui/icons/Edit'
import TextField from '@material-ui/core/TextField'

const EditDoctorInformation = () => {
  const { doctorId } = useParams()
  const [form, setForm] = React.useState(InitialState)
  const [errors, setErrors] = React.useState({})
  const [openModal, setOpenModal] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [change, setChange] = React.useState(false)
  const {
    profile,
    setIsToUpdate,
    setProfile,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    headerData
  } = React.useContext(DoctorProfile)

  React.useEffect(() => {
    if (profile) {
      const newForms = { ...form }
      const result = Destructure(profile, newForms)
      setForm(result)
    }
  }, [])

  const HandleChange = e => {
    const { name, value } = e.target
    const doctorForm = { ...form }
    setChange(true)
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    doctorForm[name] = value

    setForm(doctorForm)
  }

  const HandleSave = async () => {
    setIsLoading(true)
    try {
      const formResult = SavingData(form)
      const {
        data: { data: newData }
      } = await ProfileService.updateDoctorProfile(doctorId, formResult)
      setIsLoading(false)
      handleSubmitClose(false)
      setIsSave(true)
      setProfile(newData)
      const newHeaderData = { ...headerData }
      const lastName = newData ? (newData.name ? newData.name.last : '') : ''
      const firstName = newData ? (newData.name ? newData.name.first : '') : ''
      const middleName = newData ? (newData.name ? newData.name.middle : '') : ''
      const suffix = newData ? (newData.name ? (newData.name.suffix ? newData.name.suffix : '') : '') : ''
      newHeaderData['name'] = `${lastName}, ${firstName} ${middleName} ${suffix}`
      newHeaderData['fullName'] = `${firstName} ${middleName} ${lastName} ${suffix}`
      setHeaderData(newHeaderData)
      setTimeout(() => {
        setIsToUpdate(false)
        setIsEdit(false)
      }, 1000)
    } catch (error) {
      setIsLoading(false)
    }
  }

  const validate = () => {
    const { errors, isValid } = DoctorValidation(form)
    if (isValid) {
      setOpenModal(true)
      setErrors({})
    } else {
      setErrors(errors)
    }
  }

  const handleSubmitClose = () => {
    setOpenModal(false)
  }
  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const refValue = InputRefs(profile, change, form)
  const Information = DoctorProfileData(refValue, HandleChange, form, errors, setForm, setErrors)

  React.useEffect(() => {
    HandleChangeName(profile)
  }, [form.first_name, form.last_name, form.middle_name])

  React.useEffect(() => {
    HandleChangeName(profile)
  }, [isEdit])

  const HandleChangeName = () => {
    if (isEdit) {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(false)
              }}
            />
          </div>
          <TextField
            id='last_name'
            className={classes.TextField}
            autoComplete='off'
            name={'last_name'}
            placeholder='Last Name'
            value={form.last_name}
            onChange={HandleChange}
          />
          <TextField
            id='first_name'
            className={classes.TextField}
            autoComplete='off'
            name={'first_name'}
            placeholder='First Name'
            value={form.first_name}
            onChange={HandleChange}
          />
          <TextField
            id='middle_name'
            className={classes.TextField}
            autoComplete='off'
            name={'middle_name'}
            placeholder='Middle Name'
            value={form.middle_name}
            onChange={HandleChange}
          />
        </div>
      )
    } else {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(true)
              }}
            />
          </div>
          {`${form.last_name} , ${form.first_name}  ${form.middle_name}`}
        </div>
      )
    }
  }

  return (
    <React.Fragment>
      <form>
        <div id='root' className={classes.root}>
          <FacilityAssignmentForDoctors
            form={form}
            setForm={setForm}
            refValue={refValue}
            errors={errors}
            setErrors={setErrors}
          />
          <PartnerViewInformation
            data={Information}
            hideStar
            listStyles={classes.listStyles}
            firstColumStyle={classes.firstRow}
            secondColumnStyle={classes.secondRow}
          />
        </div>

        <PartnerInformationFooterButton isLoading={isLoading} setIsCancel={setIsCancel} handleSubmit={handleSubmit} />
      </form>
      <div>
        <DialogBoxIfUpdate
          handleSubmitClose={handleSubmitClose}
          HandleSave={HandleSave}
          isLoading={isLoading}
          openModal={openModal}
        />
      </div>
      <div>
        <DialogBoxIfCancel isCancel={isCancel} setIsCancel={setIsCancel} setIsToUpdate={setIsToUpdate} />
        {isSave && <CustomAlertSnackbar isSave={isSave} setIsSave={setIsSave} />}
      </div>
    </React.Fragment>
  )
}

export default React.memo(
  EditDoctorInformation,

  (prevProps, nextProps) => {
    try {
      if (
        prevProps.Information === nextProps.Information &&
        prevProps.errors === nextProps.errors &&
        prevProps.form === nextProps.form &&
        prevProps.refValue === nextProps.refValue
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)
