import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './PartnerInformationFooterButton.module.scss'
import Box from '@material-ui/core/Box'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import CustomDialog from '../../Components/CustomDialog/CustomDialog'
import { PropTypes } from 'prop-types'

const PartnerInformationFooterButton = props => {
  const { isLoading, setIsCancel, handleSubmit } = props
  return (
    <div className={classes.FooterButtonContainer}>
      <span className={classes.FooterButtonWrapper}>
        <div>
          <Button
            onClick={() => setIsCancel(true)}
            variant='contained'
            color='secondary'
            className={classes.cancelButton}
          >
            Cancel
          </Button>
        </div>
        <div />
        <div>
          <div>
            <Button variant='contained' type='submit' className={classes.SaveBtn} onClick={handleSubmit}>
              {isLoading ? 'sending...' : 'Save'}
            </Button>
          </div>
        </div>
      </span>
    </div>
  )
}

const DialogBoxIfUpdate = props => {
  const { handleSubmitClose, HandleSave, isLoading, openModal } = props

  return (
    <Dialog
      id='id_submit_modal'
      open={openModal}
      onClose={handleSubmitClose}
      aria-labelledby='alert-dialog-title'
      aria-describedby='alert-dialog-description'
    >
      <DialogContent>
        <DialogContentText id='alert-dialog-submit-description'>
          <Box display='flex' flexDirection='column' justifyContent='center' alignItems='center'>
            <h3 style={{ textAlign: 'center' }}>Are you sure you want to update?</h3>
            <div className={classes.componentFooter}>
              <Button
                style={{ backgroundColor: '#fd5451', color: 'white', marginRight: '10px' }}
                onClick={handleSubmitClose}
              >
                No
              </Button>
              <Button style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '10px' }} onClick={HandleSave}>
                {isLoading ? 'sending...' : 'Yes'}
              </Button>
            </div>
          </Box>
        </DialogContentText>
      </DialogContent>
    </Dialog>
  )
}

const DialogBoxIfCancel = props => {
  const { isCancel, setIsCancel, setIsToUpdate } = props

  return (
    <CustomDialog id='sos_custom_dialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
      <div id='sos_custom_dialog_container' className={classes.dialogContainer}>
        <h3 id='sos_custom_dialog_message'>All changes will not be saved, are you sure you want to cancel?</h3>
        <div id='sos_custom_dialog_btn_group' className={classes.btnGroup}>
          <Button
            onClick={() => setIsCancel(false)}
            id='sos_custom_dialog_btn_no'
            className={classes.btnCancel}
            variant='contained'
            style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
          >
            No
          </Button>
          <Button
            onClick={() => setIsToUpdate(false)}
            id='sos_custom_dialog_btn_yes'
            variant='contained'
            style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
          >
            Yes
          </Button>
        </div>
      </div>
    </CustomDialog>
  )
}

export { PartnerInformationFooterButton, DialogBoxIfUpdate, DialogBoxIfCancel }

PartnerInformationFooterButton.propTypes = {
  isLoading: PropTypes.bool,
  setIsCancel: PropTypes.func.isRequired,
  handleSubmit: PropTypes.func.isRequired
}

DialogBoxIfUpdate.propTypes = {
  handleSubmitClose: PropTypes.func.isRequired,
  HandleSave: PropTypes.func.isRequired,
  isLoading: PropTypes.bool,
  openModal: PropTypes.bool.isRequired
}

DialogBoxIfCancel.propTypes = {
  isCancel: PropTypes.bool.isRequired,
  setIsCancel: PropTypes.func.isRequired,
  setIsToUpdate: PropTypes.func.isRequired
}
