import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import Preview from '../../../PreviewImage/PreviewImage'

import classes from './UploadFIleTable.module.scss'
import { useSelector } from 'react-redux'
import validateForm from './validation'
import UploadFileContext from '../../../../Components/context/fileUploadContext'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
const URL = `${process.env.REACT_APP_SERVER_API}`

const PhotoOfPhicID = () => {
  return (
    <div id='PhotoOfPhicID' className={classes.WithSubScript}>
      <text>Photo of PHIC ID</text>
      <sub id='subscript-PhotoOfPhicID' className={classes.subscript}>
        *For PHIC Accredited Doctors
      </sub>
    </div>
  )
}

const SwornDeclaration = () => {
  return (
    <div id='SwornDeclaration' className={classes.WithSubScript}>
      <text>Sworn Declaration of Gross Receipts/Sales</text>
      <sub id='subscript-SwornDeclaration' className={classes.subscript}>
        *For New Affiliation of Doctor
      </sub>
    </div>
  )
}

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const UploadFIleTable = (props) => {
  const { disable, setDisable, setIsApproved } = props
  const [mainFiles, setMainFiles] = useState([])
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [errors, setErrors] = React.useState({})
  const [form, setForm] = useState({
    'doctor-resume': 0,
    'certificate-of-fellowship-diplomate': 0,
    'prc-license-id': 0,
    'sworn-declaration': 0,
    'bir-cor-2303': 0
  })

  const validate = () => {
    const { errors, isValid } = validateForm(form)
    if (isValid) {
      setErrors({})
      if (setDisable) {
        setDisable(false)
      }
      return true
    } else {
      if (setDisable) {
        setDisable(true)
      }
      setErrors(errors)
      return false
    }
  }

  React.useEffect(() => {
    if (affiliationType['affiliationStatus'] === 'Submitted') {
      setDisable(true)
      setIsApproved(true)
    } else {
      validate()
    }
  }, [form, mainFiles, affiliationType.affiliationStatus])

  React.useEffect(() => {
    if (props.affiliationTypeId) {
      fetchData(props.affiliationTypeId)
    }

    if (affiliationType.id) {
      fetchData()
    }
  }, [])

  const fetchData = async () => {
    try {
      const filesData = await AffiliationEncoderService.getUploadFile(affiliationType.id)
      const updateForm = {...form}
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles((previousState) => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file => Object.assign(file, {
            preview: `${URL}/uploads/${file.path}`,
            fileType: fileData.file_type
          }))
        }))
        if (form && form.hasOwnProperty(fileData.file_type) && tmpFiles.length > 0) {
          updateForm[fileData.file_type] = tmpFiles.length
        }
      }
      setForm(updateForm)
    } catch (e) {
      console.log('error', e)
    }
  }
  const rows = [
    createData(<Preview desc={'Doctor Resume'} fileType={'doctor-resume'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    createData(<Preview desc={'Copy of Certificate of Fellowship and Diplomate'} fileType={'certificate-of-fellowship-diplomate'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    // createData(<Preview desc={'List of Doctors to be Accredited'} fileType={'list-of-doctors-accredited'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    createData(<Preview desc={'Recent 2x2 Photo'} fileType={'recent-photo'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    createData(<Preview desc={<PhotoOfPhicID />} fileType={'photo-phic-id'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    createData(<Preview desc={'Photocopy of PRC License ID'} fileType={'prc-license-id'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    createData(<Preview desc={<SwornDeclaration />} fileType={'sworn-declaration'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />),
    createData(<Preview desc={'BIR COR 2303'} fileType={'bir-cor-2303'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} form={form} setForm={setForm} errors={errors} />)
  ]
  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <UploadFileContext.Provider value={{disabled: disable}}>
        <Table className={classes.table} size='small' aria-label='a dense table'>
          <TableBody id='table-body'>
            {rows.map((row, index) => (
              <TableRow id='TableRow' className={classes['table-row']} key={index}>
                <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                  {row.description}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </UploadFileContext.Provider>
    </TableContainer>
  )
}

export default UploadFIleTable
