import React from 'react'

import classes from './GeneralInformation.module.scss'
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from 'SharedComponents/DatePicker/DatePicker'
import moment from 'moment'

const GeneralInformation = props => {
  const {
    readOnly = false,
    nameField = {},
    levelField = {},
    mnoField = {},
    addressField = {},
    inspectorField = {},
    dateField = {},
    dispatch = {},
    onChange = () => {},
    errors = {}
  } = props

  const onChangeInspectorName = e => {
    dispatch(onChange({ nameOfInspector: e.target.value }))
  }

  const onChangeDateOfInspection = date => {
    dispatch(onChange({ dateOfInspection: date }))
  }

  return (
    <React.Fragment>
      <div id='generalInformationRoot' className={classes.root}>
        <h2 id='generalInformationTitle' className={classes.title}>
          General Information
        </h2>
        <div id='generalInformationContent' className={classes.content}>
          <div id='generalInformationFirstRow' className={classes.row}>
            <div className={classes.fieldContainer}>
              <h4 className={classes.fieldLabel}>{nameField.label}</h4>
              <h3 className={classes.fieldContent}>{nameField.content}</h3>
            </div>
            <div className={classes.fieldContainer}>
              <h4 className={classes.fieldLabel}>{levelField.label}</h4>
              <h3 className={classes.fieldContent}>{levelField.content}</h3>
            </div>
            <div className={classes.fieldContainer}>
              <h4 className={classes.fieldLabel}>{mnoField.label}</h4>
              <h3 className={classes.fieldContent}>{mnoField.content}</h3>
            </div>
          </div>
          <div id='generalInformationSecondRow' className={classes.row}>
            <div className={classes.fieldContainer}>
              <h4 className={classes.fieldLabel}>{addressField.label}</h4>
              <h3 className={classes.fieldContent}>{addressField.content}</h3>
            </div>
          </div>
          <div id='generalInformationSecondRow' className={classes.row}>
            <div className={classes.fieldContainer}>
              <h4 id='tg_label' className={classes.fieldLabel}>
                {inspectorField.label}
              </h4>
              <h3 id='tg_content' className={classes.fieldContent}>
                {readOnly ? (
                  inspectorField.value
                ) : (
                  <CustomTextField
                    id='inspectorFieldNameID'
                    value={inspectorField.value}
                    onChange={onChangeInspectorName}
                    error={errors.nameOfInspector}
                  />
                )}
              </h3>
            </div>
            <div className={classes.fieldContainer}>
              <h4 id='tg_label' className={classes.fieldLabel} style={!readOnly ? { marginBottom: '5px' } : {}}>
                {dateField.label}
              </h4>
              <h3 id='tg_content' className={classes.fieldContent}>
                {readOnly ? (
                  moment(dateField.value).format('MM/DD/YYYY')
                ) : (
                  <DatePicker
                    id='inspectorDateFieldID'
                    value={dateField.value}
                    onChange={onChangeDateOfInspection}
                    error={errors.dateOfInspection}
                  />
                )}
              </h3>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default React.memo(GeneralInformation, (prevProps, nextProps) => {
  try {
    if (
      prevProps.readOnly === nextProps.readOnly &&
      prevProps.nameField.content === nextProps.nameField.content &&
      prevProps.levelField.content === nextProps.levelField.content &&
      prevProps.mnoField.content === nextProps.mnoField.content &&
      prevProps.addressField.content === nextProps.addressField.content &&
      prevProps.inspectorField.value === nextProps.inspectorField.value &&
      prevProps.dateField.value === nextProps.dateField.value &&
      prevProps.errors.nameOfInspector === nextProps.errors.nameOfInspector &&
      prevProps.errors.dateOfInspection === nextProps.errors.dateOfInspection
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
