import React from 'react'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'

import classes from './JoiValidatiionCustomTextField.module.scss'

const CustomTextField = (props) => {
  const { label = '', required, readOnly, constainerStyle = {}, error, onChange, type, minWidth, ...other } = props

  let specialChar = RegExp(/[!@#$%^&*(),.?":{}|<>]/)
  let numbersOnly = RegExp(/^[\d\b]+$/)
  let lettersOnly = RegExp(/^[a-z]+$/)
  // let dashAndNum = RegExp(/^[\d -]{14}/)

  const DisableCharacters = (e, type) => {
    switch (type) {
      case 'number':
        if (e.key === 'e' || e.key === 'E') {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'noSpecialChar':
        return specialChar.test(e.key) && e.preventDefault()

      case 'dashAndNum':
        if (specialChar.test(e.key) || lettersOnly.test(e.key)) {
          return e.preventDefault()
        } else {
          return {}
        }

      case 'lettersOnly':
        if (specialChar.test(e.key) || numbersOnly.test(e.key)) {
          return e.preventDefault()
        } else {
          return {}
        }
      case 'integerOnly':
        if (
          e.key === 'e' ||
            e.key === 'E' ||
            e.keyCode === 190 ||
            e.keyCode === 189 ||
            e.keyCode === 111 ||
            e.keyCode === 106 ||
            e.keyCode === 107 ||
            e.keyCode === 109 ||
            e.keyCode === 187 ||
            e.keyCode === 219 ||
            e.keyCode === 191 ||
            e.keyCode === 221 ||
            e.keyCode === 220 ||
            e.keyCode === 222 ||
            specialChar.test(e.key) ||
            lettersOnly.test(e.key)
        ) {
          return e.preventDefault()
        } else {
          return {}
        }

      default:
        return {}
    }
  }

  return (
    <div className={classes.root} style={constainerStyle}>
      <Typography variant='subtitle2' className={classes.label}>
        {label}
        {!readOnly && required ? ' *' : ''}
      </Typography>
      <div className={classes.wrapper} style={minWidth}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {props.value}
          </Typography>
        ) : (
          <React.Fragment>
            <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
              <InputBase
                className={classes.input}
                onChange={({ target: { value, name } }) => onChange(value, name)}
                type={type}
                {...other}
                autoComplete='new-password'
                onKeyDown={e => DisableCharacters(e, type)}
              />
            </Paper>
            {error && <span className={classes.error}>{error}</span>}
          </React.Fragment>
        )}
      </div>
    </div>
  )
}

export default React.memo(CustomTextField)
