import React from 'react'
import Button from '@material-ui/core/Button'
// import history from '../../../../../Components/History/History'
import CustomAlerSnackBar from '../CustomSnackbar/CustomAlertSnackbar'
import YesOrNoDialogBox from '../../../../../Components/YesOrNoDialogBox/YesOrNoDialogBox'
import { useSelector } from 'react-redux'
// import { setAffiliationFormData } from '../../../../../../Store/actions/Partner/hospitalAffiliationForm'
// import ApplicationRequirements from '../../../Partner/Hospital/Pages/ApplicationRequirements/ApplicationRequirements'
import classes from './FooterWithButtons.module.scss'

const FooterWithButtons = props => {
  const {affiliationType} = useSelector(state => state.affiliationType)
  const { footerStatus, FacilityformPage, disableButton, onClickSave, isSuccess = false, setIsSuccess, onClickProceed, isSubmitted } = props
  console.log('affil sa footer button', affiliationType)

  let BackgroundChange

  const PrevPage = e => {
    e.preventDefault()
    props.history.push('/medical-partner-enrollment')
  }

  if (disableButton) {
    BackgroundChange = classes.backgroundChange
  }

  return (
    <React.Fragment>
      <CustomAlerSnackBar isSave={isSuccess} setIsSave={setIsSuccess} />
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div>
            <YesOrNoDialogBox PrevPage={PrevPage} />
          </div>
          <div />
          <div id-='TwoButtons'>
            <div id='SaveButton'>
              <Button variant='contained' disabled={isSubmitted} onClick={onClickSave} className={isSubmitted ? classes.disabledButton : classes.SaveButton}>
                <div style={{ fontSize: '12px' }}> Save</div>
              </Button>
            </div>
            <div id='ProceedButton'>
              <Button
                variant='contained'
                onClick={onClickProceed}
                disabled={disableButton}
                className={BackgroundChange || classes.ProceedButton}
              >
                Proceed
              </Button>
            </div>
          </div>
        </span>
        <div style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}>
          {footerStatus} Affiliation Form 1 out 2
        </div>
      </div>
    </React.Fragment>
  )
}

export default FooterWithButtons
