import React from 'react'
import TableCell from '@material-ui/core/TableCell'
import DropDown from '../../../Components/CustomDrowpdown'
import CustomTextField from '../../../Components/JoiValidationInputField/InputFieldWithDebounce'
import TableRow from '@material-ui/core/TableRow'
import DialogTitle from '@material-ui/core/DialogTitle'
import DialogContent from '@material-ui/core/DialogContent'
import classes from './AddRowForm.module.scss'
import DialogActions from '@material-ui/core/DialogActions'
import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DisplayProcedures from './DisplayProcedures'
import DisplaySelectedProcedures from './DisplaySelectedProcedures'
import DatePicker from '../../../Components/DatePicker/DatePicker'
import RowAction from './RowActions'
import AddDiscountValidations from '../validations/AddDiscountValidation'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import { GeneralController } from '../../../../../../Controllers/GeneralController'
import IcdCptStorageServices from 'Services/Storage/IcdCptStorageServices'
import CustomAutoComplete from 'Pages/Protected/Partner/Components/AutoSuggest'
import { DialogBoxIfUpdate } from '../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'

const AddRowForm = props => {
  const {
    index,
    setCurrentData,
    currentDataRef,
    LimitRows,
    LimitRowsRef,
    setLimitRows,
    currentFormData,
    form,
    setForm,
    errors,
    setErrors,
    setIsDisabled,
    setShouldValidate,
    shouldValidate,
    isEdit
  } = props
  const status = [
    { id: 1, text: 'Active', value: 'Active' },
    { id: 2, text: 'Inactive', value: 'Inactive' }
  ]
  const discount = [
    { id: 1, text: 'Early Payment Discount', value: 'Early Payment Discount' },
    { id: 2, text: 'Monthly Volume Discount', value: 'Monthly Volume Discount' },
    { id: 3, text: 'Annual Volume Discount', value: 'Annual Volume Discount' },
    { id: 4, text: 'Quarterly Volume Discount', value: 'Quarterly Volume Discount' }
  ]
  const claims = [
    { id: 1, text: 'All Claims', value: 'All Claims' },
    { id: 2, text: 'Out-Patient Procedure', value: 'Out-Patient Procedure' },
    { id: 3, text: 'Out-Patient Consultation', value: 'Out-Patient Consultation' },
    { id: 4, text: 'In-Patient', value: 'In-Patient' },
    { id: 5, text: 'Reimbursement', value: 'Reimbursement' },
    { id: 6, text: 'Emergency', value: 'Emergency' },
    { id: 7, text: 'Dental', value: 'Dental' },
    { id: 8, text: 'Medical Allowance', value: 'Medical Allowance' },
    { id: 9, text: 'APE', value: 'APE' },
    { id: 10, text: 'ECU', value: 'ECU' },
    { id: 11, text: 'Pharmacy', value: 'Pharmacy' },
    { id: 12, text: 'PPE', value: 'PPE' },
    { id: 13, text: 'Medical Supplies', value: 'Medical Supplies' },
    { id: 14, text: 'Clinic Management', value: 'Clinic Management' }
  ]
  const discounts = [
    { id: 1, text: 'Per Patient', value: 'Per Patient' },
    { id: 2, text: 'Per Procedure', value: 'Per Procedure' },
    { id: 3, text: 'All Gross Billing', value: 'All Gross Billing' },
    { id: 4, text: 'All Net Billing', value: 'All Net Billing' },
    { id: 5, text: 'All Procedures', value: 'All Procedures' }
  ]
  const paymentTypes = [
    { id: 1, text: 'Outright', value: 'Outright' },
    { id: 2, text: 'Rebates', value: 'Rebates' },
    { id: 3, text: 'Rebates via CM', value: 'Rebates via CM' },
    { id: 4, text: 'Immediately', value: 'Immediately' },
    { id: 5, text: 'Prompt Payment', value: 'Prompt Payment' }
  ]
  const discountDeductedFrom = [
    { id: 1, text: 'Net Amount', value: 'Net Amount' },
    { id: 2, text: 'Gross Amount', value: 'Gross Amount' }
  ]

  const [addRowForm, setAddRowForm] = React.useState(isEdit ? currentFormData : form)
  const [targetName, setTargetName] = React.useState('')

  const [cptCodesIndexDbList, setCptCodesIndexDbList] = React.useState([])

  const cptCodesList = () => {
    IcdCptStorageServices.readStorageEntries(IcdCptStorageServices.OBJECT_STORE_NAME_CPT, function (entries) {
      // Check if there are no CPT entries
      if (!entries || entries.length === 0) {
        // Trigger fetching and saving of ICD and CPT entries
        GeneralController.fetchAndSaveIcdAndCptEntries()
        return
      }

      setCptCodesIndexDbList(entries)
    })
  }

  const [procedures, setProcedures] = React.useState([])
  const [proceduresForm, setProceduresForm] = React.useState({
    cpt_code: '',
    procedure: ''
  })
  const [validationDialog, setValidationDialog] = React.useState(false)

  const HandleCptCode = (value, obj) => {
    const updateForm = { ...proceduresForm }
    updateForm['cpt_code'] = value
    updateForm['procedure'] = value ? (value.description !== 'null' ? value.description : '') : ''
    setProceduresForm(updateForm)
  }

  const [isCancel, setIsCancel] = React.useState(false)
  const [openModal, setOpenModal] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)

  const dataheader = [{ name: 'Procedures Included In the Discount', prop: 'procedure' }]

  const handleRemoveProcedures = index => {
    const newArray = [...procedures]
    const res = newArray.filter((procedures, i) => i !== index)
    setProcedures(res)
  }

  const handleDropdown = (index, e) => {
    setTargetName(e.target.name)

    const dataRefArr = currentDataRef.current
    const dataRefIndex = dataRefArr.length
    const updateForm = [...dataRefArr]
    updateForm[index][e.target.name] = e.target.value
    setCurrentData(updateForm)

    const formUpdate = { ...addRowForm }
    formUpdate[e.target.name] = e.target.value
    setForm(formUpdate)

    const updateFormAddRow = { ...addRowForm }
    updateFormAddRow[e.target.name] = e.target.value
    setAddRowForm(updateFormAddRow)
  }

  React.useEffect(() => {
    const { errors, isValid } = AddDiscountValidations(addRowForm, targetName)
    if (!isValid) {
      setIsDisabled(true)
      setErrorVal(errors)
    } else {
      setIsDisabled(false)
      setErrorVal({})
    }
  }, [addRowForm, targetName])

  const handleChange = (index, e, field) => {
    const { errors, isValid } = AddDiscountValidations(addRowForm)
    if (!isValid) {
      setErrorVal(errors)
      setIsDisabled(true)
    } else {
      setErrorVal({})
      setIsDisabled(false)
    }
    const dataRefArr = currentDataRef.current
    const contactForm = { ...addRowForm }
    contactForm[field] = e
    setForm(contactForm)

    const updateFormAddRow = { ...addRowForm }
    updateFormAddRow[field] = e
    setAddRowForm(updateFormAddRow)

    const updateForm = [...dataRefArr]
    const dataRefIndex = dataRefArr.length
    updateForm[index][field] = e
    setCurrentData(updateForm)
  }

  const handleDateChange = (field, e) => {
    const dataRefArr = currentDataRef.current
    const newForm = { ...dataRefArr }
    newForm[field] = new Date(e).toISOString()
    setForm(newForm)

    const updateFormAddRow = { ...addRowForm }
    updateFormAddRow[field] = new Date(e).toISOString()
    setAddRowForm(updateFormAddRow)

    const updateForm = [...dataRefArr]
    const dataRefIndex = dataRefArr.length
    updateForm[index][field] = e
    setCurrentData(updateForm)
  }

  const handleChangeProcedure = e => {
    const { errors, isValid } = AddDiscountValidations(addRowForm)
    if (!isValid) {
      setErrorVal(errors)
      setIsDisabled(true)
    } else {
      setErrorVal({})
      setIsDisabled(false)
    }
    const contactForm = { ...proceduresForm }
    contactForm[e.target.name] = e.target.value
    setProceduresForm(contactForm)
  }

  const addProcedures = () => {
    if (proceduresForm.cpt_code != '' && proceduresForm.procedure != '') {
      setProcedures([...procedures, proceduresForm])
      setProceduresForm({
        cpt_code: '',
        procedure: ''
      })
    } else {
      setValidationDialog(true)
    }
  }

  const submitProcedures = () => {
    setOpenModal(false)
    setSuccess(true)

    setTimeout(() => {
      const dataRefArr = currentDataRef.current
      const contactForm = { ...addRowForm }
      contactForm['included_procedure'] = procedures
      setForm(contactForm)

      const updateFormAddRow = { ...addRowForm }
      updateFormAddRow['included_procedure'] = procedures
      setAddRowForm(updateFormAddRow)

      const updateForm = [...dataRefArr]
      const dataRefIndex = dataRefArr.length
      updateForm[index]['included_procedure'] = procedures

      setCurrentData(updateForm)
      setOpen(false)
    }, 1000)
    setIsLoading(false)
    setOpen(false)
    setProcedures([])
  }

  const HandleSubmitProcedure = () => {
    setOpenModal(true)
  }

  const HandleSave = () => {
    setIsLoading(true)
    submitProcedures()
  }

  const handleSubmitClose = () => {
    setOpenModal(false)
  }

  const [errorVal, setErrorVal] = React.useState(errors)

  React.useEffect(() => {
    const { errors, isValid } = AddDiscountValidations(addRowForm)
    setErrorVal(errors)
    setIsDisabled(true)
    cptCodesList()
  }, [])
  const [open, setOpen] = React.useState(false)
  const [success, setSuccess] = React.useState(false)

  const displayProcedures = procedures => {
    setProcedures(procedures)
  }

  const AddProcedureError = () => {
    if (errorVal['addProcedure']) { return '1px solid red' }
  }

  return (

    <TableRow key={`row_${index}`} role={'row'} >
      <RowAction
        data={currentDataRef[index]}
        setCurrentData={setCurrentData}
        currentDataRef={currentDataRef}
        index={index}
        LimitRows={LimitRows}
        setLimitRows={setLimitRows}
        LimitRowsRef={LimitRowsRef}
        setForm={setForm}
        fetchData={props.fetchData}
        setIsDisabled={setIsDisabled}
        setShouldValidate={setShouldValidate}
        shouldValidate={shouldValidate}
      />

      <TableCell>
        <DropDown
          items={status}
          onChange={e => {
            handleDropdown(index, e)
          }}
          value={addRowForm.status}
          name='status'
          error={errorVal['status']}
          minWidth={{ minWidth: '90px' }}
        />
      </TableCell>
      <TableCell>
        <DropDown
          items={discount}
          onChange={e => {
            handleDropdown(index, e)
          }}
          value={addRowForm.discount_type}
          name='discount_type'
          error={errorVal['discount_type']}
          minWidth={{ minWidth: '120px' }}
        />
      </TableCell>
      <TableCell>
        <DropDown
          items={claims}
          onChange={e => {
            handleDropdown(index, e)
          }}
          value={addRowForm.claim_type}
          name='claim_type'
          error={errorVal['claim_type']}
          minWidth={{ minWidth: '120px' }}
        />
      </TableCell>
      <TableCell>
        <DropDown
          items={discounts}
          onChange={e => {
            handleDropdown(index, e)
          }}
          value={addRowForm.discount_application}
          error={errorVal['discount_application']}
          name='discount_application'
          minWidth={{ minWidth: '120px' }}
        />
      </TableCell>
      <TableCell>
        {addRowForm.included_procedure.length ? (
          <DisplaySelectedProcedures
            data={addRowForm.included_procedure}
            setOpen={setOpen}
            displayProcedures={displayProcedures}
          />
        ) : (
          <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center'}}>
            <button style={{border: AddProcedureError()}} onClick={() => setOpen(true)}>Add Procedure</button>
            {errorVal['addProcedure'] && <label style={{color: 'red', textAlign: 'center'}}>{errorVal['addProcedure']} </label> }
          </div>
        )}
      </TableCell>
      <TableCell>
        <DropDown
          items={paymentTypes}
          onChange={e => {
            handleDropdown(index, e)
          }}
          value={addRowForm.payment_type}
          name='payment_type'
          error={errorVal['payment_type']}
          minWidth={{ minWidth: '120px' }}
        />
      </TableCell>

      <TableCell className={classes.centerElement}>
        <div>
          <CustomTextField
            onChange={e => {
              handleChange(index, e, 'terms_in_days')
            }}
            name='terms_in_days'
            type={addRowForm.discount_type === 'Early Payment Discount' ? 'integerOnly' : 'number'}
            error={errorVal['terms_in_days']}
            value={addRowForm.terms_in_days}
            minWidth={{ minWidth: '120px' }}

          />
        </div>
      </TableCell>
      <TableCell className={classes.centerElement} >
        <div>
          <CustomTextField
            onChange={e => {
              handleChange(index, e, 'rate')
            }}
            name='rate'
            value={addRowForm.rate}
            type={'number'}
            error={errorVal['rate']}
            minWidth={{ minWidth: '90px' }}
          />

        </div>
      </TableCell>
      <TableCell className={classes.dateElement}>
        <div>
          <DatePicker onChange={handleDateChange} name='effectivity_date' value={addRowForm.effectivity_date} />

        </div>
      </TableCell >
      <TableCell className={classes.dateElement}>
        <div>
          <DatePicker onChange={handleDateChange} name='end_date' value={addRowForm.end_date} />
        </div>
      </TableCell>
      <TableCell >

        <DropDown
          items={discountDeductedFrom}
          onChange={e => {
            handleDropdown(index, e)
          }}
          name='discount_to_be_deducted_from'
          error={errorVal['discount_to_be_deducted_from']}
          value={addRowForm.discount_to_be_deducted_from}
          minWidth={{ minWidth: '120px' }}
        />

      </TableCell>
      <TableCell className={classes.centerElement}>
        <div>
          <CustomTextField
            onChange={e => {
              handleChange(index, e, 'other_details_of_discount')
            }}
            error={errorVal['other_details_of_discount']}
            name='other_details_of_discount'
            value={addRowForm.other_details_of_discount}
            minWidth={{ minWidth: '120px' }}
          />

        </div>
      </TableCell>

      <Dialog fullWidth maxWidth={'sm'} open={open} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%', cursor: 'pointer' }}>
          ADD PROCEDURES
        </DialogTitle>
        <DialogContent>
          <form className={classes.root} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <CustomAutoComplete
                suggestions={cptCodesIndexDbList}
                labelName='code'
                label='CPT CODE*'
                inputValueLengthSize={2}
                value={proceduresForm.cpt_code}
                onChange={HandleCptCode}
                customStyle
              />
            </div>
            <div id='firstInput' className={classes.secondInput}>
              <CustomAutoComplete
                suggestions={cptCodesIndexDbList}
                labelName='description'
                label='PROCEDURES*'
                inputValueLengthSize={2}
                value={proceduresForm.procedure}
                onChange={HandleCptCode}
                customStyle
              />
              {/* <DropDown
                items={proceduresData}
                label='PROCEDURES*'
                onChange={e => {
                  handleChangeProcedure(e)
                }}
                name='procedure'
                value={proceduresForm.procedure}
              /> */}
            </div>
            <div className={classes.twoInput} style={{ display: 'inline-flex' }}>
              <a style={{ margin: '20px', cursor: 'pointer' }} className={classes.AddProcedure} onClick={addProcedures}>
                +Add Procedure
              </a>
            </div>
            <div className={classes.twoInput}>
              <DisplayProcedures
                className={classes.twoInput}
                dataheader={dataheader}
                dataValue={procedures.length > 0 ? procedures : []}
                handleRemove={handleRemoveProcedures}
              />
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button onClick={() => setIsCancel(true)} variant='contained' className={classes.Cancel}>
            Cancel
          </Button>
          <Button
            variant='contained'
            onClick={HandleSubmitProcedure}
            className={classes.Add}
            disabled={procedures.length < 1}
          >
            Save
          </Button>
        </DialogActions>
      </Dialog>
      <DialogBoxIfUpdate
        handleSubmitClose={handleSubmitClose}
        HandleSave={HandleSave}
        isLoading={isLoading}
        openModal={openModal}
      />
      <CustomDialog id='cancelCustomDialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
        <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
          <h3 id='cancelCustomDialogMessage'>All changes will not be saved. Are you sure you want to cancel?</h3>
          <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup} style={{display: 'flex', justifyContent: 'center'}}>

            <Button
              onClick={() => setIsCancel(false)}
              id='cancelCustomDialogBtnNo'
              className={classes.btnCancel}
              variant='contained'
              style={{ backgroundColor: '#fd5451',
                color: 'white',
                marginRight: '1em',
                marginBottom: '10px'
              }}
            >
              No
            </Button>
            <Button
              onClick={() => {
                setIsCancel(false)
                setOpen(false)
                setProcedures([])
                setProceduresForm({cpt_code: '',
                  procedure: ''})
              }}
              id='cancelCustomDialogBtnYes'
              variant='contained'
              style={{ backgroundColor: '#48b77c',
                color: 'white',
                marginLeft: '1em',
                marginBottom: '10px'
              }}

            >
              Yes
            </Button>
          </div>
        </div>
      </CustomDialog>
      <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
      <Dialog open={validationDialog} onClose={() => setValidationDialog(false)}>
        <DialogContent>
          <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='cancelCustomDialogMessage'>CPT Code and Procedures should not be empty</h3>
            <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup} style={{ float: 'right' }}>
              <Button
                onClick={() => {
                  setValidationDialog(false)
                }}
                id='cancelCustomDialogBtnYes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginBottom: '10px' }}
              >
                OK
              </Button>
            </div>
          </div>
        </DialogContent>
      </Dialog>

    </TableRow>
  )
}

export default React.memo(AddRowForm, (prevProps, nextProps) => {
  try {
    if (
      prevProps.proceduresForm.procedure === nextProps.proceduresForm.procedure &&
      prevProps.proceduresData === nextProps.proceduresData
    ) {
      return true
    }
    return false
  } catch (e) {
    return false
  }
})
