import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
const schema = Joi.object({
  roomTypeDesc: Joi.string()
    .label('Room type description')
    .required(), // required
  roomTypeRemarks: Joi.string()
    .label('Room type remarks')
    .required(), // required
  roomTypeCode: Joi.string()
    .label('Room type code')
    .required() // required
})

export default function AddRoomTypeValidation (form) {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
