import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import Button from '@material-ui/core/Button'
import classes from './CustomConfirmationDialog'

export default function CustomConfirmationDialog ({ message, onApprove, close, ...rest }) {
  return (
    <Dialog {...rest}>
      <DialogContent>
        <div id='sos_custom_dialog_container' className={classes.dialogContainer}>
          <h3 id='sos_custom_dialog_message'>{message}</h3>
          <div
            id='sos_custom_dialog_btn_group'
            className={classes.btnGroup}
            style={{ display: 'flex', justifyContent: 'center' }}
          >
            <Button
              id='sos_custom_dialog_btn_no'
              className={classes.btnCancel}
              variant='contained'
              onClick={close}
              style={{
                backgroundColor: '#fd5451',
                color: 'white',
                marginRight: '10px',
                marginBottom: '10px'
              }}
            >
              No
            </Button>
            <Button
              id='sos_custom_dialog_btn_yes'
              variant='contained'
              onClick={onApprove}
              style={{
                backgroundColor: '#48b77c',
                color: 'white',
                marginLeft: '10px',
                marginBottom: '10px'
              }}
            >
              Yes
            </Button>
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}
