import { useRef } from 'react'

const InputRefs = form => {
  const newForm = { ...form }

  const prcNumber = useRef({})
  const philNumber = useRef({})
  const pdaNumber = useRef({})
  const tiNumber = useRef({})
  const vatTaxRate = useRef({})
  const paymentTerms = useRef({})
  const professionalFee = useRef({})
  const withTax = useRef({})
  const facilityName = useRef({})
  const facilityAddress = useRef({})
  const clinicSched = useRef({})
  const exclusiveTo = useRef({})
  const reason = useRef({})

  reason.current.value = newForm['onHoldReason']
  prcNumber.current.value = newForm['prcNumber']
  philNumber.current.value = newForm['philNumber']
  pdaNumber.current.value = newForm['pdaNumber']
  tiNumber.current.value = newForm['tiNumber']
  vatTaxRate.current.value = newForm['vatTaxRate']
  paymentTerms.current.value = newForm['paymentTerms']
  professionalFee.current.value = newForm['professionalFee']
  withTax.current.value = newForm['withTax']
  facilityName.current.value = newForm['facilityName']
  facilityAddress.current.value = newForm['facilityAddress']
  clinicSched.current.value = newForm['clinicSched']
  exclusiveTo.current.value = newForm['exclusiveTo']

  return {
    reason,
    prcNumber,
    philNumber,
    pdaNumber,
    tiNumber,
    vatTaxRate,
    paymentTerms,
    professionalFee,
    withTax,
    facilityName,
    facilityAddress,
    clinicSched,
    exclusiveTo
  }
}

export default InputRefs
