import React from 'react'
import PageOne from './DentalClinicAffiliationOI_p1'
import PageTwo from './DentalClinicAffiliationOI_p2'
import DentalClinicAffiliationOIContext from './context/DentalClinicAffiliationOIContext'
const DentalClinicAffiliationOI = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [isApproved, setIsApproved] = React.useState(false)

  return (
    <DentalClinicAffiliationOIContext.Provider value={{paramId: match.params.id, isApproved, setIsApproved}}>
      {!isNextPage && <PageOne setPage={setIsNextPage} history={history} /> }
      {isNextPage && <PageTwo setPage={setIsNextPage} history={history} /> }
    </DentalClinicAffiliationOIContext.Provider>
  )
}
export default DentalClinicAffiliationOI
