import React from 'react'
import ReactTable from '../../Helper/Pagination'
import EditableTable from '../../Helper/EditablePagination'
import Button from '@material-ui/core/Button'
// import classes from './PFMultiplierMaintenance.module.scss'
import axios from 'axios'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import CustomDialogSave from '../components/CustomDialogSave/CustomDialogSave'
import CustomAlerSnackBar from '../../../Components/CustomSnackBar/CustomAlertSnackbar'
import AddMultiplierModal from '../components/AddMultiplierModal/AddMultiplierModal'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbsNoStepper from '../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import classes from './PFMultiplierMaintenance.module.scss'
import { useSelector } from 'react-redux'
import { pfMultiplierCol } from '../../../../../../Constant/tableColumnData'

const URL = `${process.env.REACT_APP_SERVER_API}`

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL COMPLIANCE ASSISTANT MANAGER',
    to: '/'
  },
  Maintenance: {
    text: 'MAINTENANCE TABLE',
    to: '/'
  },
  Table: {
    text: 'PROFESSIONAL FEE MULTIPLIER MAINTENANCE',
    to: '/ProfessionalFeeMultiplierMaintenance'
  }
}

const PFMultiplierMaintenance = ({open, setOpen, endPoint}) => {
  const [tableData, setTableData] = React.useState([])
  const [tableColumn, setTableCol] = React.useState([])
  const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const { pfMultiplierEditable } = useSelector(state => state.pfMultiplierEditable)
  const [forUpdateData, setForUpdateData] = React.useState([])

  let headerTitle = 'Professional Fee Maintenance'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Maintenance', 'Table'])

  React.useEffect(() => {
    fetchData()
    return () => {
      setOpen(false)
    }
  }, [])

  const fetchData = async () => {
    const { data } = await axios.get(`${URL}/p/f/partners/pf-multipliers`)
    setTableData(data.data)
    setTableCol(pfMultiplierCol)
  }

  const updateData = async () => {
    for (let data of forUpdateData) {
      await axios.put(`${URL}/p/f/partners/pf-multipliers/${data._id}`, {
        amhopiSociety: data.amhopi_society,
        status: data.status,
        pfType: data.pf_type,
        roomType: data.room_type,
        rate: data.rate,
        code: data.code
      })
    }
    setIsSave(false)
    setIsToUpdate(false)
    setSuccess(true)
    setTimeout(async () => {
      fetchData()
    }, 1500)
  }

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbsNoStepper
        id='HeaderWithBreadCrumbsNoStpper'
        HeaderTitle={headerTitle}
      />
      <div id='container' className={classes.container}>
        <div id='updateButton'>
          <Button className={classes.buttonAdd} variant='contained'>
            <AddMultiplierModal dataLength={tableData.length} fetchData={fetchData} setSuccess={setSuccess} data={tableData} />
          </Button>
          <Button className={classes.buttonUpdate} onClick={() => setIsToUpdate(true)} variant='contained'>
                Update
          </Button>
        </div>
        { !isToUpdate && <ReactTable columns={tableColumn} data={tableData} label={'PF Multipliers'} />}
        { isToUpdate && <EditableTable columns={tableColumn} data={tableData} label={'PF Multipliers'} setForUpdateData={setForUpdateData} isForPf />}
      </div>
      { isToUpdate &&
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div>
            <Button
              onClick={() => setIsCancel(true)}
              variant='contained'
              color='secondary'
              className={classes.cancelButton}
            >
                Cancel
            </Button>
          </div>
          <div />
          <div>
            <div>
              <Button variant='contained' type='submit' className={classes.SaveBtn} onClick={() => setIsSave(true)}>
                  Save
              </Button>
            </div>
          </div>
        </span>
      </div>
      }
      <div>
        <CustomDialog id='cancelCustomDialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
          <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='cancelCustomDialogMessage'>All changes will not be saved. Are you sure you want to cancel?</h3>
            <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={() => {
                  setIsCancel(false)
                  setIsToUpdate(false)
                }}
                id='cancelCustomDialogBtnYes'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
              >
                Yes
              </Button>
              <Button
                onClick={() => setIsCancel(false)}
                id='cancelCustomDialogBtnNo'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                No
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <div>
        <CustomDialogSave
          onClose={() => {
            setOpen(false)
          }}
          id='saveCustomDialog'
          isSave={isSave}
          setIsSave={() => setIsSave(false)}>
          <div id='saveCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='saveCustomDialogMessage'>Are you sure you want to update?</h3>
            <div id='saveCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={() => setIsSave(false)}
                id='saveCustomDialogBtnNo'
                variant='contained'
                style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
              >
                No
              </Button>
              <Button
                onClick={updateData}
                id='saveCustomDialogBtnYes'
                className={classes.btnCancel}
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
              >
                Yes
              </Button>
            </div>
          </div>
        </CustomDialogSave>
        <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
      </div>
    </div>
  )
}

export default PFMultiplierMaintenance
