import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { Provider } from 'react-redux'

import ListItems from './Helpers/ListItemsContainer'
import MMC from '../Assets/Icons/mmc.png'

import Store from '../../Store'

import {
  Hospital,
  MedicalClinic,
  DentalClinic,
  MedicalDoctors,
  SpecialServices,
  MedicalPartners,
  AHMOPIDoctors,
  UtilizationReport,
  ContractRenewal
} from './List'

export default function HospitalProfile (props) {
  const [open, setOpen] = React.useState(false)
  const [headerData, setHeaderData] = React.useState({
    status: '',
    name: ''
  })

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Reports'])
  }, [])

  const breadcrumbs = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    Reports: {
      text: 'Reports',
      to: '/ReportsDashboard'
    }
  }

  const row = [
    {
      to: `/Reports/AffiliatedHospital`,
      name: 'List of Affiliated Hospital',
      path: `/Reports/AffiliatedHospital`,
      importPage: <Hospital />
    },
    {
      to: `/Reports/AffiliatedMedical`,
      name: 'List of Affiliated Medical Clinic',
      path: `/Reports/AffiliatedMedical`,
      importPage: <MedicalClinic />
    },
    {
      to: `/Reports/AffiliatedDentalClinic`,
      name: 'List of Affiliated Dental Clinic and Dentist',
      path: `/Reports/AffiliatedDentalClinic`,
      importPage: <DentalClinic />
    },
    {
      to: `/Reports/AffiliatedMedicalDoctors`,
      name: 'List of Affiliated Medical Doctors',
      path: `/Reports/AffiliatedMedicalDoctors`,
      importPage: <MedicalDoctors />
    },
    {
      to: `/Reports/AffiliatedSpecialServices`,
      name: 'List of Special/Other Services',
      path: `/Reports/AffiliatedSpecialServices`,
      importPage: <SpecialServices />
    },
    {
      to: `/Reports/AffiliatedMedicalPartners`,
      name: 'Total Number of Affiliated Medical Partners',
      path: `/Reports/AffiliatedMedicalPartners`,
      importPage: <MedicalPartners />
    },
    {
      to: `/Reports/AffiliatedAHMOPIDoctors`,
      name: 'List of AHMOPI Doctors',
      path: `/Reports/AffiliatedAHMOPIDoctors`,
      importPage: <AHMOPIDoctors />
    },
    {
      to: `/Reports/ContractRenewal`,
      name: 'List of Contract for Renewal',
      path: `/Reports/ContractRenewal`,
      importPage: <ContractRenewal />
    }
  ]

  return (
    <Provider store={Store}>
      <ListItems ListItems={row} leftSideImage={MMC} />
    </Provider>
  )
}
