import React from 'react'
import classes from './PartnerInformation.module.scss'

import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'
import ClinicProfileContext from '../../context'

import { MonthdateFormat, getTinStatus } from '../../../../../../Components/Utils/utils'

import getNestedObjects from '../../../../../../Components/getNestedObjects/getNestedObjects'

const DentalClinicInformation = () => {
  const { profile } = React.useContext(ClinicProfileContext)

  let street = getNestedObjects(profile, ['street'])
  let region = getNestedObjects(profile, ['region'])
  let district = getNestedObjects(profile, ['district'])
  let city = getNestedObjects(profile, ['city'])
  let barangay = getNestedObjects(profile, ['barangay'])
  let zipCode = getNestedObjects(profile, ['zipCode'])
  let philhealthExp = MonthdateFormat(profile.philhealthExp)
  let dateOfAffiliation = MonthdateFormat(profile.dateOfAffiliation && profile.dateOfAffiliation)
  let dateOfDisaffiliation = MonthdateFormat(profile.dateOfDisaffiliation && profile.dateOfDisaffiliation)
  let discount = getNestedObjects(profile, ['facilityDiscount'])
  let years = getNestedObjects(profile, ['years'])
  let months = getNestedObjects(profile, ['months'])
  let assignments = getNestedObjects(profile, ['assignments'])

  let yearsMonths = ''

  if (years.length > 0 && months.length > 0) {
    yearsMonths = `${years} year/s and ${months} month/s`
  } else if (years.length > 0) {
    yearsMonths = `${years} year/s`
  } else if (months.length > 0) {
    yearsMonths = `${months} month/s`
  } else {
    yearsMonths = ''
  }

  const Information = [
    { id: 301, title: 'TVS', value: profile.tvs && profile.tvs },
    {
      id: 304,
      title: 'Medical Partner ID',
      value: profile.id && profile.id
    },
    {
      id: 305,
      title: 'Former Name',
      value: profile.formerName && profile.formerName
    },
    {
      id: 306,
      title: 'Facility Address',
      value: `${street}, ${barangay}, ${city}, ${district}, ${region} ${zipCode}`
    },
    {
      id: 3066,
      title: 'Years Of Operation',
      value: yearsMonths
    },
    {
      id: 308,
      title: 'PhilHealth Accreditation no.',
      value: getNestedObjects(profile, ['accreditationNumber']),
      title2: 'PHILHEALTH EXP.',
      value2: philhealthExp
    },
    {
      id: 309,
      title: 'CHECK PAYEE',
      value: getNestedObjects(profile, ['payee'])
    },
    {
      id: 310,
      title: 'CREDIT LIMIT ',
      value: getNestedObjects(profile, ['creditLimit'])
    },
    {
      id: 311,
      title: 'PAYMENT TERMS',
      value: getNestedObjects(profile, ['paymentTerms'])
    },
    {
      id: 312,
      title: 'TIN STATUS',
      value: getTinStatus(getNestedObjects(profile, ['tinStatus']))
    },
    { id: 313, title: 'TAX RATE', value: getNestedObjects(profile, ['vatTaxRate']) },
    {
      id: 314,
      title: 'FACILITY discount',
      value: discount
    },
    {
      id: 315,
      title: 'Date of affiliation',
      value: dateOfAffiliation
    },
    {
      id: 316,
      title: 'Date of disaffiliation',
      value: dateOfDisaffiliation
    },
    {
      id: 317,
      title: 'Remarks',
      value: getNestedObjects(profile, ['specialInstructions'])
    },
    {
      id: 318,
      title: 'PROCESS TYPE',
      value: getNestedObjects(profile, ['processType'])
    },
    {
      id: 319,
      title: 'Clinic Schedule',
      value: getNestedObjects(profile, ['clinicSchedule'])
    }
  ]

  return (
    <PartnerViewInformation
      data={Information}
      facilityLabel={assignments}
      type={'facility'}
      listStyles={classes.listStyles}
      firstColumStyle={classes.firstRow}
      secondColumnStyle={classes.secondRow}
      ifHastTitle2
      listStyles2={classes.listStyles2}
    />
  )
}

export default DentalClinicInformation
