import React from 'react'
import { useTable, useFilters, useGlobalFilter, useSortBy, usePagination } from 'react-table'
import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TextField from 'SharedComponents/TextField/CustomTextField'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { RenderInputFields, RenderNumberFormat } from '../../Components/InputsForProfiles/InputsForProfile'
import AutosuggestComponent from '../Components/AutoSuggest'
import matchSorter from 'match-sorter'
import classes from './scss/EditableTable.module.scss'
// import Input from '@material-ui/core/Input'
import StatusSelect from './Components/StatusSelect'
import InputField from './Components/inputField'
import RoomTypeAutoSuggest from '../Components/RoomTypeAutoSuggest'

// Create an editable cell renderer

const EditableCell = ({
  value: initialValue,
  row: { index },
  column: { id },
  data = [],
  updateMyData, // This is a custom function that we supplied to our table instance
  cptCodeData,
  roomDescData,
  handleAutoFill,
  style
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)
  const [selectedVal, setSelectedVal] = React.useState({})
  const status = ['status']
  const effectiveDate = ['effectivityDate']

  const handleAutoFillSelect = selectedVal => {
    console.log('data sa auto fill', data)
    console.log('data sa auto fill', selectedVal)
    let arr = JSON.parse(JSON.stringify(data))

    arr.map((d, i) => {
      if (i === index) {
        console.log('da', { ...d, ...selectedVal })
      }
      return true
    })

    handleAutoFill(index, selectedVal)
    setSelectedVal(selectedVal)
  }

  const onChange = React.useCallback(e => {
    console.log('auto sug data', e.target.value)
    setValue(e.target.value)
  }, [])

  const validateCpt = code => {
    return data.some(d => d.cpt === code)
  }

  const Handledate = e => {
    const { value } = e.target
    setValue(value)
  }

  // We'll only update the external data when the input is blurred

  const onBlur = () => {
    updateMyData(index, id, value, selectedVal)
  }

  React.useMemo(() => {
    console.log('val', value, 'id', id)
    updateMyData(index, id, value, selectedVal)
  }, [value])

  // If the initialValue is changed external, sync it up with our state
  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue])
  return status.includes(id) ? (
    <StatusSelect onChange={onChange} value={value} onBlur={onBlur} index={index} />
  ) : effectiveDate.includes(id) ? (
    <RenderInputFields type={'date'} style={style} onChange={Handledate} onBlur={onBlur} value={value} />
  ) : ['cpt'].includes(id) ? (
    <AutosuggestComponent
      onChange={onChange}
      setValue={setValue}
      value={value}
      onBlur={onBlur}
      data={cptCodeData}
      setSelectedVal={handleAutoFillSelect}
    />
  ) : ['price', 'opd', 'standard'].includes(id) ? (
    <RenderNumberFormat decimalScale={2} type={'integerOnly'} onChange={onChange} onBlur={onBlur} value={value} />
  ) : ['roomType'].includes(id) ? (
    <RoomTypeAutoSuggest data={roomDescData} name={'roomType'} setValue={setValue} onBlur={onBlur} formValue={value} />
  ) : (
    <InputField value={value} onChange={onChange} name={`${id}`} onBlur={onBlur} />
  )
}

// data, setForm, form, formValue, name, error

const DefaultColumnFilter = ({ column: { filterValue, setFilter } }) => {
  return (
    <div id='textField' className={classes.textField}>
      <TextField
        value={filterValue || ''}
        onChange={e => {
          setFilter(e.target.value || undefined)
        }}
      />
    </div>
  )
}

const fuzzyTextFilterFn = (rows, id, filterValue) => {
  return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
}

fuzzyTextFilterFn.autoRemove = val => !val

const ReactTable = props => {
  const filterTypes = React.useMemo(
    () => ({
      fuzzyText: fuzzyTextFilterFn,
      text: (rows, id, filterValue) => {
        return rows.filter(row => {
          const rowValue = row.values[id]
          return rowValue !== undefined
            ? String(rowValue)
                .toLowerCase()
                .startsWith(String(filterValue).toLowerCase())
            : true
        })
      }
    }),
    []
  )

  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Cell: EditableCell,
      Filter: DefaultColumnFilter
    }),
    []
  )

  const {
    columns,
    data,
    handleAutoFill,
    updateMyData,
    HandleAddProcedure,
    cptCodeData,
    roomDescData,
    countName,
    type,
    style
  } = props

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize }
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      cptCodeData,
      roomDescData,
      filterTypes,
      updateMyData,
      disableSortRemove: true,
      handleAutoFill,
      style,
      initialState: { pageIndex: 0, pageSize: 10 }
      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  )

  return (
    <React.Fragment>
      <div id='table' className={classes.tableContainer}>
        <Table
          {...getTableProps()}
          className={type === 'standardRates' ? classes.rootTable : classes.hospitalContainer}
        >
          <TableHead className={classes.tableHead}>
            {headerGroups.map(headerGroup => (
              <TableRow {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
                {headerGroup.headers.map(column => (
                  <React.Fragment>
                    <TableCell component='th' scope='row' align='left' className={classes.tableCell}>
                      {column.canFilter ? (
                        <div id='withTextfield' className={classes.withTextfield}>
                          {column.render('Header')}
                          <span
                            id='showIcon'
                            className={classes.showIcon}
                            {...column.getHeaderProps(column.getSortByToggleProps())}
                          >
                            {column.isSortedDesc ? (
                              <div id='sortIcon' className={classes.sortIcon}>
                                <FontAwesomeIcon icon='sort' />
                              </div>
                            ) : (
                              <div id='sortIcon' className={classes.sortIcon}>
                                <FontAwesomeIcon icon='sort' />
                              </div>
                            )}
                          </span>
                          <div> {column.render('Filter')} </div>
                        </div>
                      ) : column.Header === 'deleteIcon' ? (
                        ''
                      ) : (
                        <div id='noTextField' className={classes.noTextField}>
                          {column.render('Header')}
                        </div>
                      )}
                    </TableCell>
                  </React.Fragment>
                ))}
              </TableRow>
            ))}
          </TableHead>
          <div className={classes.tableRoot}>
            <div id='tableBodyRoot' className={classes.tableBody}>
              <TableBody id='tableBody' {...getTableBodyProps()}>
                {page.map((row, i) => {
                  prepareRow(row)
                  return (
                    <TableRow key={i} {...row.getRowProps()} className={classes.tableRow}>
                      {row.cells.map(cell => {
                        return (
                          <TableCell
                            component='th'
                            scope='row'
                            align='left'
                            className={classes.tableCell}
                            {...cell.getCellProps()}
                          >
                            {cell.render('Cell')}
                          </TableCell>
                        )
                      })}
                    </TableRow>
                  )
                })}
                <TableRow className={classes.addProcedureRow}>
                  <TableCell colSpan='12' component='th' scope='row' align='left' className={classes.tableCell}>
                    <label id='addProcedure' className={classes.addProcedure} onClick={HandleAddProcedure}>
                      +Add Procedure
                    </label>
                  </TableCell>
                </TableRow>
              </TableBody>
            </div>
          </div>
        </Table>
      </div>

      <div className={classes.paginationDiv}>
        <div id='pageCount' className={classes.pageCount}>
          {pageIndex + 1} - {page.length} Out of {data.length} {countName}
        </div>
        <div id='emptySpace' className={classes.emptySpace} />
        <div className={classes.Pagination}>
          <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
            {'<<'}
          </button>{' '}
          <button onClick={() => previousPage()} disabled={!canPreviousPage}>
            {'<'}
          </button>{' '}
          <button onClick={() => nextPage()} disabled={!canNextPage}>
            {'>'}
          </button>{' '}
          <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
            {'>>'}
          </button>{' '}
          <span style={{ marginLeft: '1em' }}>
            Page{' '}
            <strong>
              {pageIndex + 1} of {pageOptions.length}
            </strong>{' '}
          </span>
          <select
            value={pageSize}
            onChange={e => {
              setPageSize(Number(e.target.value))
            }}
            style={{ marginLeft: '1.5em' }}
          >
            {[10, 15, 20].map(pageSize => (
              <option key={pageSize} value={pageSize}>
                Show {pageSize}
              </option>
            ))}
          </select>
        </div>
      </div>
    </React.Fragment>
  )
}

const ReactTableComponents = React.memo(ReactTable, (prevProps, nextProps) => {
  try {
    if (
      prevProps.page === nextProps.page &&
      prevProps.data === nextProps.data &&
      prevProps.hasDuplicate === nextProps.hasDuplicate &&
      prevProps.columns === nextProps.columns &&
      prevProps.headerGroups === nextProps.headerGroups
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})

function EditableTable ({
  data: tdata,
  columns,
  setRows,
  indexTodelete,
  setPassIndex,
  setForUpdateData,
  cptCodeData,
  roomDescData,
  countName,
  type,
  style,
  hide
}) {
  const [data, setData] = React.useState(tdata)
  const [tmpData, setTmpData] = React.useState(tdata)
  const [hasDuplicate, setHasDuplicate] = React.useState(false)

  React.useEffect(() => {
    if (hasDuplicate) {
      alert('Duplicate CPT code and duplicate room type')
      // setHasDuplicate(false)
    }
  }, [hasDuplicate])

  React.useEffect(() => {
    setForUpdateData(tmpData)
    setTimeout(() => {
      if (validateDuplicateCpt(getCptCodes(tmpData))) {
        setHasDuplicate(true)
      } else {
        setHasDuplicate(false)
      }
    }, 200)
  }, [tmpData])

  React.useEffect(() => {
    if (setRows) {
      setRows(tmpData)
    }
  }, [tmpData, indexTodelete])

  /*  React.useEffect(() => {
    setTimeout(() => {
      if (validateDuplicateCpt(getCptCodes(tmpData))) {
        setHasDuplicate(true)
      } else {
        setHasDuplicate(false)
      }
    }, 200)
  }, [tmpData]) */

  const getCptCodes = (arr = []) => {
    return arr.map(d => d.cpt + d.roomType)
  }

  const validateDuplicateCpt = arr => {
    return new Set(arr).size !== arr.length
  }

  React.useEffect(() => {
    setData(data => data.filter((items, index) => index !== indexTodelete))
    if (setPassIndex) {
      setPassIndex(-1)
    }
    setTmpData(data => data.filter((items, index) => index !== indexTodelete))
    if (setPassIndex) {
      setPassIndex(-1)
    }
  }, [indexTodelete])

  const updateMyData = (rowIndex, columnName, value) => {
    // setTmpData(newMap)
    setTmpData(previousData =>
      previousData.map((data, i) => {
        if (i === rowIndex) {
          return {
            ...data,
            [columnName]: value
          }
        } else {
          return data
        }
      })
    )
  }

  const handleAutoFill = (index, val) => {
    setData(data.map((d, i) => (i === index ? { ...d, ...val } : d)))
    // setTmpData(newTempData.map((d, i) => (i === index ? { ...d, ...val } : d)))
    setTmpData(previousData =>
      previousData.map((data, i) => {
        if (i === index) {
          return {
            ...data,
            ...val
          }
        } else {
          return data
        }
      })
    )
  }

  const HandleAddProcedure = () => {
    if (!hasDuplicate) {
      setData(currentData => [
        ...currentData,
        {
          status: 'Active',
          cpt: '',
          procedureDescription: '',
          opd: '',
          standard: '',
          roomType: '',
          price: '',
          effectivityDate: '',
          remarks: ''
        }
      ])
      setTmpData(currentData => [
        ...currentData,
        {
          status: 'Active',
          cpt: '',
          procedureDescription: '',
          opd: '',
          standard: '',
          roomType: '',
          price: '',
          effectivityDate: '',
          remarks: ''
        }
      ])
    } else {
      alert('Duplicate CPT code and duplicate room type')
    }
  }

  return (
    <React.Fragment>
      <ReactTableComponents
        columns={columns}
        data={data}
        handleAutoFill={handleAutoFill}
        updateMyData={updateMyData}
        HandleAddProcedure={HandleAddProcedure}
        setForUpdateData={setForUpdateData}
        cptCodeData={cptCodeData}
        roomDescData={roomDescData}
        countName={countName}
        type={type}
        style={style}
        hide={hide}
        hasDuplicate={hasDuplicate}
      />
    </React.Fragment>
  )
}

export default EditableTable
