import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { medicalDoctorsCol } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'

const Hospital = () => {
  const [medicalDoctors, setMedicalDoctors] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  React.useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    const { data } = await await ReportServices.getAllDoctorsByType('DOCTOR')
    const dataArr = data.data
    dataArr.forEach((value, index) => {
      dataArr[index].dataCol = {
        status: value.status,
        medical_id: value.medical_id,
        full_name: value.full_name,
        specialization: value.specialization,
        facility_name: value.facility_name,
        room: value.room, // value.facility_info ? value.facility_info[0].room_number : null,
        sched: value.sched, // value.acility_info ? value.acility_info[0].clinic_schedule : null,
        street: value.street,
        barangay: value.barangay,
        city: value.city,
        district: value.district,
        region: value.region,
        contact: value.contact
      }
    })
    setMedicalDoctors(dataArr)
  }
  return (
    <div>
      <div className={classes.rightBody}>
        <div id='root' className={classes.root}>
          <ReactTable columns={medicalDoctorsCol} data={medicalDoctors} setDownloadData={setDownloadData} />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={medicalDoctorsCol} filename={`${moment().format('YYYYMMDD')}-doctors.csv`} />
      </div>
    </div>
  )
}

export default Hospital
