import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import { Provider } from 'react-redux'

import ProfileList from '../../Components/ProfileList/ProfileList'
import Partner from './Pages/PartnerInformation/index'
import StandardRates from './Pages/StandardRates/StandardRates'
import ContactDetails from '../../Components/ContactDetails/ContactDetails'
import FacilityDiscounts from '../FacilityDiscount/FacilityDiscountPage'
import ListOfAffiliatedDoctors from '../Components/ListOfAffiliatedDoctors/ListOfAffiliatedDoctors'
import HospitalList from './Pages/HospitalList/HospitalList'
import OtherDocuments from './Pages/OtherDocuments/OtherDocuments'
import ContractDetailsViewer from './Pages/ViewerContract/ContractDetailsViewer'
import MMC from '../../Assets/Icons/mmc.png'

import Store from '../../../Store'
// import ListOfAffiliatedDoctors from '../DentalClinicProfile/Pages/ListOfAffiliatedDoctors/ListOfAffiliatedDoctors'
import HospitalProfileParamsContext from './Pages/context/HospitalProfileParamsContext'

export default function HospitalProfile (props) {
  const [isOpenAffiliatedDoctorsModal, setIsOpenAffiliatedDoctorsModal] = React.useState(false)
  const [isOpenHospitalListModal, setIsOpenHospitalListModal] = React.useState(false)
  const [open, setOpen] = React.useState(false)
  const [paramId, setParamId] = React.useState('')
  const [headerName, setHeaderName] = React.useState('')
  const [status, setStatus] = React.useState('')

  const [changeName, setChangeName] = React.useState(null)
  const [isEdit, setIsEdit] = React.useState(false)
  const [isToUpdate, setIsToUpdate] = React.useState(false)

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Partners', 'HospitalPartnerProfile', 'LastPath'])
    setIsOpenAffiliatedDoctorsModal(false)
    return () => {
      setIsOpenAffiliatedDoctorsModal(false)
    }
  }, [headerName, status])

  const breadcrumbs = {
    Medical: {
      text: 'MEDICAL DATA SUPPORT',
      to: '/'
    },
    Partners: {
      text: 'PARTNERS',
      to: '/'
    },
    HospitalPartnerProfile: {
      text: 'HOSPITAL PARTNER PROFILE',
      to: '/hospitalPartnerProfile'
    },
    LastPath: {
      text: headerName.toUpperCase(),
      to: `/hospitalName`
    }
  }

  const row = [
    {
      to: `/HospitalProfile/PartnerInformation/${paramId}`,
      name: ' Partner Information',
      path: '/HospitalProfile/PartnerInformation/:facilityId',
      click: () => setIsToUpdate(false),
      importPage: (
        <HospitalProfileParamsContext.Provider
          value={{
            setParamId: setParamId,
            paramId,
            isToUpdate,
            setIsToUpdate,
            setChangeName,
            isEdit,
            setIsEdit,
            setStatus,
            setHeaderName,
            status
          }}
        >
          <Partner />
        </HospitalProfileParamsContext.Provider>
      )
    },
    {
      to: `/HospitalProfile/ContractDetails/${paramId}`,
      name: 'Contract Details',
      path: '/HospitalProfile/ContractDetails/:paramId',
      importPage: <ContractDetailsViewer paramId={paramId} />
    },
    {
      to: `/HospitalProfile/StandardRates/${paramId}`,
      name: 'Standard Rates',
      path: '/HospitalProfile/StandardRates/:facilityId',
      importPage: (
        <StandardRates
          paramId={paramId}
          setOpen={setOpen}
          open={open}
          pathName={`/HospitalProfile/PartnerInformation/${paramId}`}
        />
      ),
      open: setOpen
    },
    {
      to: `/HospitalProfile/FacilityDiscounts/${paramId}`,
      name: 'Facility Discounts',
      path: '/HospitalProfile/FacilityDiscounts/:paramId',
      importPage: <FacilityDiscounts open={open} setOpen={setOpen} paramId={paramId} />,
      open: setOpen
    },
    {
      to: `/HospitalProfile/ListOfAffiliatedDoctors/${paramId}`,
      name: 'List Of Affiliated Doctors',
      path: '/HospitalProfile/ListOfAffiliatedDoctors/:facilityId',
      open: setIsOpenAffiliatedDoctorsModal
    },
    {
      to: `/HospitalProfile/ContactDetails/${paramId}`,
      name: 'Contact Details',
      path: '/HospitalProfile/ContactDetails/:facilityId',
      importPage: <ContactDetails paramId={paramId} />
    },
    {
      to: `/HospitalProfile/OtherDocuments/${paramId}`,
      name: 'Other Documents',
      path: '/HospitalProfile/OtherDocuments/:paramId',
      importPage: <OtherDocuments paramId={paramId} />
    }
  ]

  return (
    <Provider store={Store}>
      <ProfileList
        ListItems={row}
        HeaderTitle={isToUpdate ? changeName : headerName}
        leftSideImage={MMC}
        status={status}
      />
      {isOpenHospitalListModal && (
        <HospitalList open={isOpenHospitalListModal} setOpen={setIsOpenHospitalListModal} setParamId />
      )}
      {isOpenAffiliatedDoctorsModal && (
        <ListOfAffiliatedDoctors
          open={isOpenAffiliatedDoctorsModal}
          setOpen={setIsOpenAffiliatedDoctorsModal}
          paramId={paramId}
          endPoint={'/affiliatedDoctors'}
        />
      )}
    </Provider>
  )
}
