import instance from '../instance'
import axios from 'axios'
const facilities = instance('/p/f')

const URL = `${process.env.REACT_APP_SERVER_API}`

export default class FacilityGroupingService {
  static async add (data) {
    await axios.post(`${URL}/p/f/partners/facility-group/add`, data)
  }
  static async get () {
    return facilities({
      url:    `/partners/facility-groups`,
      method: 'GET'
    })
  }

  static async update (data) {
    await axios.put(`${URL}/p/f/partners/facility-group/${data._id}`, {
      code:          data.code,
      desc:          data.desc,
      facilityNames: data.facilityNames,
      facility_type: data.facility_type,
      status:        data.status
    })
  }
}
