import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
import datePickerValidation, { datepickerInvalidDate } from 'Pages/Protected/Partner/Components/DatePicker/DatePickerUtils'
import { validateTaxRateVal, validateZipCode } from 'Pages/Protected/Partner/Components/Utils/utils'

const notRequiredFields = [
  'dentalAssociates',
  'subSpecialization',
  'otherSpecialization',
  'pda',
  'philhealthExpiration',
  'prcLicenseNo',
  'affiliationInfos',
  'roomNumber',
  'suffix',
  'unitFlrBldg',
  'bldgName',
  'externalRemarks',
  'id',
  'dentistId',
  'mobile',
  'mainCoordinator',
  'name',
  'checkPayee'
]

const schema = Joi.object({
  clinicName: Joi.string()
    .label('Name')
    .required(), // required
  email: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(),
  emailAddress: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(),
  contactPersonNo: Joi.string()
    .label('Mobile Number')
    .required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  natureOfPractice: Joi.string()
    .label('Nature of Practice')
    .required(),
  middleName: Joi.string()
    .label('Middle Name')
    .required(),
  firstName: Joi.string()
    .label('First Name')
    .required(),
  lastName: Joi.string()
    .label('Last Name')
    .required(),
  payee: Joi.string()
    .label('Check Payee')
    .required(), // required
  nonVatable: Joi.string()
    .label('Tin Status Value')
    .required(),
  tinStatus: Joi.string()
    .label('Tin Status')
    .required(), // required
  contactPerson: Joi.string()
    .label('Main Coordinator')
    .required(), // required
  unitFlrBldg: Joi.string().label('Unit no./Floor/Bldg. No.'),
  street: Joi.string()
    .label('Street')
    .required(), // required
  subdivision: Joi.string()
    .label('Subdivision')
    .required(), // required
  region: Joi.string()
    .label('Region')
    .required(), // required
  district: Joi.string()
    .label('District')
    .required(), // required
  barangay: Joi.string()
    .label('Barangay')
    .required(), // required
  city: Joi.string()
    .label('City')
    .required(), // required
  zipCode: Joi.string()
    .label('Zip code')
    .required(),
  birthDate: Joi.string()
    .label('Birth Date')
    .required(), // required
  civilStatus: Joi.string()
    .label('Civil Status')
    .required(), // required
  contactNumber: Joi.string()
    .label('Contact Number')
    .required(), // required,
  gender: Joi.string()
    .label('Gender')
    .required(), // required,
  internalRemarks: Joi.string()
    .label('Internal Remarks')
    .required(), // required,
  mainSpecialization: Joi.string()
    .label('Main Specification')
    .required(), // required,
  majorSpecialization: Joi.string()
    .label('Major Specification')
    .required(), // required,
  prcExpiration: Joi.date()
    .label('PRC Expiration')
    .required(), // required,
  prcNumber: Joi.string()
    .label('PRC License Number')
    .required(), // required,
  vatTaxRate: Joi.number()
    .label('Tax Rate')
    .required(), // required,
  tinNumber: Joi.string()
    .label('Tin Number')
    .required() // required,
    .max(12)
    .min(12),
  philhealthNumber: Joi.string()
    .label('PhilHealth Accreditation No')
    .max(12)
    .min(12)
    .required()
})
let newErrors = {}

export default function DentalClinicValidationForm (form, errors, dateVal = {}) {
  newErrors = { ...errors }
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      newErrors[i.path[0]] = i.message
      if (form['tinStatus'] === '12%' || form['tinStatus'] === '') {
        delete newErrors['nonVatable']
      }

      newErrors = {...datePickerValidation(dateVal, 'prcExpiration', newErrors, true)} // identify if input is required or invalid format
      newErrors = {...datePickerValidation(dateVal, 'birthDate', newErrors, true)}
      newErrors = {...validateTaxRateVal(form, newErrors)}
      newErrors = {...validateZipCode(form, newErrors)}

      if (form['philhealthNumber'] === '') {
        delete newErrors['philhealthNumber']
      } else {
        if (form['philhealthNumber'].length === 12) {
          delete newErrors['philhealthNumber']
        }
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete newErrors[i.path[0]]
      }
      if (Object.keys(dateVal).length > 0) {
        newErrors = {...datePickerValidation(dateVal, 'philhealthExpiration', newErrors, false)} // ad error message if invalid format
      }
    }
    console.log('new erorrs', newErrors)
  }

  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
