import React from 'react'

import { HashRouter as Router, Switch, Route, Link } from 'react-router-dom'
import TitleHeader from '../../Components/HeaderWithEditButtons/HeaderWithEditButtons'
import history from '../../Components/History/History'
import classes from '../scss/ListItemContainer.module.scss'

const ListItemsContainer = props => {
  const { HeaderTitle, leftSideImage, ListItems, status } = props
  return (
    <Router history={history}>
      <div id='root' className={classes.root}>
        <div id='body' className={classes.body}>
          <div id='wrapper' className={classes.leftSidebarWrapper}>
            <div id='bodySideBar' className={classes.leftbodySideBar}>
              <h1>Reports</h1>
              {ListItems.map((items, index) => (
                <div id='partnerInformation' key={items.to} className={classes.text}>
                  <Link
                    key={items.to}
                    to={items.to}
                    onClick={() => (items.open ? items.open(true) : items.click ? items.click() : '')}
                    className={classes.link}
                  >
                    {items.name}
                  </Link>
                </div>
              ))}
            </div>
          </div>

          <div id='rightSidebody' className={classes.rightSidebody}>
            <Switch>
              <div id='rightSidebodyWrapper' className={classes.rightSidebodyWrapper}>
                {ListItems.map((items, index) => (
                  <Route key={index} exact path={items.path}>
                    {items.importPage}
                  </Route>
                ))}
              </div>
            </Switch>
          </div>
        </div>
      </div>
    </Router>
  )
}

export default ListItemsContainer
