import React from 'react'
import { useTable, useFilters, useGlobalFilter, useSortBy, usePagination } from 'react-table'
import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TextField from '../../../../../SharedComponents/TextField/CustomTextField'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { setFacilityGroupingEditable } from 'Pages/Protected/Store/actions/Partner/facilityGroupingEditable'
import { useDispatch } from 'react-redux'

import matchSorter from 'match-sorter'
import classes from './scss/CustomTable.module.scss'
// import Input from '@material-ui/core/Input'
import StatusSelect from './StatusSelect'
import DropDown from '../../Components/CustomDrowpdown'
import RoomTypeService from '../../../../../Services/Api/RoomType/RoomType'
import Pagination from '@material-ui/lab/Pagination'

// Create an editable cell renderer
const EditableCell = ({
  value: initialValue,
  row: { index },
  column: { id },
  updateMyData, // This is a custom function that we supplied to our table instance
  editFacilityNames,
  isForPf,
  cell
}) => {
  // We need to keep and update the state of the cell normally
  const [value, setValue] = React.useState(initialValue)
  const [roomTypes, setRoomTypes] = React.useState([])
  const status = [
    { id: 1, text: 'Active', value: 'Active' },
    { id: 2, text: 'Inactive', value: 'Inactive' },
  ]

   React.useEffect(() => {
     if (isForPf) {
       fetchRoomType()
     }

    }, [])
  const fetchRoomType = async () => {
    const { data } = await RoomTypeService.get()
    if (data.data) {
      let roomTypesData = []
      for (let tmp of data.data) {
        roomTypesData.push({
          id:    tmp.code,
          text:  tmp.desc,
          value: tmp.desc
        })
      }
      setRoomTypes(roomTypesData)
    }
  }
  const amhopiSociety = [
    { id: 1, text: 'PCS', value: 'PCS' },
    { id: 2, text: 'POGS', value: 'POGS' },
    { id: 3, text: 'PPS', value: 'PS' },
    { id: 4, text: 'PSA', value: 'PSA' },
    { id: 5, text: 'Regular', value: 'regular' },
    { id: 6, text: 'UDAA', value: 'UDAA' },
    { id: 7, text: 'UHDA', value: 'UHDA' },
    { id: 8, text: 'MACU', value: 'MACU' }
  ]

  const pfTypes = [
    { id: 1, text: 'Surgical', value: 'Surgical' },
    { id: 2, text: 'Out-Patient', value: 'Out-Patient' },
    { id: 2, text: 'Daily Visit', value: 'Daily Visit' }
  ]

  const onChange = (e) => {
    setValue(e.target.value)
  }

  React.useEffect(() => {
    updateMyData(index, id, value)
  }, [value])

  const handleClick = (cell) => {
    console.log('editable table cell', cell)
    editFacilityNames(cell.row, index)
  }

  // If the initialValue is changed external, sync it up with our state
  React.useEffect(() => {
    setValue(initialValue)
  }, [initialValue])
  if (isForPf && id == 'amhopi_society') {
    return <span>{value}</span>;
  }
  if (isForPf && id == 'pf_type') {
    return <span>{value}</span>
  }
  if (isForPf && id == 'room_type') {
    return <span>{value}</span>
  }
  if (isForPf && id == 'rate') {
    return <TextField value={value} onChange={onChange} type={'number'} />
  }
  if (id == 'facilityNames') {
    const faciltyNames = Array.prototype.map.call(value, function(item) { return item.name; }).join(",");
    return <button style={{cursor: 'pointer', height: '50px'}} onClick={() => handleClick(cell)}>{faciltyNames ? faciltyNames : '+click to add facility'}</button>
  } else if (id == 'code') {
    return <span>{value}</span>
  }

  if (id == 'status') {
    return <DropDown onChange={onChange} value={value} items={status}  />;
  }
  return status.includes(value) ? (
    <StatusSelect onChange={onChange} value={value} />
  ) : (
    <TextField value={value} onChange={onChange} />
  )
}

const DefaultColumnFilter = ({ column: { filterValue, setFilter } }) => {
  return (
    <div id='textField' className={classes.textField}>
      <TextField
        value={filterValue || ''}
        onChange={(e) => {
          setFilter(e.target.value || undefined)
        }}
      />
    </div>
  )
}

const fuzzyTextFilterFn = (rows, id, filterValue) => {
  return matchSorter(rows, filterValue, { keys: [(row) => row.values[id]] })
}

fuzzyTextFilterFn.autoRemove = (val) => !val

const ReactTable = (props) => {
  const filterTypes = React.useMemo(
    () => ({
      fuzzyText: fuzzyTextFilterFn,
      text: (rows, id, filterValue) => {
        return rows.filter((row) => {
          const rowValue = row.values[id]
          return rowValue !== undefined
            ? String(rowValue).toLowerCase().startsWith(String(filterValue).toLowerCase())
            : true
        })
      }
    }),
    []
  )

  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Cell: EditableCell,
      Filter: DefaultColumnFilter
    }),
    []
  )

  const [currentPage,setCurrentPage] = React.useState(1)


  const { columns, data, updateMyData, editFacilityNames, isForPf } = props
  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    prepareRow,
    page, // Instead of using 'rows', we'll use page,
    // which has only the rows for the active page

    // The rest of these things are super handy, too ;)
    rows,
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    setPageSize,
    state: { pageIndex, pageSize }
  } = useTable(
    {
      columns,
      data,
      defaultColumn,
      filterTypes,
      updateMyData,
      editFacilityNames,
      isForPf,
      disableSortRemove: true,
      initialState: { pageIndex: 0, pageSize: 10 }
      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy,
    usePagination
  )




  const HandlePagination = (e,value) => {
    const page = value ? Number(value) - 1 : 0
    
    setCurrentPage(page + 1)
    gotoPage(page)
  }





  return (
    <>
      <Table {...getTableProps()} className={classes.rootTable}>
        <TableHead className={classes.tableHead}>
          {headerGroups.map((headerGroup) => (
            <TableRow {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
              {headerGroup.headers.map((column) => (
                <React.Fragment>
                  <TableCell
                    component='th'
                    scope='row'
                    align='left'
                    className={classes.tableCell}
                    {...column.getHeaderProps(column.getSortByToggleProps())}
                  >
                    {column.canFilter ? (
                      <div id='withTextfield' className={classes.withTextfield}>
                        {column.render('Header')}
                        <span id='showIcon' className={classes.showIcon}>
                          {column.isSortedDesc ? (
                            <div id='sortIcon' className={classes.sortIcon}>
                              <FontAwesomeIcon icon='sort' />
                            </div>
                          ) : (
                            <div id='sortIcon' className={classes.sortIcon}>
                              <FontAwesomeIcon icon='sort' />
                            </div>
                          )}
                        </span>
                        <div> {column.render('Filter')} </div>
                      </div>
                    ) : (
                      <div id='noTextField' className={classes.noTextField}>
                        {column.render('Header')}
                        {/* <span>{column.isSorted ? (column.isSortedDesc ? ' ' : ' ') : ''}</span> */}
                      </div>
                    )}
                  </TableCell>
                </React.Fragment>
              ))}
            </TableRow>
          ))}
        </TableHead>
        <TableBody {...getTableBodyProps()} className={classes.tableBody}>
          {page.map((row, i) => {
            prepareRow(row)
            return (
              <TableRow key={i} {...row.getRowProps()} className={classes.tableRow}>
                {row.cells.map((cell) => {
                  return (
                    <TableCell
                      component='th'
                      scope='row'
                      align='center'
                      className={classes.tableCell}
                      {...cell.getCellProps()}
                    >
                      {cell.render('Cell')}
                    </TableCell>
                  )
                })}
              </TableRow>
            )
          })}
        </TableBody>
      </Table>
      <div className={classes.Pagination}>
    
      <div style={{flex:'2',marginLeft:'2em'}}>
        <span>
        
          <strong>  
            {page.length} - {pageSize}
          </strong>{' '}
          of {`${rows.length} ${props.label ? props.label : ''}`}
        </span>
      </div>
  <div style={{flex:'2',display:'flex',flexDirection:'row',alignItems:'center',justifyContent:'flex-end'}}> 
      <select
          value={pageSize}
          onChange={e => {
            setPageSize(Number(e.target.value))
          }}
          
        >
          {[10, 15, 20].map(pageSize => (
            <option key={pageSize} value={pageSize}>
              Show {pageSize}
            </option>
          ))}
        </select>
      <Pagination
                    count={pageOptions.length}
                   page={currentPage}
                   onChange={HandlePagination}
                    showFirstButton
                    showLastButton

/>
</div>
      </div>
    </>
  )
}

function EditableTable ({ data: tdata, columns, editFacilityNames, setForUpdateData, isForPf,label }) {
  const [data, setData] = React.useState(tdata)
  const [tmpData, setTmpData] = React.useState(tdata)
  const [originalData] = React.useState(tdata)
  // let dispatch = useDispatch()
  const updateMyData = (rowIndex, columnId, value) => {
    setTmpData((old) =>
      old.map((row, index) => {
        if (index === rowIndex) {
          return {
            ...old[rowIndex],
            [columnId]: value
          }
        }
        return row
      })
    )
  }

  React.useEffect(() => {
    setForUpdateData(tmpData)
  }, [tmpData])

  return (
    <React.Fragment>
      <ReactTable columns={columns} data={data} updateMyData={updateMyData} label={label} editFacilityNames={editFacilityNames} setForUpdateData={setForUpdateData} isForPf={isForPf}/>
    </React.Fragment>
  )
}

export default EditableTable
