import React from 'react'
import Button from '@material-ui/core/Button'
import CustomAlerSnackBar from '../CustomSnackBar/CustomAlertSnackbar'
import YesOrNoDialogBox from '../YesOrNoDialogBox/YesOrNoDialogBox'

import classes from './FooterWithButtons.module.scss'
import { useDispatch } from 'react-redux'

const FooterWithButtons = props => {
  const {
    footerStatus,
    disableButton,
    onClickSave,
    isSuccess = false,
    setIsSuccess,
    pathUrl = '/',
    save = null,
    isSubmitted,
    handleReset,
    proceedSubmitForm
  } = props

  let BackgroundChange

  const PrevPage = e => {
    handleReset()
    e.preventDefault()
    if (proceedSubmitForm) {
      proceedSubmitForm()
    }
    props.history.push('/medical-partner-enrollment')
  }

  const handleProceed = () => {
    if (typeof save === 'function' && !isSubmitted) {
      save()
    }
    props.history.push(pathUrl)
  }

  if (disableButton) {
    BackgroundChange = classes.backgroundChange
  }

  return (
    <React.Fragment>
      <CustomAlerSnackBar isSave={isSuccess} setIsSave={setIsSuccess} />
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div>
            <YesOrNoDialogBox PrevPage={PrevPage} />
          </div>
          <div />
          <div id-='TwoButtons'>
            <div id='SaveButton'>
              <Button
                variant='contained'
                disabled={isSubmitted}
                onClick={onClickSave}
                className={isSubmitted ? classes.disabledButton : classes.SaveButton}
              >
                <div style={{ fontSize: '12px' }}> Save</div>
              </Button>
            </div>
            <div id='ProceedButton'>
              <Button
                variant='contained'
                onClick={handleProceed}
                disabled={disableButton}
                className={BackgroundChange || classes.ProceedButton}
              >
                Proceed
              </Button>
            </div>
          </div>
        </span>
        <div style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}>
          {footerStatus} Affiliation Form 1 out 2
        </div>
      </div>
    </React.Fragment>
  )
}

export default FooterWithButtons
