import React from 'react'
import Grid from '@material-ui/core/Grid'
import moment from 'moment'

// Redux
import { useDispatch } from 'react-redux'

// Import Partner Components
import PartnerCard from '../../../Components/PartnerCard/PartnerCardV2'
import ApproverViewList from './Components/ApproverViewList'

// Import Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

// Import scss module
import classes from './Dashboard.module.scss'

// Constant
import cntsStage from 'Pages/Protected/Partner/Constants/stage'
import cntsType from 'Pages/Protected/Partner/Constants/partnerTypes'

// Services
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'

const Dashboard = props => {
  const dispatch = useDispatch()
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [viewList, setViewList] = React.useState({
    isOpen: false,
    stage: ''
  })
  const [forAffilToApproveOICCard, setForAffilToApproveOICCard ] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })
  const [forAffilToApproveCard, setForAffilToApproveCard] = React.useState({
    filter: 'today',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })

  React.useEffect(() => {
    const affil = {}
    affil.affiliationStatus = 'In-Progress'
    BreadcrumbsService.setSelectedItemsWithoutHome([])
    dispatch(setAffiliationType(affil))
    setIsLoaded(true)
  }, [])

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForAffilToApproveOICCard({ ...forAffilToApproveOICCard, isLoading: true })
      const { data } = await AffiliationApproverServices.getFacilitiesToApproveOICTile({
        filter: forAffilToApproveOICCard.filter,
        stage_name: cntsStage.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          dateSubmitted: moment(obj.stage.date_submitted).format('MM/DD/YYYY'),
          type: obj.type,
          name: obj.name,
          location: `${obj.address ? obj.address.city : ''}, ${obj.address ? obj.address.region : ''}`
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForAffilToApproveOICCard({ ...forAffilToApproveOICCard, dataList: sortedData, count: fixData.length, isLoading: false })
    }
    fetchTileData()
  }, [forAffilToApproveOICCard.filter])

  React.useEffect(() => {
    const fetchTileData = async () => {
      setForAffilToApproveCard({ ...forAffilToApproveCard, isLoading: true })
      const { data } = await AffiliationApproverServices.getPartnersToApproveTile({
        filter: forAffilToApproveCard.filter,
        stage_name: cntsStage.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL
      })
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          type: obj.type,
          name: obj.type === 'DOCTOR' ? obj.full_name : obj.name
        }
      })
      const sortedData = fixData.sort((a, b) => {
        const firstObj = parseInt(a.id.substring(3))
        const secondObj = parseInt(b.id.substring(3))
        return firstObj - secondObj
      })
      setForAffilToApproveCard({ ...forAffilToApproveCard, dataList: sortedData, count: fixData.length, isLoading: false })
    }
    fetchTileData()
  }, [forAffilToApproveCard.filter])

  const getSelectedForAppRow = (stage, type, id) => {
    if (stage === cntsStage.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC) {
      if (type === cntsType.PARTNER.TYPE.HOSPITAL) {
        props.history.push('/approveoi-hospital-affiliation/' + id)
      } else if (type === cntsType.PARTNER.TYPE.CLINIC) {
        props.history.push('/approveoi-medical-clinic-affiliation/' + id)
      } else if (type === cntsType.PARTNER.TYPE.DENTAL_CLINIC) {
        props.history.push('/approveoi-dental-clinic-affiliation/' + id)
      }
    } else if (stage === cntsStage.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL) {
      if (type === cntsType.PARTNER.TYPE.HOSPITAL) {
        props.history.push('/approve-hospital-affiliation/' + id)
      } else if (type === cntsType.PARTNER.TYPE.CLINIC) {
        props.history.push('/approve-medical-clinic-affiliation/' + id)
      } else if (type === cntsType.PARTNER.TYPE.DENTAL_CLINIC) {
        props.history.push('/approve-dental-clinic-affiliation/' + id)
      } else if (type === cntsType.PARTNER.TYPE.DOCTOR) {
        props.history.push('/approve-doctor-affiliation/' + id)
      } else if (type === cntsType.PARTNER.TYPE.OTHER_SERVICES) {
        props.history.push('/approve-other-service/' + id)
      }
    }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          {viewList.isOpen && (
            <ApproverViewList
              isOpen={viewList.isOpen}
              handleClose={status => setViewList({ ...viewList, isOpen: status })}
              stage={viewList.stage}
              getSelectedRow={obj => getSelectedForAppRow(viewList.stage, obj.type, obj.id)}
            />
          )}
          <div className={classes.root}>
            <h1 className={classes.pageHeader}>Dashboard</h1>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={8}>
                <PartnerCard
                  isLoading={forAffilToApproveOICCard.isLoading}
                  headerCount={forAffilToApproveOICCard.count}
                  headerTitle={'Affiliation to Approve for Ocular Inspection'}
                  cardTitle='Your Task Tray'
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forAffilToApproveOICCard.filter}
                  getSelectedFilter={filter => setForAffilToApproveOICCard({ ...forAffilToApproveOICCard, filter })}
                  headerList={['Affiliation No.', 'Date Submitted', 'Partner Type', 'Partner Name', 'Location']}
                  dataList={forAffilToApproveOICCard.dataList}
                  getSelectedRow={obj => getSelectedForAppRow(cntsStage.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC, obj.type, obj.id)}
                  actionText={'Approve'}
                  openViewList={() => setViewList({ isOpen: true, stage: cntsStage.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC })}
                />
              </Grid>
              <Grid item xs={4}>
                <PartnerCard
                  headerCount={'0'}
                  headerCountStyle={classes.headerCountStyle}
                  cardTitle='Your Ticket List'
                  cardTitleStyle={classes.cardTitleStyle}
                  headerTitle={'Open Tickets'}
                  openViewList={() => false}
                  selectListFilter={[{ key: 'assignedToAll', value: 'Assigned To All' }]}
                  defaultFilter={'assignedToAll'}
                  getSelectedFilter={() => false}
                />
              </Grid>
            </Grid>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={6}>
                <PartnerCard
                  isLoading={forAffilToApproveCard.isLoading}
                  headerCount={forAffilToApproveCard.count}
                  headerTitle={'Affiliation to Approve'}
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  defaultFilter={forAffilToApproveCard.filter}
                  getSelectedFilter={filter => setForAffilToApproveCard({ ...forAffilToApproveCard, filter })}
                  headerList={['Affiliation No.', 'Partner Type', 'Partner Name']}
                  dataList={forAffilToApproveCard.dataList}
                  getSelectedRow={obj => getSelectedForAppRow(cntsStage.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL, obj.type, obj.id)}
                  actionText={'Approve'}
                  openViewList={() => setViewList({ isOpen: true, stage: cntsStage.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL })}
                />
              </Grid>
            </Grid>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default Dashboard
