import moment from 'moment'
import { dateFormatChecker } from '../Utils/utils'

export default function datePickerValidation (form, name, newErrors = {}, required = false) {
  if (required && typeof form[name] === 'string' && form[name].length <= 0) {
    newErrors[name] = errorMessage(name)
  }
  if (typeof form[name] === 'string' && form[name].length > 0) {
    newErrors[name] = 'Invalid date format'
  }
  return newErrors
}

function errorMessage (name) {
  let errorMessage = {
    birthDate: '"Birth date" is not allowed to be empty',
    prcExpiration: '"Prc Expiry Date" is not allowed to be empty'
  }
  return errorMessage[name]
}

export function datepickerInvalidDate (form, name, newErrors = {}) {
  if (form[name] === null) {
    newErrors[name] = 'Invalid date format'
  }
  return newErrors
}

export function isDateValid (date) {
  return {
    isValid: moment(dateFormatChecker(date) ? date : formatToDate(date), 'MM/DD/YYYY', true).isValid(),
    dateFormatted: dateFormatChecker(date) ? moment(date).toISOString() : formatToDate(date)
  }
}

function formatToDate (val) {
  let str = val.match(/(\d{2})(\d{2})(\d{4})/)
  return str ? str.splice(1, 4).join('/') : ''
}
