import React from 'react'
import classes from './index.module.scss'
import { RenderInputFields } from '../../../Components/InputsForProfiles/InputsForProfile'

const AutosuggestComponent = props => {
  const { data, formValue, name, onBlur, setValue } = props

  const [suggestions, setSuggestions] = React.useState([])

  const HandleChange = e => {
    const { value } = e.target

    let suggestionsValue = []
    try {
      if (value.length > 0) {
        const regexCompare = new RegExp(`^${value}`, 'i')
        suggestionsValue = data.sort().filter(items => regexCompare.test(items))
      }
      setSuggestions(suggestionsValue)
      setValue(value)
    } catch (e) {
      console.log('error', e)
    }
  }

  const selectedSuggestion = items => {
    setValue(items)
    setSuggestions([])
  }

  const RenderSuggestion = () => {
    if (suggestions.length === 0) {
      return null
    }

    return (
      <ul id='rootList' className={classes.list}>
        {suggestions.map((items, index) => (
          <li key={items.index} onClick={() => selectedSuggestion(items)}>
            {items}
          </li>
        ))}
      </ul>
    )
  }

  return (
    <div className={classes.root}>
      <RenderInputFields value={formValue} name={name} onBlur={onBlur} onChange={HandleChange} type={'noSpecialChar'} />
      {RenderSuggestion()}
    </div>
  )
}

export default AutosuggestComponent
