import 'date-fns'
import React from 'react'
import Dialog from '@material-ui/core/Dialog'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Button from '@material-ui/core/Button'
import TextField from '@material-ui/core/TextField'
import Autocomplete from '@material-ui/lab/Autocomplete'
import Paper from '@material-ui/core/Paper'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import MomentUtils from '@date-io/moment'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import Pagination from '@material-ui/lab/Pagination'
import CircularProgress from '@material-ui/core/CircularProgress'
import moment from 'moment'
import FormControl from '@material-ui/core/FormControl'
import Modal from '@material-ui/core/Modal'

// Import scss module classes
import classes from './ContractManagerPrintList.module.scss'

const ContractManagerViewList = props => {
  // Get state props
  const { isOpen = false } = props
  const { handleClose } = props
  const { getSelectedRow = null } = props

  const [tableData, setTableData] = React.useState({
    data: [],
    pageData: [],
    page: 1,
    totalPage: 1,
    size: 10,
    isLoading: false
  })

  const [afflNo, setAfflNo] = React.useState({
    list: [],
    inputValue: '',
    value: '',
    isSort: false
  })

  const [dateSubmitted, setDateSubmitted] = React.useState({
    value: null,
    isSort: false
  })

  const [location, setLocation] = React.useState({
    list: [],
    inputValue: '',
    value: '',
    isLoading: false,
    isSort: false
  })

  const [partnerName, setPartnerName] = React.useState({
    list: [],
    inputValue: '',
    value: '',
    isLoading: false,
    isSort: false
  })

  const [partnerType, setPartnerType] = React.useState({
    list: [
      { type: 'HOSPITAL', value: 'HOSPITAL' },
      { type: 'CLINIC', value: 'CLINIC' },
      { type: 'DENTAL_CLINIC', value: 'DENTAL CLINIC' }
    ],
    inputValue: '',
    value: '',
    isLoading: false,
    isSort: false
  })

  const onHandleRowClick = (e, dataObj) => {
    e.preventDefault()
    getSelectedRow(dataObj)
  }

  React.useEffect(() => {
    const fetchData = async () => {
      setTableData({ ...tableData, isLoading: true })
      const { data } = await ContractManagerServices.getContractViewListPrint()
      const fixData = data.data.map(obj => {
        return {
          id: obj.id,
          dateSubmitted: moment(obj.stage.date_submitted).format('MM/DD/YYYY'),
          type: obj.type,
          name: obj.name,
          location: ((obj.address || {}).city || '') + ', ' + ((obj.address || {}).region || '')
        }
      })
      const totalPage = Math.ceil(fixData.length / tableData.size)
      let page = tableData.page
      if (page > totalPage) {
        page = totalPage
      }
      const pageData = fixData.slice(page * tableData.size - tableData.size, page * tableData.size)
      setTableData({ ...tableData, data: fixData, page, totalPage, pageData, isLoading: false })
      const aflNoList = fixData.map(obj => ({ id: obj.id }))
      const partnerNameList = fixData.map(obj => ({ name: obj.name }))
      let locationList = [...new Set(fixData.map(obj => obj.location))]
      locationList = locationList.map(loc => ({ location: loc }))
      setAfflNo({ ...setAfflNo, list: aflNoList })
      setPartnerName({ ...partnerName, list: partnerNameList })
      setLocation({ ...location, list: locationList })
    }
    fetchData()
  }, [])

  React.useEffect(() => {
    const objectHeaders = {
      id: (afflNo.value || {}).id || '',
      dateSubmitted: dateSubmitted.value ? moment(dateSubmitted.value).format('MM/DD/YYYY') : '',
      type: (partnerType.value || {}).type || '',
      name: (partnerName.value || {}).name || '',
      location: (location.value || {}).location || ''
    }
    const temp = tableData.data
    const filteredData = temp.filter(obj => {
      const validation = Object.keys(obj).map(keys => {
        if (objectHeaders[keys] !== '') {
          if (obj[keys] === objectHeaders[keys]) {
            return true
          }
          return false
        }
        return true
      })
      return validation.every(Boolean)
    })
    const totalPage = Math.ceil(filteredData.length / tableData.size)
    let page = tableData.page
    if (page === 0) page = 1
    if (page > totalPage) {
      page = totalPage
    }
    const pageData = filteredData.slice(page * tableData.size - tableData.size, page * tableData.size)
    setTableData({ ...tableData, pageData, totalPage, page })
  }, [
    afflNo.value,
    dateSubmitted.value,
    partnerType.value,
    location.value,
    partnerName.value,
    tableData.size,
    tableData.page
  ])

  const sortAfflNo = () => {
    const tempData = tableData.pageData
    let sortedData = []
    const compareFunction = (a, b) => {
      const firstObj = parseInt(a.id.substring(3))
      const secondObj = parseInt(b.id.substring(3))
      return firstObj - secondObj
    }
    if (afflNo.isSort) {
      sortedData = tempData.sort(compareFunction)
    } else {
      sortedData = tempData.reverse(compareFunction)
    }
    setTableData({ ...tableData, pageData: sortedData })
    setAfflNo({ ...afflNo, isSort: !afflNo.isSort })
  }

  const sortDateSubmitted = () => {
    const tempData = tableData.pageData
    let sortedData = []
    const compareFunction = (a, b) => {
      const firstObj = new Date(a.dateSubmitted)
      const secondObj = new Date(b.dateSubmitted)
      if (firstObj > secondObj) {
        return 1
      } else if (firstObj < secondObj) {
        return -1
      } else {
        return 0
      }
    }
    if (dateSubmitted.isSort) {
      sortedData = tempData.sort(compareFunction)
    } else {
      sortedData = tempData.reverse(compareFunction)
    }
    setTableData({ ...tableData, pageData: sortedData })
    setDateSubmitted({ ...dateSubmitted, isSort: !dateSubmitted.isSort })
  }

  const sortPartnerName = () => {
    const tempData = tableData.pageData
    let sortedData = []
    const compareFunction = (a, b) => {
      if (a.name.charAt(0) > b.name.charAt(0)) {
        return 1
      } else if (a.name.charAt(0) < b.name.charAt(0)) {
        return -1
      } else {
        return 0
      }
    }
    if (partnerName.isSort) {
      sortedData = tempData.sort(compareFunction)
    } else {
      sortedData = tempData.reverse(compareFunction)
    }
    setTableData({ ...tableData, pageData: sortedData })
    setPartnerName({ ...partnerName, isSort: !partnerName.isSort })
  }

  const sortLocation = () => {
    const tempData = tableData.pageData
    let sortedData = []
    const compareFunction = (a, b) => {
      if (a.location.charAt(0) > b.location.charAt(0)) {
        return 1
      } else if (a.location.charAt(0) < b.location.charAt(0)) {
        return -1
      } else {
        return 0
      }
    }
    if (location.isSort) {
      sortedData = tempData.sort(compareFunction)
    } else {
      sortedData = tempData.reverse(compareFunction)
    }
    setTableData({ ...tableData, pageData: sortedData })
    setLocation({ ...location, isSort: !location.isSort })
  }

  const sortPartnerType = () => {
    const tempData = tableData.pageData
    let sortedData = []
    const compareFunction = (a, b) => {
      if (a.type.charAt(0) > b.type.charAt(0)) {
        return 1
      } else if (a.type.charAt(0) < b.type.charAt(0)) {
        return -1
      } else {
        return 0
      }
    }
    if (partnerType.isSort) {
      sortedData = tempData.sort(compareFunction)
    } else {
      sortedData = tempData.reverse(compareFunction)
    }
    setTableData({ ...tableData, pageData: sortedData })
    setPartnerType({ ...partnerType, isSort: !partnerType.isSort })
  }

  return (
    <React.Fragment>
      <Modal style={{ outline: 'none' }} open={isOpen} onClose={handleClose}>
        <div className={classes.root} style={{ outline: 'none' }}>
          {tableData.isLoading && (
            <div className={classes.loadingComponent}>
              <CircularProgress />
            </div>
          )}
          <div id='tableWrapper' className={classes.tableWrapper}>
            {!tableData.isLoading && (
              <TableContainer style={{ minHeight: 350, backgroundColor: '#f5f7fb' }}>
                <Table stickyHeader className={classes.table} size='small'>
                  <TableHead className={classes.tableHeader}>
                    <TableRow>
                      <TableCell style={{ width: '18%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Medical Partner ID</span>
                          <FontAwesomeIcon icon='sort' className={classes.tableHeaderSortIcon} onClick={sortAfflNo} />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='combo-box-demo'
                            options={afflNo.list}
                            getOptionLabel={option => option.id}
                            onInputChange={(event, newInputValue) => {
                              setAfflNo({ ...afflNo, inputValue: newInputValue })
                            }}
                            inputValue={afflNo.inputValue}
                            value={afflNo.value}
                            onChange={(event, newValue) => {
                              setAfflNo({ ...afflNo, value: newValue })
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '18%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Date Submitted</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={sortDateSubmitted}
                          />
                        </div>
                        <Paper>
                          <MuiPickersUtilsProvider utils={MomentUtils}>
                            <KeyboardDatePicker
                              clearable
                              allowKeyboardControl={false}
                              format='MM/DD/YYYY'
                              placeholder='mm/dd/yyyy'
                              InputProps={{
                                disableUnderline: true,
                                readOnly: true,
                                style: { paddingLeft: '10px' }
                              }}
                              value={dateSubmitted.value}
                              onChange={date => setDateSubmitted({ ...dateSubmitted, value: date })}
                              KeyboardButtonProps={{
                                'aria-label': 'change date'
                              }}
                              keyboardIcon={<FontAwesomeIcon icon='calendar-day' style={{ fontSize: '1rem' }} />}
                            />
                          </MuiPickersUtilsProvider>
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '18%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Partner Type</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={sortPartnerType}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            options={partnerType.list}
                            getOptionLabel={option => option.value}
                            onInputChange={(event, newInputValue) => {
                              setPartnerType({ ...partnerType, inputValue: newInputValue })
                            }}
                            inputValue={partnerType.inputValue}
                            value={partnerType.value}
                            onChange={(event, newValue) => {
                              setPartnerType({ ...partnerType, value: newValue })
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '30%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Partner Name</span>
                          <FontAwesomeIcon
                            icon='sort'
                            className={classes.tableHeaderSortIcon}
                            onClick={sortPartnerName}
                          />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='combo-box-demo'
                            options={partnerName.list}
                            getOptionLabel={option => option.name}
                            onInputChange={(event, newInputValue) => {
                              setPartnerName({ ...partnerName, inputValue: newInputValue })
                            }}
                            inputValue={partnerName.inputValue}
                            value={partnerName.value}
                            onChange={(event, newValue) => {
                              setPartnerName({ ...partnerName, value: newValue })
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                      <TableCell style={{ width: '20%' }} className={classes.tableHeaderCell}>
                        <div>
                          <span className={classes.tableHeaderTitle}>Location</span>
                          <FontAwesomeIcon icon='sort' className={classes.tableHeaderSortIcon} onClick={sortLocation} />
                        </div>
                        <Paper>
                          <Autocomplete
                            id='combo-box-demo'
                            options={location.list}
                            getOptionLabel={option => option.location}
                            onInputChange={(event, newInputValue) => {
                              setLocation({ ...location, inputValue: newInputValue })
                            }}
                            inputValue={location.inputValue}
                            value={location.value}
                            onChange={(event, newValue) => {
                              setLocation({ ...location, value: newValue })
                            }}
                            renderInput={params => (
                              <TextField
                                {...params}
                                InputProps={{
                                  ...params.InputProps,
                                  disableUnderline: true,
                                  style: { paddingLeft: '10px' }
                                }}
                              />
                            )}
                          />
                        </Paper>
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    <React.Fragment>
                      {tableData.pageData.map((dataObj, dataIndex) => {
                        return (
                          <TableRow
                            onDoubleClick={e => onHandleRowClick(e, dataObj)}
                            key={`dataList_${dataIndex}`}
                            className={classes.tableBodyRow}
                          >
                            {Object.keys(dataObj).map((objKey, keyIndex) => {
                              return (
                                <TableCell key={`objKey_${keyIndex}`} align='left'>
                                  {dataObj[objKey]}
                                </TableCell>
                              )
                            })}
                          </TableRow>
                        )
                      })}
                    </React.Fragment>
                  </TableBody>
                </Table>
              </TableContainer>
            )}
            <div className={classes.tablePagination}>
              <div>
                <span>
                  {tableData.pageData.length}-{tableData.size} out of {tableData.data.length} Procedures
                </span>
              </div>
              <div className={classes.options}>
                <span style={{ marginRight: '10px' }}>
                  <p>Show</p>
                </span>
                <FormControl size='small' variant='outlined'>
                  <Select
                    style={{ backgroundColor: 'white' }}
                    value={tableData.size}
                    onChange={e => setTableData({ ...tableData, size: e.target.value })}
                  >
                    <MenuItem value={10}>10</MenuItem>
                    <MenuItem value={15}>15</MenuItem>
                    <MenuItem value={20}>20</MenuItem>
                  </Select>
                </FormControl>
                <Pagination
                  count={tableData.totalPage}
                  onChange={(e, value) => setTableData({ ...tableData, page: value })}
                  showFirstButton
                  showLastButton
                />
              </div>
            </div>
          </div>
          <div className={classes.footer}>
            <Button
              onClick={() => handleClose(false)}
              id='rc_btn_cancel'
              className={classes.btnRed}
              variant='contained'
            >
              Close
            </Button>
          </div>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default ContractManagerViewList
