import React from 'react'
import Star from '../../Assets/Icons/star.png'

import classes from './Facility.module.scss'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import Checkbox from '@material-ui/core/Checkbox'
import FormGroup from '@material-ui/core/FormGroup'
import FormHelperText from '@material-ui/core/FormHelperText'
import { RenderInputFields } from '../InputsForProfiles/InputsForProfile'

function FacilityAssignmentComponent (props) {
  const { form, setForm, errors, setErrors, reasonRef, exclusiveRef } = props

  const { facilityAssignment } = form

  const handleChange = ({ target: input }) => {
    const newVal = { ...form }
    const { value, name, checked } = input
    if (name === 'assignments') {
      checkedAndUncheckAssignment(value, checked)
    } else {
      newVal[name] = value
      setForm(newVal)
    }
    delete errors[name]
    setErrors(errors)
  }

  const checkedAndUncheckAssignment = (value, checked) => {
    const newVal = { ...form }
    if (checked && !newVal['assignments'].includes(value)) {
      newVal['assignments'] = [...newVal['assignments'], value]
      setForm(newVal)
    }
    if (!checked && form['assignments'].includes(value)) {
      newVal['assignments'] = newVal.assignments.filter(assign => assign !== value)
      setForm(newVal)
    }
  }

  const handleAssignmentChange = ({ target: { value, name } }) => {
    let newVal = { ...form }
    if (value === 'None') {
      newVal['assignments'] = []
      newVal['facilityAssignment'] = value
      setForm(newVal)
    } else {
      newVal['facilityAssignment'] = value
      setForm(newVal)
    }
    delete errors[name]
    setErrors(errors)
  }

  const handleDisabledCheckbox = React.useMemo(() => {
    if (facilityAssignment === 'Assigned') {
      return false
    } else {
      return true
    }
  }, [facilityAssignment])

  React.useEffect(() => {
    if (form['assignments'].length > 0) {
      let newForm = { ...form }
      newForm['facilityAssignment'] = 'Assigned'
      setForm(newForm)
    } else {
      let newForm = { ...form }
      newForm['facilityAssignment'] = 'None'
      setForm(newForm)
    }
  }, [form['assignments']])

  return (
    <div id='root' className={classes.root}>
      <div id='one' className={classes.FacilityAssignments}>
        <span id='star' className={classes.star}>
          <img src={Star} width='15px' height='15px' alt='star' />
        </span>
        <label>Facility assignments</label>
        <div className={classes.radioGroup}>
          <FormControl component='fieldset'>
            <RadioGroup
              row
              aria-label='position'
              name='facilityAssignment'
              value={form['facilityAssignment']}
              onChange={handleAssignmentChange}
            >
              <FormControlLabel
                style={{ marginLeft: '1em' }}
                value='None'
                control={<Radio color='primary' />}
                label='None'
              />
              <FormControlLabel
                style={{ marginLeft: '2em' }}
                value='Assigned'
                control={<Radio color='primary' />}
                label='Assigned'
              />
            </RadioGroup>
            {errors['assignments'] && (
              <FormHelperText className={classes.errorMessage}>{errors['assignments']}</FormHelperText>
            )}
            {errors['facilityAssignment'] && (
              <FormHelperText className={classes.errorMessage}>{errors['facilityAssignment']}</FormHelperText>
            )}
          </FormControl>
        </div>
        <div id='checkBoxes' className={classes.checkBoxes}>
          <FormControl component='fieldset' className={classes.formComponent}>
            <FormGroup
              aria-label='position'
              column
              onChange={handleChange}
              name='assignments'
              className={classes.formGroup}
            >
              <FormControlLabel
                className={classes.checkLabel}
                control={<Checkbox color='primary' name='assignments' />}
                label='Preferred Medical Partner'
                value='preferredMedicalPartner'
                labelPlacement='end'
                checked={form['assignments'].includes('preferredMedicalPartner')}
                disabled={handleDisabledCheckbox}
              />
              <FormControlLabel
                className={classes.checkLabel}
                control={<Checkbox color='primary' name='assignments' />}
                label='Fullerton One'
                value='fullertonOne'
                labelPlacement='end'
                checked={form['assignments'].includes('fullertonOne')}
                disabled={handleDisabledCheckbox}
              />
              <div id='exclusiveTo' className={classes.exclusiveTo}>
                <FormControlLabel
                  className={classes.checkLabel}
                  value='specialAccommodation'
                  control={<Checkbox color='primary' name='assignments' />}
                  label='Special Accommodation'
                  labelPlacement='end'
                  checked={form['assignments'].includes('specialAccommodation')}
                  disabled={handleDisabledCheckbox}
                />

                <span
                  id='exclusiveToField'
                  className={
                    form['assignments'].includes('specialAccommodation')
                      ? classes.exclusiveToField
                      : classes.exclusiveToFieldHide
                  }
                >
                  <label id='exclusiveTo' className={classes.label}>
                    Exclusive to:
                  </label>
                  <div>
                    <RenderInputFields
                      type='text'
                      inputRef={exclusiveRef}
                      onChange={handleChange}
                      name={'exclusiveTo'}
                      className={classes.inputField}
                      error={errors['exclusiveTo']}
                    />
                  </div>
                </span>
              </div>
            </FormGroup>
          </FormControl>
        </div>
      </div>
      <div id='two' className={classes.medicalPartnerAndTvs}>
        <div id='medicalPartnerStatus' className={classes.medicalPartnerStatus}>
          <label> Medical Partner Status </label>
          <FormControl component='fieldset' className={classes.medicalRadio}>
            <RadioGroup
              column
              aria-label='position'
              name='status'
              value={form['status']}
              className={classes.radioGroup}
              onChange={handleChange}
            >
              <FormControlLabel
                className={classes.radioLabel}
                value='Active'
                control={<Radio color='primary' />}
                label='Active'
              />
              <div className={classes.onHoldWrapper}>
                <FormControlLabel
                  className={classes.radioLabel2}
                  value='On-Hold'
                  control={<Radio color='primary' />}
                  label='On-Hold'
                />
                <span
                  id='reasonForOnhold'
                  className={form['status'] === 'On-Hold' ? classes.reasonForOnhold : classes.reasonForOnholdHide}
                >
                  <label>Reason for on-hold: </label>
                  <div>
                    <RenderInputFields
                      type='text'
                      onChange={handleChange}
                      name={'reason'}
                      inputRef={reasonRef}
                      error={errors['reason']}
                      className={classes.inputField}
                    />
                  </div>
                </span>
              </div>

              <FormControlLabel
                className={classes.radioLabel}
                value='Inactive'
                control={<Radio color='primary' />}
                label='Inactive'
              />
            </RadioGroup>
            {errors['status'] && <FormHelperText className={classes.errorMessage}>{errors['status']}</FormHelperText>}
          </FormControl>
        </div>

        <div id='Tvs' className={classes.Tvs}>
          <label>
            TVS <sub>(Transaction Verification System) </sub>
          </label>
          <FormControl component='fieldset' className={classes.medicalRadio}>
            <RadioGroup
              column
              aria-label='position'
              name='tvs'
              value={form['tvs']}
              className={classes.radioGroup}
              onChange={handleChange}
            >
              <FormControlLabel
                className={classes.radioLabel}
                value='yes'
                control={<Radio color='primary' name='tvs' />}
                label='Yes'
              />
              <FormControlLabel
                className={classes.radioLabel}
                value='no'
                control={<Radio color='primary' name='tvs' />}
                label='No'
              />
            </RadioGroup>
            {errors['tvs'] && <FormHelperText className={classes.errorMessage}>{errors['tvs']}</FormHelperText>}
          </FormControl>
        </div>
      </div>
    </div>
  )
}
// form, setForm, errors, setErrors, reasonRef, exclusiveRef

export default React.memo(FacilityAssignmentComponent, (prevProps, nextProps) => {
  try {
    if (
      prevProps.form === nextProps.form &&
      prevProps.errors === nextProps.errors &&
      prevProps.reasonRef === nextProps.reasonRef &&
      prevProps.exclusiveRef === nextProps.exclusiveRef
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
