import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
import { validateTaxRateVal, validateZipCode } from 'Pages/Protected/Partner/Components/Utils/utils'

const notRequiredFields = [
  'dohNumber',
  'dohLicenseType',
  'dohLicenseExp',
  'philhealthExpiration',
  'unitFlrBldg',
  'bldgName',
  'affiliationInfos',
  'externalRemarks',
  'id',
  'cName',
  'cDesignation',
  'cDepartment',
  'cEmail',
  'cNumber',
  'stageName',
  'type',
  'contacts',
  'affiliationStatus',
  'dateSubmitted',
  'affiliateGroup',
  'status',
  'name'
]

const schema = Joi.object({
  medName: Joi.string()
    .label('Name')
    .required(), // required
  email: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(), // required
  years: Joi.number()
    .label('Years')
    .required(), // required
  months: Joi.number()
    .label('Months')
    .required(), // required
  payee: Joi.string()
    .label('Payee')
    .required(), // required
  classification: Joi.string()
    .label('Classification')
    .required(), // required
  nonVatable: Joi.string()
    .label('Tin Status Value')
    .required(),
  tinStatus: Joi.string()
    .label('Tin Status')
    .required(), // required
  processType: Joi.string()
    .label('Process Type')
    .required(), // required
  street: Joi.string()
    .label('Street')
    .required(), // required
  subdivision: Joi.string()
    .label('Subdivision')
    .required(), // required
  region: Joi.string()
    .label('Region')
    .required(), // required
  district: Joi.string()
    .label('District')
    .required(), // required
  barangay: Joi.string()
    .label('Barangay')
    .required(), // required
  city: Joi.string()
    .label('City')
    .required(), // required
  zipCode: Joi.string()
    .label('Zip code')
    .required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  roomNumber: Joi.string()
    .label('Room Number')
    .required(),
  vatTaxRate: Joi.number()
    .label('Tax Rate')
    .required(),
  philhealthNumber: Joi.string()
    .label('PhilHealth License Number')
    .max(12)
    .min(12)
    .required(), // required
  internalRemarks: Joi.string()
    .label('Internal Remarks')
    .required() // required,
})
let errors = {}
export default function MedicalClinicFormValidation (form, {errors: newErrors}, dateValue = {}) {
  errors = {...newErrors}
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
      if (form['tinStatus'] === '12%' || form['tinStatus'] === '') {
        delete errors['nonVatable']
      }
      if (form['philhealthNumber'].length === 0) {
        delete errors['philhealthNumber']
      } else {
        if (form['philhealthNumber'].length === 14) {
          delete errors['philhealthNumber']
        }
      }
      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
      dateValidation(dateValue, 'dohLicenseExp')
      dateValidation(dateValue, 'philhealthExpiration')
      errors = {...validateTaxRateVal(form, errors)}
      errors = {...validateZipCode(form, errors)}
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}

function dateValidation (dateValue, name) {
  if (dateValue.hasOwnProperty(name)) {
    errors[name] = 'Invalid date format'
  } else {
    delete errors[name]
  }
}
