import React from 'react'
import classes from './YearsOfOperation.module.scss'
import { RenderInputWithlabel } from '../../../Components/InputsForProfiles/InputsForProfile'

const YearsOperationField = props => {
  const { errors, form, resultRef, setForm, setErrors } = props

  const HandleChange = React.useCallback(
    e => {
      const { name, value } = e.target
      const newForm = { ...form }
      const newErrors = { ...errors }
      delete newErrors[name]
      setErrors(newErrors)
      newForm[name] = value.trim()
      setForm(newForm)
    },
    [form]
  )

  return (
    <div id='root' className={classes.root}>
      <div id='years'>
        <RenderInputWithlabel
          error={errors['years']}
          name={'years'}
          // inputRef={resultRef.years}
          value={form['years']}
          label={'Years'}
          type={'number'}
          onChange={HandleChange}
        />
      </div>
      <div id='months'>
        <RenderInputWithlabel
          error={errors['months']}
          name={'months'}
          value={form['months']}
          // inputRef={resultRef.months}
          label={'Months'}
          type={'number'}
          onChange={HandleChange}
        />
      </div>
    </div>
  )
}

const YearsOperation = React.memo(YearsOperationField, (prevProps, nextProps) => {
  try {
    if (
      prevProps.errors === nextProps.errors &&
      prevProps.form === nextProps.form &&
      prevProps.value === nextProps.value
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})

export default YearsOperation
