import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
const schema = Joi.object({
  'dental-clinic-profile': Joi.number().min(1).required(), // required
  'list-service-offered-with-rates': Joi.number().min(1).required(), // required
  'location-map': Joi.number().min(1).required(), // required
  'business-permit': Joi.number().min(1).required(), // required
  'bir-cor-2303': Joi.number().min(1).required(), // required
  'sec-dti-registration': Joi.number().min(1).required(), // required
  'dentist-updated-resume': Joi.number().min(1).required(), // required
  'dentist-recent-picture': Joi.number().min(1).required(), // required
  'dentist-copy-of-diploma': Joi.number().min(1).required(), // required
  'dentist-prc-license-id': Joi.number().min(1).required(), // required
  'dentist-bir-cor-2303': Joi.number().min(1).required(), // required
  // 'dentist-requirements': Joi.number().min(1).required(),
  'declaration-gross-sales': Joi.number().min(1).required(),
  'dental-associate-updated-resume': Joi.number().min(1).required(),
  'dental-associate-copy-of-diploma': Joi.number().min(1).required(),
  'dental-associate-prc-license-id': Joi.number().min(1).required()
})

export default function AddMultiplierValidation (form) {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message
    }
  }

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
