import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './CancelDialog.module.scss'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'

const CancelDialog = props => {
  const { isCancel, setIsCancel, handleOnCancel } = props
  return (
    <CustomDialog id='cancelCustomDialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
      <div id='cancelDialogContainer' className={classes.dialogContainer}>
        <h3 id='cancelDialogMessage'>All changes will not be saved. Are you sure you want to cancel?</h3>
        <div id='cancelDialogButtonGroup' className={classes.btnGroup}>
          <Button
            onClick={() => setIsCancel(false)}
            id='cancelDialogButtonCancel'
            className={classes.btnCancel}
            variant='contained'
          >
            No
          </Button>
          <Button
            id='cancelDialogButtonCancel'
            className={classes.btnSave}
            variant='contained'
            onClick={handleOnCancel}
          >
            Yes
          </Button>
        </div>
      </div>
    </CustomDialog>
  )
}

export default CancelDialog
