import React from 'react'

// shared Components
import DatePicker from '../../../../../Components/DatePicker/DatePicker'
import CustomTextFieldNumberFormat from '../../../../../Components/JoiValidationInputFieldNumberFormat'

// material ui
import TextField from '../../../../../Components/JoiValidationInputField'
import DropDown from '../../../../../Components/CustomDrowpdown'
import Button from '@material-ui/core/Button'
import Box from '@material-ui/core/Box'
import Dialog from '@material-ui/core/Dialog'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import YesOrNoDialogBox from '../../../../../Components/YesOrNoDialogBox/YesOrNoDialogBox'
// import history from '../../../../../Components/History/History'
import Joi from '@hapi/joi'
import Axios from 'axios'

// style scss
import classes from './SOSForm.module.scss'
import { useSelector, useDispatch } from 'react-redux'

// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'
import AddressChaining from '../../../../../../../../Constant/chaining-address'
import { setOtherServiceAffil } from '../../../../../../Store/actions/Partner/specialOtherForm'
import isEmpty from 'lodash/isEmpty'
import { useParams } from 'react-router-dom'
import { FetchData } from './fetchingOtherService'
import OtherServices from 'Services/Api/Partner/OtherServices'
import ProgressComponent from '../../../../Components/ProgressComponent/ProgressComponent'
import { fetchRegion, handleAddressChaining } from 'Pages/Protected/Partner/Components/Utils/utils'
import AddressDrowpdown from 'Pages/Protected/Partner/Components/AddressDrowpdown'

const URL = `${process.env.REACT_APP_SERVER_API}`

const schema = Joi.object({
  dateReceived: Joi.date().required(),
  name: Joi.string().label('Name of service').required(),
  unitFlrBldg: Joi.string().label('UnitFlrBldg').required(),
  subdivision: Joi.string().label('Subdivision').required(),
  barangay: Joi.string().label('Barangay').required(),
  bldgName: Joi.string().label('Building Name').required(),
  region: Joi.string().label('Region').required(),
  city: Joi.string().label('City Municipality').required(),
  street: Joi.string().label('Street').required(),
  district: Joi.string().label('District').required(),
  zipCode: Joi.number().label('Zip Code').required(),
  tinNumber: Joi.number().label('Tin Number').required(),
  contactPerson: Joi.string().label('Contact Person').required(),
  contactNumber: Joi.string().label('Contact No').required().max(12)
})

function SOSForm ({history}) {
  const { id } = useParams()
  const { affiliationType } = useSelector(state => state.affiliationType)
  const { specialOther } = useSelector(state => state.specialOtherService)
  const dispatch = useDispatch()
  const [openModal, setOpenModal] = React.useState(false)
  const [isSubmitted, setIsSubmitted] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [provinces, setProvinces] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])
  const handleSubmitClose = () => {
    setOpenModal(false)
  }

  const PrevPage = e => {
    e.preventDefault()
    history.push('/medical-partner-enrollment')
  }

  const [errors, setErrors] = React.useState({})
  const [specialOthersForm, setSpecialOthersFrom] = React.useState({
    dateReceived: new Date().toISOString(),
    name: '',
    unitFlrBldg: '',
    subdivision: '',
    barangay: '',
    bldgName: '',
    region: '',
    city: '',
    street: '',
    district: '',
    zipCode: '',
    tinNumber: null,
    contactPerson: '',
    contactNumber: ''
  })
  const [stage, setStage] = React.useState({
    stageName: cnts.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL,
    dateSubmitted: new Date().toISOString()
  })

  React.useEffect(() => {
    if (id) {
      fetchAndMapping(id)
    }

    // if (!isEmpty(specialOther)) {
    //   setSpecialOthersFrom({...specialOther})
    // }
    fetchRegion(setRegions)
  }, [])

  const fetchAndMapping = async id => {
    setIsLoading(true)
    try {
      const data = await FetchData(id)
      console.log('fetch data rock sa mismong sos form', data)
      setIsSubmitted(true)
      setSpecialOthersFrom({...data})
      setIsLoading(false)
    } catch (error) {
      setIsLoading(false)
      console.log('errors occured', error)
    }
  }

  const updateStage = (val) => {
    const stgObj = { ...stage }
    console.log('updatd stage vali', val)
    stgObj['stageName'] = val
    setStage(stgObj)
  }

  const validate = async () => {
    const result = schema.validate(specialOthersForm, { abortEarly: false })
    if (!result.error) {
      setOpenModal(true)
    }
    let errors = {}
    if (result.error && result.error.details.length > 0) {
      for (const i of result.error.details) {
        errors[i.path[0]] = i.message
      }
    }
    setErrors(errors)
  }

  const save = async () => {
    console.log('save is called', cnts.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL)
    try {
      const { data: res } = await OtherServices.createSpecialOthers({ ...specialOthersForm, ...affiliationType, ...stage })
      // updateStage(cnts.PARTNER.STAGE.FOR_AFFILIATION_APPROVAL)
      dispatch(setOtherServiceAffil(specialOthersForm))
      setIsSubmitted(true)
      setOpenModal(false)
      console.log('res', res)
    } catch (error) {
      console.log('error occurred', error)
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault()
    validate()
  }

  const handleChange = ({ currentTarget: input }) => {
    const form = { ...specialOthersForm }
    const newErrors = { ...errors }
    delete newErrors[input.name]
    setErrors(newErrors)
    form[input.name] = input.value
    setSpecialOthersFrom(form)
  }

  const handleTinNumChange = (value, name) => {
    const form = { ...specialOthersForm }
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    form[name] = value
    setSpecialOthersFrom(form)
  }

  const handleDatePicker = (date) => {
    const form = { ...specialOthersForm }
    form['dateReceived'] = new Date(date).toISOString()
    setSpecialOthersFrom(form)
  }

  const handleDropdown = ({ target: select }) => {
    const form = { ...specialOthersForm }
    form[select.name] = select.value
    setSpecialOthersFrom(form)
    handleAddressChaining(select.name, select.value, form, setSpecialOthersFrom, setBarangays, setCities, setProvinces, setIsFetching, isFetching)

  }


  if (isLoading && id) return <ProgressComponent />
  return (
    <React.Fragment>
      <form onSubmit={handleSubmit}>
        <div className={classes.root}>
          <div className={classes.firstPartInputFields}>
            <div>
              <label>Date Received</label>
              <DatePicker error={errors['dateReceived']} disabled={isSubmitted} name='dateReceived' onChange={handleDatePicker} />
            </div>
            <div>
              <label>Name of Services *</label>
              <TextField disabled={isSubmitted} error={errors['name']} value={specialOthersForm['name']} name='name' onChange={handleChange} />
            </div>
          </div>

          <div className={classes.SecondPartInputFields}>
            <div>
              <div>
                <label>
                  Address <br />
                Unit No./Floor/Bldg.No. *
                </label>
                <TextField disabled={isSubmitted} error={errors['unitFlrBldg']} value={specialOthersForm['unitFlrBldg']} name='unitFlrBldg' onChange={handleChange} />
              </div>
              <div>
                <label>Subdivision *</label>
                <TextField disabled={isSubmitted} error={errors['subdivision']} value={specialOthersForm['subdivision']} name='subdivision' onChange={handleChange} />
              </div>
              <div>
                <label>Barangay *</label>
                <AddressDrowpdown
                  error={errors['barangay']}
                  disableComponents={barangays.length <= 0 || isSubmitted || isFetching['barangays']}
                  items={barangays}
                  value={isFetching['barangay'] ? 'Loading...' : specialOthersForm['barangay']}
                  name='barangay'
                  onChange={handleDropdown}
                />

              </div>
            </div>
            <div>
              <div>
                <label>
                  <div /> <br /> Building Name *
                </label>
                <TextField disabled={isSubmitted} error={errors['bldgName']} value={specialOthersForm['bldgName']} name='bldgName' onChange={handleChange} />
              </div>
              <div>
                <label>Region *</label>
                <AddressDrowpdown error={errors['region']} items={regions} disabled={isSubmitted} value={specialOthersForm['region']} name='region' onChange={handleDropdown} />
              </div>
              <div>
                <label>City/Municipality *</label>
                <AddressDrowpdown
                  error={errors['city']}
                  items={cities}
                  disableComponents={cities.length <= 0 || isSubmitted || isFetching['city']}
                  value={isFetching['city'] ? 'Loading...' : specialOthersForm['city']}
                  name='city'
                  onChange={handleDropdown}
                />
              </div>
            </div>
            <div>
              <div>
                <label>
                  <div /> <br /> Street Name *
                </label>
                <TextField disabled={isSubmitted} value={specialOthersForm['street']} error={errors['street']} name='street' onChange={handleChange} />
              </div>
              <div>
                <label>District / Province *</label>
                <AddressDrowpdown error={errors['district']} items={provinces} disableComponents={provinces.length <= 0 || isSubmitted || isFetching['province']} value={isFetching['province'] ? 'Loading...' : specialOthersForm['district']} name='district' onChange={handleDropdown} />
              </div>
              <div>
                <label>Zip Code *</label>
                <TextField disabled={isSubmitted} value={specialOthersForm['zipCode']} error={errors['zipCode']} name='zipCode' onChange={handleChange} inputProps={{ maxLength: 4 }}/>
              </div>
            </div>
          </div>

          <div className={classes.thirdPartInputFields}>
            <div>
              <label>TIN Number *</label>
              <CustomTextFieldNumberFormat
                disabled={isSubmitted}
                error={errors['tinNumber']}
                defaultValue={specialOthersForm['tinNumber']}
                name='tinNumber'
                onChange={({ target: { value, name } }) => handleTinNumChange(value, name)}
                type={'dashAndNum'}
                placeholder='Ex.000-000-000-000'
                id='tinNumber'
                format='###-###-###-###'
              />
            </div>
            <div>
              <label>Contact Person *</label>
              <TextField disabled={isSubmitted} error={errors['contactPerson']} value={specialOthersForm['contactPerson']} name='contactPerson' onChange={handleChange} />
            </div>
            <div>
              <label>Contact Number *</label>
              <TextField disabled={isSubmitted} error={errors['contactNumber']} value={specialOthersForm['contactNumber']} name='contactNumber' onChange={handleChange} />
            </div>
          </div>
        </div>
        <div>
          <Dialog
            id='id_submit_modal'
            open={openModal}
            onClose={handleSubmitClose}
            aria-labelledby='alert-dialog-title'
            aria-describedby='alert-dialog-description'
          >
            <DialogContent>
              <DialogContentText id='alert-dialog-submit-description'>
                <Box display='flex' flexDirection='column' justifyContent='center' alignItems='center'>
                  <h3 style={{ textAlign: 'center' }}>
                    Are you sure you want to proceed with the submission of Special / Other Services Affiliation form for
                    approval?
                  </h3>
                  <div className={classes.componentFooter}>
                    <Button
                      style={{ backgroundColor: '#fd5451', color: 'white', marginRight: '10px' }}
                      onClick={handleSubmitClose}
                    >
                      Cancel
                    </Button>
                    <Button
                      style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '10px' }}
                      onClick={save}
                    >
                      Submit
                    </Button>
                  </div>
                </Box>
              </DialogContentText>
            </DialogContent>
          </Dialog>
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div id='yesCancel' style={{ marginLeft: '3.5em' }}>
              <YesOrNoDialogBox PrevPage={PrevPage} />
            </div>
            <div />
            <div>
              <div>
                <Button variant='contained' type={'submit'} disabled={isSubmitted} style={{backgroundColor: isSubmitted ? '#dddfe3' : ''}} className={classes.SubmitButton}>
                  SUBMIT
                </Button>
              </div>
            </div>
          </span>
        </div>
      </form>
    </React.Fragment>
  )
}

export default SOSForm
