import React, { useState, useContext, useEffect } from 'react'

// libraries
import moment from 'moment'

// shared Components
import TwoTextArea from '../../../../../Components/TwoTextArea/TwoTextArea'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import AffiliationTableDisplay from '../../../Components/affilationTableDisplay.js/AffiliationTableDisplay'
import { dateFormat } from 'Pages/Protected/Partner/Components/Utils/utils'

// material ui
import TextField from '../../../../../../../../SharedComponents/TextField/CustomTextField'
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import Paper from '@material-ui/core/Paper'

// style scss
import classes from './AffiliationForm.module.scss'
import MedicalAffiliationOIContext from '../../context/MedicalClinicOIContext'
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
import { formatPhilHealthNumber } from '../../../../../Components/Utils/utils'

const affiliationTableHeader = [
  { label: 'Full Name', name: 'cName' },
  { label: 'Designation', name: 'cDesignation' },
  { label: 'Department', name: 'cDepartment' },
  { label: 'Email Address', name: 'cEmail' },
  { label: 'Contact Number', name: 'cNumber' }
]

const existingAffiliationHmoHeader = [
  { label: 'Existing Affiliated HMO/S', name: 'affiliateGroup' }
]

function AffiliationForm () {
  const [formData, setFormData] = useState({})
  const [radioButtonsValue, setRadioButtonsValue] = useState({})
  const [listOfContacts, setContacts] = useState([])
  const [listOfAffiliatedGroup, setAffiliatedGroup] = useState([])
  const { paramId } = useContext(MedicalAffiliationOIContext)
  useEffect(() => {
    LoadingService.loading(true)
    fetchAffiliationData(paramId)
  }, [])

  const fetchAffiliationData = (paramId) => {
    try {
      setTimeout(async () => {
        const obj = {}
        const { data } = await AffiliationApproverServices.getFacilityPartnerById(paramId)
        let { affiliation_infos: affiliationInfos, contacts, treasury, classification } = data['data']
        setContacts(contacts)
        setAffiliatedGroup(affiliationInfos)
        obj.processType = data['data'].process_type
        obj.tinStatus = treasury['tin_status']
        obj.classification = classification
        setRadioButtonsValue(obj)
        setFormData(data['data'])
        LoadingService.loading(false)
      }, 1000)
    } catch (error) {
      LoadingService.loading(false)
    }
  }
  return (
    <React.Fragment>
      <div className={classes.root}>
        <div className={classes.textContainer}>
          <div>FACILITY INFORMATION</div>
          <div>General Information</div>
        </div>
        <div className={classes.firstPartInputFields}>
          <div id='name'>
            <label id='medicalClinicLabel'>Medical Clinic Name</label>
            <TextField value={formData['name']} readonly />
          </div>
          <div id='firstLayerFields' className={classes.firstLayerFields}>
            <div id='email'>
              <label id='emailLabel'>Email Address</label>
              <TextField value={formData['email']} />
            </div>
            <div id='yearsMonths'>
              <label>Years Operation</label>
              <div id='twoInputFields'>
                <div id='years'>
                  <CustomTextFieldWithIcons value={formData['operation'] && formData['operation'].years} styles={classes.customTag} tags={'years'} />
                </div>
                <div id='months'>
                  <CustomTextFieldWithIcons value={formData['operation'] && formData['operation'].months} styles={classes.customTag} tags={'months'} />
                </div>
              </div>
            </div>
          </div>
          <div id='secondLayerFields' className={classes.secondLayerFields}>
            <div id='dohNumber'>
              <label>DOH License Number</label>
              <TextField value={formData['license'] && formData['license']['doh']['number']} />
            </div>
            <div id='dohLicenseType' className={classes.dohLicenseType}>
              <label>DOH License Type</label>
              <TextField value={formData['license'] && formData['license']['doh']['license_type']} />
            </div>
            <div id='dohLicenseExp'>
              <label>DOH License Expiry Date</label>
              <div id='dohDatePicker'>
                <TextField value={formData['license'] && dateFormat(formData['license']['doh']['license_exp'])} />
              </div>
            </div>
          </div>
          <div id='thirdLayerFields' className={classes.thirdLayerFields}>
            <div id='philhealthNumber'>
              <label>PhilHealth License Number</label>
              <TextField value={formData['philhealth'] && formatPhilHealthNumber(formData['philhealth']['accreditation_number'])} />
            </div>
            <div id='philhealthExpiration'>
              <label>PhilHealth Expiry Date</label>
              <div id='philHealthdiv'>
                <TextField value={formData['philhealth'] && dateFormat(formData['philhealth']['expiration_date'])} />
              </div>
            </div>
            <div id='checkPayee'>
              <label>Check Payee</label>
              <TextField value={formData['treasury'] && formData['treasury']['payee']} />
            </div>
          </div>
        </div>
        <div className={classes.radioButtonParts}>
          <div id='medClinicRadioButtons'>
            <FormControl component='fieldset'>
              <FormLabel className={classes.label} component='legend'>
                Medical Clinic Classification
              </FormLabel>
              <RadioGroup aria-label='gender' name='classification'>
                <FormControlLabel value='Full Service' checked={radioButtonsValue['classification'] === 'Full Service'} disabled={radioButtonsValue['classification'] !== 'Full Service'} control={<Radio color='primary' />} label='Full Service' />
                <FormControlLabel value='Diagnostics' checked={radioButtonsValue['classification'] === 'Diagnostics'} disabled={radioButtonsValue['classification'] !== 'Diagnostics'} control={<Radio color='primary' />} label='Diagnostics' />
                <FormControlLabel value='Specialty' checked={radioButtonsValue['classification'] === 'Specialty'} disabled={radioButtonsValue['classification'] !== 'Specialty'} control={<Radio color='primary' />} label='Specialty' />
              </RadioGroup>
            </FormControl>
          </div>
          <div id='tinStatusRadioButtons'>
            <FormControl component='fieldset'>
              <FormLabel className={classes.label} component='legend'>
                TIN Status
              </FormLabel>
              <RadioGroup aria-label='gender' value={radioButtonsValue['tinStatus']} >
                <FormControlLabel value='12%' checked={radioButtonsValue['tinStatus'] === '12%'} disabled={radioButtonsValue['tinStatus'] !== '12%'} control={<Radio color='primary' />} label='VATable' />
                <FormControlLabel value='Non-VaTable' checked={radioButtonsValue['tinStatus'] === 'Non-VaTable'} disabled={radioButtonsValue['tinStatus'] !== 'Non-VaTable'} control={<Radio color='primary' />} label='Non-VATable' />
              </RadioGroup>
            </FormControl>
            <div id='tinStatusDropDown' className={classes.dropDownTinStatus}>
              <TextField value={formData['treasury'] && formData['treasury']['non_vatable']} />
            </div>
          </div>
          <div id='processTypeRadioButtons' className={classes.processTypeRadioButtons}>
            <div id='vatTaxRate' className={classes.TaxRate}>
              <label>TAX Rate</label>
              <div id='vatTaxRate'>
                <CustomTextFieldWithIcons value={formData['treasury'] && formData['treasury']['vat']['tax_rate']} styles={classes.customTag} tags={'%'} />
              </div>
            </div>
            <div id='radioFields' className={classes.processType}>
              <FormControl component='fieldset'>
                <FormLabel className={classes.label} component='legend'>
                  Process Type
                </FormLabel>
                <RadioGroup aria-label='gender' name='gender1'>
                  <FormControlLabel value='Umbrella' checked={radioButtonsValue['processType'] === 'Umbrella'} disabled={radioButtonsValue['processType'] !== 'Umbrella'} control={<Radio color='primary' />} label='Umbrella' />
                  <FormControlLabel value='Regular' checked={radioButtonsValue['processType'] === 'Regular'} disabled={radioButtonsValue['processType'] !== 'Regular'} control={<Radio color='primary' />} label='Regular' />
                </RadioGroup>
              </FormControl>
            </div>
          </div>
        </div>
        <div className={classes.secondPartInputFields}>
          <div id='firstInputLayerFields' className={classes.firstInputLayerFields}>
            <div id='unitFlrBldg'>
              <label>
                Medical Clinic Address <br />
                  Unit No./Floor/Bldg.No.
              </label>
              <TextField value={formData['address'] && formData['address']['unit_flr_bldg']} />
            </div>
            <div>
              <label>
                <div /> <br /> Building Name
              </label>
              <TextField value={formData['address'] && formData['address']['building_name']} />
            </div>
            <div>
              <label>
                <div /> <br /> Street Name
              </label>
              <TextField value={formData['address'] && formData['address']['street']} />
            </div>
          </div>
          <div id='secondInputLayerFields' className={classes.secondInputLayerFields}>
            <div>
              <label>Subdivision</label>
              <TextField value={formData['address'] && formData['address']['subdivision']} />
            </div>
            <div>
              <label>Region</label>
              <TextField value={formData['address'] && formData['address']['region']} />
            </div>
            <div>
              <label>District/Province</label>
              <TextField value={formData['address'] && formData['address']['district']} />
            </div>
          </div>
          <div id='thirdInputLayerFields' className={classes.thirdInputLayerFields}>
            <div>
              <label>Barangay</label>
              <TextField value={formData['address'] && formData['address']['barangay']} />
            </div>
            <div>
              <label>City/Municipality</label>
              <TextField value={formData['address'] && formData['address']['city']} />
            </div>
            <div>
              <label>Zip Code</label>
              <TextField value={formData['address'] && formData['address']['zip_code']} />
            </div>
          </div>
          <div id='clinicScheduleAndRoomNumber' className={classes.clinicScheduleAndRoomNumber}>
            <div id='clinicSchedule'>
              <label id='clinicScheduleLabel'>Clinic Schedule</label>
              <TextField value={formData['clinic_schedule']} />
            </div>
            <div id='roomNumber'>
              <label id='roomNumberlabel'>Room Number</label>
              <TextField value={formData['room_number']} />
            </div>
          </div>
        </div>
        <div className={classes.contactInformationBoard}>
          <div>
            <Paper className={classes.contactInformationTable} elevation={3}>
              <AffiliationTableDisplay header={affiliationTableHeader} data={listOfContacts} />
            </Paper>
          </div>
        </div>
        <div className={classes.affiliateForm}>
          <div>Affiliation Information</div>
          <div>
            <Paper className={classes.affiliationInformationTable} elevation={3}>
              <AffiliationTableDisplay header={existingAffiliationHmoHeader} data={listOfAffiliatedGroup} />
            </Paper>
          </div>
        </div>
        <div className={classes.RemarksAndFooter}>
          <TwoTextArea val1={formData['remarks'] && formData['remarks']['external_remarks']} val2={formData['remarks'] && formData['remarks']['internal_remarks']} label1={'External Remarks'} label2={'Internal Remarks'} />
        </div>
      </div>
    </React.Fragment>
  )
}

export default AffiliationForm
