import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'
import SpecialOtherServices from './Pages/SpecialOtherServices/SpecialOtherServices'

import Store from '../../../Store'

const SpecialOtherServicesProfileRoutes = () => {
  const [isOpenSpecialOtherServicesListModal, setIsOpenSpecialOtherServicesListModal] = React.useState(false)
  console.log('RoutesSpecial')
  React.useEffect(() => {
    setIsOpenSpecialOtherServicesListModal(true)
    return () => {
      setIsOpenSpecialOtherServicesListModal(false)
    }
  }, [])

  return (
    <Provider store={Store}>
      <Switch>
        <Route path='/SpecialOtherServicesList' exact component={SpecialOtherServices} />
        {isOpenSpecialOtherServicesListModal && (
          <SpecialOtherServices
            open={isOpenSpecialOtherServicesListModal}
            setOpen={setIsOpenSpecialOtherServicesListModal}
            endPoint={'/SpecialOrOtherServiceList'}
          />
        )}
      </Switch>
    </Provider>
  )
}

export default SpecialOtherServicesProfileRoutes
