import React from 'react'

import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import classes from './FacilityDiscount.module.scss'
import ReactTable from './components/CustomTable'
import { facilityDiscountPage } from '../../../../../Constant/tableColumnData'
import Button from '@material-ui/core/Button'
import DialogActions from '@material-ui/core/DialogActions'
import CustomAlerSnackBar from '../../Components/CustomSnackBar/CustomAlertSnackbar'
import CustomDialogSave from '../../TableMaintainer/FacilityGroupingMaintenance/components/CustomDialogSave/CustomDialogSave'
import CustomDialog from '../../Components/CustomDialog/CustomDialog'
import { useSelector } from 'react-redux'
import ProfileService from '../../../../../Services/Api/Partner/ProfileService'
// import axios from 'axios'
import Modal from '@material-ui/core/Modal'

// const URL = `${process.env.REACT_APP_SERVER_API}`

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL COMPLIANCE ASSISTANT MANAGER',
    to: '/'
  },
  Maintenance: {
    text: 'MAINTENANCE TABLE',
    to: '/'
  },
  Table: {
    text: 'Facility Discount',
    to: '/FacilityDiscountRoutes'
  }
}

const FacilityDiscountPage = ({ open, setOpen, paramId }) => {
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'Maintenance', 'Table'])
  const [addRow, setAddRow] = React.useState(false)
  const [tableData, setTableData] = React.useState([])
  const [ deletedSucessfully, setDeletedSuccesfully ] = React.useState(false)
  const [rowDeleted, setRowDeleted] = React.useState(false)

  // React.useEffect(() => {
  // //  if (open) {
  //   ProfileService.getFacilityDiscount(paramId).then(res => setTableData(res.data.data))
  // //  }
  // }, [open])

  // React.useEffect(() => {
  //   setTableData(tableData)
  // }, [tableData])

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
    }

    return () => { mounted = false }
  }, [rowDeleted])

  const fetchData = async () => {
    try {
      const { data } = await ProfileService.getFacilityDiscount(paramId)
      setTableData(data.data)
      setRowDeleted(false)
      setOpen(false)
      setOpen(true)
    } catch (e) {
      console.log('error', e)
    }
  }

  const [isSave, setIsSave] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isDisabled, setIsDisabled] = React.useState(false)
  const [showSave, setShowSave] = React.useState(false)
  const { hospitalFacilityDiscount } = useSelector(state => state.hospitalFacilityDiscount)
  const result = useSelector(state => state.facilityDiscountToDelete.facilityDiscountToDelete)

  const confirmUpdate = async () => {
    try {
      await ProfileService.updateFacilityDiscount(paramId, hospitalFacilityDiscount) //  axios.put(`${URL}/p/f/partners/facility_discount/${paramId}`, hospitalFacilityDiscount)

      if (result.length > 0) {
        result.map(async items => {
          if (items._id) {
            await ProfileService.deleteOneFacilityDiscount(items._id)
            setDeletedSuccesfully(true)
          }
        }
        )
      }
    } catch (e) {
      console.log('error', e)
    }

    setSuccess(true)
    setIsSave(false)
    setTimeout(async () => {
      setOpen(false)
      fetchData()
    }, 1500)
    setShowSave(false)
  }

  return (
    <React.Fragment>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false)
        }}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div id='Update-button' />
            <ReactTable columns={facilityDiscountPage} showSave={showSave} isCancel={isCancel} setRowDeleted={setRowDeleted} deletedSucessfully={deletedSucessfully} setIsSave={setIsSave} setIsCancel={setIsCancel} isDisabled={isDisabled} setShowSave={setShowSave} data={tableData} addRow={addRow} setAddRow={setAddRow} fetchData={fetchData} setIsDisabled={setIsDisabled} setOpen={setOpen} />
          </div>
          <CustomDialogSave
            onClose={() => {
              setOpen(false)
            }}
            id='saveCustomDialog'
            isSave={isSave}
            setIsSave={() => setIsSave(false)}>
            <div id='saveCustomDialogContainer' className={classes.dialogContainer}>
              <h3 id='saveCustomDialogMessage'>Are you sure you want to update?</h3>
              <div id='saveCustomDialogBtnGroup' className={classes.btnGroup}>
                <Button
                  onClick={() => setIsSave(false)}
                  id='saveCustomDialogBtnNo'
                  variant='contained'
                  style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '55px', marginBottom: '10px' }}
                >
                  No
                </Button>
                <Button
                  id='saveCustomDialogBtnYes'
                  className={classes.btnCancel}
                  variant='contained'
                  style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
                  onClick={confirmUpdate}
                >
                  Yes
                </Button>
              </div>
            </div>
          </CustomDialogSave>
          <div>
            <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
          </div>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default FacilityDiscountPage
