import AffiliationApproverServices from '../../../../../../../../Services/Api/Partner/AffiliationApproverServices'
// import moment from 'moment'
import AddressChaining from '../../../../../../../../Constant/chaining-address/index'

const ReEncoding = async (id, setAddToGroups, setVATable, NonVatableData, setIsLoading) => {
  if (id) {
    try {
      const newForms = {}
      const {
        data: { data: fetchAffil }
      } = await AffiliationApproverServices.getFacilityPartnerById(id)
      let affiliationType = {}
      affiliationType.id = id
      affiliationType.dateRecived = fetchAffil['stage']['date_submitted']
      affiliationType.type = fetchAffil['type']
      affiliationType.mno = fetchAffil['mno']
      affiliationType.stageName = fetchAffil['stage'] ? fetchAffil['stage']['stage_name'] : ''

      const address = fetchAffil.address
      const operation = fetchAffil.operation
      const treasury = fetchAffil.treasury
      const doh = fetchAffil.license.doh
      const philHealth = fetchAffil.philhealth
      const remarks = fetchAffil.remarks
      AddressChaining.getProvince(address['region'])
      AddressChaining.getCityMunicipality(address['district'])
      AddressChaining.getBarangays(address['city'])
      newForms['name'] = fetchAffil['name']
      newForms['bedCapacity'] = fetchAffil['bed_capacity']
      newForms['years'] = operation['years']
      newForms['months'] = operation['months']
      newForms['email'] = fetchAffil['email']
      newForms['dohLicenseExp'] = doh['license_exp']
      newForms['philhealthExpiration'] = philHealth['expiration_date']
      newForms['philhealthNumber'] = philHealth['accreditation_number']
      newForms['unitFlrBldg'] = address['unit_flr_bldg']
      newForms['payee'] = treasury['payee']
      newForms['bldgName'] = address['building_name']
      newForms['street'] = address['street']
      newForms['zipCode'] = address['zip_code']
      newForms['dohNumber'] = doh['number']
      newForms['dohLicenseType'] = doh['license_type']
      newForms['subdivision'] = address['subdivision']
      newForms['barangay'] = address['barangay']
      newForms['city'] = address['city']
      newForms['district'] = address['district']
      newForms['region'] = address['region']
      newForms['contacts'] = fetchAffil['contacts']
      newForms['classification'] = fetchAffil['classification']
      newForms['processType'] = fetchAffil['process_type']
      newForms['tinStatus'] = treasury['tin_status']
      newForms['nonVatable'] = treasury['non_vatable']
      newForms['vatTaxRate'] = treasury['vat']['tax_rate']
      newForms['internalRemarks'] = remarks['internal_remarks']
      newForms['externalRemarks'] = remarks['external_remarks']
      newForms['affiliationInfos'] = fetchAffil['affiliation_infos']
      setAddToGroups(fetchAffil['affiliation_infos'])
      // setIsLoading(false)
      // if (treasury['nonVatable'] !== undefined) {
      //   treasury['nonVatable'].length > 0 ? setVATable(NonVatableData) : setVATable([])
      // }
      return {newForms, affiliationType, philhealth: philHealth['accreditation_number']}
    } catch (e) {
      setIsLoading(false)
      console.log('error in hospital re encoding', e)
    }
  }
}

export default ReEncoding
