import React from 'react'
import classes from './ContractDetailsViewer.module.scss'
import Contract from '../../../Components/ContractViewer/Contract'

const ContractDetailsViewer = props => {
  const { paramId, facilityInfos } = props
  const [selectedFacilityId, setSelectedFacilityId] = React.useState()
  const pdfView = id => {
    if (id) {
      setSelectedFacilityId(id)
    }
  }
  const facilities = () => {
    if (facilityInfos) {
      return facilityInfos.map(facility => {
        return (
          <li>
            <a
              href={'javascript:void(0)'}
              onClick={() => pdfView(facility.facility_id)}
            >{`${facility.name} (${facility.facility_id})`}</a>
          </li>
        )
      })
    }
  }
  return (
    <div id='root' className={classes.root}>
      <ul>{facilities()}</ul>
      {selectedFacilityId && (
        <Contract type={'doctor'} paramId={paramId} fileName={'doctor-contract'} facilityId={selectedFacilityId} />
      )}
    </div>
  )
}

export default ContractDetailsViewer
