import React from 'react'
import classes from './EditInputs.module.scss'
import moment from 'moment'
import {
  RenderInputFields,
  RenderDropDown,
  RenderDropDownWithLabel,
  RenderInputWithlabel,
  RenderInputMask
} from '../../../../../../Components/InputsForProfiles/InputsForProfile'

import FacilityAutoSuggest from '../../../../../Components/FacilityDiscountAutoSuggest'

import { TinStatus, processType } from '../../../../../../Constants/dropDownItems'
import YearsOperationField from '../../../../../Components/YearsOfOperation/YearsOfOperation'

import { getTinStatusValue, checkArray } from '../../../../../../Components/Utils/utils'

import { PropTypes } from 'prop-types'

const FacilityAddress = props => {
  const {
    streetError,
    regionError,
    districtProvError,
    cityMunError,
    barangayError,
    zipError,
    streetRef,
    zipRef,
    regionValue,
    provinceDistrictValue,
    cityValue,
    barangayValue,
    handleChange,
    regionItems,
    provincesItems,
    citiesItems,
    barangayItems,
    isFetching
  } = props

  let regionArr = []
  let provinceArr = []
  let citiesArr = []
  let barangayArr = []

  let bool = true

  checkArray(regionItems).map(items => {
    regionArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(provincesItems).map(items => {
    provinceArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(citiesItems).map(items => {
    citiesArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  checkArray(barangayItems).map(items => {
    barangayArr.push({
      id: items._id,
      text: items.name,
      value: items.name
    })
  })

  return (
    <div className={classes.facilityAddressFields}>
      <div id='streetVillage' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={streetError}
          name={'street'}
          inputRef={streetRef}
          label={'Street Village'}
          onChange={handleChange}
        />
      </div>
      <div id='Region' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={regionError}
          name={'region'}
          items={regionArr}
          value={regionValue}
          label={'Region'}
          onChange={handleChange}
        />
      </div>
      <div id='district/Province' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={districtProvError}
          name={'district'}
          items={provinceArr}
          value={isFetching['district'] ? 'Loading...' : provinceDistrictValue}
          label={'District / Province'}
          onChange={handleChange}
          disabled={provinceArr.length > 0 ? false : bool}
        />
      </div>
      <div id='city/Municipality' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={cityMunError}
          name={'city'}
          items={citiesArr}
          value={isFetching['city'] ? 'Loading...' : cityValue}
          label={'City / Municipality'}
          onChange={handleChange}
          disabled={citiesArr.length > 0 ? false : bool}
        />
      </div>
      <div id='barangay' className={classes.facilityAddressLabels}>
        <RenderDropDownWithLabel
          error={barangayError}
          items={barangayArr}
          name={'barangay'}
          label={'Barangay'}
          value={isFetching['barangay'] ? 'Loading...' : barangayValue}
          onChange={handleChange}
          disabled={barangayArr.length > 0 ? false : bool}
        />
      </div>
      <div id='zipCode' className={classes.facilityAddressLabels}>
        <RenderInputWithlabel
          error={zipError}
          name={'zipCode'}
          inputRef={zipRef}
          label={'Zip Code'}
          type={'noSpecialChar'}
          onChange={handleChange}
        />
      </div>
    </div>
  )
}

const EditInputs = (handleChange, handleDropdown, errors, form, resultRef, setForm, setErrors, discountTypeData, regions, provinces, cities, barangays, isFetching) => {
  const Information = [
    {
      id: 305,
      title: 'Former Name',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          error={errors['formerName']}
          inputRef={resultRef.formerName}
          name={'formerName'}
          onChange={handleChange}
        />
      )
    },
    {
      id: 306,
      title: 'Facility Address',
      value: (
        <FacilityAddress
          streetError={errors['street']}
          regionError={errors['region']}
          districtProvError={errors['district']}
          cityMunError={errors['city']}
          barangayError={errors['barangay']}
          zipError={errors['zipCode']}
          streetRef={resultRef.street}
          zipRef={resultRef.zipCode}
          regionValue={form['region']}
          provinceDistrictValue={form['district']}
          cityValue={form['city']}
          barangayValue={form['barangay']}
          handleChange={handleDropdown}
          regionItems={regions}
          provincesItems={provinces}
          citiesItems={cities}
          barangayItems={barangays}
          isFetching={isFetching}

        />
      )
    },
    {
      id: 3066,
      title: 'Years of Operation',
      value: (
        <YearsOperationField
          errors={errors}
          form={form}
          resultRef={resultRef}
          setForm={setForm}
          setErrors={setErrors}
        />
      )
    },

    {
      id: 308,
      title: 'PhilHealth Accreditation no.',
      value: (
        <RenderInputMask
          name={'accreditationNumber'}
          error={errors['accreditationNumber']}
          type={'dashAndNum'}
          value={form['accreditationNumber']}
          format={'####-#######-#'}
          form={form}
          setForm={setForm}
          setErrors={setErrors}
          errors={errors}
        />
      ),
      title2: 'PHILHEALTH EXP.',
      value2: (
        <RenderInputFields
          type={'date'}
          className={classes.inputField}
          name={'philhealthExp'}
          onChange={handleChange}
          error={errors['philhealthExp']}
          value={moment(form['philhealthExp']).format('YYYY-MM-DD')}
        />
      )
    },
    {
      id: 309,
      title: 'CHECK PAYEE',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          error={errors['payee']}
          inputRef={resultRef.payee}
          name={'payee'}
          onChange={handleChange}
        />
      )
    },
    {
      id: 310,
      title: 'CREDIT LIMIT ',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          error={errors['creditLimit']}
          inputRef={resultRef.creditLimit}
          name={'creditLimit'}
          onChange={handleChange}
        />
      )
    },
    {
      id: 311,
      title: 'PAYMENT TERMS',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          error={errors['paymentTerms']}
          inputRef={resultRef.paymentTerms}
          name='paymentTerms'
          onChange={handleChange}
        />
      )
    },
    {
      id: 312,
      title: 'TIN STATUS',
      value: (
        <RenderDropDown
          items={TinStatus}
          name={'tinStatus'}
          error={errors['tinStatus']}
          value={getTinStatusValue(form['tinStatus'])}
          onChange={handleChange}
        />
      )
    },
    {
      id: 313,
      title: 'TAX RATE',
      value: (
        <RenderInputFields
          type={'numPercent'}
          error={errors['vatTaxRate']}
          value={form['vatTaxRate']}
          name={'vatTaxRate'}
          inputRef={resultRef.vatTaxRate}
          onChange={handleChange}
          tags={'%'}
        />
      )
    },
    {
      id: 314,
      title: 'FACILITY discount',
      value: (
        <FacilityAutoSuggest
          data={discountTypeData}
          error={errors['facilityDiscount']}
          form={form}
          formValue={form['facilityDiscount']}
          setForm={setForm}
          name={'facilityDiscount'}
        />
      )
    },
    {
      id: 315,
      title: 'Date of affiliation',
      value: (
        <RenderInputFields
          type={'date'}
          name={'dateOfAffiliation'}
          error={errors['dateOfAffiliation']}
          onChange={handleChange}
          value={moment(form['dateOfAffiliation']).format('YYYY-MM-DD')}
        />
      )
    },
    {
      id: 316,
      title: 'Date of disaffiliation',
      value: (
        <RenderInputFields
          type={'date'}
          name={'dateOfDisaffiliation'}
          onChange={handleChange}
          error={errors['dateSubmitted']}
          value={moment(form['dateOfDisaffiliation']).format('YYYY-MM-DD')}
        />
      )
    },
    {
      id: 317,
      title: 'Remarks',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          error={errors['specialInstructions']}
          value={form['specialInstructions']}
          inputRef={resultRef.specialInstructions}
          name={'specialInstructions'}
          onChange={handleChange}
        />
      )
    },
    {
      id: 318,
      title: 'PROCESS TYPE',
      value: (
        <RenderDropDown
          items={processType}
          error={errors['processType']}
          value={form['processType']}
          name={'processType'}
          onChange={handleDropdown}
        />
      )
    },
    {
      id: 319,
      title: 'Clinic Schedule',
      value: (
        <RenderInputFields
          type={'noSpecialChar'}
          error={errors['clinicSchedule']}
          value={form['clinicSchedule']}
          name={'clinicSchedule'}
          onChange={handleChange}
          inputRef={resultRef.clinicSchedule}
        />
      )
    }
  ]

  return Information
}

export default EditInputs

FacilityAddress.propTypes = {
  streetError: PropTypes.bool,
  regionError: PropTypes.bool,
  districtProvError: PropTypes.bool,
  cityMunError: PropTypes.bool,
  barangayError: PropTypes.bool,
  zipError: PropTypes.any,
  regionValue: PropTypes.any,
  districtValue: PropTypes.any,
  cityValue: PropTypes.any,
  barangayValue: PropTypes.any,
  handleChange: PropTypes.func.isRequired
}
