import React from 'react'

import classes from './SubmitDialog.module.scss'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'
import Button from '@material-ui/core/Button'

const SubmitDialog = props => {
  const { isSubmit, setIsSubmit, handleOnSubmit, message } = props

  return (
    <CustomDialog id='ga_custom_dialog' isCancel={isSubmit} setIsCancel={() => setIsSubmit(false)}>
      <div id='ga_custom_dialog_container' className={classes.dialogContainer}>
        <h3 id='ga_custom_dialog_message' style={{ textAlign: 'center' }}>
          {message}
        </h3>
        <div id='ga_custom_dialog_btn_group' className={classes.btnGroup}>
          <Button
            style={{ textTransform: 'none' }}
            onClick={() => setIsSubmit(false)}
            id='ga_custom_dialog_btn_cancel'
            className={classes.btnRed}
            variant='contained'
          >
            Cancel
          </Button>
          <Button
            style={{ textTransform: 'none' }}
            id='ga_custom_dialog_btn_save'
            className={classes.btnGreen}
            variant='contained'
            onClick={handleOnSubmit}
          >
            Proceed
          </Button>
        </div>
      </div>
    </CustomDialog>
  )
}

export default SubmitDialog
