import React from 'react'
import Grid from '@material-ui/core/Grid'
import moment from 'moment'

import PartnerCard from '../../../Components/PartnerCard/PartnerCardV3'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import ProfileService from 'Services/Api/Partner/ProfileService'
import OICProcessorServices from 'Services/Api/Partner/OICProcessorServices'
import ContractManagerViewList from '../../../Components/ContractManager/Dashboard/ContractManagerViewList'
import ContractManagerPrintList from '../../../Components/ContractManager/Dashboard/ContractManagerPrintList'

// Import scss module
import classes from './Dashboard.module.scss'

const Dashboard = props => {
  const isComponentAttach = React.useRef(true)
  const currentFilterForPrint = React.useRef('facilities')
  const currentFilterForRenew = React.useRef('facilities')
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [viewList, setViewList] = React.useState({
    isOpen: false,
    stage: ''
  })

  const [forContractPrintLoading, setForContractPrintLoading] = React.useState(true)
  const [forContractRenewLoading, setForContractRenewLoading] = React.useState(true)

  const [forContractPrint, setForContractPrint] = React.useState({
    filter: 'today',
    typeFilter: 'facilities',
    count: 0,
    dataList: [],
    isViewList: false
  })

  const [forRenewContract, setForRenewContract] = React.useState({
    filter: 'today',
    typeFilter: 'facilities',
    count: 0,
    dataList: [],
    isViewList: false,
    isLoading: false
  })

  React.useEffect(() => {
    BreadcrumbsService.setSelectedItemsWithoutHome([])
    setIsLoaded(true)
    return () => {
      isComponentAttach.current = false
    }
  }, [])

  React.useEffect(() => {
    currentFilterForPrint.current = forContractPrint.filter
    const fetchData = async () => {
      setForContractPrintLoading(true)
      const { data } = await ContractManagerServices.getAllPrintContractsType(
        {
          filter: forContractPrint.filter,
          stageName: 'PROFILE_GENERATED'
        },
        forContractPrint.typeFilter
      )
      const dataHandler = data.data
      const fixData = dataHandler.data.map(obj => {
        return {
          id: obj.id,
          type: obj.type,
          ...(obj.facility_id ? { facilityID: obj.facility_id } : {})
        }
      })
      if (isComponentAttach.current && currentFilterForPrint.current === forContractPrint.filter) {
        setForContractPrint({ ...forContractPrint, dataList: fixData, count: fixData.length })
      }
      setForContractPrintLoading(false)
    }
    fetchData()
  }, [forContractPrint.typeFilter, forContractPrint.filter])

  React.useEffect(() => {
    currentFilterForRenew.current = forRenewContract.filter
    const fetchData = async () => {
      setForContractRenewLoading(true)
      const { data } = await ContractManagerServices.getAllRenewContractsType(
        {
          filter: forRenewContract.filter,
          stageName: 'PROFILE_GENERATED'
        },
        forRenewContract.typeFilter
      )
      const dataHandler = data.data
      const fixData = dataHandler.data.map(obj => {
        return {
          id: obj.id,
          type: obj.type,
          ...(obj.facility_id ? { facilityID: obj.facility_id } : {})
        }
      })
      if (isComponentAttach.current && currentFilterForRenew.current === forRenewContract.filter) {
        setForRenewContract({ ...forRenewContract, dataList: fixData, count: fixData.length })
      }
      setForContractRenewLoading(false)
    }
    fetchData()
  }, [forRenewContract.typeFilter, forRenewContract.filter])

  const getSelectedRow = param => {
    let type = param.type.toLowerCase()
    if (type === 'doctor') {
      const fetchDoctor = async () => {
        const data = await ProfileService.getDoctorsById(param.id)
        const facility = data.data.data.facility_info.find(obj => {
          if (moment(obj.date_submitted).format('MM/DD/YYYY') === param.dateSubmitted && obj.name === param.name) {
            return true
          }
          return false
        })
        if (facility) {
          return props.history.push(`/contractmanager/view/${type}/${param.id}/${facility.facility_id}`)
        }
      }
      fetchDoctor()
    } else {
      if (type === 'clinic') {
        type = 'medical'
      } else if (type === 'dental_clinic') {
        type = 'dental'
      }
      return props.history.push(`/contractmanager/view/${type}/${param.id}`)
    }
  }

  const getSelectedRowForEvaluate = async param => {
    let type = ''
    let data = await OICProcessorServices.getFacilityFormInfo(param.id)
    if (data.data.data) {
      type = data.data.data.type.toLowerCase()
    } else {
      data = await ProfileService.getDoctorsById(param.id)
      type = data.data.data.type.toLowerCase()
    }
    if (type === 'doctor') {
      const fetchDoctor = async () => {
        const data = await ProfileService.getDoctorsById(param.id)
        const facility = data.data.data.facility_info.filter(
          obj => moment(obj.expiration_date).format('MM/DD/YYYY') === param.expirationDate
        )
        if (facility) {
          return props.history.push(`/contractmanager/evaluate/${type}/${param.id}/${facility[0].facility_id}`)
        }
      }
      fetchDoctor()
    } else {
      if (type === 'clinic') {
        type = 'medical'
      } else if (type === 'dental_clinic') {
        type = 'dental'
      }
      return props.history.push(`/contractmanager/evaluate/${type}/${param.id}`)
    }
  }

  const getSelectFromViewlist = async param => {
    let type = ''
    let data = await OICProcessorServices.getFacilityFormInfo(param.id)
    if (data.data.data) {
      type = data.data.data.type.toLowerCase()
    } else {
      data = await ProfileService.getDoctorsById(param.id)
      type = data.data.data.type.toLowerCase()
    }
    if (type === 'doctor') {
      const fetchDoctor = async () => {
        const data = await ProfileService.getDoctorsById(param.id)
        const facility = data.data.data.facility_info.filter(
          obj => moment(obj.expiration_date).format('MM/DD/YYYY') === param.expirationDate
        )
        if (facility) {
          return props.history.push(`/contractmanager/evaluate/${type}/${param.id}/${facility[0].facility_id}`)
        }
      }
      fetchDoctor()
    } else {
      if (type === 'clinic') {
        type = 'medical'
      } else if (type === 'dental_clinic') {
        type = 'dental'
      }
      return props.history.push(`/contractmanager/evaluate/${type}/${param.id}`)
    }
  }

  const getSelectFromViewPrint = param => {
    // let type = param.type.toLowerCase()
    // if (type === 'doctor') {
    //   const fetchDoctor = async () => {
    //     const data = await ProfileService.getDoctorsById(param.id)
    //     const facility = data.data.data.facility_info.filter(
    //       obj => moment(obj.date_submitted).format('MM/DD/YYYY') === param.dateSubmitted
    //     )
    //     if (facility) {
    //       return props.history.push(`/contractmanager/view/${type}/${param.id}/${facility[0].facility_id}`)
    //     }
    //   }
    //   fetchDoctor()
    // } else {
    //   if (type === 'clinic') {
    //     type = 'medical'
    //   } else if (type === 'dental_clinic') {
    //     type = 'dental'
    //   }
    //   return props.history.push(`/contractmanager/view/${type}/${param.id}`)
    // }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          {viewList.isOpen && viewList.stage === 'FOR_RENEW' && (
            <ContractManagerViewList
              isOpen={viewList.isOpen}
              handleClose={status => setViewList({ ...viewList, isOpen: status })}
              stage={viewList.stage}
              getSelectedRow={obj => getSelectFromViewlist({ id: obj.id, expirationDate: obj.expirationDate })}
            />
          )}
          {viewList.isOpen && viewList.stage === 'FOR_PRINT' && (
            <ContractManagerPrintList
              isOpen={viewList.isOpen}
              handleClose={status => setViewList({ ...viewList, isOpen: status })}
              stage={viewList.stage}
              getSelectedRow={obj =>
                getSelectFromViewPrint({ id: obj.id, type: obj.type, dateSubmitted: obj.dateSubmitted })
              }
            />
          )}
          <div className={classes.root}>
            <h1 className={classes.pageHeader}>Dashboard</h1>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={8}>
                <PartnerCard
                  isLoading={forContractPrintLoading}
                  headerCount={forContractPrint.count}
                  headerTitle={'Contract for Printing'}
                  cardTitle='Your Task Tray'
                  selectListFilter={[
                    { key: 'today', value: 'Today' },
                    { key: 'weekly', value: 'This Week' },
                    { key: 'monthly', value: 'This Month' }
                  ]}
                  typeListFilter={[
                    { key: 'facilities', value: 'Facilities' },
                    { key: 'doctors', value: 'Doctors' }
                  ]}
                  asyncFunction={ContractManagerServices.getSingleContractData}
                  defaultTypeFilter={forContractPrint.typeFilter}
                  defaultFilter={forContractPrint.filter}
                  getSelectedFilter={filter => setForContractPrint({ ...forContractPrint, filter })}
                  getSelectedTypeFilter={typeFilter => setForContractPrint({ ...forContractPrint, typeFilter })}
                  headerList={['Medical Partner ID', 'Date Submitted', 'Partner Type', 'Partner Name', 'Location']}
                  dataList={forContractPrint.dataList}
                  getSelectedRow={obj =>
                    getSelectedRow({ id: obj.id, type: obj.type, name: obj.name, dateSubmitted: obj.dateSubmitted })
                  }
                  actionText={'Print Contract'}
                  openViewList={() => setViewList({ isOpen: true, stage: 'FOR_PRINT' })}
                />
              </Grid>
              <Grid item xs={4}>
                <PartnerCard
                  headerCount={'0'}
                  headerCountStyle={classes.headerCountStyle}
                  cardTitle='Your Ticket List'
                  cardTitleStyle={classes.cardTitleStyle}
                  headerTitle={'Open Tickets'}
                  openViewList={() => false}
                  selectListFilter={[{ key: 'assignedToAll', value: 'Assigned To All' }]}
                  defaultFilter={'assignedToAll'}
                  getSelectedFilter={() => false}
                />
              </Grid>
            </Grid>
            <Grid container direction='row' alignItems='flex-start' spacing={1}>
              <Grid item xs={6}>
                <PartnerCard
                  isLoading={forContractRenewLoading}
                  headerCount={forRenewContract.count}
                  headerTitle={'Evaluate Contract for Renewal'}
                  typeListFilter={[
                    { key: 'facilities', value: 'Facilities' },
                    { key: 'doctors', value: 'Doctors' }
                  ]}
                  asyncFunction={ContractManagerServices.getSingleRenewContractData}
                  defaultTypeFilter={forRenewContract.typeFilter}
                  getSelectedTypeFilter={typeFilter => setForRenewContract({ ...forRenewContract, typeFilter })}
                  headerList={['Medical Partner ID', 'Date of Expiration', 'Partner Name']}
                  dataList={forRenewContract.dataList}
                  getSelectedRow={obj => getSelectedRowForEvaluate({ id: obj.id, expirationDate: obj.expirationDate })}
                  actionText={'Evaluate'}
                  openViewList={() => setViewList({ isOpen: true, stage: 'FOR_RENEW' })}
                />
              </Grid>
            </Grid>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default Dashboard
