import React from 'react'
import ReactTable from '../../../Helpers/Pagination'
import Button from '@material-ui/core/Button'
import Modal from '@material-ui/core/Modal'
import classes from './DoctorList.module.scss'
import { medicalDoctorTableHead } from '../../../../../../../Constant/tableColumnData.json'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'

const DoctorList = ({ open, setOpen }) => {
  const [tableData, setTableData] = React.useState([])
  const [tableColumn, setTableCol] = React.useState(medicalDoctorTableHead)
  const [loading, setLoading] = React.useState(true)

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
    }

    return () => (mounted = false)
  }, [])

  const fetchData = async () => {
    setLoading(true)
    try {
      // let listOfAffiliatedDoctors = []
      const tableValue = []
      const { data: innerData } = await ProfileService.getDoctorByType('DOCTOR')
      const mainData = innerData.data
      setLoading(false)
      Object.keys(mainData).forEach(items => {
        let obj = {}
        obj.Status = mainData[items] ? (mainData[items].status ? mainData[items].status : '') : ''
        obj.medicalPartnerId = mainData[items] ? (mainData[items].id ? mainData[items].id : '') : ''
        obj.doctorsName = mainData[items] ? (mainData[items].full_name ? mainData[items].full_name : '') : ''
        obj.facilityName =
          mainData[items].facility_info.length > 0
            ? mainData[items].facility_info[0].name
              ? mainData[items].facility_info[0].name
              : ''
            : ''
        obj.clinicSchedule =
          mainData[items].facility_info.length > 0
            ? mainData[items].facility_info[0]
              ? mainData[items].facility_info[0].clinic_schedule
                ? mainData[items].facility_info[0].clinic_schedule
                : ''
              : ''
            : ''
        obj.specialization = mainData[items]
          ? mainData[items].specialization.main
            ? mainData[items].specialization.main
            : ''
          : ''
        tableValue.push(obj)
      })

      setTableData(tableValue)
      setTableCol(medicalDoctorTableHead || [])
    } catch (error) {
      console.log('error', error)
    }
  }

  const memoizedData = React.useMemo(() => tableData, [tableData])
  const memoizedColumn = React.useMemo(() => tableColumn, [tableColumn])

  const HandleCloseModal = data => {
    setOpen(false)
  }

  return (
    <React.Fragment>
      <Modal
        open={open}
        onClose={() => {
          setOpen(false)
        }}
        aria-labelledby='simple-modal-title'
        aria-describedby='simple-modal-description'
      >
        <div id='root' className={classes.root}>
          <div id='container' className={classes.container}>
            <div>
              <ReactTable
                columns={memoizedColumn}
                data={memoizedData}
                countName={'Doctor list'}
                CloseModal={HandleCloseModal}
                link={'/DoctorProfile/PartnerInformation'}
                type={'Doctor'}
                loading={loading}
              />
            </div>
            <div id='Close-button'>
              <Button
                className={classes.closeButton}
                variant='contained'
                onClick={() => {
                  setOpen(false)
                }}
              >
                Close
              </Button>
            </div>
          </div>
        </div>
      </Modal>
    </React.Fragment>
  )
}

export default DoctorList
