import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

// const notRequiredFields = ['cDepartment']

const schema = Joi.object({
  cDesignation: Joi.string()
    .label('Designation')
    .required(),
  cName: Joi.string()
    .label('Full Name')
    .required(),
  cNumber: Joi.number()
    .label('Contact Number')
    .min(11)
    .required(),
  cEmail: Joi.string()
    .email({ tlds: ['com', 'net', 'ph'] })
    .label('Email')
    .required(),
  cDepartment: Joi.string()
    .label('Department')
    .required()
})

export default function ContactDetailsValidation (form, type) {
  let errors = {}

  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (type) {
        console.log('type', type)
        delete errors['cDepartment']
      }
    }
  }
  delete errors['index']

  console.log('errors', errors)
  return {
    errors,
    isValid: isEmpty(errors)
  }
}
