import React from 'react'

// Components
import history from '../../../../Components/History/History'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import AffiliationForm from '../Components/AffiliationForm/AffiliationForm'
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
// Services
import SendData from '../../../Components/Api/sendData'
// Utilities
// import { CurrencyFormatter } from 'Utilities/TextFormatter'

// Styles
import classes from './MedicalInformation.module.scss'
// Validator
import ErrorMsg from '../Components/Utilities/ErrorMsg'
import Validate from '../Components/Utilities/validateForms'
import initialState from '../Components/Utilities/InitalState'

import { useSelector } from 'react-redux'

// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  MedicalClinic: {
    text: 'MEDICAL CLINIC AFFILIATION',
    to: '/medical-clinic-affiliation-form'
  }
}

let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']

let headerTitle = 'Medical Clinic Affiliation Form'

const MedicalClinicAffiliation = ({history}) => {
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [states, setStates] = React.useState(initialState)
  const [form, setForm] = React.useState(false)
  const [contacts, setAddToContacts] = React.useState([])
  const [affiliationInfos, setAffiliationInfos] = React.useState([])
  const medClinicFormData = useSelector(state => state.medClinicAffiliationForm)

  let stage = {
    stageName: cnts.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC,
    dateSubmitted: new Date().toISOString()
  }

  const handleSubmit = async () => {
    let data = await medClinicFormData.medClinicForms
    try {
      setStates({ ...data, ...affiliationType, ...stage, contacts, affiliationInfos })
      setForm(true)
    } catch (e) {
      setForm(false)
    }
  }

  const handleAddTolistValue = data => {
    setAddToContacts([...contacts, data])
  }

  const handleAddToGroupsValue = data => {
    setAffiliationInfos([...affiliationInfos, data])
  }

  if (form) {
    try {
      if (SendData('/facilities', states)) {
      }
      try {
        history.push('/medical-clinic-affiliation-requirements')
      } catch (e) {
        console.log('errors')
      }
    } catch (e) {
      throw e
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'MedicalClinic'])
  }, [])

  return (
    <div id='root' className={classes.root}>
      <HeaderWithBreadCrumbs
        id='HeaderWithBreadCrumbs'
        HeaderTitle={headerTitle}
        stepDetails={stepDetails}
        currentStep={1}
      />
      <div id='AffiliationForm-div'>
        <AffiliationForm
          states={states}
          id='AffiliationForm'
          SubmitForms={handleSubmit}
          AddTolistContacts={handleAddTolistValue}
          AddToGroups={handleAddToGroupsValue}
          contacts={contacts}
          affiliationInfos={affiliationInfos}
          history={history}
        />
      </div>
    </div>
  )
}

export default MedicalClinicAffiliation
