import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'
const schema = Joi.object({
  fullName: Joi.string()
    .label('Name')
    .required(), // required
  contactNo: Joi.string()
    .label('Contact Number')
    .required(), // required
  mainSpecification: Joi.string()
    .label('Main Specification')
    .required(), // required
  prcLinceseNumber: Joi.string()
    .label('PRC License Number')
    .required() // required
})

export default function DentistAssociatesValidationForm (form) {
  let newErrors = {}
  console.log('form dental clinic form', form)
  const result = schema.validate(form, { abortEarly: false })
  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      newErrors[i.path[0]] = i.message
      if (i.path[0] === 'prcExpiryDate') {
        console.log('ist this pric', i.path[0])
        delete newErrors[i.path[0]]
      }

      if (typeof form['prcExpiryDate'] === 'string' && form['prcExpiryDate'].length <= 0) {
        console.log('is this called sa pric rock')
        newErrors['prcExpiryDate'] = '"Prc Expiry Date" is not allowed to be empty'
      }
      if (form['prcExpiryDate'] === null) {
        newErrors['prcExpiryDate'] = 'Invalid date format'
      }
    }
  }
  console.log('contact newErrors', newErrors)

  return {
    newErrors,
    isValid: isEmpty(newErrors)
  }
}
