import React from 'react'
import NativeSelect from '@material-ui/core/NativeSelect'

export default function StatusSelect ({ onChange, value, onBlur }) {
  const status = [
    { label: 'ACTIVE', value: 'ACTIVE' },
    { label: 'ON-HOLD', value: 'ONHOLD' },
    { label: 'INACTIVE', value: 'INACTIVE' },
    { label: 'DISAFFILIATED', value: 'DISAFFILIATED' }
  ]

  return (
    <NativeSelect value={value} onChange={onChange} onBlur={onBlur} name='status' inputProps={{ 'aria-label': 'age' }}>
      {status.map((status) => (
        <option value={status.value}>{status.label} </option>
      ))}
    </NativeSelect>
  )
}
