import React from 'react'
import PdfViewer from '../../../../../../SharedComponents/PdfViewer/PdfViewer'
import Contracts from '../../../../../../Services/Api/Profile/Contracts'
import printJS from 'print-js'

const ContractDetailsViewer = props => {
  const { paramId, type, fileName, facilityId } = props
  const [file, setFile] = React.useState()

  // const getFile = () => {
  //   Contracts.getContract(paramId, type, facilityId).then(result => {
  //     setFile(result.data)
  //   })
  // }

  React.useEffect(() => {
    const asyncFunction = async () => {
      const result = await Contracts.getContract(paramId, type, facilityId)
      setFile(result.data)
    }
    asyncFunction()
  }, [facilityId])

  const download = () => {
    const url = URL.createObjectURL(file)
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `${fileName}.pdf`)
    document.body.appendChild(link)
    link.click()
  }

  const print = async () => {
    try {
      const { ipcRenderer } = window.require('electron')
      const data = await blobToBase64(file)
      ipcRenderer.send('print-to-pdf', { data, fileName })
    } catch (error) {
      const reader = new FileReader()
      reader.readAsDataURL(file)
      reader.onloadend = function () {
        const dataUrl = reader.result
        const printable = dataUrl.split(',')[1]
        printJS({ printable, type: 'pdf', base64: true })
      }
    }
  }

  const blobToBase64 = blob => {
    return new Promise(resolve => {
      const reader = new FileReader()
      reader.onload = function () {
        const dataUrl = reader.result
        const base64 = dataUrl.split(',')[1]
        resolve(base64)
      }
      reader.readAsDataURL(blob)
    })
  }

  return (
    <>
      <PdfViewer file={file} />
    </>
  )
}

export default ContractDetailsViewer
