import React from 'react'
import Paper from '@material-ui/core/Paper'
import classes from './CustomizeTextArea.module.scss'

const CustomizeTextArea = ({ label, style, ...rest }) => {
  return (
    <div id='root' className={classes.root}>
      <label>{label}</label>
      <Paper id='textAreaContainer' className={classes.textAreaContainer}>
        <textarea className={`${style}`} {...rest} />
      </Paper>
    </div>
  )
}

export default CustomizeTextArea
