import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
// import TextField from '../../../../../SharedComponents/TextField/CustomTextField'

import classes from './DialogBoxConfirmation.module.scss'
import { useSelector, useDispatch } from 'react-redux'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import PartnerDoctorService from 'Services/Api/Partner/PartnerDoctorService'

const DialogBoxConfirmation = props => {
  const dispatch = useDispatch()
  const { doctorForms } = useSelector(state => state.AffiliationDoctorForm)
  const { affiliationType } = useSelector(state => state.affiliationType)

  const { dialogText, isSubmitted, disable, setDisable, setIsApproved } = props
  const [open, setOpen] = React.useState(false)

  const handleProceed = async () => {
    try {
      affiliationType.stageName = 'FOR_AFFILIATION_APPROVAL'
      affiliationType.affiliationStatus = 'Submitted'
      affiliationType.dateSubmitted = new Date().toISOString()

      let res = await PartnerDoctorService.updatePartnerDoctor(affiliationType.id, {
        ...affiliationType,
        ...doctorForms
      })

      dispatch(setAffiliationType(affiliationType))
      setIsApproved(true)
      const stageName = res.data.status === 200 ? res.data.data.stage.stage_name : 'error'
      window.$stageName = stageName
      setOpen(false)
    } catch (error) {
      console.log('error in save', error)
    }
  }
  return (
    <React.Fragment>
      <Button
        style={{
          backgroundColor: disable ? '#dddfe3' : '#3ab77d',
          color: 'white',
          paddingLeft: '1.5em',
          paddingRight: '1.5em'
        }}
        className={classes.submitButton}
        variant='contained'
        disabled={disable}
        onClick={() => {
          setOpen(true)
          // setDisable(true)
        }}
      >
        SUBMIT
      </Button>
      <Dialog fullWidth maxWidth={'sm'} open={open} aria-labelledby='max-width-dialog-title'>
        <DialogContent>
          <div className={classes.root} noValidate autoComplete='off'>
            Are you sure you want to proceed with the submission of <br />
            {dialogText} form <br />
            for approval?
          </div>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <div id='button-div'>
            <Button
              onClick={() => {
                setOpen(false)
              }}
              variant='contained'
              className={classes.Cancel}
            >
              CANCEL
            </Button>
            <Button variant='contained' onClick={handleProceed} className={classes.Proceed}>
              Proceed
            </Button>
          </div>
        </DialogActions>
      </Dialog>
    </React.Fragment>
  )
}

export default DialogBoxConfirmation
