import React, { useState } from 'react'

import Button from '@material-ui/core/Button'

// Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbs from '../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import FacilityInformation from '../Components/FacilityInformation/FacilityInformation'
import UploadFileTable from '../../../AffiliationFormEncoder/Pages/MedicalClinicAffiliation/Components/UploadFIleTable/UploadFIleTable'
import CustomDialogApprove from '../Components/CustomDialogApprove/CustomDialogApprove'
import History from 'Pages/Protected/Partner/Components/History/History'

import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'
import MedicalAffiliationOIContext from './context/MedicalClinicOIContext'

// Styles
import classes from './MedicalClinicAffiliationOI.module.scss'

// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'
import cntsPartnerType from 'Pages/Protected/Partner/Constants/partnerTypes'
import { AffiliationStatusEnum } from 'Pages/Protected/Partner/Constants/AffiliationStatusEnum'
import Axios from 'axios'

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  MedicalClinic: {
    text: 'MEDICAL CLINIC AFFILIATION',
    to: '/'
  }
}

const MedicalClinicAffiliationOI2 = props => {
  let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
  let headerTitle = 'Medical Clinic Affiliation Form'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'MedicalClinic'])
  const { paramId, approved, setIsApproved } = React.useContext(MedicalAffiliationOIContext)
  const [isProcessed, setProcessed] = React.useState(false)
  const [isApprove, setIsApprove] = React.useState(false)

  const save = async () => {
    try {
      let stage = {
        stage_name: cnts.PARTNER.STAGE.FOR_OIC,
        date_submitted: new Date().toISOString(),
        type: cntsPartnerType.PARTNER.TYPE.CLINIC
      }
      const res = await AffiliationApproverServices.approveAffiliationForOIC(paramId, stage)
      const affiliationStatus = res.data.data.affiliation_status.name
      setProcessed(affiliationStatus === AffiliationStatusEnum.PROCESSING)
      setIsApproved(affiliationStatus === AffiliationStatusEnum.PROCESSING)
      setIsApprove(false)
      // props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  const disapproved = async () => {
    try {
      const res = await AffiliationApproverServices.disapproved(paramId, cntsPartnerType.PARTNER.TYPE.CLINIC)
      props.history.push('/affiliation-approver')
      console.log(res)
    } catch (error) {
      console.log('error', error)
    }
  }

  return (
    <React.Fragment>
      <CustomDialogApprove id='aaCustomDialog' isApprove={isApprove} setIsApprove={() => setIsApprove(false)}>
        <div id='aaCustomDialogContainer' className={classes.dialogContainer}>
          <h3 id='aaCustomDialogMessage'>
            Are you sure you want to approve the Medical Clinic Affiliation form for Ocular Inspection?
          </h3>
          <div id='aaCustomDialogBtnGroup' className={classes.btnGroup}>
            <Button
              onClick={() => setIsApprove(false)}
              id='aaCustomDialogBtnCancel'
              className={classes.btnCancel}
              variant='contained'
            >
              Cancel
            </Button>
            <Button id='aaCustomDialogBtnApprove' onClick={save} className={classes.btnApprove} variant='contained'>
              Approve
            </Button>
          </div>
        </div>
      </CustomDialogApprove>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={2}
        />
        <div id='contentWrapper' className={classes.contentWrapper}>
          <FacilityInformation
            titleText={'Medical Clinic Profile'}
            uploadFileTable={<UploadFileTable buttonText={'Upload'} affiliationTypeId={paramId} forView={true} />}
          />
        </div>
        <div className={classes.FooterButtonContainer}>
          <span className={classes.FooterButtonWrapper}>
            <div>
              <Button
                variant='contained'
                color='secondary'
                className={classes.disapproveButton}
                onClick={disapproved}
                style={{backgroundColor: approved ? '#dddfe3' : ''}}
                disabled={approved}
              >
                Disapproved
              </Button>
            </div>
            <div />
            <div>
              <div>
                <Button
                  variant='contained'
                  className={classes.returnButton}
                  onClick={() => props.setPage(false)}
                >
                  Return
                </Button>
              </div>
              <div>
                <Button variant='contained' disabled={approved} style={{backgroundColor: approved ? '#dddfe3' : ''}} className={classes.approveButton} onClick={() => setIsApprove(true)}>
                  Approve
                </Button>
              </div>
            </div>
          </span>
          <div
            style={{ float: 'right', marginBottom: '1%', marginRight: '1%', fontWeight: 'bold', position: 'sticky' }}
          >
            Medical Clinic Affiliation Form 2 out of 2
          </div>
        </div>
      </div>
    </React.Fragment>
  )
}

export default MedicalClinicAffiliationOI2
