import { green } from '@material-ui/core/colors'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import { updateRoomAndBoard } from '../../../../Store/Actions/actions'

import {
  IntegerComponent,
  PriceComponent,
  MultiTextComponent
} from '../../../../Components/RowInputComponents/RowInputComponents'
// import scss module
import classes from './RoomAndBoard.module.scss'

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: green[600]
    }
  },
  checked: {}
})(props => <Checkbox color='default' {...props} />)

const RowComponent = React.memo(
  props => {
    const { readOnly = false, pos = 0, group = '', rowObj = {}, setPaddingLeft = '', onChangeValue = () => {} } = props

    const onChangeAvailable = newState => {
      onChangeValue({ name: group, pos: pos, obj: { available: newState } })
    }

    const onChangeRooms = newState => {
      onChangeValue({ name: group, pos: pos, obj: { noOfRooms: newState } })
    }

    const onChangeBeds = newState => {
      onChangeValue({ name: group, pos: pos, obj: { noOfBeds: newState } })
    }

    const onChangePrice = newState => {
      onChangeValue({ name: group, pos: pos, obj: { price: newState } })
    }

    const onChangeInclusions = newState => {
      onChangeValue({ name: group, pos: pos, obj: { inclusions: newState } })
    }

    return (
      <TableRow>
        <TableCell className={classes.tableCell} style={{ paddingLeft: setPaddingLeft }} align='left'>
          {rowObj.roomType}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          <FormControlLabel
            control={
              <GreenCheckbox
                checked={rowObj.available}
                onChange={readOnly ? () => {} : () => onChangeAvailable(!rowObj.available)}
                inputProps={{ style: { textAlign: 'center' } }}
              />
            }
          />
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.noOfRooms !== 0 ? (
              rowObj.noOfRooms
            ) : (
              ''
            )
          ) : (
            <IntegerComponent
              id={`noOfRooms${group}${pos}`}
              value={rowObj.noOfRooms}
              disabled={!rowObj.available}
              onChange={val => onChangeRooms(val)}
            />
          )}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.noOfBeds !== 0 ? (
              rowObj.noOfBeds
            ) : (
              ''
            )
          ) : (
            <IntegerComponent
              id={`noOfBeds${group}${pos}`}
              value={rowObj.noOfBeds}
              disabled={!rowObj.available}
              onChange={val => onChangeBeds(val)}
            />
          )}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          {readOnly ? (
            CurrencyFormatter(rowObj.price) !== '0.00' ? (
              CurrencyFormatter(rowObj.price)
            ) : (
              ''
            )
          ) : (
            <PriceComponent
              id={`price${group}${pos}`}
              value={rowObj.price}
              disabled={!rowObj.available}
              onChange={val => onChangePrice(val)}
            />
          )}
        </TableCell>
        <TableCell style={{ height: '100%' }} className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.inclusions
          ) : (
            <MultiTextComponent
              id={`inclusions${group}${pos}`}
              value={rowObj.inclusions}
              disabled={!rowObj.available}
              onChange={e => onChangeInclusions(e.target.value)}
            />
          )}
        </TableCell>
      </TableRow>
    )
  },
  (prevProps, nextProps) => {
    try {
      if (
        prevProps.readOnly === nextProps.readOnly &&
        prevProps.pos === nextProps.pos &&
        prevProps.rowObj.available === nextProps.rowObj.available &&
        prevProps.rowObj.noOfRooms === nextProps.rowObj.noOfRooms &&
        prevProps.rowObj.noOfBeds === nextProps.rowObj.noOfBeds &&
        prevProps.rowObj.price === nextProps.rowObj.price &&
        prevProps.rowObj.inclusions === nextProps.rowObj.inclusions
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)

const SocialBedGroup = props => {
  const { readOnly = false, socialBed, onChange = () => {} } = props
  return <RowComponent readOnly={readOnly} group={'socialBed'} pos={0} rowObj={socialBed[0]} onChangeValue={onChange} />
}

