import React from 'react'

import { HashRouter as Router, Switch, Route, Link } from 'react-router-dom'
import TitleHeader from '../HeaderWithEditButtons/HeaderWithEditButtons'

import classes from './ProfileList.module.scss'

const ProfileList = props => {
  const { HeaderTitle, leftSideImage, ListItems, status } = props

  const listItemsValue = React.useMemo(() => ListItems, [ListItems])

  return (
    <Router>
      <React.Fragment>
        <div id='root' className={classes.root}>
          <div>
            <TitleHeader id='header' HeaderTitle={HeaderTitle} status={status} />
          </div>
          <div id='body' className={classes.body}>
            <div id='wrapper' className={classes.leftSidebarWrapper}>
              <div id='bodySideBar' className={classes.leftbodySideBar}>
                <div id='profileImage' className={classes.profileImage}>
                  <img src={leftSideImage} alt='image' />
                </div>
                {listItemsValue.map((items, index) => (
                  <div id='partnerInformation' key={items.to} className={classes.text}>
                    <Link
                      key={items.to}
                      to={items.to}
                      onClick={() => (items.open ? items.open(true) : items.click ? items.click() : '')}
                      className={classes.link}
                    >
                      {items.name}
                    </Link>
                  </div>
                ))}
              </div>
            </div>

            <Switch>
              <React.Fragment>
                <div id='rightSidebody' className={classes.rightSidebody}>
                  <div id='rightSidebodyWrapper' className={classes.rightSidebodyWrapper}>
                    {listItemsValue.map((items, index) => (
                      <Route key={index} exact path={items.path}>
                        {items.importPage}
                      </Route>
                    ))}
                  </div>
                </div>
              </React.Fragment>
            </Switch>
          </div>
        </div>
      </React.Fragment>
    </Router>
  )
}

const ProfileListName = React.memo(ProfileList, (prevProps, nextProps) => {
  try {
    if (
      prevProps.ListItems === nextProps.ListItems &&
      prevProps.status === nextProps.status &&
      prevProps.HeaderTitle === nextProps.HeaderTitle &&
      prevProps.leftSideImage === nextProps.leftSideImage
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})

export default ProfileListName
