import instance from '../instance'
// import axios from 'axios'
const axios = instance('/p/f')
const axios_pc = instance('/p/c')

const URL = process.env.REACT_APP_SERVER_API

export default class OICProcessorServices {
  static getAffiliationFormInfo (id) {
    return axios({
      url: `/partners/facilities/oic/form/${id}`,
      method: 'GET'
    })
  }

  static getPartnerTileOICData (params) {
    return axios({
      url: '/partners/facilities/oic/data',
      method: 'GET',
      params
    })
  }

  static getFacilityFormInfo (id) {
    return axios({
      url: `/partners/facilities/oic/profile/${id}`,
      method: 'GET'
    })
  }

  static saveFacilityOICForm (data) {
    return axios({
      url: '/partners/facilities/oic/form',
      method: 'PUT',
      data
    })
  }

  static getFacilityOICHeaderData (params) {
    return axios({
      url: '/partners/facilities/oic/search/field',
      method: 'GET',
      params
    })
  }

  static getFacilityOICListData (params) {
    return axios({
      url: '/partners/facilities/oic/view/list',
      method: 'GET',
      params
    })
  }

  static disapprovedAffiliationForm (data) {
    return axios_pc({
      url: '/partners/common/affiliation/disapprove',
      method: 'PUT',
      data
    })
  }
}
