import classes from './Services.module.scss'

const GreenCheckbox = withStyles({
  root: {
    color: green[400],
    '&$checked': {
      color: green[600]
    }
  },
  checked: {}
})(props => <Checkbox color='default' {...props} />)

const RowComponent = React.memo(
  props => {
    const {
      readOnly = false,
      secondLabel = '',
      pos = 0,
      group = '',
      rowObj = {},
      setPaddingLeft = '',
      onChangeValue = () => {}
    } = props

    const onChangeAvailable = newState => {
      onChangeValue({ name: group, pos: pos, obj: { available: newState } })
    }

    const onChangeUnits = newState => {
      onChangeValue({ name: group, pos: pos, obj: { noOfUnits: newState } })
    }

    const onChangeSlices = newState => {
      onChangeValue({ name: group, pos: pos, obj: { noOfSlices: newState } })
    }

    const onChangeBrand = newState => {
      onChangeValue({ name: group, pos: pos, obj: { brand: newState } })
    }

    const onChangeRemarks = newState => {
      onChangeValue({ name: group, pos: pos, obj: { remarks: newState } })
    }

    return (
      <TableRow>
        <TableCell className={classes.tableCell} style={{ paddingLeft: setPaddingLeft }} align='left'>
          {rowObj.name}
          {secondLabel ? <p style={{ fontSize: '.87rem', color: '#FD5451', marginTop: '0px' }}>{secondLabel}</p> : null}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          <FormControlLabel
            control={
              <GreenCheckbox
                checked={rowObj.available}
                onChange={readOnly ? () => {} : () => onChangeAvailable(!rowObj.available)}
                inputProps={{ style: { textAlign: 'center' } }}
              />
            }
          />
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.noOfUnits !== 0 ? (
              rowObj.noOfUnits
            ) : (
              ''
            )
          ) : (
            <IntegerComponent
              id={`noOfUnits${group}${pos}`}
              value={rowObj.noOfUnits}
              disabled={!rowObj.available}
              onChange={val => onChangeUnits(val)}
            />
          )}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.noOfSlices !== 0 ? (
              rowObj.noOfSlices
            ) : (
              ''
            )
          ) : (
            <IntegerComponent
              id={`noOfSlices${group}${pos}`}
              value={rowObj.noOfSlices}
              disabled={!rowObj.available}
              onChange={val => onChangeSlices(val)}
            />
          )}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.brand
          ) : (
            <MultiTextComponent
              id={`brand${group}${pos}`}
              value={rowObj.brand}
              disabled={!rowObj.available}
              onChange={e => onChangeBrand(e.target.value)}
            />
          )}
        </TableCell>
        <TableCell style={{ height: '100%' }} className={classes.tableCell} align='center'>
          {readOnly ? (
            rowObj.remarks
          ) : (
            <MultiTextComponent
              id={`remarks${group}${pos}`}
              value={rowObj.remarks}
              disabled={!rowObj.available}
              onChange={e => onChangeRemarks(e.target.value)}
            />
          )}
        </TableCell>
      </TableRow>
    )
  },
  (prevProps, nextProps) => {
    try {
      if (
        prevProps.readOnly === nextProps.readOnly &&
        prevProps.pos === nextProps.pos &&
        prevProps.rowObj.available === nextProps.rowObj.available &&
        prevProps.rowObj.noOfUnits === nextProps.rowObj.noOfUnits &&
        prevProps.rowObj.noOfSlices === nextProps.rowObj.noOfSlices &&
        prevProps.rowObj.brand === nextProps.rowObj.brand &&
        prevProps.rowObj.remarks === nextProps.rowObj.remarks
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)

const OtherComponent = React.memo(
  props => {
    const { readOnly = false, pos = 0, group = '', rowObj = {}, setPaddingLeft = '', onChangeValue = () => {} } = props

    const onChangeAvailable = newState => {
      onChangeValue({ name: group, pos: pos, obj: { available: newState } })
    }

    const onChangeRemarks = newState => {
      onChangeValue({ name: group, pos: pos, obj: { remarks: newState } })
    }

    return (
      <TableRow>
        <TableCell
          className={classes.tableCell}
          colSpan={6}
          style={{ paddingLeft: setPaddingLeft, display: 'flex', alignItems: 'center', border: 'none' }}
          align='left'
        >
          {rowObj.name}
          &nbsp;&nbsp;
          {readOnly ? (
            <span style={{ marginLeft: '5px' }}>{rowObj.remarks}</span>
          ) : (
            <MultiTextComponent
              id={`remarks${group}${pos}`}
              value={rowObj.remarks}
              disabled={!rowObj.available}
              onChange={e => onChangeRemarks(e.target.value)}
            />
          )}
        </TableCell>
        <TableCell className={classes.tableCell} align='center'>
          <FormControlLabel
            control={
              <GreenCheckbox
                checked={rowObj.available}
                onChange={readOnly ? () => {} : () => onChangeAvailable(!rowObj.available)}
                inputProps={{ style: { textAlign: 'center' } }}
              />
            }
          />
        </TableCell>
        <TableCell className={classes.tableCell} align='center' colSpan={4} />
      </TableRow>
    )
  },
  (prevProps, nextProps) => {
    try {
      if (
        prevProps.readOnly === nextProps.readOnly &&
        prevProps.pos === nextProps.pos &&
        prevProps.rowObj.available === nextProps.rowObj.available &&
        prevProps.rowObj.remarks === nextProps.rowObj.remarks
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)

