const Services = props => {
  const dispatch = useDispatch()
  const services = useSelector(state => state.medical.services)
  const otherServices = useSelector(state => state.medical.otherServices)
  const { readOnly = false } = props

  return (
    <React.Fragment>
      <div className={classes.root}>
        <h2 id='roomAndBoardTableTitle' className={classes.title}>
          SERVICES OFFERED
        </h2>
        <div id='roomAndBoardContent' className={classes.content}>
          <Grid id='ct_grid' container spacing={1}>
            <Grid item xs={12}>
              <TableContainer>
                <Table aria-label='simple table' className={classes.table}>
                  <TableHead className={classes.tableHead}>
                    <TableRow>
                      <TableCell className={classes.tableCell} align='center'>
                        Services
                      </TableCell>
                      <TableCell className={classes.tableCell} align='center'>
                        Available
                      </TableCell>
                      <TableCell className={classes.tableCell} align='center'>
                        No. of Units
                      </TableCell>
                      <TableCell className={classes.tableCell} align='center'>
                        No. of Slices
                      </TableCell>
                      <TableCell className={classes.tableCell} align='center'>
                        Brand
                      </TableCell>
                      <TableCell className={classes.tableCell} align='center'>
                        Remarks
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody className={classes.tableBody}>
                    <TableRow>
                      <TableCell className={classes.tableCell} align='left' colSpan={6}>
                        {'RADIOLOGY'}
                      </TableCell>
                    </TableRow>
                    <RadiologyAndImagingGroup
                      readOnly={readOnly}
                      radiologyAndImaging={services.radiologyAndImaging}
                      onChange={val => dispatch(updateMedicalService(val))}
                    />
                    <TableRow>
                      <TableCell className={classes.tableCell} align='left' colSpan={6}>
                        {'HEART STATION'}
                      </TableCell>
                    </TableRow>
                    <HeartStationGroup
                      readOnly={readOnly}
                      heartStation={services.heartStation}
                      onChange={val => dispatch(updateMedicalService(val))}
                    />
                    <TableRow>
                      <TableCell className={classes.tableCell} align='left' colSpan={6}>
                        {'LABORATORY'}
                      </TableCell>
                    </TableRow>
                    <LaboratoryGroup
                      readOnly={readOnly}
                      laboratory={services.laboratory}
                      onChange={val => dispatch(updateMedicalService(val))}
                    />
                  </TableBody>
                </Table>
              </TableContainer>
            </Grid>
          </Grid>
        </div>
        {/* Other Services */}
        <h2 id='roomAndBoardTableTitle' className={classes.title}>
          OTHER DEPARTMENTS AND SERVICES
        </h2>
        <div id='roomAndBoardContent' className={classes.content}>
          <Grid id='ct_grid' container spacing={1}>
            <Grid item xs={12}>
              <TableContainer>
                <Table aria-label='simple table' className={classes.table}>
                  <TableBody className={classes.tableBody}>
                    <OtherServicesGroupOne
                      readOnly={readOnly}
                      otherServices={otherServices}
                      onChange={val => dispatch(updateOtherService(val))}
                    />
                    <OtherServicesGroupTwo
                      readOnly={readOnly}
                      otherServices={otherServices}
                      onChange={val => dispatch(updateOtherService(val))}
                    />
                  </TableBody>
                </Table>
              </TableContainer>
            </Grid>
          </Grid>
        </div>
      </div>
    </React.Fragment>
  )
}

export default Services
