import React from 'react'
import { withStyles } from '@material-ui/core/styles'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import TrashIcon from '@material-ui/icons/Delete'
import InlineForm from './InlineForm'
import classes from './AddMultiplierModal.module.scss'

const row = (x, i, header = [], handleRemove, startEditing, editIdx, handleSave, stopEditing, isSubmitted) => {
  const currentlyEditing = editIdx === i
  return currentlyEditing ? (
    <StyledTableRow key={`inline-form-${i}`} className={classes.tableRowEdit}>
      <StyledTableCell className={classes.tableCellEdit} key={`trc-${i}`}>
        <InlineForm handleSave={handleSave} header={header} x={x} i={i} stopEditing={stopEditing} />
      </StyledTableCell>
    </StyledTableRow>
  ) : (
    <StyledTableRow key={`tr-${i}`} className={classes.inlineNormal}>
      {header.map((y, k) => (
        <StyledTableCell className={classes.inlineTableCell} key={`trc-${k}`} align='left' component='th' scope='row'>
          {x[y.prop]}
        </StyledTableCell>
      ))}
      {(!isSubmitted) && (
        <StyledTableCell className={classes.editAndDelete}>
          <TrashIcon onClick={() => handleRemove(i)} />
        </StyledTableCell>
      )}
    </StyledTableRow>
  )
}

export default ({ data, header = [], handleRemove, startEditing, editIdx, handleSave, stopEditing, isSubmitted, hasError }) => (
  <TableContainer className={classes.tableContainer}>
    <Table className={classes.tableRoot} aria-label='customized table'>
      <TableHead className={classes.tableHeader}>
        <TableRow className={classes.tableRow} style={hasError ? {border: '2px solid red'} : null}>
          {header.map((x, i) => (
            <TableCell className={classes.tableCell} key={`thc-${i}`} align='left'>
              {x.name}
            </TableCell>
          ))}
          <TableCell align='left' />
        </TableRow>
      </TableHead>
      <TableBody className={classes.tableBody}>
        {data.map((x, i) => row(x, i, header, handleRemove, startEditing, editIdx, handleSave, stopEditing, isSubmitted))}
      </TableBody>
    </Table>
  </TableContainer>
)

const StyledTableCell = withStyles(theme => ({
  head: {
    backgroundColor: theme.palette.common.lightgrey,
    color: theme.palette.common.lighgrey
  },
  body: {
    fontSize: 12
  }
}))(TableCell)

const StyledTableRow = withStyles(theme => ({
  root: {
    '&:nth-of-type(odd)': {
      backgroundColor: theme.palette.background.default
    },
    '&:nth-of-type(even)': {
      backgroundColor: '#f5f7fb'
    }
  }
}))(TableRow)
