import React from 'react'
import PageOne from './DentalClinicAffiliation_p1'
import PageTwo from './DentalClinicAffiliation_p2'
import DentalAffiliationContext from './context/DentalAffiliationContext'
const DentalClinicAffiliation = ({ match, history }) => {
  const [isNextPage, setIsNextPage] = React.useState(false)
  const [dentalForm, setDentalForm] = React.useState({})
  const [form, setForm] = React.useState({})
  const [isApproved, setIsApproved] = React.useState(false)

  return (
    <DentalAffiliationContext.Provider value={{ paramId: match.params.id, dentalForm, setDentalForm, setIsApproved, isApproved, form, setForm }}>
      {!isNextPage && <PageOne setPage={setIsNextPage} history={history} />}
      {isNextPage && <PageTwo setPage={setIsNextPage} history={history} />}
    </DentalAffiliationContext.Provider>
  )
}
export default DentalClinicAffiliation
