import React from 'react'

import ReEncoding from '../Utilities/reEncoding'
// data
import { NonVatableData } from '../../../../Components/Placeholders/Placeholders'
// shared Components

import FooterWithButtons from '../../../../../Components/DynamicFooterWithButton/FooterWithButtons'
import AffiliationTable from '../../../../../Components/AffiliationTable/AffiliationTable1'
import AffiliationFormInput from '../../../../../Components/AfiiliationTableWithInput/AfiiliationTableWithInput'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'

import CustomCalendar from '../../../../../Components/DatePicker/DatePickerInlineEdit'
import TextField from '../../../../../Components/JoiValidationInputField'
import DropDown from '../../../../../Components/CustomDrowpdown'
import AddressDropDown from '../../../../../Components/AddressDrowpdown'

import { dataheader } from '../Utilities/dataHeader'

// form validation

import HospitalFormValidation from './HospitalFormValidation'
// marterial ui
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import Paper from '@material-ui/core/Paper'
import FormHelperText from '@material-ui/core/FormHelperText'

// style scss
import classes from './AffiliationForm.module.scss'
import styles from '../../../../../Components/TwoTextArea/TwoTextArea.module.scss'
// import isEmpty from 'lodash/isEmpty'
import debounce from 'lodash/debounce'
import ContactValidation from './validations/ContactValidataion'
import { useSelector, useDispatch } from 'react-redux'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import { setAffiliationFormData } from 'Pages/Protected/Store/actions/Partner/hospitalAffiliationForm'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import { useParams } from 'react-router-dom'
import AddressChaining from '../../../../../../../../Constant/chaining-address'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'
import hospitalInputFields from '../Utilities/hospitalInputFields'
// import CustomTextFieldNumberFormat from '../../../../../Components/JoiValidationInputFieldNumberFormat'
import CustomTextFieldNumberFormat from '../../../../../Components/JoiValidationInputFieldNumberFormat'

import { isDateValid } from 'Pages/Protected/Partner/Components/DatePicker/DatePickerUtils'
import { handleAddressChaining, fetchAddress, fetchRegion } from 'Pages/Protected/Partner/Components/Utils/utils'
// let NameValues

const AffiliationForm = props => {
  const { hospitalForms } = useSelector(state => state.AffiliationHospitalForm)
  const { affiliationType } = useSelector(state => state.affiliationType)

  let dispatch = useDispatch()
  const { id } = useParams()
  let cName = React.useRef()
  let cDesignation = React.useRef()
  let cDepartment = React.useRef()
  let cEmail = React.useRef()
  let cNumber = React.useRef()
  let affiliatedGroupRef = React.useRef()
  // required

  const [editIdx, setEditIdx] = React.useState(-1)

  const [isSuccess, setIsSuccess] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const [isSubmitted, setIsSubmitted] = React.useState(false)
  const [affiliatedGroupInput, setAffiliatedGroupInput] = React.useState('')
  const [addToGroup, setAddToGroups] = React.useState([])
  const [VATable, setVATable] = React.useState([])
  const [errors, setErrors] = React.useState({})
  const [addContactsErrors, setAddContactsErrror] = React.useState({})
  const [dateValue, setDateValue] = React.useState({})
  const [philHealthNumber, setPhilHealthNumber] = React.useState('')
  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [provinces, setProvinces] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])
  const [contactsForm, setContactsForm] = React.useState({
    cName: '', // required
    cDesignation: '', // required
    cDepartment: '', // required
    cEmail: '', // required
    cNumber: '' // required
  })

  const [form, setForm] = React.useState({
    ...hospitalForms
  })

  let hospitalFormRef = React.useRef({...hospitalForms})

  const [affilID, setAffilID] = React.useState(null)

  React.useEffect(() => {
    form['tinStatus'] === '12%' ? setVATable([]) : setVATable(NonVatableData)
    // disabledButton()
  }, [form['tinStatus']])

  React.useEffect(() => {
    if (affiliationType.affiliationStatus === 'Submitted') {
      setIsSubmitted(true)
    }
  }, [affiliationType.affiliationStatus])

  /* populate fields if global store or redux has a value on component load  */
  React.useEffect(() => {
    if (affiliationType.hasOwnProperty('id') || id) {
      setAffilID(affiliationType['id']) // setting back the id value
    }
    if (id) {
      fetchData(id)
      setAffilID(id)
    } else {
      setAddToGroups(hospitalForms['affiliationInfos'] ? hospitalForms['affiliationInfos'] : [])
      try {
        mapFieldsToRef({...hospitalForms})
      } catch (error) {
        console.log('hospital form error', error)
      }
    }
    if (affiliationType['stageName'] === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
    }
    fetchRegion(setRegions)
  }, [])


  const mapFieldsToRef = form => {
    for (let key in form) {
      if (hospitalInputFields.includes(key)) {
        hospitalFormRef.current[key].value = form[key]
      }
    }
  }

  const fetchData = async (id) => {
    setIsLoading(true)
    try {
      const { newForms, affiliationType, philhealth } = await ReEncoding(id, setAddToGroups, setVATable, NonVatableData, setIsLoading)
      if (philhealth) {
        setPhilHealthNumber(philhealth)
      }
      fetchAddress(newForms, setBarangays, setCities, setProvinces)
      setForm(newForms)
      dispatch(setAffiliationFormData(newForms))
      dispatch(setAffiliationType(affiliationType))
      if (affiliationType.stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
        setIsSubmitted(true)
      } else {
        setIsSubmitted(false)
      }
      setIsLoading(false)
      try {
        mapFieldsToRef({...newForms})
      } catch (error) {
        console.log('maps state error', error)
      }
    } catch (error) {
      console.log('fetch error', error)
    }
  }

  let pathUrl = '/hospital-affiliation-requirements'

  let affilConstValue = {
    stageName: 'ENCODING',
    status: 'Active',
    affiliationStatus: 'In-Progress'
  }

  const save = async () => {
    LoadingService.loading(true)
    if (!affilID) {
      createHospitalAffil()
    } else {
      updateHospitalAffil()
    }
    LoadingService.loading(false)
  }

  const createHospitalAffil = async () => {
    try {
      const newForm = { ...form }
      const affilType = { ...affiliationType }
      affilType.stageName = 'ENCODING'
      affilType.source = 'PNM'
      newForm.dateSubmitted = new Date().toISOString()
      const {
        data: {
          data: { id }
        }
      } = await AffiliationEncoderService.createFacilities({ ...affilType, ...newForm })
      affilType.id = id
      setAffilID(id)
      dispatch(setAffiliationType({ ...affilConstValue, ...affilType }))
      dispatch(setAffiliationFormData(form))
      setIsSuccess(true)
    } catch (error) {
      console.log('error', error)
    }
  }

  const updateHospitalAffil = async () => {
    try {
      const newForm = { ...form }
      const affilType = { ...affiliationType }
      affilType.stageName = 'ENCODING'
      affilType.dateSubmitted = new Date().toISOString()
      const res = await AffiliationEncoderService.updateFacilities(affilID, {
        ...affilType,
        ...newForm
      })
      dispatch(setAffiliationType({ ...affilConstValue, ...affilType }))
      dispatch(setAffiliationFormData(newForm))
      setIsSuccess(true)
    } catch (error) {
      console.log('error ocurred on updating', error)
    }
  }

  const validate = () => {
    const { errors: newErrors, isValid } = HospitalFormValidation(form, errors, dateValue)
    if (isValid && form['contacts'].length > 0) {
      dispatch(setAffiliationFormData(form))
      save()
      setErrors({})
    } else {
      setErrors(newErrors)
    }
  }

  const handleRemoveContact = index => {
    const newForm = { ...form }

    const contacts = [...newForm['contacts']]
    const res = contacts.filter((contact, i) => i !== index)
    newForm['contacts'] = res
    setForm(newForm)
    dispatch(setAffiliationFormData(newForm))
  }

  const startEditing = i => {
    setEditIdx(i)
  }

  const stopEditing = () => {
    setEditIdx(-1)
  }

  const handleSave = (i, x) => {
    const newForm = { ...form }
    const contacts = [...newForm['contacts']]
    const editedValue = contacts.map((row, j) => (j === i ? x : row))
    newForm['contacts'] = editedValue
    setForm(newForm)
    stopEditing()
  }

  const disabledButton = React.useMemo(() => {
    try {
      const { isValid } = HospitalFormValidation(form, errors, dateValue)
      if (isValid) {
        return false
      } else {
        return true
      }
    } catch (error) {
      console.log('error in disabled button', error)
    }
  }, [form])

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const SubmitForms = e => {
    e.preventDefault()
    props.formSubmit()
  }

  const proceedSubmitForm = () => {
    if (props.formSubmit) {
      props.formSubmit()
    }
  }

  const HandleRemove = React.useCallback(
    i => {
      setAddToGroups(addToGroup => addToGroup.filter((row, j) => j !== i))
    },
    [addToGroup]
  )

  const handleChange = (value, name) => {
    const hospitalForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    hospitalForm[name] = value

    if (hospitalForm[name] !== '12%') {
      setVATable(NonVatableData)
    } else {
      setVATable([])
    }
    dispatch(setAffiliationFormData(hospitalForm))
    setForm(hospitalForm)
  }

  const HandleAddContactsChange = (value, name) => {
    const addContactsForm = { ...contactsForm }
    const newErrors = { ...addContactsErrors }
    delete newErrors[name]
    setAddContactsErrror(newErrors)
    addContactsForm[name] = value
    dispatch(setAffiliationFormData(addContactsForm))
    setContactsForm(addContactsForm)
  }

  const handleDatePicker = (name, date) => {
    let dateVal = { ...dateValue }
    let newErrors = { ...errors }
    const hospitalForm = { ...form }
    const { isValid, dateFormatted } = isDateValid(date)
    if (isValid) {
      hospitalForm[name] = dateFormatted
      delete newErrors[name]
      dispatch(setAffiliationFormData(hospitalForm))
      setForm(hospitalForm)
      setErrors(newErrors)
      delete dateVal[name]
      setDateValue(dateVal)
    } else {
      if (date !== '') {
        dateVal[name] = date
        setDateValue(dateVal)
        newErrors[name] = 'Invalid date format'
      } else {
        delete newErrors[name]
        delete dateVal[name]
        setDateValue(dateVal)
      }
      setErrors(newErrors)
    }
  }

  const handleDropdown = React.useCallback(
    ({ target: select }) => {
      const hospitalForm = { ...form }
      hospitalForm[select.name] = select.value
      errors[select.name] = false
      setErrors(errors)
      dispatch(setAffiliationFormData(hospitalForm))
      setForm(hospitalForm)
      handleAddressChaining(select.name, select.value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
    },
    [form]
  )

  const handleAddToContacts = () => {
    const copiedForm = { ...contactsForm }
    const hospitalForm = { ...form }
    const { errors, isValid } = ContactValidation(copiedForm)
    if (isValid) {
      hospitalForm['contacts'] = [...hospitalForm['contacts'], copiedForm]
      setForm(hospitalForm)
      dispatch(setAffiliationFormData(hospitalForm))
      setAddContactsErrror(errors)
      cName.current.value = ''
      cDesignation.current.value = ''
      cDepartment.current.value = ''
      cEmail.current.value = ''
      cNumber.current.value = ''
      setContactsForm({
        cName: '', // required
        cDesignation: '', // required
        cDepartment: '', // required
        cEmail: '', // required
        cNumber: '' // required
      })
    } else {
      setAddContactsErrror(errors)
    }
  }

  const handleGroup = () => {
    let obj = {}
    obj.affiliateGroup = affiliatedGroupInput
    if (affiliatedGroupRef.current.value !== '') {
      setAddToGroups([...addToGroup, obj])
      dispatch(setAffiliationFormData(form))
      affiliatedGroupRef.current.value = ''
    }
  }

  const HandleEdit = React.useCallback(
    (i, x) => {
      setAddToGroups((addToGroup) => addToGroup.map((row, j) => (j === i ? x : row)))
    },
    [addToGroup]
  )

  const handleGroupValue = data => {
    const newForm = { ...form }
    newForm['affiliationInfos'] = data
    setForm(newForm)
    dispatch(setAffiliationFormData(newForm))
    props.AddToGroups(data)
  }

  const handleAffiliatedTextChange = val => {
    setAffiliatedGroupInput(val)
  }

  const handleResetForm = () => {
    dispatch(setAffiliationFormData({ name: '', // required
      bedCapacity: '', // required
      email: '', // required
      years: '', // required
      months: '', // required
      dohNumber: '',
      dohLicenseType: '',
      dohLicenseExp: '',
      philhealthNumber: '',
      philhealthExpiration: '',
      payee: '', // required
      classification: '', // required
      nonVatable: '', // required
      tinStatus: '', // required
      vatTaxRate: '',
      processType: '', // required
      unitFlrBldg: '', // required
      bldgName: '',
      street: '', // required
      subdivision: '', // required
      region: '', // required
      district: '', // required
      barangay: '', // required
      city: '', // required
      contacts: [],
      zipCode: '', // required
      cName: '', // required
      cDesignation: '', // required
      cDepartment: '', // required
      cEmail: '', // required
      cNumber: '', // required
      internalRemarks: '', // required,
      externalRemarks: '',
      affiliationInfos: []
    }))
    dispatch(setAffiliationType({}))
  }

  const debouncedUpdate = debounce((value, name) => handleChange(value, name), 250)
  const debounceContact = debounce((value, name) => HandleAddContactsChange(value, name), 250)
  const debounceAffiliatedInput = debounce((value, name) => handleAffiliatedTextChange(value), 250)

  if (isLoading && id) return <ProgressComponent /> //
  return (
    <React.Fragment>
      <form onSubmit={SubmitForms}>
        <div className={classes.root}>
          <div className={classes.textContainer}>
            <div>FACILITY INFORMATION</div>
            <div>General Information</div>
          </div>
          <div className={classes.firstPartInputFields}>
            <div id='name'>
              <label id='hospitalLabel'>Hospital Name *</label>
              <TextField
                error={errors['name']}
                name='name'
                onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                inputRef={(el) => (hospitalFormRef.current.name = el)}
                disabled={isSubmitted}
              />
            </div>
            <div id='firstLayerFields' className={classes.firstLayerFields}>
              <div id='email'>
                <label id='emailLabel'>Email Address *</label>
                <TextField
                  error={errors['email']}
                  name='email'
                  type={'email'}
                  disabled={isSubmitted}
                  inputRef={(el) => (hospitalFormRef.current.email = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div id='bedCapacity' className={classes.bedCapacity}>
                <label id='bedlabel'>Bed Capacity *</label>
                <TextField
                  error={errors['bedCapacity']}
                  name='bedCapacity'
                  type={'integerOnly'}
                  inputRef={(el) => (hospitalFormRef.current.bedCapacity = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div id='years'>
                <label>Years Operation *</label>
                <CustomTextFieldWithIcons
                  type={'integerOnly'}
                  error={errors['years']}
                  name='years'
                  inputRef={(el) => (hospitalFormRef.current.years = el)}
                  styles={classes.customTag}
                  tags={'years'}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div id='blankSpace'>
                <label>'</label>
                <CustomTextFieldWithIcons
                  name='months'
                  error={errors['months']}
                  type={'integerOnly'}
                  styles={classes.customTag}
                  tags={'months'}
                  inputRef={(el) => (hospitalFormRef.current.months = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
            </div>
            <div id='secondLayerFields' className={classes.secondLayerFields}>
              <div id='dohNumber'>
                <label>DOH License Number</label>
                <TextField
                  name='dohNumber'
                  inputRef={(el) => (hospitalFormRef.current.dohNumber = el)}
                  type={'noSpecialChar'}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div id='dohLicenseType' className={classes.dohLicenseType}>
                <label>DOH License Type</label>
                <TextField
                  name='dohLicenseType'
                  inputRef={(el) => (hospitalFormRef.current.dohLicenseType = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div id='dohLicenseExp'>
                <label>DOH License Expiry Date</label>
                <div id='dohLicenseExp'>
                  <CustomCalendar
                    error={errors['dohLicenseExp']}
                    errors={errors}
                    setErrors={setErrors}
                    id='dohLicenseExp'
                    name='dohLicenseExp'
                    placeholder='Select date'
                    value={form['dohLicenseExp']}
                    onChange={handleDatePicker}
                    disabled={isSubmitted}
                  />
                </div>
              </div>
            </div>
            <div id='thirdLayerFields' className={classes.thirdLayerFields}>
              <div id='philhealthNumber'>
                <label>PhilHealth Accreditation No.</label>
                <CustomTextFieldNumberFormat
                  defaultValue={form['philhealthNumber'] ? form['philhealthNumber'] : philHealthNumber}
                  disabled={isSubmitted}
                  name='philhealthNumber'
                  type={'dashAndNum'}
                  placeholder='Ex.0000-0000000-0'
                  error={errors['philhealthNumber']}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  format='####-#######-#'
                />
              </div>
              <div id='philhealthExpiration'>
                <label>PhilHealth Expiry Date</label>
                <div id='philHealthdiv'>
                  <CustomCalendar
                    error={errors['philhealthExpiration']}
                    errors={errors}
                    setErrors={setErrors}
                    id='philhealthExpiration'
                    name='philhealthExpiration'
                    placeholder='Select date'
                    value={form['philhealthExpiration']}
                    onChange={handleDatePicker}
                    disabled={isSubmitted}
                  />
                </div>
              </div>
              <div id='checkPayee'>
                <label>Check Payee *</label>
                <TextField
                  name='payee'
                  error={errors['payee']}
                  type={'lettersOnly'}
                  inputRef={(el) => (hospitalFormRef.current.payee = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
            </div>
          </div>
          <div className={classes.RadioButtonParts}>
            <div id='hospitalRadioButtons'>
              <FormControl component='fieldset'>
                <FormLabel className={classes.label} component='legend'>
                  Hospital Classification *
                </FormLabel>
                <RadioGroup
                  aria-label='gender'
                  name='classification'
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  value={form['classification']}
                >
                  <FormControlLabel
                    disabled={isSubmitted}
                    value='Primary'
                    control={<Radio color='primary' />}
                    label='Primary'
                  />
                  <FormControlLabel
                    disabled={isSubmitted}
                    value='Secondary'
                    control={<Radio color='primary' />}
                    label='Secondary'
                  />
                  <FormControlLabel
                    disabled={isSubmitted}
                    value='Tertiary'
                    control={<Radio color='primary' />}
                    label='Tertiary'
                  />
                </RadioGroup>
                {errors['classification'] && (
                  <FormHelperText style={{ color: 'red' }}>{errors['classification']} </FormHelperText>
                )}
              </FormControl>
            </div>
            <div id='tinStatusRadioButtons'>
              <FormControl component='fieldset'>
                <FormLabel className={classes.label} component='legend'>
                  TIN Status *
                </FormLabel>
                <RadioGroup
                  aria-label='gender'
                  name='tinStatus'
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  value={form['tinStatus']}
                >
                  <FormControlLabel
                    disabled={isSubmitted}
                    value='12%'
                    control={<Radio color='primary' />}
                    label='VATable'
                  />
                  <FormControlLabel
                    disabled={isSubmitted}
                    value='Non-VaTable'
                    control={<Radio color='primary' />}
                    label='Non-VATable'
                  />
                </RadioGroup>
                {errors['tinStatus'] && (
                  <FormHelperText style={{ color: 'red' }}>{errors['tinStatus']} </FormHelperText>
                )}
              </FormControl>
              <div id='tinStatusDropDown' className={classes.dropDown}>
                <DropDown
                  items={VATable}
                  onChange={handleDropdown}
                  value={form['nonVatable'] && form['nonVatable']}
                  name='nonVatable'
                  disableComponents={form['tinStatus'] === '12%'}
                  readOnly={isSubmitted}
                  error={errors['nonVatable']}
                />
              </div>
            </div>
            <div id='ProcessTypeRadioButtons' className={classes.ProcessTypeRadioButtons}>
              <div id='vatTaxRate' className={classes.vatTaxRate}>
                <label>TAX Rate *</label>
                <div id='taxInput'>
                  <CustomTextFieldWithIcons
                    type={'number'}
                    name='vatTaxRate'
                    error={errors['vatTaxRate']}
                    tags={'%'}
                    inputRef={(el) => (hospitalFormRef.current.vatTaxRate = el)}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                    disabled={isSubmitted}
                  />
                </div>
              </div>
              <div id='radioFields' className={classes.ProcessType}>
                <FormControl component='fieldset'>
                  <FormLabel className={classes.label} component='legend'>
                    Process Type *
                  </FormLabel>
                  <RadioGroup
                    aria-label='gender'
                    name='processType'
                    value={form['processType']}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  >
                    <FormControlLabel
                      disabled={isSubmitted}
                      value='Umbrella'
                      control={<Radio color='primary' />}
                      label='Umbrella'
                    />
                    <FormControlLabel
                      disabled={isSubmitted}
                      value='Regular'
                      control={<Radio color='primary' />}
                      label='Regular'
                    />
                  </RadioGroup>
                  {errors['processType'] && (
                    <FormHelperText style={{ color: 'red' }}>{errors['processType']} </FormHelperText>
                  )}
                </FormControl>
              </div>
            </div>
          </div>
          <div className={classes.SecondPartInputFields}>
            <div id='firstInputLayerFields' className={classes.firstInputLayerFields}>
              <div>
                <label>
                  Hospital Address <br />
                  Unit No./Floor/Bldg.No.
                </label>
                <TextField
                  name='unitFlrBldg'
                  type={'text'}
                  inputRef={(el) => (hospitalFormRef.current.unitFlrBldg = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div>
                <label>
                  <div /> <br /> Building Name
                </label>
                <TextField
                  name='bldgName'
                  type={'text'}
                  inputRef={(el) => (hospitalFormRef.current.bldgName = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div>
                <label>
                  <div /> <br /> Street Name *
                </label>
                <TextField
                  name='street'
                  error={errors['street']}
                  type={'text'}
                  inputRef={(el) => (hospitalFormRef.current.street = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
            </div>
            <div id='SedcondInputLayerFields' className={classes.SecondInputLayerFields}>
              <div>
                <label>Subdivision *</label>
                <TextField
                  name='subdivision'
                  error={errors['subdivision']}
                  type='text'
                  inputRef={(el) => (hospitalFormRef.current.subdivision = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                />
              </div>
              <div>
                <label>Region *</label>
                <AddressDropDown
                  items={regions}
                  value={form['region']}
                  error={errors['region']}
                  name='region'
                  onChange={handleDropdown}
                  disableComponents={isSubmitted}
                />
              </div>
              <div>
                <label>District / Province *</label>
                <AddressDropDown
                  items={provinces}
                  value={isFetching['province'] ? 'Loading...' : form['district']}
                  error={errors['district']}
                  name='district'
                  onChange={handleDropdown}
                  disableComponents={isSubmitted || (provinces instanceof Array && provinces.length <= 0) || isFetching['province']}
                />
              </div>
            </div>
            <div id='ThirdInputLayerFields' className={classes.ThirdInputLayerFields}>
              <div>
                <label>Barangay *</label>
                <AddressDropDown
                  name='barangay'
                  value={isFetching['barangay'] ? 'Loading...' : form['barangay']}
                  error={errors['barangay']}
                  items={barangays}
                  onChange={handleDropdown}
                  disableComponents={isSubmitted || (barangays instanceof Array && barangays.length <= 0) || isFetching['barangay']}
                />
              </div>
              <div>
                <label>City / Municipality *</label>
                <AddressDropDown
                  items={cities}
                  value={isFetching['city'] ? 'Loading...' : form['city']}
                  name='city'
                  error={errors['city']}
                  onChange={handleDropdown}
                  disableComponents={isSubmitted || (cities instanceof Array && cities.length <= 0) || isFetching['city']}
                />
              </div>
              <div>
                <label>Zip Code *</label>
                <TextField
                  name='zipCode'
                  error={errors['zipCode']}
                  type={'noSpecialChar'}
                  inputRef={(el) => (hospitalFormRef.current.zipCode = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  disabled={isSubmitted}
                  inputProps={{ maxLength: 4 }}
                />
              </div>
            </div>
          </div>
          <div className={classes.ContactInformationField}>
            <div>Contact Information</div>
            <div id='InputFields' className={classes.InputFields}>
              <div>
                <div>
                  <label>Full Name *</label>
                  <TextField
                    error={addContactsErrors['cName']}
                    name='cName'
                    type={'noSpecialChar'}
                    inputRef={cName}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                    disabled={isSubmitted}
                  />
                </div>
                <div>
                  <label>Designation *</label>
                  <TextField
                    error={addContactsErrors['cDesignation']}
                    name='cDesignation'
                    type={'text'}
                    inputRef={cDesignation}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                    disabled={isSubmitted}
                  />
                </div>
                <div>
                  <label>Department *</label>
                  <TextField
                    error={addContactsErrors['cDepartment']}
                    name='cDepartment'
                    type={'text'}
                    inputRef={cDepartment}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                    disabled={isSubmitted}
                  />
                </div>
              </div>
              <div>
                <div>
                  <label>Email Address *</label>
                  <TextField
                    type={'email'}
                    error={addContactsErrors['cEmail']}
                    name='cEmail'
                    inputRef={cEmail}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                    disabled={isSubmitted}
                  />
                </div>
                <div>
                  <label>Contact Number *</label>
                  <TextField
                    error={addContactsErrors['cNumber']}
                    name='cNumber'
                    inputRef={cNumber}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                    disabled={isSubmitted}
                  />
                </div>
                <div>
                  <label onClick={handleAddToContacts}>+Add Contact</label>
                </div>
              </div>
            </div>
          </div>
          <div className={classes.ContactInformationBoard}>
            <div>
              <Paper className={classes.ContactInformationTable} elevation={3}>
                <AffiliationTable
                  // HandleRemoveContacts={HandleRemoveContacts}
                  dataheader={dataheader}
                  dataValue={form['contacts'] ? (form['contacts'].length >= 1 ? form['contacts'] : []) : []}
                  handleRemove={handleRemoveContact}
                  startEditing={startEditing}
                  stopEditing={stopEditing}
                  isSubmitted={isSubmitted}
                  handleSave={handleSave}
                  hasError={errors['contacts'] && form['contacts'].length <= 0}
                  editIdx={editIdx}
                />
              </Paper>
              {(errors['contacts'] && form['contacts'].length <= 0) && <p className={classes.contactInfoError}> {errors['contacts']} </p>}
            </div>
          </div>
          <div className={classes.AffiliateForm}>
            <div>Affiliation Information</div>
            <div>
              <label>Existing Affiliated HMO/S</label>
              <Paper className={classes.AffiliationInformationTable} elevation={3}>
                <AffiliationFormInput
                  HandleRemove={HandleRemove} // remove by index
                  handleValue={addToGroup}
                  onClick={affiliatedGroupInput.length <= 0 ? null : handleGroup}
                  AddToGroupsArr={handleGroupValue}
                  HandleEdit={HandleEdit}
                  inputField={
                    <TextField
                      name='affiliateGroup'
                      inputRef={affiliatedGroupRef}
                      placeholder='Type Affiliate group'
                      onChange={e => debounceAffiliatedInput(e.target.value)}
                      disabled={isSubmitted}
                    />
                  }
                  dataheader={[{ prop: 'affiliateGroup' }]}
                />
              </Paper>
            </div>
          </div>
          <div className={classes.RemarksDiv}>
            <div className={styles.RemarksField}>
              <span>
                <div>
                  <label>External Remarks</label>
                  <textarea
                    name='externalRemarks'
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                    ref={(el) => (hospitalFormRef.current.externalRemarks = el)}
                    disabled={isSubmitted}
                  />
                </div>
                <div>
                  <label>Internal Remarks *</label>
                  <textarea
                    name='internalRemarks'
                    className={errors['internalRemarks'] && styles.errorClass}
                    ref={(el) => (hospitalFormRef.current.internalRemarks = el)}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                    disabled={isSubmitted}
                  />
                  <label className={styles.error}>{errors['internalRemarks']} </label>
                </div>
              </span>
            </div>
          </div>
        </div>
        <FooterWithButtons
          footerStatus={'Hospital'}
          onClickSave={handleSubmit}
          isSuccess={isSuccess}
          proceedSubmitForm={proceedSubmitForm}
          setIsSuccess={setIsSuccess}
          disableButton={disabledButton}
          isSubmitted={isSubmitted}
          handleReset={handleResetForm} // reset form when click cancel
          // setDisabledButton={setDisabledButton}
          pathUrl={pathUrl} // use for proceed button
          save={save}
          history={props.history}
        />
        {/* <CustomAlertSnackbar /> */}
      </form>
    </React.Fragment>
  )
}

export default AffiliationForm
