import React from 'react'

import isEmpty from 'lodash/isEmpty'

import classes from './EditPartnerInformation.module.scss'

import MedicalClinicFormValidation from '../Utilities/MedicalClinicFormValidation'

import ClinicProfileContext from '../../context'

import FacilityAssignment from 'Pages/Protected/Partner/Components/FacilityAssignment'

import { dateFormat, handleAddressChaining } from '../../../../../../Components/Utils/utils'

import {
  PartnerInformationFooterButton,
  DialogBoxIfUpdate,
  DialogBoxIfCancel
} from '../../../../../../Components/PartnerInformationFooterButton/PartnerInformationFooterButton'

import CustomAlertSnackbar from '../../../../../../Components/CustomSnackBar/CustomAlertSnackbar'

import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'

import EditInputs from '../Utilities/EditInputs/EditInputs'

import { set, Save } from '../Utilities/setAndSave'

import InputRefs from '../Utilities/InputRefs'

import EditIcon from '@material-ui/icons/Edit'

import TextField from '@material-ui/core/TextField'

const EditMedicalInformation = () => {
  const [openModal, setOpenModal] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)
  const [isSave, setIsSave] = React.useState(false)
  const [isLoading, setIsLoading] = React.useState(false)
  const {
    profile,
    setIsToUpdate,
    setProfile,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    setStatus,
    setCity,
    discountType
  } = React.useContext(ClinicProfileContext)
  const [errors, setErrors] = React.useState({})

  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [provinces, setProvinces] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])

  const [form, setForm] = React.useState({
    facilityAssignment: '',
    medicalPartnerId: '',
    formerName: '',
    clinicName: '',
    assignments: [],
    reason: '',
    status: '',
    exclusiveTo: '',
    tvs: '',
    street: '',
    region: '',
    district: '',
    city: '',
    barangay: '',
    zipCode: '',
    years: '',
    months: '',
    classification: '',
    clinicSchedule: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    checkPayee: '',
    processType: '',
    creditLimit: '',
    paymentTerms: '',
    tinStatus: '',
    vatTaxRate: '',
    facilityDiscount: '',
    dateSubmitted: '',
    dateOfDisaffiliation: '',
    specialInstructions: ''
  })

  React.useEffect(() => {
    set(form, profile, dateFormat, setForm, setBarangays, setCities, setProvinces, setRegions)
  }, [profile])

  const resultRef = InputRefs(form)

  const handleSubmitClose = () => {
    setOpenModal(false)
  }

  const save = async () => {
    setIsLoading(true)

    try {
      const result = await Save(form, profile)
      setIsLoading(false)
      handleSubmitClose(false)
      setProfile(result)
      setIsSave(true)
      setHeaderData({ name: form.clinicName })
      setStatus(result ? result.status : '')
      setCity(result.address ? (result.address ? (result.address.city ? result.address.city : '') : '') : '')
      setTimeout(() => {
        setIsToUpdate(false)
        setIsEdit(false)
      }, 1000)
    } catch (error) {
      console.log('error', error)

      setIsLoading(false)
    }
  }

  const validate = () => {
    const { errors } = MedicalClinicFormValidation(form)
    if (form['status'] !== '') {
      delete errors['status']
    }
    if (isEmpty(errors)) {
      setOpenModal(true)
      setErrors({})
    }
    setErrors(errors)
  }

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const handleChange = ({ currentTarget: input }) => {
    const dateIncludes = ['philhealthExpiration', 'dateSubmitted', 'dateOfDisaffiliation']
    const ClinicForm = { ...form }
    if (dateIncludes.includes(input.name)) {
      ClinicForm[input.name] = dateFormat(input.value)
    }
    const newErrors = { ...errors }
    delete newErrors[input.name]
    setErrors(newErrors)
    ClinicForm[input.name] = input.value
    setForm(ClinicForm)
  }

  const handleDropdown = ({ target: select }) => {
    const ClinicForm = { ...form }
    ClinicForm[select.name] = select.value
    errors[select.name] = false
    setErrors(errors)
    setForm(ClinicForm)
    handleAddressChaining(select.name, select.value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }

  React.useEffect(() => {
    HandleChangeName(profile)
  }, [form.clinicName])

  React.useEffect(() => {
    HandleChangeName(profile)
  }, [isEdit])

  const HandleChangeName = () => {
    if (isEdit) {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(false)
              }}
            />
          </div>
          <TextField
            id='inputName'
            className={classes.TextField}
            autoComplete='off'
            name='clinicName'
            // /inputRef={nameRef}
            value={form['clinicName']}
            onChange={handleChange}
          />
        </div>
      )
    } else {
      setChangeName(
        <div id='changeName' className={classes.changeName}>
          <div id='editIcon' className={classes.editIcon}>
            <EditIcon
              onClick={() => {
                setIsEdit(true)
              }}
            />
          </div>
          {form.clinicName}
        </div>
      )
    }
  }

  const discountTypeData = React.useMemo(() => discountType, [discountType])

  const Information = EditInputs(
    handleChange,
    handleDropdown,
    errors,
    form,
    resultRef,
    setForm,
    setErrors,
    discountTypeData, regions, provinces, cities, barangays, isFetching
  )

  return (
    <React.Fragment>
      <form onSubmit={handleSubmit}>
        <div id='root' className={classes.root}>
          <FacilityAssignment
            form={form}
            setForm={setForm}
            errors={errors}
            setErrors={setErrors}
            reasonRef={resultRef.reason}
            exclusiveRef={resultRef.exclusiveTo}
          />
          <div id='wrapper' className={classes.wrapper}>
            <PartnerViewInformation
              data={Information}
              facilityLabel={'Preferred Medical Partner'}
              listStyles={classes.listStyles}
              firstColumStyle={classes.firstRow}
              secondColumnStyle={classes.secondRow}
              //  ifHastTitle2
              hideStar
              listStyles2={classes.listStyles2}
            />
          </div>
        </div>
        {/* END OF ROOT DIV */}
        <PartnerInformationFooterButton setIsCancel={setIsCancel} handleSubmit={handleSubmit} />
      </form>
      <div>
        <DialogBoxIfUpdate
          handleSubmitClose={handleSubmitClose}
          HandleSave={save}
          isLoading={isLoading}
          openModal={openModal}
        />
      </div>
      <div>
        <DialogBoxIfCancel isCancel={isCancel} setIsCancel={setIsCancel} setIsToUpdate={setIsToUpdate} />
        {isSave && <CustomAlertSnackbar isSave={isSave} setIsSave={setIsSave} />}
      </div>
    </React.Fragment>
  )
}

export default React.memo(EditMedicalInformation, (prevProps, nextProps) => {
  try {
    if (prevProps.Information === nextProps.Information || prevProps.form === nextProps.form) {
      return true
    }
    return false
  } catch (e) {
    console.log('error', e)
  }
})
