import React from 'react'
import RadioGroup from '@material-ui/core/RadioGroup'
import Radio from '@material-ui/core/Radio'
import FormControl from '@material-ui/core/FormControl'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Typography from '@material-ui/core/Typography'
import Button from '@material-ui/core/Button'
import Grid from '@material-ui/core/Grid'
import { useDispatch } from 'react-redux'
import { setAffiliationType, setDentistAffiliation } from 'Pages/Protected/Store/actions/affiliationType'

// Components
import CustomTextField from 'SharedComponents/TextField/CustomTextField'
import DatePicker from '../../../Components/DatePicker/DatePicker'

// Services
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbsNoStepper from '../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'

// Style
import classes from './MedicalPartnerEnrollmentPage.module.scss'

// Redux Actions
import { setDentalClinicAffilForm, setDentistProfile } from '../../../../Store/actions/Partner/dentalClinicAffilForm'

// Constant
import cnts from 'Pages/Protected/Partner/Constants/partnerTypes'
import defaultValues from './defaultValues'
import { setDoctorAffiliation } from '../../../../Store/actions/Partner/doctorAffiliationForm'
import { getDoctorSpecialization } from 'Pages/Protected/Partner/Components/Utils/utils'
import { setDoctorSpecialization } from '../../../../Store/actions/Partner/autoSuggestAction'
import { setMedClinicFormData } from 'Pages/Protected/Store/actions/Partner/medicalClinicAffiliationForm'
import { setAffiliationFormData } from 'Pages/Protected/Store/actions/Partner/hospitalAffiliationForm'

const breadcrumbs = {
  dashboard: {
    text: 'Dashboard',
    to: '/'
  },
  user: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  affiliation: {
    text: 'MEDICAL PARTNER AFFILIATION ',
    to: '/medical-partner-enrollment'
  }
}

export default function MedicalPartnerEnrollmentPage (props) {
  let dispatch = useDispatch()
  let headerTitle = 'Medical Partner Enrollment'
  BreadcrumbsService.setItems(breadcrumbs)
  BreadcrumbsService.setSelectedItemsWithoutHome(['user', 'affiliation'])

  const [value, setValue] = React.useState(cnts.PARTNER.TYPE.HOSPITAL)
  const [disabled, setDisabled] = React.useState(true)

  const [form, setForm] = React.useState({
    dateReceived: new Date().toISOString(),
    type: cnts.PARTNER.TYPE.HOSPITAL,
    affiliationType: 'new_affiliation',
    mno: ''
  })

  React.useEffect(() => {
    dispatch(setAffiliationType(form))
    dispatch(setDentistAffiliation({}))
    dispatch(setDentalClinicAffilForm(defaultValues.dentalClinic))
    dispatch(setDentistProfile(defaultValues.dentist))
    dispatch(setDoctorAffiliation(defaultValues.doctor))
    dispatch(setMedClinicFormData(defaultValues.getMedicalClinic))
    dispatch(setAffiliationFormData(defaultValues.hospitalForms))
    fetchDoctorSpecialization()
  }, [])

  const fetchDoctorSpecialization = async () => {
    let data = await getDoctorSpecialization()
    dispatch(setDoctorSpecialization(data))
  }


  const handleRadioChange = ({target: radio}) => {
    const newForm = {...form}
    newForm.type = radio.value
    setValue(radio.value)
    if (radio.value === cnts.PARTNER.TYPE.DOCTOR || radio.value === cnts.PARTNER.TYPE.OTHER_SERVICES) {
      setDisabled(false)
    } else {
      setDisabled(true)
      newForm.affiliationType = 'new_affiliation'
    }
    setForm(newForm)
    dispatch(setAffiliationType(newForm))
  }
  const handleAffiliationType = ({target: radio}) => {
    const newForm = {...form}
    newForm.affiliationType = radio.value
    setForm(newForm)
    dispatch(setAffiliationType(newForm))

  }
  const handleChange = ({target: input}) => {
    const newForm = {...form}
    newForm.mno = input.value
    setForm(newForm)
    dispatch(setAffiliationType(newForm))
  }
  const handleDateChange = (date) => {
    const newForm = {...form}
    newForm.dateReceive = new Date(date).toISOString()
    setForm(newForm)
    dispatch(setAffiliationType(newForm))
  }

  const handleSubmit = event => {
    event.preventDefault()

    if (value === cnts.PARTNER.TYPE.HOSPITAL) {
      props.history.push('/hospital-affiliation-form')
    } else if (value === cnts.PARTNER.TYPE.DENTAL_CLINIC) {
      props.history.push('/dental-clinic-affiliation-form')
    } else if (value === cnts.PARTNER.TYPE.CLINIC) {
      props.history.push('/medical-clinic-affiliation-form')
    } else if (value === cnts.PARTNER.TYPE.DOCTOR) {
      props.history.push('/doctor-affiliation-form')
    } else {
      props.history.push('/special-other-services-form')
    }
  }

  const MedicalPartnerEnrollmentFooter = props => {
    return (
      <div className={classes.FooterButtonContainer}>
        <span className={classes.FooterButtonWrapper}>
          <div />
          <div />
          <div>
            <div>
              <Button type='submit' variant='contained' className={classes.proceedButton}>
                Proceed
              </Button>
            </div>
          </div>
        </span>
      </div>
    )
  }

  return (
    <>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbsNoStepper id='header-with-breadcrumbs' HeaderTitle={headerTitle} />
        <form onSubmit={handleSubmit}>
          <FormControl component='fieldset' className={classes.formControl}>
            <div className={classes.datepicker}>
            <DatePicker id='mpeDateReceived' onChange={handleDateChange} label='Date Received' />
            </div>
            <div className={classes.nature}>
              <Typography style={{ color: '#192965', marginBottom: '0.5rem' }} variant='h6' component='h6'>
                Specify nature of the form
              </Typography>
            </div>
            <div className={classes.medaffitype}>
              <Typography style={{ color: '#192965', marginBottom: '0.2rem' }} fontSize='13'>
                Medical Partner Affiliation Type
              </Typography>
            </div>
            <div className={classes.radiogroup}>
              <RadioGroup
                aria-label='_medical_partner_affiliation_type_1'
                name='medical_partner_affiliation_type_1'
                id='medicalPartnerAffiliationType1'
                defaultValue={cnts.PARTNER.TYPE.HOSPITAL}
                row
                value={value}
                onChange={handleRadioChange}
              >
                <Grid container spacing={3}>
                  <Grid item xs={12}>
                    <FormControlLabel
                      value={cnts.PARTNER.TYPE.HOSPITAL}
                      className={classes['radio-hospital-affi']}
                      control={<Radio color='primary' />}
                      label={
                        <Typography style={{ color: '#192965' }} fontSize='10'>
                          Hospital Affiliation
                        </Typography>
                      }
                    />
                    <FormControlLabel
                      value={cnts.PARTNER.TYPE.DENTAL_CLINIC}
                      className={classes['radio-dental-affi']}
                      control={<Radio color='primary' />}
                      label={
                        <Typography style={{ color: '#192965' }} fontSize='10'>
                          Dental Clinic Affiliation
                        </Typography>
                      }
                    />
                  </Grid>
                  <Grid item xs={12}>
                    <FormControlLabel
                      value={cnts.PARTNER.TYPE.CLINIC}
                      className={classes['radio-med-affi']}
                      control={<Radio color='primary' />}
                      label={
                        <Typography style={{ color: '#192965' }} fontSize='10'>
                          Medical Clinic Affiliation
                        </Typography>
                      }
                    />
                    <FormControlLabel
                      value={cnts.PARTNER.TYPE.DOCTOR}
                      className={classes['radio-doctor-affi']}
                      control={<Radio color='primary' />}
                      label={
                        <Typography style={{ color: '#192965' }} fontSize='10'>
                          Doctor Affiliation
                        </Typography>
                      }
                    />
                    <FormControlLabel
                      value={cnts.PARTNER.TYPE.OTHER_SERVICES}
                      className={classes['radio-special']}
                      control={<Radio color='primary' />}
                      label={
                        <Typography style={{ color: '#192965' }} fontSize='10'>
                          Special/Other Services
                        </Typography>
                      }
                    />
                  </Grid>
                </Grid>
              </RadioGroup>
            </div>
            <div className={classes.affitype}>
              <Typography style={{ color: '#192965', marginBottom: '0.2rem' }} fontSize='13'>
                Affiliation Type
              </Typography>
            </div>
            <div className={classes.radiogroup}>
              <RadioGroup aria-label='_affiliation_type' id='affiliationType' name='affiliation_type' onChange={handleAffiliationType} value={form['affiliationType']} row>
                <FormControlLabel
                  value='new_affiliation'
                  className={classes['radio-newaffi']}
                  control={<Radio color='primary' />}
                  label={
                    <Typography style={{ color: '#192965' }} fontSize='10'>
                      New Affiliation
                    </Typography>
                  }
                />
                <FormControlLabel
                  value='additional_affiliation'
                  className={classes['radio-addaffi']}
                  control={<Radio color='primary' />}
                  label={
                    <Typography style={{ color: '#192965' }} fontSize='10'>
                      Additional Affiliation
                    </Typography>
                  }
                  disabled={disabled}
                />
              </RadioGroup>
            </div>
            <div className={classes.mnofield}>
              <CustomTextField id='assignedMno' onChange={handleChange} name='nameofAs' label='Name of Assigned MNO' />
            </div>
            <div className={classes.footer}>
              <MedicalPartnerEnrollmentFooter />
            </div>
            <div />
          </FormControl>
        </form>
      </div>
    </>
  )
}
