import React from 'react'
import TextField from '@material-ui/core/TextField'
import CheckIcon from '@material-ui/icons/Check'
import CancelIcon from '@material-ui/icons/Cancel'
import classes from './InlineText.module.scss'

let errorsData = {
  firstName: '',
  lastName: '',
  email: '',
  password: '',
  username: ''
}

const Form = props => {
  const { x } = props
  const [values, setValues] = React.useState(x)
  // const [errors, setErrors] = React.useState(errorsData)

  const change = e => {
    const { name, value } = e.target
    setValues({ ...values, [name]: value })
  }

  // const validate = () => {
  //   let isError = false
  //   const NewErrors = {
  //     firstName: '',
  //     lastName: '',
  //     username: '',
  //     email: '',
  //     password: ''
  //   }

  //   setErrors(NewErrors)

  //   return isError
  // }

  const onSubmit = e => {
    e.preventDefault()
    props.handleSave(props.i, values)
    // const err = validate()
    // if (!err) {
    //   props.handleSave(props.i, values)
    // }
  }

  const { header, i } = props

  return [
    header.map((y, k) => (
      <TextField
        variant='outlined'
        name={y.prop}
        onChange={change}
        className={classes.textEdit}
        value={values[y.prop]}
        // errorText={errors[y.prop]}
      />
    )),

    <div className={classes.checkIcon}>
      <CheckIcon onClick={onSubmit} />
      <CancelIcon onClick={props.stopEditing} />
    </div>
  ]
}

export default Form
