import React from 'react'
import { useSelector, useDispatch } from 'react-redux'

// Library
import debounce from 'lodash/debounce'
import moment from 'moment'

// Material UI
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'
import Paper from '@material-ui/core/Paper'
import FormHelperText from '@material-ui/core/FormHelperText'

// Shared Components
import TextField from '../../../../../Components/JoiValidationInputField'
import CustomTextFieldNumberFormat from '../../../../../Components/JoiValidationInputFieldNumberFormat'
import DropDown from '../../../../../Components/CustomDrowpdown'
import AffiliationTable from '../../../../../Components/AffiliationTable/AffiliationTable1'
import AffiliationFormInput from '../../../../../Components/AfiiliationTableWithInput/AfiiliationTableWithInput'
import DatePicker from '../../../../../Components/DatePicker/DatePicker'
import CustomCalendar from '../../../../../Components/DatePicker/DatePickerInlineEdit'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'
import FooterWithButtons from '../FooterWithButtons/FooterWithButtons'
import { setMedClinicFormData } from 'Pages/Protected/Store/actions/Partner/medicalClinicAffiliationForm'
// import history from '../../../../../Components/History/History'

// Validations
import MedicalClinicFormValidation from './MedicalClinicFormValidation'
import ContactValidation from './ContactValidation'
import { NonVatableData } from '../../../../Components/Placeholders/Placeholders'

// Styles
import classes from './AffiliationForm.module.scss'
import styles from '../../../../../Components/TwoTextArea/TwoTextArea.module.scss'
import { setAffiliationType } from 'Pages/Protected/Store/actions/affiliationType'
import medicalInputFieldArr from '../Utilities/inputFieldArr'
import { useParams } from 'react-router-dom'
import { mapAndFetchMedical } from './services/medicalService'
import AddressChaining from '../../../../../../../../Constant/chaining-address/index'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import ProgressComponent from 'Pages/Protected/Partner/AffiliationFormEncoder/Components/ProgressComponent/ProgressComponent'
import { isDateValid } from 'Pages/Protected/Partner/Components/DatePicker/DatePickerUtils'
import { fetchAddress, handleAddressChaining, fetchRegion } from 'Pages/Protected/Partner/Components/Utils/utils'
import AddressDrowpdown from 'Pages/Protected/Partner/Components/AddressDrowpdown'

const AffiliationForm = props => {
  let { id } = useParams()
  let dispatch = useDispatch()
  let { medClinicForms } = useSelector(state => state.medClinicAffiliationForm)

  let cName = React.useRef()
  let cDesignation = React.useRef()
  let cDepartment = React.useRef()
  let cEmail = React.useRef()
  let cNumber = React.useRef()
  let affiliatedGroupRef = React.useRef()
  const formInputRef = React.useRef({ ...medClinicForms })

  const [isLoading, setIsLoading] = React.useState(false)
  const [editIdx, setEditIdx] = React.useState(-1)
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [isSuccess, setIsSuccess] = React.useState(false)
  const [affiliatedGroupInput, setAffiliatedGroupInput] = React.useState('')
  const [addToGroup, setAddToGroups] = React.useState([])
  const [VATable, setVATable] = React.useState([])
  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [provinces, setProvinces] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])
  const [errors, setErrors] = React.useState({})
  const [addContactsErrors, setAddContactsError] = React.useState({})
  const [dateValue, setDateValue] = React.useState({})
  const [contactsForm, setContactsForm] = React.useState({
    cName: '', // required
    cDesignation: '', // required
    cDepartment: '', // required
    cEmail: '', // required
    cNumber: '' // required
  })

  const [medicalId, setMedicalId] = React.useState('')
  const [isSubmitted, setIsSubmitted] = React.useState(false)
  const dataheader = [
    { name: 'Full Name', prop: 'cName' },
    { name: 'Designation', prop: 'cDesignation' },
    { name: 'Department', prop: 'cDepartment' },
    { name: 'Email Address', prop: 'cEmail' },
    { name: 'Contact Number', prop: 'cNumber' }
  ]

  const [form, setForm] = React.useState({...medClinicForms})

  React.useEffect(() => {
    // reset non-vatable dropdwown
    if (form['tinStatus'] === '12%') {
      const newForm = { ...form }
      newForm['nonVatable'] = ''
      setForm(newForm)
      delete errors['nonVatable']
    }
  }, [form['tinStatus']])

  React.useEffect(() => {
    if (id) {
      fetchData(id)
      setMedicalId(id)
    } else {
      setAddToGroups(form['affiliationInfos'] ? form['affiliationInfos'] : [])
      try {
        mapFieldsToRef(form)
      } catch (error) {
        console.log('maps to props ', error)
      }
    }
    if (affiliationType.hasOwnProperty('id')) {
      setMedicalId(affiliationType.id)
    }
    if (affiliationType['stageName'] === 'AFFILIATION_APPROVAL_FOR_OIC') {
      setIsSubmitted(true)
    }
    fetchRegion(setRegions)
  }, [])



  const mapFieldsToRef = form => {
    if (form.name) {
      form.medName = form.name
    }
    for (let key in form) {
      if (medicalInputFieldArr.includes(key)) {
        formInputRef.current[key].value = form[key]
      }
    }
  }

  const fetchData = async id => {
    setIsLoading(true)
    try {
      const { affil, medForm } = await mapAndFetchMedical(id)
      setForm({...medForm})
      setAddToGroups([...addToGroup, ...medForm['affiliationInfos']])
      fetchAddress(medForm, setBarangays, setCities, setProvinces)
      dispatch(setMedClinicFormData({...medForm}))
      dispatch(setAffiliationType({...affil}))
      setIsLoading(false)
      if (affil.stageName === 'AFFILIATION_APPROVAL_FOR_OIC') {
        setIsSubmitted(true)
      } else {
        setIsSubmitted(false)
      }
      try {
        mapFieldsToRef({...medForm})
      } catch (error) {
        console.log('error mapping input field', error)
      }
    } catch (error) {
      setIsLoading(false)
      console.log('eerrorr', error)
    }
  }

  const proceedSubmitForm = () => {
    if (props.formSubmit) {
      props.formSubmit()
    }
  }

  const save = async () => {
    const medForm = { ...form }
    const newForm = renameMedName(medForm, 'medName', 'name')
    let res = {}

    try {
      if (!medicalId) {
        affiliationType.stageName = 'ENCODING'
        affiliationType.dateSubmitted = new Date().toISOString()
        affiliationType.status = 'Active'
        affiliationType.affiliationStatus = 'In-Progress'
        affiliationType.source = 'PNM'
        const { data: { data: { id } } } = await AffiliationEncoderService.createFacilities({ ...affiliationType, ...newForm })
        affiliationType.id = id
        setMedicalId(id)
        dispatch(setAffiliationType(affiliationType))
      } else {
        res = await AffiliationEncoderService.updateFacilities(affiliationType.id, { ...affiliationType, ...newForm })
      }
      const origForm = renameMedName(newForm, 'name', 'medName')
      dispatch(setMedClinicFormData(origForm))
      setIsSuccess(true)
    } catch (error) {
      console.log('errors in creating medical doctor', error)
    }
  }

  const renameMedName = (form, toReplaceName, name) => {
    if (form.hasOwnProperty(toReplaceName)) { // changing 'clinicName' to 'name' needed for backend
      form[name] = form[toReplaceName]
      delete form[toReplaceName]
      return form
    }
  }

  const validate = () => {
    const { errors: newErrors, isValid } = MedicalClinicFormValidation(form, errors, dateValue)
    console.log('errors sa validate', newErrors)
    if (isValid) {
      setErrors({})
      save()
    } else {
      setErrors(newErrors)
    }
  }

  const handleRemoveContact = index => {
    const newForm = { ...form }
    const contacts = [...newForm['contacts']]

    const res = contacts.filter((contact, i) => i !== index)
    newForm['contacts'] = res
    setForm(newForm)
    dispatch(setMedClinicFormData(newForm))
  }

  const handleRemove = React.useCallback(
    i => {
      setAddToGroups(addToGroup => addToGroup.filter((row, j) => j !== i))
    },
    [addToGroup]
  )

  const startEditing = i => {
    setEditIdx(i)
  }

  const stopEditing = () => {
    setEditIdx(-1)
  }

  const handleSave = (i, x) => {
    const newForm = { ...form }
    const contacts = [...newForm['contacts']]
    const editedValue = contacts.map((row, j) => (j === i ? x : row))
    newForm['contacts'] = editedValue
    setForm(newForm)
    stopEditing()
  }

  const disabledButton = React.useMemo(() => {
    const { isValid } = MedicalClinicFormValidation(form, errors, dateValue)
    if (isValid && form['contacts'].length > 0) {
      return false
    } else {
      return true
    }
  }, [form])

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }

  const handleProceed = e => {
    e.preventDefault()
    if (!isSubmitted) {
      validate()
    }
    props.history.push('/medical-clinic-affiliation-requirements')
  }

  const SubmitForms = e => {
    e.preventDefault()
    props.formSubmit()
  }

  const handleChange = (value, name) => {
    const medClinicForm = { ...form }
    const newErrors = { ...errors }
    delete newErrors[name]
    setErrors(newErrors)
    medClinicForm[name] = value
    if (medClinicForm[name] !== '12%') {
      setVATable(NonVatableData)
    } else {
      setVATable([])
    }
    dispatch(setMedClinicFormData(medClinicForm))
    setForm(medClinicForm)
  }

  const handleAddContactsChange = (value, name) => {
    const addContactsForm = { ...contactsForm }
    const newErrors = { ...addContactsErrors }
    delete newErrors[name]
    setAddContactsError(newErrors)
    addContactsForm[name] = value
    dispatch(setMedClinicFormData(addContactsForm))
    setContactsForm(addContactsForm)
  }

  const handleDatePicker = (name, date) => {
    let dateVal = { ...dateValue }
    let newErrors = { ...errors }
    const { isValid, dateFormatted } = isDateValid(date)
    if (isValid) {
      delete newErrors[name]
      const medClinicForm = { ...form }
      medClinicForm[name] = dateFormatted
      dispatch(setMedClinicFormData(medClinicForm))
      setForm(medClinicForm)
      setErrors(newErrors)
      delete dateVal[name]
      setDateValue(dateVal)
    } else {
      if (date !== '') {
        dateVal[name] = date
        setDateValue(dateVal)
        newErrors[name] = 'Invalid date format'
      } else {
        delete newErrors[name]
        delete dateVal[name]
        setDateValue(dateVal)
      }
      setErrors(newErrors)
    }
  }

  const handleDropdown = ({ target: select }) => {
    const medClinicForm = { ...form }
    medClinicForm[select.name] = select.value
    delete errors[select.name]
    setErrors(errors)
    dispatch(setMedClinicFormData(medClinicForm))
    setForm(medClinicForm)
    handleAddressChaining(select.name, select.value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching)
  }


  const handleAddToContacts = () => {
    const copiedForm = { ...contactsForm }
    const medClinicForm = { ...form }
    const { errors, isValid } = ContactValidation(copiedForm)
    if (isValid) {
      const cForm = { ...contactsForm }
      medClinicForm['contacts'] = [...medClinicForm['contacts'], copiedForm]
      setForm(medClinicForm)
      dispatch(setMedClinicFormData(medClinicForm))
      setAddContactsError(errors)
      cForm.cDepartment = ''
      cForm.cDesignation = ''
      cForm.cEmail = ''
      cForm.cName = ''
      cForm.cNumber = ''
      cName.current.value = ''
      cDesignation.current.value = ''
      cDepartment.current.value = ''
      cEmail.current.value = ''
      cNumber.current.value = ''
      setContactsForm(copiedForm)
    } else {
      setAddContactsError(errors)
    }
  }

  const handleGroup = () => {
    let obj = {}
    obj.affiliateGroup = affiliatedGroupInput
    if (obj.affiliateGroup.length > 0) {
      setAddToGroups([...addToGroup, obj])
      dispatch(setMedClinicFormData(form))
      affiliatedGroupRef.current.value = ''
      setAffiliatedGroupInput('')
    }
  }

  const HandleEdit = React.useCallback(
    (i, x) => {
      setAddToGroups((addToGroup) => addToGroup.map((row, j) => (j === i ? x : row)))
      console.log('handle edir affi', addToGroup)
    },
    [addToGroup]
  )

  const handleGroupValue = data => {
    const newForm = { ...form }
    newForm['affiliationInfos'] = data
    setForm(newForm)
    dispatch(setMedClinicFormData(newForm))
    props.AddToGroups(data)
  }

  const handleAffiliatedTextChange = val => {
    setAffiliatedGroupInput(val)
  }

  const debouncedUpdate = debounce((value, name) => handleChange(value, name), 250)
  const debounceContact = debounce((value, name) => handleAddContactsChange(value, name), 250)
  const debounceAffiliatedInput = debounce((value, name) => handleAffiliatedTextChange(value), 250)

  if (isLoading) return <ProgressComponent />

  return (
    <React.Fragment>
      <form onSubmit={SubmitForms}>
        <div className={classes.root}>
          <div className={classes.textContainer}>
            <div>FACILITY INFORMATION</div>
            <div>General Information</div>
          </div>
          <div className={classes.firstPartInputFields}>
            <div id='name'>
              <label id='medicalClinicLabel'>Medical Clinic Name *</label>
              <TextField
                disabled={isSubmitted}
                error={errors['medName']}
                name='medName'
                type={'text'}
                inputRef={el => (formInputRef.current.medName = el)}
                onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
              />
            </div>
            <div id='firstLayerFields' className={classes.firstLayerFields}>
              <div id='email'>
                <label id='emailLabel'>Email Address *</label>
                <TextField
                  disabled={isSubmitted}
                  error={errors['email']}
                  name='email'
                  type={'email'}
                  inputRef={el => (formInputRef.current.email = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div id='yearsMonths'>
                <label>Years Operation *</label>
                <div id='twoInputFields'>
                  <div id='years'>
                    <CustomTextFieldWithIcons
                      type={'integerOnly'}
                      disabled={isSubmitted}
                      error={errors['years']}
                      name='years'
                      styles={classes.customTag}
                      tags={'years'}
                      inputRef={el => (formInputRef.current.years = el)}
                      onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                    />
                  </div>
                  <div id='months'>
                    <CustomTextFieldWithIcons
                      name='months'
                      disabled={isSubmitted}
                      error={errors['months']}
                      type={'integerOnly'}
                      styles={classes.customTag}
                      tags={'months'}
                      inputRef={el => (formInputRef.current.months = el)}
                      onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div id='secondLayerFields' className={classes.secondLayerFields}>
              <div id='dohNumber'>
                <label>DOH License Number</label>
                <TextField
                  disabled={isSubmitted}
                  name='dohNumber'
                  type={'noSpecialChar'}
                  inputRef={el => (formInputRef.current.dohNumber = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div id='dohLicenseType' className={classes.dohLicenseType}>
                <label>DOH License Type</label>
                <TextField
                  disabled={isSubmitted}
                  name='dohLicenseType'
                  type={'noSpecialChar'}
                  inputRef={el => (formInputRef.current.dohLicenseType = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div id='dohLicenseExp'>
                <label>DOH License Expiry Date</label>
                <div id='dohDatePicker'>
                  <CustomCalendar
                    error={errors['dohLicenseExp']}
                    errors={errors}
                    setErrors={setErrors}
                    id='dohLicenseExp'
                    name='dohLicenseExp'
                    placeholder='Select date'
                    value={form['dohLicenseExp']}
                    onChange={handleDatePicker}
                    disabled={isSubmitted}
                  />
                </div>
              </div>
            </div>
            <div id='thirdLayerFields' className={classes.thirdLayerFields}>
              <div id='philhealthNumber'>
                <label>PhilHealth Accreditation No.</label>
                <CustomTextFieldNumberFormat
                  disabled={isSubmitted}
                  name='philhealthNumber'
                  type={'dashAndNum'}
                  defaultValue={form['philhealthNumber']}
                  error={errors['philhealthNumber']}
                  // inputRef={el => (formInputRef.current.philhealthNumber = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  format='####-#######-#'
                />
              </div>
              <div id='philhealthExpiration'>
                <label>PhilHealth Expiry Date</label>
                <div id='philHealthdiv'>
                  <CustomCalendar
                    error={errors['philhealthExpiration']}
                    errors={errors}
                    setErrors={setErrors}
                    id='philhealthExpiration'
                    name='philhealthExpiration'
                    placeholder='Select date'
                    value={form['philhealthExpiration']}
                    onChange={handleDatePicker}
                    disabled={isSubmitted}
                  />
                  {/* <DatePicker disabled={isSubmitted} name='philhealthExpiration' onChange={handleDatePicker} /> */}
                </div>
              </div>
              <div id='checkPayee'>
                <label>Check Payee *</label>
                <TextField
                  disabled={isSubmitted}
                  name='payee'
                  inputRef={el => (formInputRef.current.payee = el)}
                  error={errors['payee']}
                  type={'text'}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
            </div>
          </div>
          <div className={classes.radioButtonParts}>
            <div id='medClinicRadioButtons'>
              <FormControl component='fieldset'>
                <FormLabel className={classes.label} component='legend'>
                Medical Clinic Classification *
                </FormLabel>

                <RadioGroup
                  id='classification'
                  aria-label='gender'
                  name='classification'
                  value={form['classification']}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                >
                  <FormControlLabel value='Full Service' control={<Radio color='primary' disabled={isSubmitted} />} label='Full Service' />
                  <FormControlLabel value='Diagnostics' control={<Radio color='primary' disabled={isSubmitted} />} label='Diagnostics' />
                  <FormControlLabel value='Specialty' control={<Radio color='primary' disabled={isSubmitted} />} label='Specialty' />
                </RadioGroup>
                {errors['classification'] && (
                  <FormHelperText style={{ color: 'red' }}>{errors['classification']} </FormHelperText>
                )}
              </FormControl>
            </div>
            <div id='tinStatusRadioButtons'>
              <FormControl component='fieldset'>
                <FormLabel className={classes.label} component='legend'>
                  TIN Status *
                </FormLabel>
                <RadioGroup
                  id='tinStatus'
                  aria-label='gender'
                  name='tinStatus'
                  disabled={isSubmitted}
                  value={form['tinStatus']}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                >
                  <FormControlLabel value='12%' control={<Radio color='primary' disabled={isSubmitted} />} label='VATable' />
                  <FormControlLabel value='Non-VaTable' control={<Radio color='primary' disabled={isSubmitted} />} label='Non-VATable' />
                </RadioGroup>
                {errors['tinStatus'] && (
                  <FormHelperText style={{ color: 'red' }}>{errors['tinStatus']} </FormHelperText>
                )}
              </FormControl>
              <div id='tinStatusDropDown' className={classes.dropDown}>
                <DropDown
                  id='nonVatable'
                  items={VATable}
                  onChange={handleDropdown}
                  value={form['nonVatable']}
                  name='nonVatable'
                  disableComponents={form['tinStatus'] === '12%' || isSubmitted}
                  error={errors['nonVatable']}
                />
              </div>
            </div>
            <div id='processTypeRadioButtons' className={classes.processTypeRadioButtons}>
              <div id='vatTaxRate' className={classes.TaxRate}>
                <label>TAX Rate *</label>
                <div id='vatTaxRate'>
                  <CustomTextFieldWithIcons
                    type={'number'}
                    name='vatTaxRate'
                    error={errors['vatTaxRate']}
                    disabled={isSubmitted}
                    inputRef={el => (formInputRef.current.vatTaxRate = el)}
                    tags={'%'}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  />
                </div>
              </div>
              <div id='radioFields' className={classes.processType}>
                <FormControl component='fieldset'>
                  <FormLabel className={classes.label} component='legend'>
                    Process Type *
                  </FormLabel>
                  <RadioGroup
                    disabled={isSubmitted}
                    id='processType'
                    aria-label='gender'
                    name='processType'
                    value={form['processType']}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  >
                    <FormControlLabel value='Umbrella' control={<Radio color='primary' disabled={isSubmitted} />} label='Umbrella' />
                    <FormControlLabel value='Regular' control={<Radio color='primary' disabled={isSubmitted} />} label='Regular' />
                  </RadioGroup>
                  {errors['processType'] && (
                    <FormHelperText style={{ color: 'red' }}>{errors['processType']} </FormHelperText>
                  )}
                </FormControl>
              </div>
            </div>
          </div>
          <div className={classes.secondPartInputFields}>
            <div id='firstInputLayerFields' className={classes.firstInputLayerFields}>
              <div id='unitFlrBldg'>
                <label>
                Medical Clinic Address <br />
                  Unit No./Floor/Bldg.No.
                </label>
                <TextField
                  disabled={isSubmitted}
                  id='unitFlrBldg'
                  name='unitFlrBldg'
                  type={'text'}
                  inputRef={el => (formInputRef.current.unitFlrBldg = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div>
                <label>
                  <div /> <br /> Building Name
                </label>
                <TextField
                  disabled={isSubmitted}
                  id='bldgName'
                  name='bldgName'
                  type={'text'}
                  inputRef={el => (formInputRef.current.bldgName = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div>
                <label>
                  <div /> <br /> Street Name *
                </label>
                <TextField
                  disabled={isSubmitted}
                  id='street'
                  name='street'
                  error={errors['street']}
                  inputRef={el => (formInputRef.current.street = el)}
                  type={'text'}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
            </div>
            <div id='secondInputLayerFields' className={classes.secondInputLayerFields}>
              <div>
                <label>Subdivision *</label>
                <TextField
                  disabled={isSubmitted}
                  id='subdivision'
                  name='subdivision'
                  error={errors['subdivision']}
                  type='text'
                  inputRef={el => (formInputRef.current.subdivision = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div>
                <label>Region *</label>
                <AddressDrowpdown
                  disabled={isSubmitted}
                  id='region'
                  items={regions}
                  value={form['region'] || ''}
                  error={errors['region']}
                  name='region'
                  onChange={handleDropdown}
                />
              </div>
              <div>
                <label>District / Province *</label>
                <AddressDrowpdown
                  disableComponents={isSubmitted ||(provinces instanceof Array && provinces.length <= 0) || isFetching['province']}
                  id='district'
                  items={provinces}
                  value={isFetching['province'] ? 'Loading...' : form['district']}
                  error={errors['district']}
                  name='district'
                  onChange={handleDropdown}
                />
              </div>
            </div>
            <div id='thirdInputLayerFields' className={classes.thirdInputLayerFields}>
              <div>
                <label>Barangay *</label>
                <AddressDrowpdown
                  disableComponents={isSubmitted || (barangays instanceof Array && barangays.length <= 0) || isFetching['barangay']}
                  id='barangay'
                  name='barangay'
                  value={isFetching['barangay'] ? 'Loading...' : form['barangay']}
                  error={errors['barangay']}
                  items={barangays}
                  onChange={handleDropdown}
                />
              </div>
              <div>
                <label>City / Municipality *</label>
                <AddressDrowpdown
                  disableComponents={isSubmitted | (cities instanceof Array && cities.length <= 0) || isFetching['city']}
                  id='city'
                  items={cities}
                  value={isFetching['city'] ? 'Loading...' : form['city']}
                  name='city'
                  error={errors['city']}
                  onChange={handleDropdown}
                />
              </div>
              <div>
                <label>Zip Code *</label>
                <TextField
                  disabled={isSubmitted}
                  id='zipCode'
                  name='zipCode'
                  error={errors['zipCode']}
                  styled={{fontSize: 10}}
                  type={'noSpecialChar'}
                  inputRef={el => (formInputRef.current.zipCode = el)}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  inputProps={{ maxLength: 4 }}
                />
              </div>
            </div>
            <div id='clinicScheduleAndRoomNumber' className={classes.clinicScheduleAndRoomNumber}>
              <div id='clinicSchedule'>
                <label id='clinicScheduleLabel'>Clinic Schedule *</label>
                <TextField
                  disabled={isSubmitted}
                  id='clinicSchedule'
                  name='clinicSchedule'
                  error={errors['clinicSchedule']}
                  inputRef={el => (formInputRef.current.clinicSchedule = el)}
                  type={'text'}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
              <div id='roomNumber'>
                <label id='roomNumberlabel'>Room Number *</label>
                <TextField
                  disabled={isSubmitted}
                  id='roomNumber'
                  name='roomNumber'
                  error={errors['roomNumber']}
                  inputRef={el => (formInputRef.current.roomNumber = el)}
                  type={'text'}
                  onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                />
              </div>
            </div>
          </div>
          <div className={classes.contactInformationField}>
            <div>Contact Information</div>
            <div id='inputFields' className={classes.inputFields}>
              <div>
                <div>
                  <label>Full Name *</label>
                  <TextField
                    disabled={isSubmitted}
                    error={addContactsErrors['cName']}
                    name='cName'
                    type={'text'}
                    inputRef={cName}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                  />
                </div>
                <div>
                  <label>Designation *</label>
                  <TextField
                    disabled={isSubmitted}
                    error={addContactsErrors['cDesignation']}
                    name='cDesignation'
                    type={'text'}
                    inputRef={cDesignation}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                  />
                </div>
                <div>
                  <label>Department *</label>
                  <TextField
                    disabled={isSubmitted}
                    error={addContactsErrors['cDepartment']}
                    name='cDepartment'
                    type={'text'}
                    inputRef={cDepartment}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                  />
                </div>
              </div>
              <div>
                <div>
                  <label>Email Address *</label>
                  <TextField
                    disabled={isSubmitted}
                    type={'email'}
                    error={addContactsErrors['cEmail']}
                    name='cEmail'
                    inputRef={cEmail}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                  />
                </div>
                <div>
                  <label>Contact Number *</label>
                  <TextField
                    disabled={isSubmitted}
                    error={addContactsErrors['cNumber']}
                    name='cNumber'
                    type={'text'}
                    inputRef={cNumber}
                    onChange={({ target: { value, name } }) => debounceContact(value, name)}
                  />
                </div>
                <div>
                  <label onClick={handleAddToContacts}>+Add Contact</label>
                </div>
              </div>
            </div>
          </div>
          <div className={classes.contactInformationBoard}>
            <div>
              <Paper className={classes.contactInformationTable} elevation={3}>
                <AffiliationTable
                  dataheader={dataheader}
                  dataValue={form['contacts']}
                  handleRemove={handleRemoveContact}
                  startEditing={startEditing}
                  stopEditing={stopEditing}
                  handleSave={handleSave}
                  editIdx={editIdx}
                  isSubmitted={isSubmitted}
                />
              </Paper>
            </div>
          </div>
          <div className={classes.affiliateForm}>
            <div>Affiliation Information</div>
            <div>
              <label>Existing Affiliated HMO/S</label>
              <Paper className={classes.affiliationInformationTable} elevation={3}>
                <AffiliationFormInput
                  HandleRemove={handleRemove} // remove by index
                  handleValue={addToGroup}
                  onClick={affiliatedGroupInput.length <= 0 ? null : handleGroup}
                  AddToGroupsArr={handleGroupValue}
                  HandleEdit={HandleEdit}
                  inputField={
                    <TextField
                      disabled={isSubmitted}
                      name='affiliateGroup'
                      inputRef={affiliatedGroupRef}
                      placeholder='Type Affiliate group'
                      onChange={e => debounceAffiliatedInput(e.target.value)}
                    />
                  }
                  dataheader={[{ prop: 'affiliateGroup' }]}
                />
              </Paper>
            </div>
          </div>
          <div className={classes.remarksDiv}>
            <div className={styles.RemarksField}>
              <span>
                <div>
                  <label>External Remarks</label>
                  <textarea
                    disabled={isSubmitted}
                    id='externalRemarks'
                    name='externalRemarks'
                    ref={el => (formInputRef.current.externalRemarks = el)}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  />
                </div>
                <div>
                  <label>Internal Remarks *</label>
                  <textarea
                    disabled={isSubmitted}
                    id='internalRemarks'
                    name='internalRemarks'
                    ref={el => (formInputRef.current.internalRemarks = el)}
                    className={errors['internalRemarks'] && styles.errorClass}
                    onChange={({ target: { value, name } }) => debouncedUpdate(value, name)}
                  />
                  <label className={styles.error}>{errors['internalRemarks']} </label>
                </div>
              </span>
            </div>
          </div>
        </div>
        <FooterWithButtons
          footerStatus={'Medical Clinic'}
          onClickSave={handleSubmit}
          proceedSubmitForm={proceedSubmitForm}
          isSuccess={isSuccess}
          setIsSuccess={setIsSuccess}
          disableButton={isSubmitted ? false : disabledButton}
          onClickProceed={handleProceed}
          isSubmitted={isSubmitted}
          history={props.history}
        />
      </form>
    </React.Fragment>
  )
}

export default AffiliationForm
