import React from 'react'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../../../../Components/JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './AddMultiplierModal.module.scss'
import CustomDialog from '../../../../Components/CustomDialog/CustomDialog'
import DisplayFacilitiesAdded from './DisplayFaciliitesAdded'
import AddFacilityGroup from '../validations/AddFacilityGrouping'
import DropDown from '../../../../Components/CustomDrowpdown'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
import CustomAutoComplete from 'Pages/Protected/Partner/Components/AutoSuggest'
import FormHelperText from '@material-ui/core/FormHelperText'
import FacilityGroupingService from '../../../../../../../Services/Api/FacilityGrouping/FacilityGrouping'
import CustomAlerSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
import AutoSuggest from 'Pages/Protected/Partner/Components/AutoSuggest/customInput'

const facilityTypes = [
  { id: 'HSP1', text: 'Hospital', value: 'Hospital' },
  { id: 'MD2', text: 'Medical Clinic', value: 'Medical Clinic' },
  { id: 'DC3', text: 'Dental Clinic', value: 'Dental Clinic' }
]

const AddRoomTypeModal = props => {
  const ctr = parseInt(props.dataLength) + 1
  const [errors, setErrors] = React.useState({})
  const [isLoading, setIsLoading] = React.useState(false)
  const [facilityType, setFacilityType] = React.useState('HOSPITAL')
  const [isSuccess, setIsSuccess] = React.useState(false)
  const [open, setOpen] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const [change, setChange] = React.useState(false)
  const [form, setForm] = React.useState({
    desc: '',
    facility_type: 'Hospital',
    facilityNames: [],
    code: ''
  })
  const [validationModal, setValidationModal] = React.useState(false)
  const [facilityNames, setFacilityNames] = React.useState([])
  const [facilityNamesByType, setFacilityNamesByType] = React.useState([])
  const [tmpFacilityName, setTmpFacilityName] = React.useState({
    '_id': '',
    'name': ''
  })
  const [searchTerm, setSearchTerm] = React.useState('')
  const [isFetching, setIsFetching] = React.useState(false)



  const handleSubmit = (e) => {
    e.preventDefault()
    validate(true)
  }

  const validate = (toSubmit) => {
    const { errors, isValid } = AddFacilityGroup({ desc: form.desc, facility_type: form.facility_type, facility_names: form.facilityNames })
    if (isValid) {
      setErrors({})
      if (toSubmit) {
        save()
      }
    } else {
      setErrors(errors)
    }
  }

  const save = async () => {
    try {
      await FacilityGroupingService.add(form)
      setTimeout(async () => {
        setIsSuccess(true)
        setSuccess(true)
        setOpen(false)
        props.fetchData()
        setFacilityNames([])
        setForm({
          desc: '',
          facility_type: 'Hospital',
          facilityNames: [],
          code: ''
        })
      }, 1000)
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  const handleChange = ({ currentTarget: input }) => {
    const newError = { ...errors }
    delete newError['desc']
    setErrors(newError)
    const contactForm = { ...form }
    contactForm[input.name] = input.value
    setForm(contactForm)
  }

  const handleFacilityNameChange = async (value, name) => {
    setSearchTerm(value)
    setIsFetching(true)
    if (typeof value === 'string' && value.length >= 1) {
      try {
        const {
          data: { data }
        } = await AffiliationEncoderService.getFacilityByType({ name: value, type: facilityType })
        setFacilityNamesByType(data)
        setIsFetching(false)
      } catch (error) {
        setIsFetching(false)
        console.log('error occured', error)
      }
    }
  }

  const setCounterCode = (type) => {
    let counter = 1
    if (props.data) {
      const typeData = props.data.filter(function (data) {
        return data.facility_type == type
      }).length
      counter = typeData + 1
    }
    let typeText = ''
    switch (type) {
      case 'Hospital':
        typeText = 'HSPGR'
        break
      case 'Medical Clinic':
        typeText = 'MCLGRP'
        break
      case 'Dental Clinic':
        typeText = 'DCLGRP'
        break
    }
    const pad = '0000'
    const counterText = (pad + counter).slice(-pad.length)
    return `${typeText}${counterText}`
  }

  const addFacility = () => {
    const newFacility = [ ...facilityNames ]
    if (tmpFacilityName.name === '' || facilityNames.some(facility => facility._id === tmpFacilityName._id)) {
      setValidationModal(true)
    } else {
      form['facilityNames'] = [...form.facilityNames, tmpFacilityName]
      setFacilityNames([...newFacility, tmpFacilityName])
      setTmpFacilityName({
        '_id': '',
        'name': ''
      })
      validate()
    }
  }

  const dataheader = [
    { name: 'Facilities Included in the Group', prop: 'name' }
  ]

  const handleRemoveFacility = index => {
    const newArray = [...facilityNames]
    const nForm = {...form}
    const nFormFacilityName = [...nForm['facilityNames']]
    const res = newArray.filter((facilityNames, i) => i !== index)
    const res1 = nFormFacilityName.filter((facilityNames, i) => i !== index)
    setFacilityNames(res)
    nForm['facilityNames'] = res1
    setForm(nForm)
  }

  const handleDropdown = ({ target: select }) => {
    const updateForm = { ...form }
    updateForm[select.name] = select.value
    if (select.name == 'facility_type') {
      updateForm['code'] = setCounterCode(select.value)
    }
    setForm(updateForm)
  }

  const HandleChangeFacility = (value) => {
    setTmpFacilityName({'_id': value._id, name: value.name})
  }

  const [names, setNames] = React.useState([])
  const [facilityIds, setFacilityIds] = React.useState([])

  const fetchAutoSuggest = async () => {
    // const { data: { data: names } } = await AffiliationEncoderService.getAllFacilityName()
    const { data: { data: fIds } } = await AffiliationEncoderService.getAllFacilityIds()
    // setNames(names)
    setFacilityIds(fIds)
  }

  React.useEffect(() => {
    const nForm = {...form}
    switch (nForm.facility_type) {
      case 'Medical Clinic':
        setFacilityType('CLINIC')
        break
      case 'Dental Clinic':
        setFacilityType('DENTAL_CLINIC')
        break
        default:
          setFacilityType('HOSPITAL')
          nForm.code = setCounterCode('Hospital')
          setForm(nForm)
          break;
    }
  }, [form.facility_type])

  React.useEffect(() => {
    const nForm = { ...form }
    fetchAutoSuggest()
    nForm.code = setCounterCode('Hospital')
    setForm(nForm)
  }, [])

  const handleCancel = () => {
    setForm({
      desc: '',
      facility_type: 'Hospital',
      facilityNames: [],
      code: ''
    })
    setValidationModal(false)
    setFacilityNames([])
    setTmpFacilityName({
      '_id': '',
      'name': ''
    })
    setErrors({})
    setOpen(false)
  }

  const HandleSelectFacility = ({_id, name}) => {
    const newErrors = { ...errors }
    setTmpFacilityName({_id, name })
    delete newErrors['facility_names']
    setErrors(newErrors)
  }

  const handleOpen = () => {
    const nForm = { ...form }
    nForm.code = setCounterCode('Hospital')
    setForm(nForm)
    setOpen(true)
  }

  return (
    <React.Fragment id='container'>
      <text
        onClick={handleOpen}
      >
        Add Group
      </text>
     <Dialog fullWidth maxWidth={'md'} open={open} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%', width: '600px' }}>ADD GROUPINGS</DialogTitle>
        <DialogContent>
          <form className={classes.root} onSubmit={handleSubmit} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <div id='desc' style={{ 'display': 'inline-flex' }}>
                <div style={{width: '50%', position: 'relative'}}>
                  <TextField name='desc' onChange={handleChange} label='Grouping Description*' />
                  {errors['desc'] && <p style={{ color: 'red', fontSize: '.75rem', padding: 0, margin: 0, position: 'absolute', bottom: 0 }}>{errors['desc']} </p>}
                </div>
                <div style={{width: '50%', marginLeft: '10px'}}>
                  <DropDown name='facility_type' error={errors['facility_type']} onChange={handleDropdown} items={facilityTypes} value={form.facility_type} label='Type of Facility*' />
                </div>
              </div>
            </div>
            <div className={classes.twoInput} style={{ 'display': 'inline-flex', alignItems: 'center', margin: '10px 0'}}>
              <div id='facilityName' className={classes.facilityField}>
                {/* <CustomAutoComplete suggestions={names} labelName={'name'} name='facility_name' label='Facility Name*' value={tmpFacilityName} onChange={HandleChangeFacility} change={change}/> */}
                <AutoSuggest
                  suggestions={facilityNamesByType}
                  error={errors['facility_names']}
                  value={tmpFacilityName['name'] ? tmpFacilityName['name'] : ''}
                  labelName='name'
                  label='Facility Name*'
                  name='facilityName'
                  searchTerm={searchTerm}
                  onChange={handleFacilityNameChange}
                  onSelect={HandleSelectFacility}
                  isFetching={isFetching}
                  setSearchTerm={setSearchTerm}
                  show={1} // length of search string to show auto suggestions
                />
              </div>
              <a style={{ paddingLeft: '10px', cursor: 'pointer', color: '#3ab77d', paddingTop: '10px' }} onClick={addFacility}>+Add Facility</a>
            </div>
            <div className={classes.twoInput}>
              <DisplayFacilitiesAdded
                dataheader={dataheader}
                dataValue={form.facilityNames.length > 0 ? form.facilityNames : []}
                handleRemove={handleRemoveFacility}
                hasError={errors.hasOwnProperty('facility_names')}
              />
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={ handleCancel}
            variant='contained'
            className={classes.Cancel}
          >
            Cancel
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            Create
          </Button>
        </DialogActions>
      </Dialog>
      <div>
        <CustomDialog id='cancelCustomDialog' isCancel={validationModal} setIsCancel={() => setValidationModal(false)}>
          <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
            <h3 id='cancelCustomDialogMessage'>Facility name empty or exist already</h3>
            <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
              <Button
                onClick={() => setValidationModal(false)}
                id='cancelCustomDialogBtnYes'
                variant='contained'
                style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '95px' }}
              >
                OKAY
              </Button>
            </div>
          </div>
        </CustomDialog>
      </div>
      <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
    </React.Fragment>
  )
}

export default AddRoomTypeModal
