import 'date-fns'
import React from 'react'
import MomentUtils from '@date-io/moment'
import Paper from '@material-ui/core/Paper'

import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import moment from 'moment'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { DateFormatter } from 'Utilities/TextFormatter'

import classes from './DatePicker.module.scss'
import { dateFormat, dateFormatChecker } from '../Utils/utils'
import InputBaseWithNumberFormat from '../InputBaseWithNumberFormat'

/**
 * Props
 * id
 * label
 * required
 * value - should be date type
 * format
 * onChange
 *
 * other props
 * https://material-ui-pickers.dev/api/KeyboardDatePicker
 *
 */
const dateChecker = date => {
  return typeof date === 'string' && date.length > 0
}
function InlineDatePicker (props) {
  const {
    id,
    label,
    editInputWidth,
    required = false,
    disableToolbar,
    placeholder,
    disabled,
    value,
    format,
    readOnly,
    name = '',
    onChange,
    error,
    errors,
    setErrors,
    ...other
  } = props
  const [open, setOpen] = React.useState(false)
  const [selectedDate, setSelectedDate] = React.useState('')
  const [isFocus, setIsFocus] = React.useState(false)

  React.useEffect(() => {
    setSelectedDate(dateChecker(value) ? dateFormat(value) : '')
  }, [value])

  const handleOnClose = () => {
    setOpen(false)
  }

  const handleDateChange = date => {
    setSelectedDate(dateFormat(date))
    onChange(name, date)
    setOpen(false)
  }

  const HandleInlineChange = ({ target: { name, value } }) => {
    onChange(name, value)
    setSelectedDate(value)
  }

  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {DateFormatter(props.value)}
          </Typography>
        ) : (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              TextFieldComponent={p => (
                <React.Fragment>
                  <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
                    <InputBaseWithNumberFormat
                      id={id}
                      autoFocus={isFocus}
                      name={name}
                      disabled={disabled}
                      placeholder={placeholder}
                      onChange={HandleInlineChange}
                      onFocus={() => setIsFocus(true)}
                      onBlur={() => setIsFocus(false)}
                      className={editInputWidth || classes.input}
                      value={selectedDate}
                      style={{minWidth: '100px'}}
                      format='##/##/####'
                    />

                    <div className={classes['date-button']}>
                      <IconButton disabled={disabled} className={classes.button} size='small' onClick={() => setOpen(true)}>
                        <FontAwesomeIcon icon='calendar-day' className={classes.icon} />
                      </IconButton>
                    </div>
                  </Paper>
                  {error && <span className={classes.error}>{error}</span>}
                </React.Fragment>
              )}
              name={name}
              value={selectedDate}
              format='MM/DD/YYYY'
              open={open}
              onClose={handleOnClose}
              onChange={handleDateChange}
              readOnly={readOnly}
              {...other}
            />
          </MuiPickersUtilsProvider>
        )}
      </div>
    </div>
  )
}

export default React.memo(InlineDatePicker)
