import React from 'react'
import { useTable, useFilters, useGlobalFilter, useSortBy } from 'react-table'
import Table from '@material-ui/core/Table'
import TableHead from '@material-ui/core/TableHead'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableRow from '@material-ui/core/TableRow'
import TextField from '../../../../../../SharedComponents/TextField/CustomTextField'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import matchSorter from 'match-sorter'
import classes from './CustomTable.module.scss'
import Button from '@material-ui/core/Button'
import AddRowForm from './AddRowForm'
import RowAction from './RowActions'
import AddDiscountValidations from '../validations/AddDiscountValidation'
import { useDispatch } from 'react-redux'
import { setHospitalFacilityDiscount } from 'Pages/Protected/Store/actions/Partner/FacilityDiscount'
import moment from 'moment'
import CustomDialog from '../../../Components/CustomDialog/CustomDialog'


const DefaultColumnFilter = ({ column: { filterValue, setFilter } }) => {
  return (
    <div id='textField' className={classes.textField}>
      <TextField
        value={filterValue || ''}
        onChange={e => {
          setFilter(e.target.value || undefined) // Set undefined to remove the filter entirely
        }}
      />
    </div>
  )
}

const fuzzyTextFilterFn = (rows, id, filterValue) => {
  return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
}

// Let the table remove the filter if the string is empty
fuzzyTextFilterFn.autoRemove = val => !val

// Our table component

const ReactTable = props => {
  const filterTypes = React.useMemo(
    () => ({
      // Add a new fuzzyTextFilterFn filter type.
      fuzzyText: fuzzyTextFilterFn,
      // Or, override the default text filter to use
      // "startWith"
      text: (rows, id, filterValue) => {
        return rows.filter(row => {
          const rowValue = row.values[id]
          return rowValue !== undefined
            ? String(rowValue)
                .toLowerCase()
                .startsWith(String(filterValue).toLowerCase())
            : true
        })
      }
    }),
    []
  )
  const dispatch = useDispatch()

  const [currentData, setCurrentData] = React.useState(false)
  const currentDataRef = React.useRef(currentData)

  React.useEffect(() => {
    setCurrentData(props.data)
  }, [])

  React.useEffect(() => {
    currentDataRef.current = currentData
    dispatch(setHospitalFacilityDiscount(currentData))
  }, [currentData])

  const defaultColumn = React.useMemo(
    () => ({
      // Let's set up our default Filter UI
      Filter: DefaultColumnFilter
    }),
    []
  )

  const {
    columns,
    data,
    setIsSave,
    showSave,
    setIsCancel,
    isDisabled,
    isCancel,
    setShowSave,
    setOpen,
    isSave,
    setIsDisabled,
    confirmCancel,deletedSucessfully,setRowDeleted
  } = props
  const { getTableProps, getTableBodyProps, headerGroups, rows, prepareRow } = useTable(
    {
      columns,
      data,
      defaultColumn,
      filterTypes
      // Be sure to pass the defaultColumn option
    },
    useFilters,
    useGlobalFilter,
    useSortBy
  )
  const [form, setForm] = React.useState({
    status: '',
    discount_type: '',
    claim_type: '',
    discount_application: '',
    included_procedure: [],
    payment_type: '',
    terms_in_days: '',
    rate: '',
    effectivity_date: new Date().toISOString(),
    end_date: new Date().toISOString(),
    discount_to_be_deducted_from: '',
    other_details_of_discount: ''
  })


  const [LimitRows, setLimitRows] = React.useState(rows.slice(0, 10))
  const [defaultLimitRows, setDefaultLimitRows] = React.useState(rows.slice(0, 10))
  const LimitRowsRef = React.useRef(LimitRows)
  const [isEdit, setIsEdit] = React.useState(false)
  const [confirmCancellation,setConfirmCancel] = React.useState(false)
  const [editIndex, setEditIndex] = React.useState(null)

  React.useEffect(() => {
    LimitRowsRef.current = LimitRows
  }, [LimitRows])

  

  const [errors, setErrors] = React.useState({})
  const [shouldValidate, setShouldValidate] = React.useState(false)

  const validate = () => {
    if (!isEdit) {
      setIndexLength(indexLength + 1)
      const { errors, isValid } = AddDiscountValidations(form)
      if (isValid) {
        setShouldValidate(false)
        setForm({
          status: '',
          discount_type: '',
          claim_type: '',
          discount_application: '',
          included_procedure: [],
          payment_type: '',
          terms_in_days: '',
          rate: '',
          effectivity_date: new Date().toISOString(),
          end_date: new Date().toISOString(),
          discount_to_be_deducted_from: '',
          other_details_of_discount: ''
        })

 
        setErrors({})
      } else {
        setShouldValidate(true)
        alert('please fill up the row first')
        setErrors(errors)
      }
    }
  }
  const [indexLength, setIndexLength] = React.useState(0)

  const save = () => {
    let createData = [...currentData, form]
    setCurrentData(createData)
    const index = createData.length - 1
    setIndexLength(createData.length - 1)
    setLimitRows([
      ...LimitRows,
      <AddRowForm
        index={index}
        setCurrentData={setCurrentData}
        currentDataRef={currentDataRef}
        LimitRows={LimitRows}
        LimitRowsRef={LimitRowsRef}
        setLimitRows={setLimitRows}
        curretData={currentData}
        form={form}
        setForm={setForm}
        errors={errors}
        setErrors={setErrors}
        fetchData={props.fetchData}
        setIsDisabled={props.setIsDisabled}
        setShouldValidate={setShouldValidate}
        shouldValidate={shouldValidate}
      />
    ])
  }

  const addRow = () => {
    if (shouldValidate == false) {
      props.setShowSave(true)
      const { errors } = AddDiscountValidations(form)
      setErrors(errors)
      setShouldValidate(true)
      setForm(
        {
        status: '',
        discount_type: '',
        claim_type: '',
        discount_application: '',
        included_procedure: [],
        payment_type: '',
        terms_in_days: '',
        rate: '',
        effectivity_date: new Date().toISOString(),
        end_date: new Date().toISOString(),
        discount_to_be_deducted_from: '',
        other_details_of_discount: ''
      })
      
      save()
    } else {
      validate()
    }
  }


 
 

  const deleteDiscount = index => {
    const LimitRowsDef = LimitRowsRef ? LimitRowsRef.current : null

    const dataArr = currentDataRef.current
    const newArray = [...dataArr]
    const res = newArray.filter((newArray, i) => i !== index)
    setCurrentData(res)

    const rows = [...LimitRowsDef]
    const rowData = rows.filter((rows, i) => i !== index)
    setLimitRows(rowData)
    setShouldValidate(false)
  }




  const isButtonDisabled = () => {
    if(isEdit){
      return false
    } else if(form['discount_type'].length > 0) {
      return isDisabled
    }else {
      return true
    }
  }


  const ConfirmationButton = () => {
    return (
      <div id='Close-button'>
        <Button
          className={classes.closeButton}
          variant='contained'
          onClick={() => {
            setIsCancel(true)
          }}
        >
          Cancel
        </Button>
        <Button
          variant='contained'
          onClick={() => setIsSave(true)}
          disabled={isEdit ? false : form['discount_type'].length > 0 ? isDisabled : true}
          className={isButtonDisabled() ? classes.btnSaveDisable  : classes.btnSave}
        >
          Save
        </Button>
      </div>
    )
  }

  const CloseModal = () => {
    return (
      <div id='Close-button'>
        <Button
          className={classes.closeButton}
          variant='contained'
          onClick={() => {
            setOpen(false)
          }}
        >
          Close
        </Button>
      </div>
    )
  }

  const cancelEdit = index => {
    console.log('nakakancancel naman di ba ?')
    const rows = [...LimitRows]
    rows[index] = defaultLimitRows[index]
    setLimitRows(rows)
    setLimitRows(rows)
    setIsEdit(false)
    setForm({
      status: '',
      discount_type: '',
      claim_type: '',
      discount_application: '',
      included_procedure: [],
      payment_type: '',
      terms_in_days: '',
      rate: '',
      effectivity_date: new Date().toISOString(),
      end_date: new Date().toISOString(),
      discount_to_be_deducted_from: '',
      other_details_of_discount: ''
    })
  }

 

  return (
    <>
      <div id='table'>
        <div>
          <div style={{ width: '1200px', overflowX: 'auto' }}>
            <Table {...getTableProps()} className={classes.rootTable}>
              <TableHead className={classes.tableHead}>
                {headerGroups.map(headerGroup => (
                  <TableRow {...headerGroup.getHeaderGroupProps()} className={classes.tableHeadRow}>
                    <TableCell />
                    {headerGroup.headers.map(column => (
                      <React.Fragment>
                        <TableCell
                          component='th'
                          scope='row'
                          align='left'
                          className={classes.tableCell}
                          {...column.getHeaderProps(column.getSortByToggleProps())}
                        >
                          {column.canFilter ? (
                            <div id='withTextfield' className={classes.withTextfield}>
                              {column.render('Header')}
                              <span id='showIcon' className={classes.showIcon}>
                                {column.isSortedDesc ? (
                                  <div id='sortIcon' className={classes.sortIcon}>
                                    <FontAwesomeIcon icon='sort' />
                                  </div>
                                ) : (
                                  <div id='sortIcon' className={classes.sortIcon}>
                                    <FontAwesomeIcon icon='sort' />
                                  </div>
                                )}
                              </span>
                              <div> {column.render('Filter')} </div>
                            </div>
                          ) : (
                            <div id='noTextField' className={classes.noTextField}>
                              {column.render('Header')}
                            </div>
                          )}
                        </TableCell>
                      </React.Fragment>
                    ))}
                  </TableRow>
                ))}
              </TableHead>
              <TableBody {...getTableBodyProps()} className={classes.tableBody}>
                {LimitRows.map((row, i) => {
                  if (row.hasOwnProperty('id')) {
                    prepareRow(row)
                    return (
                      <TableRow key={i} {...row.getRowProps()} className={classes.tableRow}>
                        <RowAction
                          index={i}
                          setCurrentData={setCurrentData}
                          currentDataRef={currentDataRef}
                          LimitRows={LimitRows}
                          LimitRowsRef={LimitRowsRef}
                          setLimitRows={setLimitRows}
                          curretData={currentData}
                          data={row}
                          haveData
                          form={form}
                          setForm={setForm}
                          errors={errors}
                          setErrors={setErrors}
                          fetchData={props.fetchData}
                          setIsDisabled={props.setIsDisabled}
                          setShowSave={setShowSave}
                          setIsEdit={setIsEdit}
                          setEditIndex={setEditIndex}
                          confirmCancel={confirmCancellation}
                          deletedSucessfully={deletedSucessfully}
                          setRowDeleted={setRowDeleted}
                        />
                        {row.cells.map(cell => {
                          const cellDisplay =
                            cell.value instanceof Array
                              ? Array.prototype.map
                                  .call(cell.value, function (item) {
                                    return item.procedure
                                  })
                                  .join(',')
                              : cell.render('Cell')
                          return (
                            <TableCell
                              component='th'
                              scope='row'
                              align='left'
                              className={classes.tableCell}
                              {...cell.getCellProps()}
                            >
                          
                              <span>{cellDisplay}</span>
                            </TableCell>
                          )
                        })}
                      </TableRow>
                    )
                  } else {
                    return row
                  }
                })}
              </TableBody>
            </Table>
          </div>
          <div style={{ display: 'block' }} className={classes.rowDiscount}>
            <label
              className={classes.buttonAdd}
              variant='contained'
              style={{
                marginLeft: '25px',
                left: 0,
              }}
              onClick={isEdit ? null : LimitRows.length === 10 ? null : addRow}
            >
              + Add Facility Discount
            </label>
          </div>
        </div>
      </div>
      {showSave && <ConfirmationButton />}
      {!showSave && <CloseModal />}
      <CustomDialog id='cancelCustomDialog' isCancel={isCancel} setIsCancel={() => setIsCancel(false)}>
        <div id='cancelCustomDialogContainer' className={classes.dialogContainer}>
          <h3 id='cancelCustomDialogMessage'>All changes will not be saved, are you sure you want to cancel?</h3>
          <div id='cancelCustomDialogBtnGroup' className={classes.btnGroup}>
            <Button
              onClick={() => setIsCancel(false)}
              id='cancelCustomDialogBtnYes'
              variant='contained'
              style={{ backgroundColor: '#fd5451', color: 'white', marginLeft: '160px', marginBottom: '10px' }}
            >
              No
            </Button>
            <Button
              onClick={() => {
                setIsCancel(false)
                setConfirmCancel(true)
                setForm({
                  status: '',
                  discount_type: '',
                  claim_type: '',
                  discount_application: '',
                  included_procedure: [],
                  payment_type: '',
                  terms_in_days: '',
                  rate: '',
                  effectivity_date: new Date().toISOString(),
                  end_date: new Date().toISOString(),
                  discount_to_be_deducted_from: '',
                  other_details_of_discount: ''
                })
               
                isEdit ? cancelEdit(editIndex) : deleteDiscount(currentDataRef.current.length - 1)
                setShowSave(false)
              }}
              id='cancelCustomDialogBtnNo'
              className={classes.btnCancel}
              variant='contained'
              style={{ backgroundColor: '#48b77c', color: 'white', marginLeft: '20px', marginBottom: '10px' }}
            >
              Yes
            </Button>
          </div>
        </div>
      </CustomDialog>
    </>
  )
}



export default React.memo(ReactTable,(prevProps,nextProps) => {
  try{
    if(prevProps.form === nextProps.form && prevProps.LimitRowsRef === nextProps.LimitRowsRef && prevProps.row === nextProps.row && prevProps.curretData === nextProps.curretData && prevProps.props.fetchData === nextProps.props.fetchData && prevProps.cellDisplay === nextProps.cellDisplay) {
      return true
    }
    return false
  }catch(e){
    return false
  }
})
