import React from 'react'
import DeleteIcon from '@material-ui/icons/Delete'
import classes from './UpdateColumn.module.scss'

const UpdateColumns = (setIndexToDelete, setDelete) => {
  const getData = (e, props) => {
    const indexValue = props.row.index

    setIndexToDelete(indexValue)
    setDelete(true)
  }

  const updatecolumns = React.useMemo(
    () => [
      {
        Header: 'Status',
        accessor: 'status',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'deleteIcon',
        accessor: 'deleteIcon',
        Cell: props => (
          <div id='cpt' className={classes.cptCode}>
            <DeleteIcon id='del' onClick={e => getData(e, props)} /> {props.value}
          </div>
        ),
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'CPT Code',
        accessor: 'cpt'
      },
      {
        Header: 'Procedure Description',
        accessor: 'procedure'
      },

      {
        Header: 'Price',
        accessor: 'price',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Effectivity Date',
        accessor: 'effectivityDate',
        disableFilters: true,
        disableSortBy: true
      },
      {
        Header: 'Remarks',
        accessor: 'remarks',
        disableFilters: true,
        disableSortBy: true
      }
    ],
    []
  )

  return updatecolumns
}

export default UpdateColumns
