import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'
export async function mapAndFetchMedical (id) {
  try {
    const res = await AffiliationEncoderService.getMedicalClinicById(id)
    console.log('ressssssss id', res.data.data)
    return mapData(res.data.data)
  } catch (error) {
    console.log('res error get medical', error)
  }
}

function mapData (res) {
  const {
    philhealth,
    stage,
    affiliation_status,
    status,
    id,
    type,
    name,
    source,
    mno,
    afn_number,
    tvs,
    email,
    address,
    remarks,
    treasury,
    affiliation_infos,
    classification,
    process_type,
    clinic_schedule,
    room_number,
    license,
    contacts,
    operation
  } = res

  let medForm = {
    medName: '',
    email: '',
    years: '',
    months: '',
    dohNumber: '',
    dohLicenseType: '',
    dohLicenseExp: '',
    philhealthNumber: '',
    philhealthExpiration: '',
    payee: '',
    classification: '',
    nonVatable: '',
    tinStatus: '',
    vatTaxRate: '',
    processType: '',
    unitFlrBldg: '',
    bldgName: '',
    street: '',
    subdivision: '',
    region: '',
    district: '',
    barangay: '',
    city: '',
    contacts: '',
    zipCode: '',
    clinicSchedule: '',
    roomNumber: '',
    internalRemarks: '',
    externalRemarks: ''
  }

  const affil = {
    affiliationType: '',
    dateReceived: null,
    id: '',
    mno: '',
    source: 'PNM',
    type: 'CLINIC',
    dateSubmitted: null,
    stageName: 'ENCODING',
    status: 'Active',
    affiliationStatus: 'In-Progress'
  }
  affil.dateSubmitted = stage['date_submitted'] ? stage['date_submitted'] : new Date().toISOString()
  affil.stageName = stage['stage_name'] ? stage['stage_name'] : ''
  affil.type = type
  affil.status = status
  affil.source = source
  affil.affiliationStatus = affiliation_status['name'] ? affiliation_status['name'] : ''
  affil.mno = mno
  affil.id = id

  medForm.medName = name
  medForm.email = email
  medForm.philhealthNumber = philhealth['accreditation_number'] ? philhealth['accreditation_number'] : ''
  medForm.philhealthExpiration = philhealth['expiration_date'] ? philhealth['expiration_date'] : ''
  medForm.unitFlrBldg = address['unit_flr_bldg'] ? address['unit_flr_bldg'] : ''
  medForm.bldgName = address['building_name'] ? address['building_name'] : ''
  medForm.street = address['street'] ? address['street'] : ''
  medForm.barangay = address['barangay'] ? address['barangay'] : ''
  medForm.district = address['district'] ? address['district'] : ''
  medForm.city = address['city'] ? address['city'] : ''
  medForm.region = address['region'] ? address['region'] : ''
  medForm.zipCode = address['zip_code'] ? address['zip_code'] : ''
  medForm.subdivision = address['subdivision'] ? address['subdivision'] : ''
  medForm.internalRemarks = remarks['internal_remarks'] ? remarks['internal_remarks'] : ''
  medForm.externalRemarks = remarks['external_remarks'] ? remarks['external_remarks'] : ''
  medForm.tinStatus = treasury['tin_status'] ? treasury['tin_status'] : ''
  medForm.nonVatable = treasury['non_vatable'] ? treasury['non_vatable'] : ''
  medForm.vatTaxRate = treasury['vat'] ? treasury['vat']['tax_rate'] : ''
  medForm.payee = treasury['payee'] ? treasury['payee'] : ''
  medForm.classification = classification
  medForm.processType = process_type
  medForm.clinicSchedule = clinic_schedule
  medForm.roomNumber = room_number
  medForm.years = operation['years'] || ''
  medForm.months = operation['months'] || ''
  medForm.contacts = contacts ? contacts : []
  medForm.affiliationInfos = affiliation_infos ? affiliation_infos : []
  medForm.dohNumber = license['doh'] ? license['doh']['number'] : ''
  medForm.dohLicenseType = license['doh'] ? license['doh']['license_type'] : ''
  medForm.dohLicenseExp = license['doh'] ? license['doh']['license_exp'] : ''

  return {affil, medForm}
}
