import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'facilityAssignment',
  'medicalPartnerId',
  'formerName',
  'assignments',
  'reason',
  'status',
  'exclusiveTo',
  'tvs',
  'philhealthExpiration',
  'dateOfDisaffiliation',
  'creditLimit',
  'paymentTerms',
  'facility',
  'facilityDiscount',
  'dateSubmitted',
  'dateOfDisaffiliation',
  'name',
  'clinicName',
  'specialInstructions'
]

const schema = Joi.object({
  facilityAssignment: Joi.string().label('Facility Assignment'),
  medicalPartnerName: Joi.string().label('Medical Partner Name'),
  status: Joi.string().label('Medical Partner Status'),
  tvs: Joi.string().label('TVS'),
  medicalPartnerId: Joi.string()
    .label('Medical Partner ID')
    .required(),
  formerName: Joi.string()
    .label('Former Name')
    .required(),
  years: Joi.number()
    .label('Years')
    .required(),
  checkPayee: Joi.string()
    .label('Check Payee')
    .required(),
  months: Joi.number()
    .label('Months')
    .required(),
  tinStatus: Joi.string()
    .label('Tin Status')
    .required(),
  processType: Joi.string()
    .label('Process Type')
    .required(),
  street: Joi.string()
    .label('Street')
    .required(),
  classification: Joi.string()
    .label('CLASSIFICATION')
    .required(),
  region: Joi.string()
    .label('Region')
    .required(),
  district: Joi.string()
    .label('District')
    .required(),
  barangay: Joi.string()
    .label('Barangay')
    .required(),
  city: Joi.string()
    .label('City')
    .required(),
  zipCode: Joi.string()
    .label('Zip Code')
    .required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  philhealthNumber: Joi.number()
    .label('Philhealth Number')
    .min(12)
    .max(12)
    .required(),
  philhealthExpiration: Joi.date().label('Philhealth Expiry Date'),
  dateSubmitted: Joi.date()
    .label('Date of Affiliation')
    .required(),
  dateOfDisaffiliation: Joi.date().label('Date of Disaffiliation'),
  specialInstructions: Joi.string().label('Remarks'),
  vatTaxRate: Joi.number().label('Tax Rate').required()
})

export default function MedicalClinicProfileForm (form) {
  let errors = {}
  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (typeof form['philhealthNumber'] !== 'undefined') {
        if (form['philhealthNumber'].length === 0) {
          delete errors['philhealthNumber']
        } else {
          if (form['philhealthNumber'].length === 12) {
            delete errors['philhealthNumber']
          }
        }
      }

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }
    }
    if (form['facilityAssignment'] === 'Assigned' && form['assignments'].length <= 0) {
      errors['assignments'] = 'Facility Assignments must have 1 selected'
    } else {
      delete errors['assignments']
    }
    if (form['assignments'].includes('specialAccommodation') && form['exclusiveTo'] === '') {
      errors['exclusiveTo'] = 'Required Fields'
    } else {
      delete errors['exclusiveTo']
    }
    if (form['status'] === 'On-Hold' && form['reason'] === '') {
      errors['reason'] = 'Required Fields'
    } else {
      delete errors['reason']
    }

    const taxRatePattern = new RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)

    if (!taxRatePattern.test(form['vatTaxRate'])) {
      errors['vatTaxRate'] = 'Tax Rate must not exceed 100%'
    } else {
      delete errors['vatTaxRate']
    }
  }

  console.log('errors', errors)

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
