import React from 'react'
import PdfViewer from 'SharedComponents/PdfViewer/PdfViewer'
import Grid from '@material-ui/core/Grid'
import Button from '@material-ui/core/Button'
import { useParams } from 'react-router-dom'
// import Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import HeaderWithBreadCrumbsNoStepper from '../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import ContractManagerServices from 'Services/Api/Partner/ContractManagerServices'
import PrintPDFService from 'Services/PrintPDFReport'
import classes from './EvaluatePage.module.scss'

const ContractPage = props => {
  const { type = '', id = '', facilityId = '' } = useParams()
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [file, setFile] = React.useState(null)

  React.useEffect(() => {
    setIsLoaded(true)
    BreadcrumbsService.setSelectedItemsWithoutHome(['CONTRACT_MANAGER', 'PRINT_CONTRACT'])
    LoadingService.loading(true)
    if (facilityId) {
      ContractManagerServices.getContract({ type, id, facilityId })
        .then(res => {
          const blob = new Blob([res.data])
          setFile(blob)
          LoadingService.loading(false)
        })
        .catch(() => {
          LoadingService.loading(false)
        })
    } else {
      ContractManagerServices.getContract({ type, id })
        .then(res => {
          const blob = new Blob([res.data])
          setFile(blob)
          LoadingService.loading(false)
        })
        .catch(() => {
          LoadingService.loading(false)
        })
    }
  }, [])

  const downloadData = async ({ type, id, facilityId = '' }) => {
    LoadingService.loading(true)
    const res = await ContractManagerServices.getContract({ type, id, facilityId })
    const url = URL.createObjectURL(new Blob([res.data]))
    LoadingService.loading(false)
    const link = document.createElement('a')
    link.href = url
    link.setAttribute('download', `Contract_${type}_${id}.pdf`)
    document.body.appendChild(link)
    link.click()
  }

  const onPrintContract = async ({ type, id, facilityId = '' }) => {
    LoadingService.loading(true)
    const res = await ContractManagerServices.getContract({ type, id, facilityId })
    const blob = new Blob([res.data])
    LoadingService.loading(false)
    PrintPDFService(blob)
  }

  const onRenewContract = async (type, id, facilityId = '') => {
    try {
      LoadingService.loading(true)
      const data = await ContractManagerServices.setRenew({ type, id, facilityId })
      LoadingService.loading(false)
      if (data.data) {
        props.history.push('/contractmanager')
      }
    } catch (err) {
      LoadingService.loading(false)
      SnackbarService.set({
        open: true,
        message: 'Request Failed',
        severity: 'error'
      })
    }
  }

  const onTerminalContract = async (type, id, facilityId = '') => {
    try {
      LoadingService.loading(true)
      const data = await ContractManagerServices.setTerminate({ type, id, facilityId })
      LoadingService.loading(false)
      if (data.data) {
        props.history.push('/contractmanager')
      }
    } catch (err) {
      LoadingService.loading(false)
      SnackbarService.set({
        open: true,
        message: 'Request Failed',
        severity: 'error'
      })
    }
  }

  return (
    <React.Fragment>
      <div id='mainContractPage' className={classes.root}>
        <HeaderWithBreadCrumbsNoStepper id='header' HeaderTitle='Medical Partner Contract' />
        <Grid container className={classes.content}>
          <Grid item xs={2} />
          <Grid item xs={8}>
            {isLoaded && (
              <PdfViewer
                file={file}
                print={() => onPrintContract({ type, id, facilityId })}
                download={() => downloadData({ type, id, facilityId })}
              />
            )}
          </Grid>
          <Grid item xs={2} className={classes.buttonContainer}>
            <Button
              variant='contained'
              className={classes.buttonGreen}
              onClick={() => onRenewContract(type, id, facilityId)}
            >
              Renew Contract
            </Button>
            <Button
              variant='contained'
              className={classes.buttonRed}
              onClick={() => onTerminalContract(type, id, facilityId)}
            >
              Terminate Contract
            </Button>
          </Grid>
        </Grid>
      </div>
    </React.Fragment>
  )
}

export default ContractPage
