import React from 'react'
import classes from './index.module.scss'
import { RenderInputFields } from '../../../Components/InputsForProfiles/InputsForProfile'

const AutosuggestComponent = props => {
  const { data, setForm, form, formValue, name, error } = props

  const [suggestions, setSuggestions] = React.useState([])

  const uniqueData = new Set(data)
  const convertToArray = [...uniqueData]

  const HandleChange = e => {
    const { value } = e.target
    const newForm = { ...form }

    let suggestionsValue = []
    try {
      if (value.length > 0) {
        const newValue = value
        const regexCompare = new RegExp(`^${newValue}`, 'i')
        suggestionsValue = convertToArray.sort().filter(items => regexCompare.test(items))
      }
      setSuggestions(suggestionsValue)
      newForm[name] = value.trim()
      setForm(newForm)
    } catch (e) {
      console.log('error', e)
    }
  }

  const selectedSuggestion = items => {
    const newForm = { ...form }
    newForm[name] = items
    setForm(newForm)
    setSuggestions([])
  }

  const RenderSuggestion = () => {
    if (suggestions.length === 0) {
      return null
    }

    return (
      <ul id='rootList' className={classes.list}>
        {suggestions.map((items, index) => (
          <li key={items.index} onClick={() => selectedSuggestion(items)}>
            {items}
          </li>
        ))}
      </ul>
    )
  }

  return (
    <div>
      <RenderInputFields value={formValue} name={name} error={error} onChange={HandleChange} type={'noSpecialChar'} />
      {RenderSuggestion()}
    </div>
  )
}

export default AutosuggestComponent
