import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'medicalPartnerId',
  'address',
  'reason',
  'exclusiveTo',
  'profileStatus',
  'discount',
  'type',
  'id',
  'classification',
  'dateOfDisaffiliation',
  'specialInstructions',
  'status',
  'dateOfAffiliation',
  'contacts',
  'partnerName',
  'formerName',
  'callFlow'
]

const schema = Joi.object({
  facilityAssignment: Joi.string().label('Facility Assignment'),
  partnerName: Joi.string().label('Medical Partner Name'),
  partnerStatus: Joi.string().label('Medical Partner Status'),
  tvs: Joi.string().label('TVS'),
  formerName: Joi.string()
    .label('Former Name')
    .required(),
  street: Joi.string()
    .label('Street/Village')
    .required(),
  region: Joi.string()
    .label('Region')
    .required(),
  district: Joi.string()
    .label('District/Province')
    .required(),
  city: Joi.string()
    .label('City/Municipality')
    .required(),
  barangay: Joi.string()
    .label('Barangay')
    .required(),
  zipCode: Joi.string()
    .label('Zip Code')
    .required(),
  classification: Joi.string()
    .label('CLASSIFICATION')
    .required(),
  accreditationNumber: Joi.string()
    .label('PHILHEALTH NO.')
    .required(),
  philhealthExp: Joi.date()
    .label('PHILHEALTH EXP.')
    .required(),
  payee: Joi.string()
    .label('CHECK PAYEE')
    .required(),
  creditLimit: Joi.number()
    .label('CREDIT LIMIT')
    .required(),
  paymentTerms: Joi.string()
    .label('PAYMENT TERMS')
    .required(),
  tinStatus: Joi.string()
    .label('TIN STATUS')
    .required(),
  vatTaxRate: Joi.number()
    .label('TAX RATE')
    .required(),
  facilityDiscount: Joi.string()
    .label('FACILITY discount')
    .required(),
  dateOfAffiliation: Joi.date()
    .label('date of affiliation')
    .required(),
  dateOfDisaffiliation: Joi.date()
    .label('date of disaffiliation')
    .required(),
  specialInstructions: Joi.string()
    .label('Remarks')
    .required(),
  processType: Joi.string()
    .label('PROCESS TYPE')
    .required(),
  clinicSchedule: Joi.string()
    .label('Clinic Schedule')
    .required(),
  years: Joi.number()
    .label('Years')
    .required(),
  months: Joi.number()
    .label('Months')
    .required()
})

export default function DentalClinicProfileForm (form) {
  let errors = {}
  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      } else {
        errors[i.path[0]] = i.message
      }
    }

    if (form['facilityAssignment'] === 'Assigned' && form['assignments'].length <= 0) {
      errors['assignments'] = 'Facility Assignments must have 1 selected'
    } else {
      delete errors['assignments']
    }

    if (form['assignments'].includes('specialAccommodation') && form['exclusiveTo'] === '') {
      errors['exclusiveTo'] = 'Required Fields'
    } else {
      delete errors['exclusiveTo']
    }

    if (form['status'] === 'On-Hold' && form['reason'] === '') {
      errors['reason'] = 'Required Fields'
    } else {
      delete errors['reason']
    }

    const taxRatePattern = new RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)

    if (!taxRatePattern.test(form['vatTaxRate'])) {
      errors['vatTaxRate'] = 'Tax Rate must not exceed 100%'
    } else {
      delete errors['vatTaxRate']
    }
  }
  console.log('errors', errors)
  return {
    errors,
    isValid: isEmpty(errors)
  }
}
