import React, { useState } from 'react'

import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import Button from '@material-ui/core/Button'

import classes from './UploadFIleTable.module.scss'
import Preview from '../../../../../AffiliationFormEncoder/Pages/PreviewImage/PreviewImage'
import Axios from 'axios'
import { useSelector } from 'react-redux'

const PhotoOfPhicID = () => {
  return (
    <div id='PhotoOfPhicID' className={classes.WithSubScript}>
      <text>PHOTO of PHIC ID</text>
      <sub id='subscript-PhotoOfPhicID' className={classes.subscript}>
        *For PHIC Accredited Doctors
      </sub>
    </div>
  )
}

const SwornDeclaration = () => {
  return (
    <div id='SwornDeclaration' className={classes.WithSubScript}>
      <text>Sworn Declaration of Gross Receipts/Sales</text>
      <sub id='subscript-SwornDeclaration' className={classes.subscript}>
        *For New Affiliation of Doctor
      </sub>
    </div>
  )
}
const URL = `${process.env.REACT_APP_SERVER_API}`

const createData = (description, uploadButton) => {
  return { description, uploadButton }
}

const UploadFIleTable = (props) => {
  const [mainFiles, setMainFiles] = useState([])
  const { affiliationType } = useSelector(state => state.affiliationType)
  React.useEffect(() => {
    if (props.affiliationTypeId) {
      fetchData(props.affiliationTypeId)
    }
  }, [])
  const fetchData = async (affiliationTypeId) => {
    try {
      let urlRequest = `${URL}/p/c/partners/common/files/${affiliationType.id}`
      if (affiliationTypeId) {
        urlRequest = `${URL}/p/c/partners/common/files/${affiliationTypeId}`
      }
      const filesData = await Axios.get(urlRequest)
      for (let fileData of filesData.data.data) {
        let tmpFiles = fileData.files
        setMainFiles((previousState) => ({
          ...previousState,
          [fileData.file_type]: tmpFiles.map(file => Object.assign(file, {
            preview: `${URL}/uploads/${file.path}`,
            fileType: fileData.file_type
          }))
        }))
      }
    } catch (e) {
      console.log('error', e)
    }
  }
  const rows = [
    createData(<Preview desc={'Doctor Resume'} fileType={'doctor-resume'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />),
    createData(<Preview desc={'Copy of Certificate of Fellowship and Diplomate'} fileType={'certificate-of-fellowship-diplomate'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />),
    // createData(<Preview desc={'List of Doctors to be Accredited'} fileType={'list-of-doctors-accredited'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />),
    createData(<Preview desc={'Recent 2x2 Photo'} fileType={'recent-photo'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />),
    createData(<Preview desc={<PhotoOfPhicID />} fileType={'photo-phic-id'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />),
    createData(<Preview desc={'Photocopy of PRC License ID'} fileType={'prc-license-id'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />),
    createData(<Preview desc={<SwornDeclaration />} fileType={'sworn-declaration'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'}forView />),
    createData(<Preview desc={'BIR COR 2303'} fileType={'bir-cor-2303'} mainFiles={mainFiles} setMainFiles={setMainFiles} buttonText={'Upload'} forView />)
  ]
  return (
    <TableContainer id='tableContainer' className={classes.tableContainer}>
      <Table className={classes.table} size='small' aria-label='a dense table'>
        <TableBody id='table-body'>
          {rows.map((row, index) => (
            <TableRow id='TableRow' className={classes['table-row']} key={index}>
              <TableCell id='description-row' className={classes['description-row']} component='th' scope='row'>
                {row.description}
              </TableCell>
              <TableCell id='button-row' className={classes['button-row']} component='th' scope='row' align='right'>
                {row.uploadButton}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  )
}

export default UploadFIleTable
