import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { dentalClinic } from '../../../../../Constant/Reports/ColumnsHeaders'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import ReportServices from '../../../../../Services/Api/Reports/Reports'

const DentalClinic = () => {
  const [dentalClinics, setDentalClinics] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  React.useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    const { data } = await ReportServices.getFacilitiesByType('DENTAL_CLINIC')
    setDentalClinics(data.data)
  }
  return (
    <div>
      <div className={classes.rightBody}>
        <div id='root' className={classes.root}>
          <ReactTable columns={dentalClinic} data={dentalClinics} setDownloadData={setDownloadData} />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtn}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={dentalClinic} filename={`${moment().format('YYYYMMDD')}-dental-clinic.csv`} />
      </div>
    </div>

  )
}

export default DentalClinic
