import React from 'react'
import Paper from '@material-ui/core/Paper'
import Select from '@material-ui/core/Select'
import MenuItem from '@material-ui/core/MenuItem'
import classes from './PartnerCardV2.module.scss'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import CircularProgress from '@material-ui/core/CircularProgress'
import { Link } from 'react-router-dom'
import Button from '@material-ui/core/Button'

const PartnerCard = props => {
  // Card Labels
  const {
    headerCount = null,
    headerTitle = null,
    cardTitle = null,
    selectListFilter = null,
    defaultFilter,
    headerList,
    actionText = null,
    dataList = null,
    isLoading = false,
    enableFilter = true,
    defaultTypeFilter = '',
    typeListFilter = false,
    addPartnerEnrollment = null,
    pathName
  } = props
  // Card Labels Tiles
  const { headerStyle = null, headerCountStyle = null, headerTitleStyle = null, cardTitleStyle = null } = props
  // Component Props
  const { bodyComponent } = props

  // State Props
  const { showFooterProps = true } = props
  const { getSelectedFilter = () => {}, getSelectedTypeFilter = () => {} } = props
  const { openViewList } = props
  const { getSelectedRow = null } = props

  const [showFooter, setShowFooter] = React.useState(showFooterProps)

  const handleOnViewClick = e => {
    e.preventDefault()
    openViewList()
  }

  const onHandleRowClick = (e, dataObj) => {
    e.preventDefault()
    getSelectedRow(dataObj)
  }

  return (
    <React.Fragment>
      <div className={classes.root}>
        {cardTitle !== null ? <h2 className={cardTitleStyle || classes.cardTitleStyle}>{cardTitle}</h2> : null}
        <Paper className={classes.cardContainerStyle}>
          <div className={headerStyle || classes.headerStyle}>
            <div>
              {headerCount !== null ? (
                <h1 className={headerCountStyle || classes.headerCountStyle}>
                  {headerCount < 10 ? `0${headerCount}` : headerCount}
                </h1>
              ) : null}
              {headerTitle !== null ? (
                <h2 className={headerTitleStyle || classes.headerTitleStyle}>{headerTitle}</h2>
              ) : null}
            </div>

            <div className={classes.selectStyle}>
              {typeListFilter ? (
                <Select
                  className={classes.selectValueStyle}
                  id='demo-simple-select'
                  value={defaultTypeFilter}
                  onChange={e => getSelectedTypeFilter(e.target.value)}
                >
                  {typeListFilter.map((obj, index) => {
                    return (
                      <MenuItem key={index} value={obj.key}>
                        {obj.value}
                      </MenuItem>
                    )
                  })}
                </Select>
              ) : null}
              {selectListFilter ? (
                <Select
                  className={classes.selectValueStyle}
                  id='demo-simple-select'
                  style={{ marginLeft: '15px' }}
                  value={defaultFilter}
                  onChange={e => getSelectedFilter(e.target.value)}
                >
                  {selectListFilter.map((obj, index) => {
                    return (
                      <MenuItem key={index} value={obj.key}>
                        {obj.value}
                      </MenuItem>
                    )
                  })}
                </Select>
              ) : null}
              {addPartnerEnrollment !== null ? (
                <Button component={Link} to={pathName} className={classes.addPartner}>
                  {addPartnerEnrollment}
                </Button>
              ) : null}
            </div>
          </div>

          <div className={classes.bodyStyle}>
            {isLoading && (
              <div className={classes.loadingComponent}>
                <CircularProgress />
              </div>
            )}
            <TableContainer>
              <Table stickyHeader size='small'>
                <TableHead>
                  {!isLoading && headerList && (
                    <TableRow>
                      {headerList.map((header, index) => {
                        return (
                          <TableCell
                            key={`header_${index}`}
                            style={{ color: '#1e2071', whiteSpace: 'nowrap' }}
                            align='left'
                          >
                            {header}
                          </TableCell>
                        )
                      })}
                      {actionText ? <TableCell align='left' /> : null}
                    </TableRow>
                  )}
                </TableHead>
                <TableBody>
                  {!isLoading && dataList && (
                    <React.Fragment>
                      {dataList.map((dataObj, dataIndex) => {
                        return (
                          <TableRow key={`dataList_${dataIndex}`}>
                            {Object.keys(dataObj).map((objKey, keyIndex) => {
                              return (
                                <TableCell key={`objKey_${keyIndex}`} align='left'>
                                  {dataObj[objKey]}
                                </TableCell>
                              )
                            })}
                            {actionText && (
                              <TableCell align='left'>
                                <a
                                  style={{ color: '#3ab77d', fontWeight: 'bold', whiteSpace: 'nowrap' }}
                                  href='#'
                                  onClick={e => onHandleRowClick(e, dataObj)}
                                >
                                  {actionText}
                                </a>
                              </TableCell>
                            )}
                          </TableRow>
                        )
                      })}
                    </React.Fragment>
                  )}
                </TableBody>
              </Table>
            </TableContainer>
          </div>
          {!isLoading && showFooter ? (
            <div className={classes.footerStyle}>
              <a href='#' className={classes.footer} onClick={handleOnViewClick}>
                View List
              </a>
            </div>
          ) : null}
        </Paper>
      </div>
    </React.Fragment>
  )
}

export default PartnerCard
