import React, { useRef } from 'react'
import { Provider, useDispatch, useSelector } from 'react-redux'
import { useParams } from 'react-router-dom'
import moment from 'moment'
import store from '../../../../../Store'
import HeaderWithBreadCrumbsNoStepper from '../../../../Components/HeaderWithBreadCrumbsNoStepper/HeaderWithBreadCrumbsNoStepper'
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import OICProcessorServices from 'Services/Api/Partner/OICProcessorServices'

// Import Sub Pages
import classes from './Hospital.module.scss'
import GeneralInformation from '../../../Components/GeneralInformation/GeneralInformation'
import RoomAndBoard from '../Subpages/RoomAndBoard/RoomAndBoard'
import Services from '../Subpages/Services/Services'
import GeneralAppearance from '../Subpages/GeneralAppearance/GeneralAppearance'
import TypeOcularInspection from '../../../Components/TypeOcularInspection/TypeOcularInspection'
import Footer from '../../../Components/Footer/Footer'
import CustomAlertSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'
// Import State Constants
import SubmitDialog from '../../../Components/SubmitDialog/SubmitDialog'
import CancelDialog from '../../../Components/CancelDialog/CancelDialog'
import { SnackbarService } from 'SharedComponents/SnackbarComponent/SnackbarComponent'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import {
  updateGeneralInformation,
  updateInspectorType,
  validateField,
  savePage,
  nextPage,
  submitPageForApproval,
  returnPage,
  openSaveDialog,
  updateState,
  setReadOnly,
  openSubmitDialog
} from '../../../Store/Actions/actions'

// This will be the container for hospital form
const HospitalPage = props => {
  const { role = 'OICProcessor', group = '', history = {} } = props
  const { id } = useParams()
  const isComponentAttached = React.useRef(true)
  const formStage = React.useRef({})
  const version = React.useRef(0)
  const dispatch = useDispatch()
  const hospitalState = useSelector(state => {
    return state.hospital
  })
  const [isLoaded, setIsLoaded] = React.useState(false)
  const [isCancel, setIsCancel] = React.useState(false)

  React.useEffect(() => {
    setIsLoaded(true)
    BreadcrumbsService.setSelectedItemsWithoutHome(['MEDICAL_NETWORK_OFFICER', 'HOSPITAL_OCULAR_INSPECTION_CHECKLIST'])
  }, [])

  React.useEffect(() => {
    LoadingService.loading(true)
    const fetchData = async () => {
      const { data } = await OICProcessorServices.getAffiliationFormInfo(id)
      if (isComponentAttached && data.data) {
        let generalInformation = {
          name: data.data.name,
          level: data.data.classification,
          mno: data.data.mno,
          address: data.data.full_address
        }
        if (data.data.partner_oic) {
          version.current = data.data.partner_oic.version
          generalInformation = {
            ...generalInformation,
            dateOfInspection: moment(data.data.partner_oic.generalInformation.dateOfInspection)
          }
          dispatch(
            updateState({
              ...data.data.partner_oic,
              generalInformation: { ...data.data.partner_oic.generalInformation, ...generalInformation }
            })
          )
        } else {
          generalInformation = {
            ...generalInformation,
            dateOfInspection: moment(hospitalState.generalInformation.dateOfInspection)
          }
          dispatch(
            updateState({
              generalInformation: { ...hospitalState.generalInformation, ...generalInformation }
            })
          )
        }
        formStage.current = { ...data.data.stage }

        if (
          ['FOR_OIC_APPROVAL', 'PROFILE_GENERATED', 'FOR_AFFILIATION_APPROVAL'].includes(data.data.stage.stage_name)
        ) {
          dispatch(setReadOnly(true))
        }
      }
      LoadingService.loading(false)
    }
    fetchData()
  }, [])

  React.useEffect(() => {
    if (hospitalState.save) {
      LoadingService.loading(true)
      updateData(formStage.current)
      LoadingService.loading(false)
    }
  }, [hospitalState.save])

  const updateData = async stage => {
    LoadingService.loading(true)
    let finalState = { ...hospitalState, stage }
    delete finalState.readOnly
    delete finalState.openSaveDialog
    delete finalState.openSubmitDialog
    delete finalState.page
    delete finalState.save
    delete finalState.errors
    delete finalState.openConditionDialog
    delete finalState.openRecommendationDialog

    const { data } = await OICProcessorServices.saveFacilityOICForm({
      id,
      stage: stage,
      partner_oic: finalState,
      version: version.current
    })
    if (data.data.writeConflict) {
      dispatch(savePage(false))
      LoadingService.loading(false)
      return SnackbarService.set({
        open: true,
        message: 'Form used by others. Return to dashboard',
        severity: 'error'
      })
    }
    if (isComponentAttached.current) version.current = data.data.partner_oic.version
    if (data.data.stage.stage_name === 'FOR_OIC_APPROVAL') {
      dispatch(setReadOnly(true))
      dispatch(openSaveDialog(false))
    } else {
      dispatch(openSaveDialog(true))
    }
    dispatch(savePage(false))
    dispatch(openSubmitDialog(false))
    LoadingService.loading(false)
  }

  const onClickReturnPage = () => {
    switch (hospitalState.page) {
      case 'roomAndBoard':
        return setIsCancel(true)
      case 'generalAppearance':
        return dispatch(returnPage('services'))
      case 'services':
        return dispatch(returnPage('roomAndBoard'))
    }
  }

  const onClickNextPage = () => {
    dispatch(validateField())
    switch (hospitalState.page) {
      case 'roomAndBoard':
        return dispatch(nextPage('services'))
      case 'services':
        return dispatch(nextPage('generalAppearance'))
    }
  }

  const onClickSavePage = () => {
    dispatch(validateField())
    dispatch(savePage(true))
  }

  const handleOnSubmit = () => {
    updateData({ stage_name: 'FOR_OIC_APPROVAL', date_submitted: moment().format() })
  }

  const renderCurrentPage = () => {
    switch (hospitalState.page) {
      case 'roomAndBoard':
        return <RoomAndBoard id='hospitalRoomAndBoard' readOnly={hospitalState.readOnly} />
      case 'services':
        return <Services id='hospitalServices' readOnly={hospitalState.readOnly} />
      case 'generalAppearance':
        return <GeneralAppearance id='hospitalGeneralAppearance' readOnly={hospitalState.readOnly} />
    }
  }

  return (
    <React.Fragment>
      {isLoaded && (
        <React.Fragment>
          <CustomAlertSnackBar
            isSave={hospitalState.openSaveDialog}
            setIsSave={val => dispatch(openSaveDialog(false))}
          />
          <SubmitDialog
            isSubmit={hospitalState.openSubmitDialog}
            setIsSubmit={val => dispatch(openSubmitDialog(val))}
            handleOnSubmit={handleOnSubmit}
            message='Are you sure  you want to proceed with the submission of the Hospital Ocular Inspection Checklist for approval?'
          />
          <CancelDialog
            isCancel={isCancel}
            setIsCancel={setIsCancel}
            handleOnCancel={() => history.push('/oicprocessor')}
          />
          <div id='hospitalRoot' className={classes.root}>
            <Provider store={store}>
              <HeaderWithBreadCrumbsNoStepper id='hospitalHeader' HeaderTitle='Hospital Checklist' />
            </Provider>
            <div id='hospitalContent' className={classes.content}>
              {hospitalState.page !== 'generalAppearance' && (
                <React.Fragment>
                  <GeneralInformation
                    id='hospitalGeneralInformation'
                    readOnly={hospitalState.readOnly}
                    nameField={{ label: 'Name of Facility', content: `${hospitalState.generalInformation.name}` }}
                    levelField={{ label: 'Facility Level', content: `${hospitalState.generalInformation.level}` }}
                    mnoField={{ label: 'MNO Assigned', content: `${hospitalState.generalInformation.mno}` }}
                    addressField={{ label: 'Facility Address', content: `${hospitalState.generalInformation.address}` }}
                    inspectorField={{
                      label: 'Name of Inspector *',
                      value: `${hospitalState.generalInformation.nameOfInspector}`
                    }}
                    dateField={{
                      label: 'Date of Inspection *',
                      value: `${hospitalState.generalInformation.dateOfInspection}`
                    }}
                    errors={{
                      nameOfInspector: hospitalState.errors.nameOfInspector,
                      dateOfInspection: hospitalState.errors.dateOfInspection
                    }}
                    dispatch={dispatch}
                    onChange={updateGeneralInformation}
                  />
                  <TypeOcularInspection
                    id='hospitalTypeOcularInspection'
                    readOnly={hospitalState.readOnly}
                    inspectorType={hospitalState.inspectorType.type}
                    inspectorName={hospitalState.inspectorType.name}
                    dispatch={dispatch}
                    onChange={updateInspectorType}
                    errors={{
                      name: hospitalState.errors.name
                    }}
                  />
                </React.Fragment>
              )}
              {renderCurrentPage()}
            </div>
            <Footer
              id='hospitalFooter'
              role={role}
              group={group}
              readOnly={hospitalState.readOnly}
              currentPage={hospitalState.page}
              onClickReturnPage={onClickReturnPage}
              onClickNextPage={onClickNextPage}
              onClickSavePage={onClickSavePage}
              onClickSubmitPage={() => dispatch(openSubmitDialog(true))}
            />
            <div id='hospitalFooterPage' className={classes.footerPage}>
              <h3 className={classes.footerText}>
                {hospitalState.page === 'roomAndBoard' && 'Hospital Ocular Inspection Checklist 1 out of 3'}
                {hospitalState.page === 'services' && 'Hospital Ocular Inspection Checklist 2 out of 3'}
                {hospitalState.page === 'generalAppearance' && 'Hospital Ocular Inspection Checklist 3 out of 3'}
              </h3>
            </div>
          </div>
        </React.Fragment>
      )}
    </React.Fragment>
  )
}

export default HospitalPage
