export function setDoctorsIdsForAutoSuggest (payload) {
  return {
    type: 'SET_DOCTORS_IDS_FOR_AUTO_SUGGEST',
    payload
  }
}

export function setFaciltyNameForAutoSuggest (payload) {
  return {
    type: 'SET_FACILITY_NAME_FOR_AUTO_SUGGEST',
    payload
  }
}

export function setFacilityIdsForAutoSuggest (payload) {
  return {
    type: 'SET_FACILITY_IDS_FOR_AUTO_SUGGEST',
    payload
  }
}

export function setDoctorSpecialization (payload) {
  return {
    type: 'SET_DOCTOR_SPECIALIZATION',
    payload
  }
}
