import React from 'react'
import Axios from 'axios'
// Components
import HeaderWithBreadCrumbs from '../../../../Components/HeaderWithBreadCrumbs/HeaderWithBreadCrumbs'
import AffiliationForm from '../Components/AffiliationForm/AffiliationForm'
// import ErrorMsg from '../Components/Utilities/ErrorMsg'
import Validate from '../Components/Utilities/validateForms'
import history from '../../../../Components/History/History'
import InitalState from '../Components/Utilities/InitalState'

// import SendData from '../../../Components/Api/sendData'
import { useSelector } from 'react-redux'

// Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'

import classes from './DentalClinicInformation.module.scss'

// Constant
import cnts from 'Pages/Protected/Partner/Constants/stage'

const URL = `${process.env.REACT_APP_SERVER_API}`

const breadcrumbs = {
  Medical: {
    text: 'MEDICAL DATA SUPPORT',
    to: '/'
  },
  MedicalPartner: {
    text: 'MEDICAL PARTNER  AFFILIATION',
    to: '/'
  },
  Doctor: {
    text: 'DENTAL CLINIC AFFILIATION',
    to: '/dental-clinic-affiliation-form'
  }
}

let stepDetails = ['Fill-up Form', 'Upload Documents', 'Submit']
let headerTitle = 'Dental Clinic Affiliation Form'

const DentalClinicInformation = ({history}) => {
  const { affiliationType } = useSelector(state => state.affiliationType)
  const [states, setStates] = React.useState(InitalState)
  const [errorMsgs, setErrorMsg] = React.useState({})
  const [dentalAssociates, setAddDentalAssociate] = React.useState([])
  const [affiliationInfos, setaffiliationInfos] = React.useState([])
  const [formResultValue, setFormResultValue] = React.useState(false)
  const [dentist, setDentist] = React.useState({})

  let stage = {
    stageName: cnts.PARTNER.STAGE.AFFILIATION_APPROVAL_FOR_OIC,
    dateSubmitted: new Date().toISOString()
  }

  const OnSubmit = async data => {
    const dentistValue = DentisData(data)
    const dentalClinicValue = DentalClinic(data)

    setStates({ ...dentalClinicValue, ...stage, ...affiliationType, dentalAssociates, affiliationInfos })
    setDentist({ ...dentistValue })
    const { errors, formResult } = await Validate(data)
    setFormResultValue(formResult)
    setErrorMsg(errors)
  }

  const DentisData = data => {
    let dentistObj = {}
    dentistObj.firstName = data.firstName // required
    dentistObj.middleName = data.middleName // required
    dentistObj.lastName = data.lastName // required
    dentistObj.birthDate = data.birthDate // required
    dentistObj.civilStatus = data.civilStatus // required
    dentistObj.gender = data.gender // required
    dentistObj.emailAddress = data.emailAddress // required
    dentistObj.contactNumber = data.contactNumber // required
    dentistObj.mainSpecialization = data.mainSpecialization // required
    dentistObj.majorSpecialization = data.majorSpecialization // required
    dentistObj.subSpecialization = data.subSpecialization
    dentistObj.otherSpecialization = data.otherSpecialization
    dentistObj.pdaNumber = data.pdaNumber
    dentistObj.prcNumber = data.prcNumber // required
    dentistObj.prcExpiration = data.prcExpiration // required
    dentistObj.philhealthExpiration = data.philhealthExpiration
    dentistObj.tinNumber = data.tinNumber // required
    dentistObj.tinStatus = data.tinStatus // required
    dentistObj.taxRate = data.taxRate
    dentistObj.nonVatable = data.nonVatable

    return dentistObj
  }

  const DentalClinic = data => {
    let dentalObj = {}

    dentalObj.name = data.name // required
    dentalObj.mobile = data.mobile // required
    dentalObj.mainCoordinator = data.mainCoordinator // required
    dentalObj.mainCoordinator = data.mainCoordinator // required
    dentalObj.checkPayee = data.checkPayee // required
    dentalObj.unitFlrBldg = data.unitFlrBldg // required
    dentalObj.unitFlrBldg = data.unitFlrBldg // required
    dentalObj.street = data.street // required
    dentalObj.subdivision = data.subdivision // required
    dentalObj.region = data.region // required
    dentalObj.district = data.district // required
    dentalObj.barangay = data.barangay // required
    dentalObj.city = data.city // required
    dentalObj.zipCode = data.zipCode // required
    dentalObj.clinicSchedule = data.clinicSchedule // required
    dentalObj.roomNumber = data.roomNumber

    return dentalObj
  }

  const handleAddAffiliatedGroup = React.useCallback(
    data => {
      setaffiliationInfos([...affiliationInfos, data])
    },
    [formResultValue]
  )

  const HandleDentalAssociate = React.useCallback(
    data => {
      let datas = data
      setAddDentalAssociate([...dentalAssociates, datas])
    },
    [formResultValue]
  )

  const CallDentist = async () => {
    try {
      dentist.type = 'DENTIST' /** todo found error here. temporary fix */
      const { data: res } = await Axios.post(URL + '/p/d/partners/doctors', {
        ...affiliationType,
        ...dentist
      })
      console.log('res', res)
    } catch (e) {
      console.log('error', e)
    }
  }

  const CallDentalClinic = async () => {
    try {
      const { data: res } = await Axios.post(URL + '/p/f/partners/facilities', {
        ...affiliationType,
        ...states
      })
      console.log('res', res)
    } catch (e) {
      console.log('error', e)
    }
  }

  const proceedSubmitForm = () => {
    console.log('Proceed Submit Form')
    CallDentist()
    CallDentalClinic()
  }

  if (formResultValue) {
    try {
      CallDentist()
      CallDentalClinic()
      try {
        history.push('/dental-clinic-affiliation-requirements')
      } catch (e) {}
    } catch (e) {
      console.log('error', e)
    }
  }

  React.useEffect(() => {
    BreadcrumbsService.setItems(breadcrumbs)
    BreadcrumbsService.setSelectedItemsWithoutHome(['Medical', 'MedicalPartner', 'Doctor'])
  }, [])

  return (
    <React.Fragment>
      <div id='root' className={classes.root}>
        <HeaderWithBreadCrumbs
          id='HeaderWithBreadCrumbs'
          HeaderTitle={headerTitle}
          stepDetails={stepDetails}
          currentStep={1}
        />
        <div>
          <AffiliationForm
            onSubmit={OnSubmit}
            errorMsg={errorMsgs}
            proceedSubmitForm={proceedSubmitForm}
            states={states}
            AddAffiliatedGroups={handleAddAffiliatedGroup}
            handleDentalAssociateDatas={HandleDentalAssociate}
            history={history}
          />
        </div>
      </div>
    </React.Fragment>
  )
}

export default DentalClinicInformation
