import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Grid from '@material-ui/core/Grid'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import { RowRadioComponent } from '../../../../Components/RowInputComponents/RowInputComponents'
import { updateGeneralAppearance } from '../../../../Store/Actions/actions'
// import scss module
import classes from './GeneralAppearance.module.scss'

const SpaceGroup = props => {
  const { readOnly = false, space, onChange = () => {} } = props
  return (
    <React.Fragment>
      <RowRadioComponent
        id=''
        readOnly={readOnly}
        pos={0}
        title='SPACE'
        question='Reception/HMO area can accommodate at least 3 patients at the same time.'
        onChangeValue={onChange}
        rowObj={space[0]}
        group={'Space'}
      />
    </React.Fragment>
  )
}

const AccessibilityGroup = props => {
  const { readOnly = false, accessibility, onChange = () => {} } = props
  return (
    <React.Fragment>
      <RowRadioComponent
        id=''
        readOnly={readOnly}
        pos={0}
        title='ACCESSIBILITY'
        question='Facility location is accessible (near a main road).'
        onChangeValue={onChange}
        rowObj={accessibility[0]}
        group={'Accessibility'}
      />
      <RowRadioComponent
        id=''
        readOnly={readOnly}
        pos={1}
        questNo='2'
        question='Minimum of 2 parking slots.'
        onChangeValue={onChange}
        rowObj={accessibility[1]}
        group={'Accessibility'}
      />
    </React.Fragment>
  )
}

