const GeneralAppearance = props => {
  const dispatch = useDispatch()
  const generalAppearance = useSelector(state => state.dental.generalAppearance)
  const { readOnly = false } = props

  return (
    <React.Fragment>
      <div className={classes.root}>
        <div id='generalAppearanceHeaderContainer' className={classes.headerContainer}>
          <div>
            <h2 id='generalAppearanceHeaderTitle' className={classes.title}>
              General Appearance
            </h2>
            <h3 id='generalAppearanceHeaderSubTitle' className={classes.subTitle}>
              Please rate the general appearance of the facility based on the following description.
            </h3>
          </div>
          <div className={classes.ratingContainer}>
            <span id='gat_ratingLabel' className={classes.ratingLabel}>
              Facility Rating:{' '}
              <span id='gat_ratingScore' className={classes.ratingScore}>
                {generalAppearance.facilityRating === 0
                  ? generalAppearance.facilityRating
                  : generalAppearance.facilityRating.toFixed(2)}
              </span>
            </span>
          </div>
        </div>
        <div className={classes.content}>
          <Grid id='ct_grid' container spacing={1}>
            <Grid item xs={12}>
              <TableContainer style={{ maxHeight: '100vh' }}>
                <Table stickyHeader aria-label='sticky table'>
                  <TableHead className={classes.tableHead}>
                    <TableRow>
                      <TableCell className={classes.tableCell} align='left' style={{ width: '30%' }}>
                        {'Question'}
                      </TableCell>
                      <TableCell className={classes.tableCell} align='center' style={{ width: '30%' }}>
                        {'Choose Rating'}
                      </TableCell>
                      <TableCell className={classes.tableCell} align='left' style={{ width: '20%' }}>
                        {'Issues Encountered'}
                      </TableCell>
                      <TableCell className={classes.tableCell} align='left' style={{ width: '20%' }}>
                        {'Recommendations'}
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    <SpaceGroup
                      readOnly={readOnly}
                      space={generalAppearance.space}
                      onChange={val => dispatch(updateGeneralAppearance(val))}
                    />
                    <AccessibilityGroup
                      readOnly={readOnly}
                      accessibility={generalAppearance.accessibility}
                      onChange={val => dispatch(updateGeneralAppearance(val))}
                    />
                    <CleanlinessGroup
                      readOnly={readOnly}
                      cleanliness={generalAppearance.cleanliness}
                      onChange={val => dispatch(updateGeneralAppearance(val))}
                    />
                    <OrderlinessGroup
                      readOnly={readOnly}
                      orderliness={generalAppearance.orderliness}
                      onChange={val => dispatch(updateGeneralAppearance(val))}
                    />
                    <AppearanceGroup
                      readOnly={readOnly}
                      appearance={generalAppearance.appearance}
                      onChange={val => dispatch(updateGeneralAppearance(val))}
                    />
                  </TableBody>
                </Table>
              </TableContainer>
            </Grid>
          </Grid>
        </div>
      </div>
    </React.Fragment>
  )
}

export default GeneralAppearance
