import Moment from 'moment'
import AddressChaining from 'Constant/chaining-address'
import AffiliationEncoderService from 'Services/Api/Partner/affiliationEncoderService'

export function dateFormat (date = new Date(), format = 'MM/DD/YYYY') {
  if (!date) return ''
  return Moment(date).format(format)
}

export function ISOformat (date) {
  if (date === null || date === 'Invalid Date' || date === undefined) return ''
  return Moment(date).format()
}

export function dateFormatChecker (date, format = 'MM/DD/YYYY') {
  return Moment(date, format, true).isValid()
}

export function MonthdateFormat (date) {
  if (
    date === '7000-01-01T00:00:00.000Z' ||
    date === '1970-01-01T00:00:07.000Z' ||
    date === '1970-01-01T00:00:00.000Z' ||
    date === 'Invalid date' ||
    date === undefined ||
    date === 'undefined' ||
    date === null ||
    !isNaN(date) ||
    date.length === 0
  ) {
    return ''
  }
  return Moment(date).format('L')
}

export function YearDateFormat (date) {
  if (
    date === '7000-01-01T00:00:00.000Z' ||
    date === '1970-01-01T00:00:07.000Z' ||
    date === '1970-01-01T00:00:00.000Z' ||
    date === 'Invalid date' ||
    date === undefined ||
    date === 'undefined' ||
    date === null ||
    !isNaN(date) ||
    date.length === 0
  ) {
    return ''
  }
  return Moment(date).format('YYYY-MM-DD')
}

export function DateErrorHandler (date) {
  if (date === null || date === 'Invalid date' || date === undefined || date.length === 0 || date === 'undefined') return 7000
  return Moment(date).format('YYYY-MM-DD')
}

export function validateEmail (email, name, setErrors, errors) {
  let emailPattern = /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(email)
  if (email && !emailPattern) {
    errors[name] = 'Invalid email format'
    setErrors({ ...errors })
  }
}
export function validateMobileNo (mobileNo, name, setErrors, errors) {
  let mobileNoPattern = /^(09|\+639)\d{9}$/.test(mobileNo)
  if (mobileNo && !mobileNoPattern) {
    errors[name] = 'Invalid mobile number format'
    setErrors({ ...errors })
  }
}

export function getTinStatusValue (data) {
  if (data === '12%') {
    return '12%'
  } else if (data.trim() === '') {
    return ''
  } else if (data.trim().length === 0) {
    return ''
  } else if (data === 'Non-VaTable') {
    return 'Non-VATable'
  } else {
    return 'Non-VATable'
  }
}

export function getTinStatus (data) {
  if (data === '12%') {
    return 'VATable'
  } else if (data.trim() === '') {
    return ''
  } else if (data.trim().length === 0) {
    return ''
  } else if (data === 'Non-VaTable') {
    return 'Non-VATable'
  } else {
    return 'Non-VATable'
  }
}

export const getNestedObjects = (nestedObj, pathArr) => {
  return pathArr.reduce((obj, key) => (obj ? obj && obj[key] !== 'undefined' ? (obj[key] ? obj[key] : ' ') : '' : ''), nestedObj)
}

export function formatTinNumber (str) {
  let res = str.match(/(\d{3})(\d{3})(\d{3})(\d{3})/)
  return res ? res.splice(1, 4).join('-') : ''
}

export function formatPhilHealthNumber (str) {
  let res = str.match(/(\d{4})(\d{7})(\d{1})/)
  return res ? res.splice(1, 4).join('-') : ''
}

export function checkArray (data) {
  if (data === undefined || typeof data === 'undefined' || data.length === 0) {
    return []
  }

  return data
}

export async function handleAddressChaining (name, value, form, setForm, setBarangays, setCities, setProvinces, setIsFetching, isFetching) {
  let nForm = { ...form }
  const fetching = { ...isFetching }
  if (name === 'region') {
    nForm['region'] = value
    nForm['district'] = ''
    nForm['city'] = ''
    nForm['barangay'] = ''
    setBarangays([])
    setCities([])
    setForm(nForm)
    fetching['province'] = true
    setIsFetching(fetching)
    try {
      const province = await AddressChaining.fetchAddress({ category: 'province', field: 'region', value })
      fetching['province'] = false
      setIsFetching(fetching)
      setProvinces(province || [])
    } catch (error) {
      console.log('error occured', error)
      fetching['province'] = false
      setIsFetching(fetching)
    }
  }
  if (name === 'district') {
    nForm['district'] = value
    nForm['city'] = ''
    nForm['barangay'] = ''
    setBarangays([])
    setCities([])
    setForm(nForm)
    fetching['city'] = true
    setIsFetching(fetching)
    try {
      const cities = await AddressChaining.fetchAddress({category: 'city', field: 'province', value})
      fetching['city'] = false
      setIsFetching(cities || [])
      setCities(cities)
    } catch (error) {
      console.log('error occured', error)
      fetching['city'] = false
      setIsFetching(fetching)
    }
  }
  if (name === 'city') {
    nForm['city'] = value
    nForm['barangay'] = ''
    setForm(nForm)
    fetching['barangay'] = true
    setIsFetching(fetching)
    try {
      const barangays = await AddressChaining.fetchAddress({category: 'barangay', field: 'city', value})
      fetching['barangay'] = false
      setBarangays(barangays || [])
      setIsFetching(fetching)
    } catch (error) {
      console.log('error occured', error)
      fetching['barangay'] = false
      setIsFetching(fetching)
    }
  }
}

export async function fetchAddress (form, setBarangays, setCities, setProvinces) {
  try {
    const cities = await AddressChaining.fetchAddress({category: 'city', field: 'province', value: form['district']})
    const barangays = await AddressChaining.fetchAddress({category: 'barangay', field: 'city', value: form['city']})
    const province = await AddressChaining.fetchAddress({ category: 'province', field: 'region', value: form['region'] })
    const res = await Promise.all([province, cities, barangays])
    setProvinces(res ? res[0] : [])
    setCities(res ? res[1] : [])
    setBarangays(res ? res[2] : [])
  } catch (error) {
    setProvinces([])
    setCities([])
    setBarangays([])
    console.log('error in fecth address', error)
  }
}

export async function fetchRegion (setRegions) {
  try {
    const region = await AddressChaining.fetchAddress({ category: 'region' })
    setRegions(region)
  } catch (error) {
    setRegions([])
    console.log('error in fetch region', error)
  }
}

export function SpecializationListItems (doctorSpecialization) {
  let arr = []
  if (doctorSpecialization === undefined || typeof doctorSpecialization === 'undefined' || doctorSpecialization.length === 0) {
    return []
  } else {
    doctorSpecialization.map(items => {
      arr.push({
        id: items._id,
        text: items.description,
        value: items.description
      })
    })
  }
  return arr
}

export function validateTaxRateVal (form, errors) {
  // const taxRatePattern = new RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)
  // if (!taxRatePattern.test(form['vatTaxRate'])) {
  if (Math.round(form['vatTaxRate'] > 100)) {
    errors['vatTaxRate'] = 'Tax Rate must not exceed 100%'
  } else if (Math.round(form['vatTaxRate']) === 0) {
    errors['vatTaxRate'] = '"Tax Rate" is not allowed to be empty'
  } else if (Math.round(form['vatTaxRate']) < 0) {
    errors['vatTaxRate'] = '"Tax Rate" is not allowed to be negative'
  } else {
    delete errors['vatTaxRate']
  }
  return errors
}

export function validateZipCode (form, errors) {
  if (form['zipCode'] && form['zipCode'].length >= 5) {
    errors['zipCode'] = '"Zip Code" must be less than or equal to 4 digits'
  } else if (form['zipCode'].length <= 0) {
    errors['zipCode'] = '"Zip Code" is not allowed to be empty'
  } else {
    delete errors['zipCode']
  }
  return errors
}

export async function getDoctorSpecialization () {
  const {data: {data: { result }}} = await AffiliationEncoderService.getDoctorSpecialization()
  return result
}
