import React  from 'react'
import Axios from 'axios'

import Button from '@material-ui/core/Button'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import TextField from '../../../../Components/JoiValidationInputField'
import DialogTitle from '@material-ui/core/DialogTitle'

import classes from './AddRoomTypeModal.module.scss'
import AddRoomTypeValidation from '../validations/AddRoomTypeValidation'
import FormHelperText from '@material-ui/core/FormHelperText'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import RoomTypeService from '../../../../../../../Services/Api/RoomType/RoomType'
import CustomAlerSnackBar from '../../../../Components/CustomSnackBar/CustomAlertSnackbar'

const AddRoomTypeModal = props => {
  const ctr = parseInt(props.dataLength) + 1;
  const [errors, setErrors] = React.useState({})
  const [isLoading, setIsLoading] = React.useState(false)
  const [isSuccess, setIsSuccess] = React.useState(false)
  const [open, setOpen] = React.useState(false)
  const [success, setSuccess] = React.useState(false)
  const [form, setForm] = React.useState({
    roomTypeDesc: '',
    roomTypeRemarks: '',
    roomTypeCode: '',
  })

  const handleSubmit = e => {
    e.preventDefault()
    validate()
  }


  const validate = React.useCallback(() => {
    const { errors, isValid } = AddRoomTypeValidation(form)
    if (isValid) {
      setErrors({})
      save()
    } else {
      setErrors(errors)
    }
  },[form])

  const save = async () => {
    setSuccess(true)
    try {
    
        await RoomTypeService.add(form)
        LoadingService.loading(false)
        setIsSuccess(true)
        setOpen(false)
        props.fetchData()
        setForm({
          roomTypeDesc: '',
          roomTypeRemarks: '',
          roomTypeCode: '',
        })
    
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  const handleChange = ({ currentTarget: input }) => {
    const contactForm = { ...form }
    contactForm[input.name] = input.value
    setForm(contactForm)
    const pad = '0000'
    const counterText = (pad + ctr).slice(-pad.length)

    setForm((previousState) => ({
      ...previousState, ['roomTypeCode']: 'RM' + counterText
    }))
  }

  return (
    <>
      <React.Fragment>
        <Button id='container' className={classes.buttonAdd} variant='contained' onClick={() => { setOpen(true) ; setSuccess(false)}}>
        <text>
          Add Room Type
        </text>
        </Button>
      </React.Fragment>
      <Dialog fullWidth maxWidth={'md'} open={open} aria-labelledby='dialogTitle'>
        <DialogTitle id='dialogTitle' style={{ color: '#9ea3ac', marginTop: '2%' }}>ADD ROOM TYPE</DialogTitle>
        <DialogContent>
          <form className={classes.root} onSubmit={handleSubmit} noValidate autoComplete='off'>
            <div id='firstInput' className={classes.firstInput}>
              <div id='roomTypeDescription'>
                <TextField name='roomTypeDesc' onChange={handleChange} label='Room Type Description*' />
                <FormHelperText style={{ color: 'red' }}>{errors['roomTypeDesc']} </FormHelperText>
              </div>
              <div id='remarks'>
                <TextField onChange={handleChange} label='Remarks*' name='roomTypeRemarks' />
                <FormHelperText style={{ color: 'red' }}>{errors['roomTypeRemarks']} </FormHelperText>
              </div>
            </div>
          </form>
        </DialogContent>
        <DialogActions className={classes.DialogActions}>
          <Button
            onClick={() => {
              setOpen(false);
              setForm({  
              roomTypeDesc: '',
              roomTypeRemarks: '',
              roomTypeCode: '',
            });
            setErrors({})
            }}
            variant='contained'
            className={classes.Cancel}
          >
            CANCEL
          </Button>
          <Button variant='contained' onClick={handleSubmit} className={classes.Add}>
            CREATE
          </Button>
        </DialogActions>
      </Dialog>
      <CustomAlerSnackBar isSave={success} setIsSave={() => setSuccess(false)} />
    </>
  )
}

export default AddRoomTypeModal
