import React, { useContext } from 'react'

import Button from '@material-ui/core/Button'
import classes from './index.module.scss'
import MedicalClinicInformation from './MedicalClinicInformation/PartnerInformation/PartnerInformation'
import UpdateMedicalClinicInformation from './MedicalClinicInformation/EditPartnerInformation/EditPartnerInformation'
import ClinicProfileContext from './context'
import ClinicProfileParams from '../Context/ClinicProfileParams'
import { useParams } from 'react-router-dom'
import { useDispatch } from 'react-redux'
import { setListOfAffiliatedContacts } from '../../../../../Store/actions/Partner/listOfAffiliatedContacts'
import ProfileService from '../../../../../../../Services/Api/Partner/ProfileService'

const PartnerMedicalClinicInformation = props => {
  const dispatch = useDispatch()
  const { medicalId } = useParams()
  const {
    setParamId,
    isToUpdate,
    setIsToUpdate,
    setChangeName,
    isEdit,
    setIsEdit,
    setHeaderData,
    setStatus,
    setCity
  } = useContext(ClinicProfileParams)
  // const [isToUpdate, setIsToUpdate] = React.useState(false)
  const [profile, setProfile] = React.useState({})
  const [discountTypeData, setDiscountType] = React.useState([])

  React.useEffect(() => {
    setIsToUpdate(false)
  }, [])

  React.useEffect(() => {
    let mounted = true

    if (mounted) {
      fetchData()
      setParamId(medicalId)
    }

    return () => (mounted = false)
  }, [medicalId])

  const fetchData = async () => {
    try {
      const {
        data: { data: newData }
      } = await ProfileService.getClinicById(medicalId)

      const { address, name, status } = newData

      const { data: facilityData } = await ProfileService.getFacilityDiscount(medicalId)

      const { data } = facilityData

      setDiscountType(data.map(items => (items ? (items.discount_type ? items.discount_type : '') : '')))

      const callFlow = newData ? (newData.callFlow ? newData.callFlow : '') : ''
      const contacts = newData ? (newData.contacts ? newData.contacts : []) : []
      const contactObj = { callFlow, contacts }

      dispatch(setListOfAffiliatedContacts(contactObj))

      let city = address ? address.city : ''
      props.ClinicName(name || '')
      setStatus(status || '')
      props.setHeaderData({ status: status ? status.toUpperCase() : '', name })
      setCity(city)

      setProfile(newData)
    } catch (e) {
      throw e
    }
  }

  const discountType = React.useMemo(() => discountTypeData, [discountTypeData])

  return (
    <React.Fragment>
      <div id='editButton' className={classes.editButton}>
        <div id='buttonContainer'>
          <Button variant='contained' onClick={() => setIsToUpdate(true)} className={classes.buttonContainer}>
            <div id='insideText' className={classes.insideText}>
              <text>Edit Partner</text>
              <text>Information</text>
            </div>
          </Button>
        </div>
      </div>
      <ClinicProfileContext.Provider
        value={{
          profile,
          setIsToUpdate,
          setProfile,
          setChangeName,
          isEdit,
          setIsEdit,
          setHeaderData,
          setStatus,
          setCity,
          discountType
        }}
      >
        {!isToUpdate && <MedicalClinicInformation />}
        {isToUpdate && <UpdateMedicalClinicInformation />}
      </ClinicProfileContext.Provider>
    </React.Fragment>
  )
}

export default React.memo(PartnerMedicalClinicInformation)
