import React, { useState, useEffect, useContext } from 'react'
import moment from 'moment'

// Components

import TextField from '../../../../../../../../SharedComponents/TextField/CustomTextField'
import CustomTextFieldWithIcons from '../../../../../Components/CustomTextFieldWithIcons/CustomTextFieldWithIcons'
import CustomizedTextArea from '../../../../../Components/CustomizeTextArea/CustomizeTextArea'
import TwoTextArea from '../../../../../Components/TwoTextArea/TwoTextArea'

// marterial ui
import Radio from '@material-ui/core/Radio'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import FormControl from '@material-ui/core/FormControl'
import FormLabel from '@material-ui/core/FormLabel'

// doctor affiliation
import DoctorAffiliationContext from '../../context/DoctorAffiliationContext'

// style scss
import classes from './AffiliationForm.module.scss'
import { LoadingService } from 'SharedComponents/LoadingComponent/LoadingComponent'
import AffiliationTableDisplay from '../../../Components/affilationTableDisplay.js/AffiliationTableDisplay'
import getNestedObjects from '../../../../../Components/getNestedObjects/getNestedObjects'

// http service
import AffiliationApproverServices from 'Services/Api/Partner/AffiliationApproverServices'

const existingAffilitaionHmoHeader = [{ label: 'Existing Affiliated HMO/S', name: 'affiliatedGroup' }]

const AffiliationForm = props => {
  const [radioButtonValue, setRadioButtonValue] = useState('Vatable')

  const [formData, setFormData] = useState({})
  const [facilityInfo, setFacilityInfo] = useState({})
  const [radioButtonsValue, setRadioButtonsValue] = useState({})
  const [addedAssistant, setAddedAssistant] = useState([])
  const [AffilType, setAffilType] = useState(null)
  const { paramId, setAffiliationType, form, setForm, isApproved } = useContext(DoctorAffiliationContext)

  useEffect(() => {
    if (isApproved) {
      setFormData(form)
    } else {
      LoadingService.loading(true)
      fetchAffiliationData(paramId)
    }
  }, [])

  const fetchAffiliationData = async paramId => {
    try {
      const obj = {}
      const data = await AffiliationApproverServices.getDoctorPartnerById(paramId)

      obj.tinStatus = data.data.data.treasury ? data.data.data.treasury.tin_status : {}

      obj.nonVatValue = getNestedObjects(data, ['data', 'data', 'treasury', 'non_vatable'])

      setAffiliationType(data.data.data)
      console.log('datasss add assistant', data.data.data.treasury)
      console.log('#######################', obj)
      if (data.data.data['affiliation_type']) {
        setAffilType(data.data.data['affiliation_type'])
      }
      setAddedAssistant(data.data.data['coordinators']['asst_coordinators'])
      setFacilityInfo(data.data.data['facility_info'] ? data.data.data['facility_info'][0] : {})
      setRadioButtonsValue(obj)
      setFormData(data.data.data)
      setForm(data.data.data)
      LoadingService.loading(false)
    } catch (error) {
      LoadingService.loading(false)
    }
  }

  return (
    <React.Fragment>
      <div id='root' className={classes.root}>
        <div id='topText' className={classes.topText}>
          DOCTOR INFORMATION
        </div>
        <div id='medicalPartnerID' className={classes.medicalPartnerID}>
          <div id='medicalPartnerIdContainer' className={classes.medicalPartnerIdContainer}>
            <label>Medical Partner ID</label>
            <TextField
              value={formData['id']}
              readonly
              className={AffilType === 'new_affiliation' ? classes.disabledField : ''}
            />
          </div>
        </div>
        <div id='GeneralInformation' className={classes.GeneralInformationField}>
          <div id='generalInfoText' className={classes.generalInfoText}>
            General Information
          </div>
          <div id='generalInfoFields' className={classes.generalInfoFields}>
            <div id='firstPartField' className={classes.firstPartField}>
              <div id='11' className={classes.InputContainer}>
                <label>First Name</label>
                <TextField id='firstName' value={formData['name'] && formData['name']['first']} readonly />
              </div>
              <div id='12' className={classes.InputContainerTwo}>
                <div id='Suffix-div' className={classes.suffix}>
                  <label>Suffix</label>
                  <TextField id='Suffix' value={formData['name'] && formData['name']['suffix']} readonly />
                </div>
                <div className={classes.birthDate}>
                  <label>Birthdate</label>
                  <TextField value={moment(formData['dohDatePicker']).format('MM/DD/YYYY')} />
                </div>
              </div>
              <div id='13' className={classes.InputContainer}>
                <label>Email Address</label>
                <TextField value={formData['email_address']} readonly />
              </div>
              <div id='14' className={classes.InputContainerDropDown}>
                <label>Main Specialization</label>
                <TextField
                  id='Main Specialization'
                  value={formData['specialization'] && formData['specialization']['main']}
                  readonly
                />
              </div>
              <div id='15' className={classes.InputContainerDropDown}>
                <label>Sub-Specialization</label>
                <TextField
                  id='Sub-Specialization'
                  value={formData['specialization'] && formData['specialization']['sub']}
                  readonly
                />
              </div>
              <div id='16' className={classes.InputContainer}>
                <label>PMA Number</label>
                <TextField id='PMA-Number' value={formData['license'] && formData['license']['pma']} readonly />
              </div>
              <div id='17' className={classes.InputContainer}>
                <label>PRC License No.</label>
                <TextField
                  id='PRC-License'
                  value={formData['license'] && formData['license']['prc']['number']}
                  readonly
                />
              </div>
              <div id='18' className={classes.InputContainer}>
                <label>TIN Number</label>
                <TextField id='Tin-Number' value={formData['treasury'] && formData['treasury']['tin']} readonly />
              </div>
            </div>
            <div id='secondPartField' className={classes.secondPartField}>
              <div id='21' className={classes.firstTextField}>
                <div id='middle-name' className={classes.middleName}>
                  <label>Middle Name</label>
                  <TextField id='middle-name' value={formData['name'] && formData['name']['middle']} readonly />
                </div>
                <div id='last-name' className={classes.lastName}>
                  <label>Last Name</label>
                  <TextField id='Last Name' value={formData['name'] && formData['name']['last']} readonly />
                </div>
              </div>
              <div id='22' className={classes.secondTextField1}>
                <div id='civil-status' className={classes.civilStatus}>
                  <label>Civil Status</label>
                  <TextField id='Last Name' value={formData['civil_status']} readonly />
                </div>
                <div id='gender' className={classes.gender}>
                  <label>Gender</label>
                  <TextField id='gender-label' value={formData['gender']} readonly />
                </div>
              </div>
              <div id='23' className={classes.textField}>
                <label>Contact Number (phone/mobile)</label>
                <TextField id='Contact-Number-phone' value={formData['contact_number']} readonly />
              </div>
              <div id='24' className={classes.DropDowntextField}>
                <label>Major Specialization</label>
                <TextField
                  id='Major-Specialization'
                  value={formData['specialization'] && formData['specialization']['major']}
                  readonly
                />
              </div>
              <div id='25' className={classes.DropDowntextField}>
                <label>Other Specialization</label>
                <TextField
                  id='Other-Specialization'
                  value={formData['specialization'] && formData['specialization']['other']}
                  readonly
                />
              </div>
              <div id='26' className={classes.ptrNumberAndS2Number}>
                <div id='PTR-number-label'>
                  <label>PTR Number</label>
                  <TextField id='PTR-Number' value={formData['license'] && formData['license']['ptr']} readonly />
                </div>
                <div id='S2-number-label'>
                  <label>S2 Number</label>
                  <TextField id='S2-Number' value={formData['license'] && formData['license']['s2']} readonly />
                </div>
              </div>
              <div id='27' className={classes.textFieldforPRCfield}>
                <div id='PRC-Expiry-date'>
                  <label>PRC Expiry Date </label>
                  <TextField id='PRC-Expiry-date' value={moment(formData['dohDatePicker']).format('MM/DD/YYYY')} />
                </div>
                <div id='PhilHealth-Acrreditation-No'>
                  <label>PhilHealth Acrreditation No.</label>
                  <TextField
                    id='PhilHealth-Acrreditation-No-label'
                    value={formData['philhealth'] && formData['philhealth']['accreditation_number']}
                    readonly
                  />
                </div>
                <div id='Philhealth-EXPIRY-DATE'>
                  <label>Philhealth Expiry Date</label>
                  <TextField
                    id='Philhealth-EXPIRY-DATE'
                    value={moment(formData['philHealthExpiryDate']).format('MM/DD/YYYY')}
                  />
                </div>
              </div>
              <div id='28' className={classes.textField}>
                <label>Check Payee</label>
                <TextField id='Check-Payee' value={formData['treasury'] && formData['treasury']['payee']} readonly />
              </div>
            </div>
          </div>
        </div>
        <div id='Tin-status' className={classes.RadioButtons}>
          <div id='TinStatus' className={classes.TinStatus}>
            <div id='tinStatusRadioButtons' className={classes.tinStatusRadioButtons}>
              <div id='tinStatusSelect' className={classes.tinStatusSelect}>
                <FormControl component='fieldset'>
                  <FormLabel className={classes.label} component='legend'>
                    <label style={{ color: '#1e2071' }}> TIN Status </label>
                  </FormLabel>
                  <RadioGroup aria-label='gender' value={radioButtonValue}>
                    <FormControlLabel
                      value='12%'
                      checked={radioButtonsValue['tinStatus'] === '12%'}
                      disabled={radioButtonsValue['tinStatus'] !== '12%'}
                      control={<Radio color='primary' />}
                      label='VATable'
                    />
                    <FormControlLabel
                      value='Non-VaTable'
                      checked={radioButtonsValue['tinStatus'] === 'Non-VaTable'}
                      disabled={radioButtonsValue['tinStatus'] !== 'Non-VaTable'}
                      control={<Radio color='primary' />}
                      label='Non-VATable'
                    />
                  </RadioGroup>
                </FormControl>
              </div>
              <div id='taxRate' className={classes.taxRate}>
                <label>Tax Rate </label>
                <div id='taxRateInput' className={classes.taxRateInput}>
                  <CustomTextFieldWithIcons
                    value={formData['treasury'] && formData['treasury']['vat']['tax_rate']}
                    styles={classes.customTag}
                    tags={'%'}
                  />
                </div>
              </div>
            </div>
            <div id='taxRateAndNonVatableDropDown' className={classes.taxRateAndNonVatableDropDown}>
              {formData['treasury'] && formData['treasury']['tin_status'] === 'Non-VaTable' && (
                <div id='NonVatableDropDown' className={classes['DropDownContainer']}>
                  <TextField value={radioButtonsValue['nonVatValue']} readonly />
                </div>
              )}
            </div>
          </div>
        </div>
        <div id='facilityInformationField' className={classes.facilityInformationField}>
          <div id='facilityInformation'>Facility Information</div>
          <div id='textFieldArea' className={classes.textFieldArea}>
            <div id='firstLayerInputs' className={classes['firstLayerInputs']}>
              <div id='facilityInput' className={classes.facilityInput}>
                <label>Facility Name</label>
                <TextField id='Facility-Name' value={facilityInfo['name'] && facilityInfo['name']} readonly />
              </div>
              <div id='medicalPartnerId' className={classes.medicalPartnerId}>
                <label id='medicalPartnerId-label'> Medical Partner ID (Facility) </label>
                <TextField
                  id='Medical-partner-ID-facility'
                  value={facilityInfo['facility_id'] && facilityInfo['facility_id']}
                  readonly
                />
              </div>
            </div>
            <div id='SecondLayerInputs' className={classes['SecondLayerInputs']}>
              <div id='addressInput' className={classes.addressInput}>
                <CustomizedTextArea
                  value={facilityInfo['full_address'] && facilityInfo['full_address']}
                  id='Address'
                  style={classes.AddressField}
                  label={'Address'}
                />
              </div>
            </div>
            <div id='ThirdLayerInputs' className={classes['ThirdLayerInputs']}>
              <div id='room-number-div' className={classes.roomNumber}>
                <label>Room Number </label>
                <TextField
                  id='Room-Number'
                  value={formData['facility_info'] && formData['facility_info'][0]['room_number']}
                  readonly
                />
              </div>
              <div id='contact-number-fields' className={classes.contactNumber}>
                <label>Contact Number/s</label>
                <TextField
                  id='Contact-Number'
                  value={formData['facility_info'] && formData['facility_info'][0]['contact_numbers']}
                  readonly
                />
              </div>
              <div id='Clinic-Schedule-div' className={classes['Clinic-Schedule-div']}>
                <label>Clinic Schedule</label>
                <TextField
                  id='Clinic-Schedule'
                  value={formData['facility_info'] && formData['facility_info'][0]['clinic_schedule']}
                  readonly
                />
              </div>
            </div>
            <div id='FourthLayerInputs' className={classes['FourthLayerInputs']}>
              <div id='coordinators-input' className={classes['coordinators-input']}>
                <label> Coordinators</label>
                <div>
                  <label>Name of Main Coordinators</label>
                  <TextField
                    id='Name-of-Coordinators'
                    value={formData['coordinators'] && formData['coordinators']['main_coordinator_name']}
                    readonly
                  />
                </div>
              </div>
              <div id='Contact-Number-division' className={classes['Contact-Number']}>
                <label> '</label>
                <div>
                  <label>Contact Number</label>
                  <TextField
                    id='Contact-Number-textField'
                    value={formData['coordinators'] && formData['coordinators']['main_contact_num']}
                    readonly
                  />
                </div>
              </div>
            </div>
            <div id='FifthLayerInputs' className={classes['FifthLayerInputs']}>
              <div id='namenameOfAssitantFields' className={classes.namenameOfAssitantFields}>
                <div id='nameOfAssitantInputs'>
                  <label>Name of Assistant Coordinator/s</label>
                  {/* <TextField
                    id='Name-of-Assistant-Coordinator/s'
                    value={formData['assistantCoordinatorName']}
                    readonly
                  /> */}
                </div>
                <div id='Contact-Number-One-div' className={classes['Contact-Number-One-div']}>
                  <label>Contact Number </label>
                  {/* <TextField id='Contact-Number-One' value={formData['assistantCoordinatorContactNo']} readonly /> */}
                </div>
              </div>
              <div id='contactFields' className={classes.AdditionalNameOfAssistantFields}>
                {addedAssistant.map((assistant, i) => {
                  return (
                    <div key={i + 32} id='AddAssistantWrapper' className={classes.AddAssistantWrapper}>
                      <div id='lastInputField'>
                        <TextField id='thirdInput' value={assistant.asstCoordinatorName} />
                      </div>
                      <div>
                        <div id='Contact-Number-two-div' className={classes['Contact-Number-two-div']}>
                          <TextField value={assistant.asstContactNum} />
                        </div>
                      </div>
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
        </div>
        <div id='AffiliationsPart' className={classes.AffiliationPart}>
          <label id='AffiliaionText' className={classes.AffiliaionText}>
            Affiliations
          </label>
          {/* <div id='checkBox' className={classes.checkBox}>
            <CheckBox label={'Affiliated to AHMOPI?'} />
          </div> */}
          <div id='tableDropDownInput' className={classes.tableDropDownInput}>
            <label id='textDesciption' className={classes.textDesciption}>
              AHMOPI Affiliations, Health Organizations and Specialty Societies
            </label>
            <AffiliationTableDisplay header={existingAffilitaionHmoHeader} data={formData['affiliation_infos']} />
          </div>
        </div>
        <div id='textArea' className={classes.textArea}>
          <TwoTextArea
            val1={formData['remarks'] && formData['remarks']['applicant']}
            val2={formData['remarks'] && formData['remarks']['intellicare']}
            label1={'Applicant Remarks'}
            label2={'Intellicare Remarks'}
          />
        </div>
      </div>
    </React.Fragment>
  )
}

export default AffiliationForm
