import { DentalState } from '../../StateConstants/Dental'
import ProcessorValidator from '../../Validator/ProcessorValidator'
import {
  UPDATE_GENERAL_INFORMATION,
  UPDATE_INSPECTOR_TYPE,
  UPDATE_SERVICES,
  UPDATE_GENERAL_APPEARANCE,
  VALIDATE_FIELD,
  SAVE_PAGE,
  RETURN_PAGE,
  NEXT_PAGE,
  OPEN_SAVE_DIALOG,
  UPDATE_STATE,
  SET_READ_ONLY,
  OPEN_SUBMIT_DIALOG,
  OPEN_CONDITION_DIALOG,
  UPDATE_CONDITION,
  UPDATE_RECOMMENDATION,
  OPEN_RECOMMENDATION_DIALOG,
  DISAPPROVED_FORM,
  UPDATE_DENTAL_SERVICE,
  RESET_STATE
} from '../types'
const initState = {
  ...DentalState,
  ...{
    condition: '',
    recommendation: '',
    readOnly: false,
    openSaveDialog: false,
    openSubmitDialog: false,
    openConditionDialog: false,
    openRecommendationDialog: false,
    page: 'services',
    save: false,
    errors: {}
  }
}

const StateReducer = (state = initState, action) => {
  switch (action.type) {
    case UPDATE_GENERAL_INFORMATION:
      const generalInformation = { generalInformation: { ...state.generalInformation, ...action.payload } }
      return { ...state, ...generalInformation }
    case UPDATE_INSPECTOR_TYPE:
      const inspectorType = { inspectorType: { ...state.inspectorType, ...action.payload } }
      return { ...state, ...inspectorType }
    case UPDATE_DENTAL_SERVICE:
      const services = { services: { ...state.services, ...action.payload } }
      return { ...state, ...services }
    case UPDATE_GENERAL_APPEARANCE:
      let newArray = [...state.generalAppearance[action.payload.name]]
      newArray[action.payload.pos] = { ...newArray[action.payload.pos], ...action.payload.obj }
      const updatedState = {
        generalAppearance: { ...state.generalAppearance, ...{ [action.payload.name]: newArray } }
      }
      const spaceScoreTotal = updatedState.generalAppearance.space.reduce((total, obj) => obj.score + total, 0)
      const accessibilityScoreTotal = updatedState.generalAppearance.accessibility.reduce(
        (total, obj) => obj.score + total,
        0
      )
      const cleanlinessScoreTotal = updatedState.generalAppearance.cleanliness.reduce(
        (total, obj) => obj.score + total,
        0
      )
      const orderlinessScoreTotal = updatedState.generalAppearance.orderliness.reduce(
        (total, obj) => obj.score + total,
        0
      )
      const appearanceScoreTotal = updatedState.generalAppearance.appearance.reduce(
        (total, obj) => obj.score + total,
        0
      )
      const average =
        (spaceScoreTotal +
          accessibilityScoreTotal +
          cleanlinessScoreTotal +
          orderlinessScoreTotal +
          appearanceScoreTotal) /
        8

      const generalAppearance = {
        generalAppearance: { ...updatedState.generalAppearance, facilityRating: average }
      }
      return { ...state, ...generalAppearance }
    case VALIDATE_FIELD:
      const { nameOfInspector, dateOfInspection } = state.generalInformation
      const { name, type } = state.inspectorType
      let processorError = ProcessorValidator({
        nameOfInspector,
        dateOfInspection: dateOfInspection ? dateOfInspection.toDate() : '',
        name: name
      }).error
      if (processorError && type === 'walkIn') {
        processorError = {
          ...(processorError.nameOfInspector ? { nameOfInspector: processorError.nameOfInspector } : {}),
          ...(processorError.dateOfInspection ? { dateOfInspection: processorError.dateOfInspection } : {})
        }
      }
      return { ...state, errors: { ...processorError } }

    case SAVE_PAGE:
      if (state.errors && Object.keys(state.errors).length > 0) return { ...state, save: false }
      return { ...state, save: action.payload }

    case OPEN_SAVE_DIALOG:
      return { ...state, openSaveDialog: action.payload }

    case NEXT_PAGE:
      if (state.errors && Object.keys(state.errors).length > 0) return { ...state }
      return { ...state, page: action.payload }

    case RETURN_PAGE:
      return { ...state, page: action.payload }

    case UPDATE_STATE:
      return { ...state, ...action.payload }

    case SET_READ_ONLY:
      return { ...state, readOnly: action.payload }

    case OPEN_SUBMIT_DIALOG:
      return { ...state, openSubmitDialog: action.payload }

    case OPEN_CONDITION_DIALOG:
      return { ...state, openConditionDialog: action.payload }

    case OPEN_RECOMMENDATION_DIALOG:
      return { ...state, openRecommendationDialog: action.payload }

    case UPDATE_CONDITION:
      return { ...state, condition: action.payload }

    case UPDATE_RECOMMENDATION:
      return { ...state, recommendation: action.payload }

    case RESET_STATE:
      return { ...initState }
    default:
      return { ...state }
  }
}

export default StateReducer
