import React from 'react'
import { Switch, Route } from 'react-router-dom'
import { Provider } from 'react-redux'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'

// import Shared Components
import { BreadcrumbsService } from 'SharedComponents/BreadcrumbsComponent/BreadcrumbsComponent'
import Dashboard from './Pages/Dashboard/Dashboard'
import ContractPage from './Pages/ContractPage/ContractPage'
import EvaluatePage from './Pages/EvaluatePage/EvaluatePage'

import store from '../../Store'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/contractmanager'
  },
  {
    label: 'Partners',
    icon: 'users',
    to: '/test'
  },
  {
    label: 'Tickets',
    icon: 'list',
    to: '/test2'
  },
  {
    label: 'Reports',
    icon: 'chart-bar',
    to: '/test3'
  },
  {
    label: 'User',
    icon: 'user',
    to: '/test4'
  },
  {
    label: 'Table Maintenance',
    icon: 'calendar-alt',
    to: '/test5'
  }
]

const breadcrumbs = {
  CONTRACT_MANAGER: {
    text: 'CONTRACT MANAGER',
    to: '/contractmanager'
  },
  PRINT_CONTRACT: {
    text: 'PRINT CONTRACT',
    to: '/'
  }
}

const ContractManager = props => {
  const [isLoaded, setIsLoaded] = React.useState(false)

  React.useEffect(() => {
    SideBarService.setItems(sideBarItems)
    BreadcrumbsService.setItems(breadcrumbs)
    setIsLoaded(true)
    props.history.push('/contractmanager')
  }, [])

  return (
    <React.Fragment>
      <Provider store={store}>
        {isLoaded && (
          <Switch>
            <Route
              path='/contractmanager'
              exact
              render={props => <Dashboard {...props} group='contract' role='manager' />}
            />
            <Route
              path='/contractmanager/view/:type/:id/:facilityId?'
              exact
              render={props => <ContractPage {...props} group='contract' role='manager' />}
            />
            <Route
              path='/contractmanager/evaluate/:type/:id/:facilityId?'
              exact
              render={props => <EvaluatePage {...props} group='evaluate' role='manager' />}
            />
          </Switch>
        )}
      </Provider>
    </React.Fragment>
  )
}

export default ContractManager
