import AffiliationEncoderService from '../../../../../../../../Services/Api/Partner/affiliationEncoderService'

import { YearDateFormat, getNestedObjects } from '../../../../../Components/Utils/utils'

export async function getDentalClinicProfile (id) {
  const {
    data: { data }
  } = await AffiliationEncoderService.getSingleDentalClinic(id)

  return mapData(data)
}

function mapData (profile) {
  if (profile) {
    const {
      contact,
      philhealth,
      disaffiliation,
      stage,
      status,
      discount,
      id,
      type,
      name,
      former_name,
      full_address,
      address,
      deviations,
      remarks,
      clinic_schedule,
      treasury,
      dental_associates,
      affiliation_infos,
      affiliated_partners,
      afn_number,
      classification,
      facility_assignment,
      affiliation_status,
      license,
      other_affiliations,
      room_number,
      rooms,
      special_instruction,
      process_type,
      tvs,
      reason
    } = profile

    const requiredObject = {}
    requiredObject.unitFlrBldg = profile['address'] ? profile['address']['unit_flr_bldg'] : ''
    requiredObject.bldgName = profile['address'] ? profile['address']['building_name'] : ''
    requiredObject.subdivision = profile['address'] ? profile['address']['subdivision'] : ''
    requiredObject.dohNumber = profile['license'] ? profile['license']['doh']['license_type'] : ''
    requiredObject.dohLicenseType = profile['license'] ? profile['license']['doh']['number'] : ''
    requiredObject.dohLicenseExp = profile['license'] ? profile['license']['doh']['license_exp'] : ''
    requiredObject.stageName = profile['stage']['stage_name']
    requiredObject.dateSubmitted = stage['date_submitted'] ? stage['date_submitted'] : new Date().toISOString()
    requiredObject.contacts = profile['contacts']
    requiredObject.internalRemarks = profile['remarks'] ? profile['remarks']['internal_remarks'] : ''
    requiredObject.externalRemarks = profile['remarks'] ? profile['remarks']['external_remarks'] : ''
    requiredObject.type = profile['type']

    let obj = {}
    obj.formerName = former_name
    obj.id = id
    obj.partnerName = name || ''
    obj.status = status || ''
    obj.address = full_address || ''
    obj.tinStatus = getNestedObjects(treasury, ['tin_status']) // treasury ? treasury['tin_status'] : ''
    obj.payee = getNestedObjects(treasury, ['payee']) // treasury ? treasury['payee'] : ''
    obj.creditLimit = getNestedObjects(treasury, ['credit_limit']) //  treasury ? treasury['credit_limit'] : ''
    obj.paymentTerms = getNestedObjects(treasury, ['payment_terms']) // treasury ? treasury['payment_terms'] : ''
    obj.vatTaxRate = getNestedObjects(treasury, ['vat', 'tax_rate']) // treasury ? (treasury['vat'] ? treasury['vat']['tax_rate'] : '') : ''
    obj.accreditationNumber = getNestedObjects(philhealth, ['accreditation_number']) // philhealth['accreditation_number'] ? philhealth['accreditation_number'] : ''
    obj.dateOfDisaffiliation = disaffiliation
      ? disaffiliation['date']
        ? YearDateFormat(disaffiliation['date'])
        : ''
      : ''
    obj.dateOfAffiliation = affiliation_status
      ? affiliation_status.date_submitted
        ? YearDateFormat(affiliation_status.date_submitted)
        : ''
      : ''
    obj.philhealthExp = philhealth
      ? philhealth['expiration_date']
        ? YearDateFormat(philhealth['expiration_date'])
        : ''
      : ''
    obj.years = getNestedObjects(profile, ['operation', 'years']) // profile['operation'] ? profile['operation']['years'] : ''
    obj.months = getNestedObjects(profile, ['operation', 'months']) // profile['operation'] ? profile['operation']['months'] : ''
    obj.street = getNestedObjects(address, ['street']) // address['street']
    obj.region = getNestedObjects(address, ['region']) //  address['region']
    obj.district = getNestedObjects(address, ['district']) // address['district']
    obj.city = getNestedObjects(address, ['city']) // address['city']
    obj.barangay = getNestedObjects(address, ['barangay']) // address['barangay']
    obj.zipCode = getNestedObjects(address, ['zip_code']) // address['zip_code']
    obj.clinicSchedule = clinic_schedule || ''
    obj.tvs = tvs || ''
    obj.facilityDiscount = getNestedObjects(treasury, ['discount'])
    obj.classification = classification
    obj.assignments = facility_assignment
      ? facility_assignment['assignments']
        ? facility_assignment['assignments']
        : []
      : []

    obj.exclusiveTo = facility_assignment
      ? facility_assignment['exclusive_to']
        ? facility_assignment['exclusive_to']
        : []
      : []
    obj.processType = process_type || ''
    obj.specialInstructions = getNestedObjects(treasury, ['special_instruction']) // treasury ? treasury['special_instruction'] : ''
    obj.callFlow = getNestedObjects(profile, ['callFlow']) // profile ? (profile.callFlow ? profile.callFlow : '') : ''
    obj.contacts = profile ? (profile.contacts ? profile.contacts : []) : ''
    obj.reason = reason || ''

    return { profile: obj, additionalProfileInfo: requiredObject }
  } else {
    return false
  }
}
