import printJS from 'print-js'

const options = { printable: '', type: 'pdf', base64: true }

export default function PrintPDFReport (blob) {
  //Convert blob to Base64
  let reader = new FileReader()
  reader.readAsDataURL(blob)
  reader.onloadend = () => {
    const content = reader.result.split(',')
    const base64data = content[1]

    printJS({ ...options, printable: base64data })
  }
}
