import React from 'react'
import InputBase from '@material-ui/core/InputBase'
import NumberFormat from 'react-number-format'
import TableRow from '@material-ui/core/TableRow'
import TableCell from '@material-ui/core/TableCell'
import Box from '@material-ui/core/Box'
import Paper from '@material-ui/core/Paper'

import classes from './RowInputComponents.module.scss'

export const IntegerComponent = props => {
  const { readOnly = false, id = '', value = '', disabled = false, onChange = () => {}, paperStyle = {} } = props

  return readOnly ? (
    <p>{value}</p>
  ) : (
    <Paper style={disabled ? { ...paperStyle, background: '#9ea3ac' } : { ...paperStyle }}>
      <NumberFormat
        id={id}
        style={readOnly ? { color: '#151c1b', height: '100%' } : { height: '100%' }}
        disabled={disabled}
        value={value === '0' || value === 0 || value === '' ? '' : value}
        onValueChange={value => onChange(value.floatValue)}
        className={classes.integerComponent}
        decimalSeparator={false}
        customInput={InputBase}
        isNumericString
        allowNegative={false}
        displayType={'input'}
        allowLeadingZeros={false}
      />
    </Paper>
  )
}

export const PriceComponent = props => {
  const { id = '', value = '', disabled = false, onChange = () => {}, paperStyle = {} } = props
  return (
    <Paper style={disabled ? { ...paperStyle, background: '#9ea3ac' } : { ...paperStyle }}>
      <NumberFormat
        id={id}
        disabled={disabled}
        value={value === '0.00' || value === 0.0 ? '' || value === '' : value}
        onValueChange={value => onChange(value.floatValue)}
        className={classes.priceComponent}
        fixedDecimalScale
        customInput={InputBase}
        thousandSeparator
        decimalScale={2}
        isNumericString
        allowNegative={false}
        displayType={'input'}
        allowLeadingZeros={false}
      />
    </Paper>
  )
}

export const MultiTextComponent = props => {
  const {
    id = '',
    value = '',
    disabled = false,
    onChange = () => {},
    rowsMax = 1,
    placeHolder = '',
    readOnly = false
  } = props

  return (
    <Paper style={disabled ? { background: '#9ea3ac' } : {}}>
      <InputBase
        id={id}
        disabled={disabled || readOnly}
        placeholder={placeHolder}
        style={readOnly ? { color: '#151c1b' } : {}}
        className={classes.multiTextComponent}
        value={value}
        onChange={onChange}
        type={'text'}
        multiline
        rows={rowsMax}
        rowsMax={rowsMax}
      />
    </Paper>
  )
}

export const CircularButton = props => {
  const { id = '', value = '', disabled = false, onChange = () => {}, paperStyle = {}, readOnly = false } = props

  const { title, subTitle } = props
  return (
    <div className={classes.root}>
      <button
        id={id}
        onClick={readOnly ? () => {} : props.onClickCB}
        style={props.cbBackground}
        className={classes.circularButton}
      >
        <div classes={classes.circularButtonContent}>
          <p style={{ margin: 0, fontSize: '1.1rem', color: '#151c1b' }}>{title}</p>
          <p style={{ margin: 0, fontSize: '.5rem', color: '#151c1b' }}>{subTitle}</p>
        </div>
      </button>
    </div>
  )
}

export const RowRadioComponent = React.memo(
  props => {
    const {
      readOnly = false,
      pos = '',
      title = '',
      question = '',
      onChangeValue = () => {},
      rowObj = {},
      group = {}
    } = props

    const onChangeScore = newState => {
      onChangeValue({ name: group.toLowerCase(), pos: pos, obj: { score: newState } })
    }

    const onChangeIssues = newState => {
      onChangeValue({ name: group.toLowerCase(), pos: pos, obj: { issuesEncountered: newState } })
    }

    const onChangeRecommendations = newState => {
      onChangeValue({ name: group.toLowerCase(), pos: pos, obj: { recommendations: newState } })
    }

    return (
      <React.Fragment>
        <TableRow>
          <TableCell className={classes.tableCell} align='left' style={{ border: 'none' }}>
            <Box display='flex' flexDirection='column' justifyContent='flex-start'>
              <h3 className={classes.tableCellTitle}>{title.toUpperCase()}</h3>
              <h3 className={classes.tableCellText}>
                {rowObj.questNo}. {question}
              </h3>
            </Box>
          </TableCell>
          <TableCell align='center' style={{ display: 'flex', justifyContent: 'center', border: 'none' }}>
            <CircularButton
              id={`circularButton${group}1`}
              readOnly={readOnly}
              cbBackground={rowObj.score === 1 ? { background: '#FD5451' } : { background: 'white' }}
              title='1'
              onClickCB={() => onChangeScore(1)}
              subTitle='Very Poor'
            />
            <CircularButton
              id={`circularButton${group}2`}
              readOnly={readOnly}
              cbBackground={rowObj.score === 2 ? { background: '#F05E23' } : { background: 'white' }}
              title='2'
              onClickCB={() => onChangeScore(2)}
              subTitle='Poor'
            />
            <CircularButton
              id={`circularButton${group}3`}
              readOnly={readOnly}
              cbBackground={rowObj.score === 3 ? { background: '#FAB435' } : { background: 'white' }}
              title='3'
              onClickCB={() => onChangeScore(3)}
              subTitle='Fair'
            />
            <CircularButton
              id={`circularButton${group}4`}
              readOnly={readOnly}
              cbBackground={rowObj.score === 4 ? { background: '#9ACD32' } : { background: 'white' }}
              title='4'
              onClickCB={() => onChangeScore(4)}
              subTitle='Good'
            />
            <CircularButton
              id={`circularButton${group}5`}
              readOnly={readOnly}
              cbBackground={rowObj.score === 5 ? { background: '#3ab77d' } : { background: 'white' }}
              title='5'
              onClickCB={() => onChangeScore(5)}
              subTitle='Excellent'
            />
          </TableCell>
          <TableCell align='center' style={{ border: 'none' }}>
            <MultiTextComponent
              id={`issuesEncountered${group}${rowObj.questNo}`}
              readOnly={readOnly}
              rowsMax={3}
              paperStyle={{ height: '70px' }}
              value={rowObj.issuesEncountered}
              onChange={e => onChangeIssues(e.target.value)}
              placeHolder={readOnly ? '' : 'Type any issues you found.'}
            />
          </TableCell>
          <TableCell align='center' style={{ border: 'none' }}>
            <MultiTextComponent
              id={`recommendations${group}${rowObj.questNo}`}
              readOnly={readOnly}
              rowsMax={3}
              paperStyle={{ height: '70px' }}
              value={rowObj.recommendations}
              onChange={e => onChangeRecommendations(e.target.value)}
              placeHolder={readOnly ? '' : 'Type any issues you found.'}
            />
          </TableCell>
        </TableRow>
      </React.Fragment>
    )
  },
  (prevProps, nextProps) => {
    try {
      if (
        prevProps.pos === nextProps.pos &&
        prevProps.readOnly === nextProps.readOnly &&
        prevProps.rowObj.questNo === nextProps.rowObj.questNo &&
        prevProps.rowObj.score === nextProps.rowObj.score &&
        prevProps.rowObj.issuesEncountered === nextProps.rowObj.issuesEncountered &&
        prevProps.rowObj.recommendations === nextProps.rowObj.recommendations
      ) {
        return true
      }
      return false
    } catch (err) {
      return false
    }
  }
)
