import instance from './../instance'
const axios = instance('/p/d/partners')

export default class PartnerDoctorService {
  static createdPartnerDoctor (data) {
    return axios({
      url: '/doctors',
      method: 'POST',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static updatePartnerDoctor (id, data) {
    return axios({
      url: `/doctors/${id}`,
      method: 'PUT',
      headers: { skip_generate_form_data: 1 },
      data
    })
  }

  static getPartnerDoctorById (id) {
    return axios({
      url: `/doctors/${id}`,
      method: 'GET'
    })
  }

  static getMedicalPartner (id) {
    return axios({
      url: `/doctors/medical-partner/${id}`,
      method: 'GET'
    })
  }

  static getIDs (params, token = '') {
    return axios({
      url: '/doctors/auto-suggest-ids',
      method: 'GET',
      params,
      cancelToken: token
    })
  }

  /* static doctorFileUpload () {
    return axios({
      url: '/doctors/auto-suggest-ids',
      p
    })
  } */
}
