import React from 'react'
import classes from './PartnerInformation.module.scss'
import HospitalProfileContext from '../../context'
import PartnerViewInformation from '../../../../../../Components/PartnerViewInformation/PartnerViewInformation'
import { getTinStatus, MonthdateFormat, getNestedObjects } from '../../../../../../Components/Utils/utils'

const PartnerHospitalInformation = props => {
  const { profile } = React.useContext(HospitalProfileContext)

  let assignments = profile
    ? profile.facility_assignment
      ? profile.facility_assignment.assignments
        ? profile.facility_assignment.assignments
        : []
      : []
    : []

  const HospitalInformation = [
    { id: 301, title: 'Tvs', value: getNestedObjects(profile, ['tvs']) },
    {
      id: 302,
      title: 'Medical Partner Id',
      value: getNestedObjects(profile, ['id'])
    },
    {
      id: 3022,
      title: 'Former Name',
      value: getNestedObjects(profile, ['former_name'])
    },
    {
      id: 303,
      title: 'Facility Address',
      value:
        `${getNestedObjects(profile, ['address', 'street'])}, ${getNestedObjects(profile, ['address', 'barangay'])}, ${getNestedObjects(profile, ['address', 'city'])}, ${getNestedObjects(profile, ['address', 'district'])}, ${getNestedObjects(profile, ['address', 'region'])} ${getNestedObjects(profile, ['address', 'zip_code'])}`
    },
    {
      id: 304,
      title: 'Classification',
      value: getNestedObjects(profile, ['classification'])
    },
    {
      id: 305,
      title: 'No. of Beds',
      value: getNestedObjects(profile, ['bed_capacity'])
    },
    {
      id: 306,
      title: 'PhilHealth Accreditation no.',
      value: getNestedObjects(profile, ['philhealth', 'accreditation_number']),
      title2: 'PHILHEALTH EXP.',
      value2:
        profile.philhealth && profile.philhealth.expiration_date
          ? MonthdateFormat(profile.philhealth.expiration_date)
          : ''
    },
    {
      id: 307,
      title: 'CHECK PAYEE',
      value: getNestedObjects(profile, ['treasury', 'payee'])
    },
    { id: 308, title: 'PROCESS TYPE', value: getNestedObjects(profile, ['process_type']) },
    { id: 309, title: 'CREDIT LIMIT', value: getNestedObjects(profile, ['treasury', 'credit_limit']) },
    { id: 310, title: 'PAYMENT TERMS', value: getNestedObjects(profile, ['treasury', 'payment_terms']) },
    {
      id: 311,
      title: 'TIN STATUS',
      value: getTinStatus(getNestedObjects(profile, ['treasury', 'tin_status'])),
      title2: 'TAX RATE',
      value2: getNestedObjects(profile, ['treasury', 'vat', 'tax_rate'])
    },
    { id: 312, title: 'FACILITY discount', value: getNestedObjects(profile, ['treasury', 'discount']) },
    {
      id: 313,
      title: 'Date of affiliation',
      value: MonthdateFormat(profile.affiliation_status && profile.affiliation_status.date_submitted)
    },
    {
      id: 314,
      title: 'Date of disaffiliation',
      value: profile.disaffiliation && profile.disaffiliation.date ? MonthdateFormat(profile.disaffiliation.date) : ''
    },
    {
      id: 315,
      title: 'Remarks',
      value: getNestedObjects(profile, ['treasury', 'special_instruction'])
    }
  ]

  const HospitalInformationData = React.useMemo(() => HospitalInformation, [HospitalInformation])

  return (
    <PartnerViewInformation
      data={HospitalInformationData}
      facilityLabel={assignments}
      type={'facility'}
      listStyles={classes.listStyles}
      firstColumStyle={classes.firstRow}
      secondColumnStyle={classes.secondRow}
    />
  )
}

export default PartnerHospitalInformation
