import isEmpty from 'lodash/isEmpty'
import Joi from '@hapi/joi'

const notRequiredFields = [
  'sub',
  'other',
  'pmaNumber',
  's2Number',
  'ptrNumber',
  'processType',
  'paymentTerms',
  'professionalFee',
  'withTax',
  'dateOfAffiliation',
  'dateOfDisaffiliation',
  'ahmopi',
  'exclusiveTo',
  'facility',
  'status',
  'assignments',
  'philExp',
  'processType',
  'id',
  'facilityAssignment'
]

const schema = Joi.object({
  first_name: Joi.string()
    .label('First name')
    .required(),
  last_name: Joi.string()
    .label('Last name')
    .required(),
  middle_name: Joi.string()
    .label('Middle name')
    .required(),
  birthdate: Joi.date()
    .label('Birth Date')
    .required(),
  civilStatus: Joi.string()
    .label('Civil status')
    .required(),
  gender: Joi.string()
    .label('Gender')
    .required(),
  main: Joi.string()
    .label('Main specification')
    .required(),
  major: Joi.string()
    .label('Major Specialization')
    .required(),
  sub: Joi.string().label('Sub Specialization'),
  other: Joi.string().label('Other Specialization'),
  prcNumber: Joi.number()
    .label('PRC Number')
    .required(),
  prcExp: Joi.date()
    .label('PRC Expiration')
    .required(),
  philNumber: Joi.number()
    .label('Philhealth Accreditation No.')
    .max(12)
    .min(12)
    .required(),
  philExp: Joi.date().label('Phil Health Expiration Date'),
  pdaNumber: Joi.number().label('PDA Number'),
  tiNumber: Joi.number()
    .label('Tin Number')
    .max(12)
    .min(12)
    .required(),
  tinStatus: Joi.string()
    .label('Tin Status')
    .required(),
  vatTaxRate: Joi.number().label('Tax Rate').required(),
  processType: Joi.string()
    .label('Process Type')
    .required(),
  paymentTerms: Joi.string().label('Payment of Terms'),
  professionalFee: Joi.number().label('Professional Fee'),
  withTax: Joi.number().label('WithTax'),
  clinicSched: Joi.string()
    .label('Clinic Schedule')
    .required(),
  facilityName: Joi.string()
    .label('Medical Facility')
    .required(),
  facilityAddress: Joi.string()
    .label('Address')
    .required(),
  dateOfAffiliation: Joi.date().label('Date of Affiliation'),
  dateOfDisaffiliation: Joi.date().label('Date of Disaffiliation'),
  onHoldReason: Joi.string()
    .label('On Hold Reason')
    .required()
})

export default function DoctorValidation (form) {
  let errors = {}
  const result = schema.validate(form, { abortEarly: false })

  if (result.error && result.error.details.length > 0) {
    for (const i of result.error.details) {
      errors[i.path[0]] = i.message

      if (typeof form['philNumber'] !== 'undefined') {
        if (form['philNumber'].length === 0) {
          delete errors['philNumber']
        } else {
          if (form['philNumber'].length === 12) {
            delete errors['philNumber']
          }
        }
      }

      if (form['tiNumber'].length === 0) {
        delete errors['tiNumber']
      } else {
        if (form['tiNumber'].length === 12) {
          delete errors['tiNumber']
        }
      }

      if (form['pdaNumber'].length === 0) {
        delete errors['pdaNumber']
      }

      if (form['status'] !== 'On-Hold') {
        delete errors['onHoldReason']
      }

      if (notRequiredFields.includes(i.path[0])) {
        delete errors[i.path[0]]
      }

      const taxRatePattern = new RegExp(/^(\d{0,2}(\.\d{1,5})?|100(\.00?)?)$/)

      if (!taxRatePattern.test(form['vatTaxRate'])) {
        errors['vatTaxRate'] = 'Tax Rate must not exceed 100%'
      } else {
        delete errors['vatTaxRate']
      }
    }
  }

  console.log('errors', errors)

  return {
    errors,
    isValid: isEmpty(errors)
  }
}
