import React from 'react'
import Button from '@material-ui/core/Button'
import classes from './Footer.module.scss'

const buttonDisabled = {
  background: '#dddfe3',
  color: '#f5f7fb',
  margin: '0 10px',
  textTransform: 'none'
}

const buttonColorRed = {
  background: '#fd5451',
  color: '#f5f7fb',
  margin: '0 10px',
  textTransform: 'none'
}

const buttonColorGreen = {
  background: '#3ab77d',
  color: '#f5f7fb',
  margin: '0 10px',
  textTransform: 'none'
}

const FirstPageFooter = props => {
  const {
    readOnly = false,
    onClickNextPage = () => {},
    onClickReturnPage = () => {},
    onClickSavePage = () => {}
  } = props
  return (
    <React.Fragment>
      <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Button variant='contained' style={{ ...buttonColorRed, marginRight: 'auto' }} onClick={onClickReturnPage}>
          Cancel
        </Button>
        <Button
          disabled={readOnly}
          variant='contained'
          style={readOnly ? buttonDisabled : buttonColorGreen}
          onClick={onClickSavePage}
        >
          <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
            <p style={{ margin: '0px' }}>Save</p>
            <p style={{ fontSize: 'x-small', margin: '0px' }}>auto-saved 5 min ago</p>
          </div>
        </Button>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickNextPage}>
          Next
        </Button>
      </div>
    </React.Fragment>
  )
}

const SecondPageFooter = props => {
  const { readOnly, onClickNextPage = () => {}, onClickReturnPage = () => {}, onClickSavePage = () => {} } = props
  return (
    <React.Fragment>
      <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickReturnPage}>
          Return
        </Button>
        <Button
          disabled={readOnly}
          variant='contained'
          style={readOnly ? buttonDisabled : buttonColorGreen}
          onClick={onClickSavePage}
        >
          <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
            <p style={{ margin: '0px' }}>Save</p>
            <p style={{ fontSize: 'x-small', margin: '0px' }}>auto-saved 5 min ago</p>
          </div>
        </Button>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickNextPage}>
          Next
        </Button>
      </div>
    </React.Fragment>
  )
}

const ThirdPageFooter = props => {
  const { readOnly, onClickReturnPage = () => {}, onClickSavePage = () => {}, onClickSubmitPage = () => {} } = props
  return (
    <React.Fragment>
      <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickReturnPage}>
          Return
        </Button>
        <Button
          disabled={readOnly}
          variant='contained'
          style={readOnly ? buttonDisabled : buttonColorGreen}
          onClick={onClickSavePage}
        >
          <div style={{ display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
            <p style={{ margin: '0px' }}>Save</p>
            <p style={{ fontSize: 'x-small', margin: '0px' }}>auto-saved 5 min ago</p>
          </div>
        </Button>
        <Button
          disabled={readOnly}
          variant='contained'
          style={readOnly ? buttonDisabled : buttonColorGreen}
          onClick={onClickSubmitPage}
        >
          Submit
        </Button>
      </div>
    </React.Fragment>
  )
}

const WithCloseAndNextButton = props => {
  const { onClickReturnPage = () => {}, onClickNextPage } = props
  return (
    <React.Fragment>
      <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Button style={buttonColorRed} onClick={onClickReturnPage} variant='contained'>
          Close
        </Button>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickNextPage}>
          Next
        </Button>
      </div>
    </React.Fragment>
  )
}

const WithDisapprovedButton = props => {
  const { readOnly, onClickDisapprove = () => {}, onClickApproval = () => {}, onClickCondition = () => {} } = props
  return (
    <React.Fragment>
      <div style={{ display: 'flex', justifyContent: 'flex-end' }}>
        <Button style={buttonColorRed} onClick={onClickDisapprove} variant='contained'>
          Disapprove
        </Button>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickCondition}>
          Approve with Condition
        </Button>
        <Button variant='contained' style={buttonColorGreen} onClick={onClickApproval}>
          Recommend for Approval
        </Button>
      </div>
    </React.Fragment>
  )
}

const Footer = props => {
  const {
    group,
    role,
    readOnly = false,
    currentPage,
    onClickNextPage = () => {},
    onClickReturnPage = () => {},
    onClickSavePage = () => {},
    onClickSubmitPage = () => {},
    onClickDisapprove = () => {},
    onClickApproval = () => {},
    onClickCondition = () => {}
  } = props

  const generateCurrentFooter = currentPage => {
    if (group === 'hospital') {
      switch (currentPage) {
        case 'roomAndBoard':
          return role === 'OICProcessor' ? (
            <FirstPageFooter
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
            />
          ) : (
            <WithCloseAndNextButton
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
            />
          )
        case 'services':
          return role === 'OICProcessor' ? (
            <SecondPageFooter
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
            />
          ) : (
            <WithCloseAndNextButton
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
            />
          )
        case 'generalAppearance':
          return role === 'OICProcessor' ? (
            <ThirdPageFooter
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
              onClickSubmitPage={onClickSubmitPage}
            />
          ) : (
            <WithDisapprovedButton
              readOnly={readOnly}
              onClickDisapprove={onClickDisapprove}
              onClickApproval={onClickApproval}
              onClickCondition={onClickCondition}
            />
          )
      }
    } else {
      switch (currentPage) {
        case 'services':
          return role === 'OICProcessor' ? (
            <FirstPageFooter
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
            />
          ) : (
            <WithCloseAndNextButton
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
            />
          )
        case 'generalAppearance':
          return role === 'OICProcessor' ? (
            <ThirdPageFooter
              readOnly={readOnly}
              onClickNextPage={onClickNextPage}
              onClickReturnPage={onClickReturnPage}
              onClickSavePage={onClickSavePage}
              onClickSubmitPage={onClickSubmitPage}
            />
          ) : (
            <WithDisapprovedButton
              readOnly={readOnly}
              onClickDisapprove={onClickDisapprove}
              onClickApproval={onClickApproval}
              onClickCondition={onClickCondition}
            />
          )
      }
    }
  }

  return (
    <React.Fragment>
      <div className={classes.root}>{generateCurrentFooter(currentPage)}</div>
    </React.Fragment>
  )
}

export default Footer
