import React from 'react'
import ReactTable from '../Helpers/TableDisplay'
import { totalMedicalPartnersCol } from '../../../../../Constant/Reports/ColumnsHeaders'
import ReportServices from '../../../../../Services/Api/Reports/Reports'
import classes from '../scss/TableDisplay.module.scss'
import Button from '@material-ui/core/Button'
import PreviewDownload from '../Helpers/PreviewDownload'
import moment from 'moment'
import DropDown from '../../Components/CustomDrowpdown'
import AddressChaining from '../../../../../Constant/chaining-address'
import { fetchRegion } from '../../Components/Utils/utils'

const MedicalPartners = () => {
  const [medicalPartners, setMedicalPartners] = React.useState([])
  const [downloadData, setDownloadData] = React.useState([])
  const [openPreview, setOpenPreview] = React.useState(false)
  const [locationFilterData, setLocationFilterData] = React.useState([])
  const medicalPartnersRef = React.useRef('')
  const [locationFilterValue, setLocationFilterValue] = React.useState(false)
  const [isFetching, setIsFetching] = React.useState({})
  const [regions, setRegions] = React.useState([])
  const [province, setProvince] = React.useState([])
  const [cities, setCities] = React.useState([])
  const [barangays, setBarangays] = React.useState([])
  const [locationFilter, setLocationFilter] = React.useState([])
  const [medicalDoctorLocationFilter, setMedicalDoctorLocationFilter] = React.useState('')
  React.useEffect(() => {
    fetchData(medicalDoctorLocationFilter)
  }, [])

  React.useEffect(() => {
    gettingAllRegions()
  }, [medicalPartners])

  React.useEffect(() => {
    gettingAllProvinces()
  }, [regions])

  React.useEffect(() => {
    gettingAllCities()
  }, [province])

  React.useEffect(() => {
    gettingBarangays()
  }, [cities])


  React.useEffect(() => {
    // fetchRegion(setRegions)
  }, [])
  const fetchData = async (location) => {
    const { data } = await ReportServices.getTotalMedicalPartnersData(location)
    const dataArr = data.data.dataResult
    dataArr.forEach((value, index) => {
      dataArr[index].dataCol = {
        location: value.location,
        hospitalCount: value.hospitalCount,
        medicalClinicCount: value.medicalClinicCount,
        singleDoctorCount: value.singleDoctorCount,
        multipleDoctorCount: value.multipleDoctorCount,
        singleDentistCount: value.singleDentistCount,
        multipleDentistCount: value.multipleDentistCount,
        dentalClinicCount: value.dentalClinicCount
      }
    })
    setMedicalPartners(dataArr)
    setLocationFilter(data.data.locations.map(function (obj, key) {
      return { id: key, text: obj.location, value: obj.location }
    }))
  }

  const locationCategories = [
    { id: 1, text: 'Region', value: 'Region' },
    { id: 2, text: 'Province', value: 'Province' },
    { id: 3, text: 'City/Municipality', value: 'City/Municipality' },
    { id: 4, text: 'Barangay', value: 'Barangay' }
  ]

  const handleDropdown = async ({ target: select }) => {
    setMedicalDoctorLocationFilter(select.value)
    switch (select.value) {
      case 'Region':
        getRegions()
        break
      case 'Province':
        getProvince()
        break
      case 'City/Municipality':
        getCities()
        break
      case 'Barangay':
        getBarangays()
        break
      default:
        return ''
    }
  }

  const filterData = (data = [], index) => {
    let result = []
    for (let a of data) {
      for (let medical of medicalPartners) {
        const medicalLocation = medical.location.split('/')
        if (medicalLocation[index] === a.value) {
          result.push(a)
        }
      }
    }
    return getUnique(result, 'value')
  }

  const getUnique = (arr, comp) => {

    // store the comparison  values in array
    const unique = arr.map(e => e[comp])

      // store the indexes of the unique objects
      .map((e, i, final) => final.indexOf(e) === i && i)

      // eliminate the false indexes & return unique objects
      .filter((e) => arr[e]).map(e => arr[e])

    return unique
  }

  const getRegions = () => {
    setLocationFilterData(regions)
  }

  const getProvince = () => {
    setLocationFilterData(province)
  }

  const getCities = () => {
    setLocationFilterData(cities)
  }

  const getBarangays = () => {
    setLocationFilterData(barangays)
  }

  const gettingAllRegions = () => {
    const data = AddressChaining.regions
    const result = filterData(data, 0)
    setRegions(result)
  }

  const gettingAllProvinces = () => {
    let data = []
    for (let region of AddressChaining.regions) {
      // AddressChaining.getProvince(region.value)
      const provinceArr = AddressChaining.provinceArr
      data = [...data, ...provinceArr]
    }
    const result = filterData(data, 1)
    setProvince(result)
  }

  const gettingAllCities = () => {
    var data = []
    for (let region of AddressChaining.regions) {
      AddressChaining.getProvince(region.value)
      const provinceArr = AddressChaining.provinceArr
      for (let prov of provinceArr) {
        AddressChaining.getCityMunicipality(prov.value)
        const cityArr = AddressChaining.cityArr
        data = [...data, ...cityArr]
      }
    }

    const result = filterData(data, 2)
    setCities(result)
  }

  const gettingBarangays = () => {
    let data = []
    for (let region of AddressChaining.regions) {
      AddressChaining.getProvince(region.value)
      const provinceArr = AddressChaining.provinceArr
      for (let prov of provinceArr) {
        AddressChaining.getCityMunicipality(prov.value)
        const cityArr = AddressChaining.cityArr
        for (let city of cityArr) {
          AddressChaining.getBarangays(city.value)
          const barangayArr = AddressChaining.barangayArr
          data = [...data, ...barangayArr]
        }
      }
    }

    const result = filterData(data, 3)
    setBarangays(result)
  }

  return (
    <div>
      <div className={classes.rightBody}>
        <div style={{ width: '200px' }}>
          <DropDown onChange={handleDropdown} items={locationCategories} value={medicalDoctorLocationFilter} label='Location Category' />
        </div>
        <div id='root' className={classes.root} style={{ marginTop: '20px' }}>
          <ReactTable columns={totalMedicalPartnersCol} data={medicalPartners} setDownloadData={setDownloadData} locationFilterData={locationFilterData} medicalDoctorLocationFilter={medicalDoctorLocationFilter} fetchData={fetchData} />
        </div>
      </div>
      <div className={classes.previewButton}>
        <Button onClick={() => setOpenPreview(true)} variant='contained' className={classes.DownloadPreviewBtnMedical}>Download Preview</Button>
        <PreviewDownload openPreview={openPreview} setOpenPreview={setOpenPreview} downloadData={downloadData} headerColumn={totalMedicalPartnersCol} filename={`${moment().format('YYYYMMDD')}-medical-partners.csv`} />
      </div>
    </div>

  )
}

export default MedicalPartners
