import React from 'react'
import RadioGroup from '@material-ui/core/RadioGroup'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Radio from '@material-ui/core/Radio'
import Box from '@material-ui/core/Box'
import classes from './TypeOcularInspection.module.scss'
import Grid from '@material-ui/core/Grid'
import CustomTextField from 'SharedComponents/TextField/CustomTextField'

const TypeOccularInspection = props => {
  const {
    readOnly = false,
    inspectorType = 'request',
    inspectorName = '',
    dispatch = {},
    onChange = () => {},
    errors = {}
  } = props

  const onChangeType = type => {
    if (type === 'walkIn') return dispatch(onChange({ type: 'walkIn', name: '' }))
    return dispatch(onChange({ type: type, name: '' }))
  }

  const onChangeName = e => {
    dispatch(onChange({ name: e.target.value, type: inspectorType }))
  }

  return (
    <div id='typeOfOcularInspectionRoot' className={classes.root}>
      <h2 id='typeOfOcularInspectionTitle' className={classes.title}>
        Type of Ocular Inspection
      </h2>
      <div id='typeOfOcularInspectionContent' className={classes.content}>
        <RadioGroup
          className={classes.componentRadioGroup}
          value={inspectorType}
          onChange={readOnly ? () => {} : e => onChangeType(e.target.value)}
          row
        >
          <div id='typeOfOcularInspectionFirstRow' className={classes.row}>
            <div className={classes.radioFieldContainer}>
              <h3
                className={
                  inspectorType === 'request' ? classes.componentRadioLabel : classes.componentRadioLabelDisable
                }
                onClick={readOnly ? () => {} : () => onChangeType('request')}
              >
                Request of Client
              </h3>
              <FormControlLabel
                value='request'
                control={
                  <Radio
                    className={
                      inspectorType === 'request' ? classes.componentRadioButton : classes.componentRadioButtonDisable
                    }
                  />
                }
              />
            </div>
            <div className={readOnly ? classes.readOnlyInputFieldContainer : classes.inputFieldContainer}>
              <h3
                className={
                  inspectorType === 'request'
                    ? classes.componentRadioInputLabel
                    : classes.componentRadioInputLabelDisable
                }
              >
                Name of Requesting Client * :
              </h3>
              <div>
                {readOnly ? (
                  inspectorType === 'request' ? (
                    <h3
                      style={{ marginLeft: '20px' }}
                      className={
                        inspectorType === 'request'
                          ? classes.componentRadioInputLabel
                          : classes.componentRadioInputLabelDisable
                      }
                    >
                      {inspectorName}
                    </h3>
                  ) : (
                    ''
                  )
                ) : (
                  <CustomTextField
                    id='typeOcularInspectionRequestedName'
                    disabled={!(inspectorType === 'request')}
                    value={inspectorType === 'request' ? inspectorName : ''}
                    style={{ width: '330px' }}
                    onChange={e => onChangeName(e)}
                    error={inspectorType === 'request' ? errors.name : ''}
                  />
                )}
              </div>
            </div>
          </div>
          <div id='typeOfOcularInspectionSecondRow' className={classes.row}>
            <div className={classes.radioFieldContainer}>
              <h3
                className={
                  inspectorType === 'walkIn' ? classes.componentRadioLabel : classes.componentRadioLabelDisable
                }
                onClick={readOnly ? () => {} : () => onChangeType('walkIn')}
              >
                Walk-In
              </h3>
              <FormControlLabel
                value='walkIn'
                control={
                  <Radio
                    className={
                      inspectorType === 'walkIn' ? classes.componentRadioButton : classes.componentRadioButtonDisable
                    }
                  />
                }
              />
            </div>
          </div>
          <div id='typeOfOcularInspectionThirdRow' className={classes.row}>
            <div className={classes.radioFieldContainer}>
              <h3
                className={
                  inspectorType === 'referral' ? classes.componentRadioLabel : classes.componentRadioLabelDisable
                }
                onClick={readOnly ? () => {} : () => onChangeType('referral')}
              >
                Referral
              </h3>
              <FormControlLabel
                value='referral'
                control={
                  <Radio
                    className={
                      inspectorType === 'referral' ? classes.componentRadioButton : classes.componentRadioButtonDisable
                    }
                  />
                }
              />
            </div>
            <div className={readOnly ? classes.readOnlyInputFieldContainer : classes.inputFieldContainer}>
              <h3
                className={
                  inspectorType === 'referral'
                    ? classes.componentRadioInputLabel
                    : classes.componentRadioInputLabelDisable
                }
              >
                Name of Referring Party * :
              </h3>
              <div>
                {readOnly ? (
                  inspectorType === 'referral' ? (
                    <h3
                      style={{ marginLeft: '20px' }}
                      className={
                        inspectorType === 'referral'
                          ? classes.componentRadioInputLabel
                          : classes.componentRadioInputLabelDisable
                      }
                    >
                      {inspectorName}
                    </h3>
                  ) : (
                    ''
                  )
                ) : (
                  <CustomTextField
                    id='typeOcularInspectionReferralName'
                    disabled={!(inspectorType === 'referral')}
                    value={inspectorType === 'referral' ? inspectorName : ''}
                    style={{ width: '330px' }}
                    onChange={e => onChangeName(e)}
                    error={inspectorType === 'referral' ? errors.name : ''}
                  />
                )}
              </div>
            </div>
          </div>
        </RadioGroup>
      </div>
    </div>
  )
}

export default React.memo(TypeOccularInspection, (prevProps, nextProps) => {
  try {
    if (
      prevProps.readOnly === nextProps.readOnly &&
      prevProps.inspectorType === nextProps.inspectorType &&
      prevProps.inspectorName === nextProps.inspectorName &&
      prevProps.errors.name === nextProps.errors.name
    ) {
      return true
    }
    return false
  } catch (err) {
    return false
  }
})
