import 'date-fns'
import React from 'react'
import MomentUtils from '@date-io/moment'
import Paper from '@material-ui/core/Paper'
import InputBase from '@material-ui/core/InputBase'
import Typography from '@material-ui/core/Typography'
import IconButton from '@material-ui/core/IconButton'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { MuiPickersUtilsProvider, KeyboardDatePicker } from '@material-ui/pickers'
import { DateFormatter } from 'Utilities/TextFormatter'

import classes from './DatePicker.module.scss'

/**
 * Props
 * id
 * label
 * required
 * value - should be date type
 * format
 * onChange
 *
 * other props
 * https://material-ui-pickers.dev/api/KeyboardDatePicker
 */
export default function DatePicker (props) {
  const {
    id,
    label,
    editInputWidth,
    required,
    disableToolbar,
    disabled,
    value,
    format,
    readOnly,
    name = '',
    onChange,
    error,
    ...other
  } = props
  const [open, setOpen] = React.useState(false)
  const [selectedDate, setSelectedDate] = React.useState()

  React.useEffect(() => {
    setSelectedDate(new Date(value))
  }, [value])

  const handleOnClose = () => {
    setOpen(false)
  }
  const handleDateChange = date => {
    setOpen(false)
    setSelectedDate(date)
    if (typeof onChange === 'function') onChange(name, date)
  }
  return (
    <div className={classes.root}>
      {label && (
        <Typography variant='subtitle2' className={classes.label}>
          {label}
          {!readOnly && required ? ' *' : ''}
        </Typography>
      )}
      <div className={classes.wrapper}>
        {readOnly ? (
          //  Read Only Value
          <Typography id={props.id} variant='subtitle2' className={`${classes.label} ${classes['read-only-value']}`}>
            {DateFormatter(props.value)}
          </Typography>
        ) : (
          <MuiPickersUtilsProvider utils={MomentUtils}>
            <KeyboardDatePicker
              TextFieldComponent={p => (
                <React.Fragment>
                  <Paper className={`${classes['text-field']} ${error && classes['has-error']}`}>
                    <InputBase
                      id={id}
                      disabled={disabled}
                      className={editInputWidth || classes.input}
                      value={p.value}
                      style={{minWidth: '100px'}}
                    />

                    <div className={classes['date-button']}>
                      <IconButton disabled={disabled} className={classes.button} size='small' onClick={() => setOpen(true)}>
                        <FontAwesomeIcon icon='calendar-day' className={classes.icon} />
                      </IconButton>
                    </div>
                  </Paper>
                  {error && <span className={classes.error}>{error}</span>}
                </React.Fragment>
              )}
              name={name}
              value={selectedDate}
              format={'MM/DD/YYYY' || format}
              open={open}
              // disableFuture={disabled}
              // disablePast={disabled}
              onClose={handleOnClose}
              onChange={handleDateChange}
              readOnly={readOnly}
              {...other}
            />
          </MuiPickersUtilsProvider>
        )}
      </div>
    </div>
  )
}
