import React from 'react'
import { Switch, Route } from 'react-router-dom'
import history from '../Components/History/History'
import { Provider } from 'react-redux'

// Pages of Affiliation Approver
import Dashboard from './Pages/Dashboard/Dashboard'
import SpecialOtherServices from './Pages/SpecialOtherServices/SpecialOtherServices'
import DoctorAffiliation from './Pages/DoctorAffiliation/DoctorAffiliation'
import HospitalAffiliation from './Pages/HospitalAffiliation/HospitalAffiliation'
import DentalClinicAffiliation from './Pages/DentalClinicAffiliation/DentalClinicAffiliation'
import DentalClinicAffiliationOI from './Pages/DentalClinicAffiliationOI/DentalClinicAffiliationOI'
import MedicalClinicAffiliation from './Pages/MedicalClinicAffiliation/MedicalClinicAffiliation'
import HospitalAffiliationOI from './Pages/HospitalAffiliationOI/HospitalAffiliationOI'
import MedicalClinicOI from './Pages/MedicalClinicAffiliationOI/MedicalClinicAffiliationOI'

// Store
import Store from '../../Store'

// Services
import { SideBarService } from 'SharedComponents/SideBarComponent/SideBarComponent'

const sideBarItems = [
  {
    label: 'Dashboard',
    icon: 'home',
    to: '/affiliation-approver'
  },
  {
    label: 'Partners',
    icon: 'users',
    to: '/affiliation-approver'
  },
  {
    label: 'Tickets',
    icon: 'list',
    to: '/affiliation-approver'
  },
  {
    label: 'Reports',
    icon: 'chart-bar',
    to: '/affiliation-approver'
  },
  {
    label: 'User',
    icon: 'user',
    to: '/affiliation-approver'
  },
  {
    label: 'Table Maintenance',
    icon: 'calendar-alt',
    to: '/affiliation-approver'
  }
]

const AffiliationApproverRoutes = props => {
  React.useEffect(() => {
    SideBarService.setItems(sideBarItems)
    props.history.push('/affiliation-approver')
  }, [])

  return (
    <React.Fragment>
      <Provider store={Store}>
        <Switch>
          <Route path='/affiliation-approver' exact render={props => <Dashboard {...props} />} />

          <Route path='/approve-medical-clinic-affiliation/:id' exact render={props => <MedicalClinicAffiliation {...props} />} />
          <Route path='/approve-hospital-affiliation/:id' exact render={props => <HospitalAffiliation {...props} />} />
          <Route path='/approve-doctor-affiliation/:id' exact render={props => <DoctorAffiliation {...props} />} />
          <Route path='/approve-dental-clinic-affiliation/:id' exact render={props => <DentalClinicAffiliation {...props} />} />
          <Route path='/approve-other-service/:id' exact render={props => <SpecialOtherServices {...props} />} />

          <Route path='/approveoi-dental-clinic-affiliation/:id' exact render={props => <DentalClinicAffiliationOI {...props} />} />
          <Route path='/approveoi-hospital-affiliation/:id' exact render={props => <HospitalAffiliationOI {...props} />} />
          <Route path='/approveoi-medical-clinic-affiliation/:id' exact render={props => <MedicalClinicOI {...props} />} />

        </Switch>
      </Provider>
    </React.Fragment>
  )
}

export default AffiliationApproverRoutes
